/* Default state for initialization */
const defaultState = Immutable.OrderedMap({});

const getChat = (state, id) => state.get(id);

const defaultChat = id => ({
  id,
  visitorId: -1,
  maxMessageId: -1,
  messages: Immutable.List(),
  sourceMessages: [],
  typingMessage: '',
  unreadMessages: 0,
  lastRepliedTime: null,
  isPinned: false,
  wrapupSubmitted: false,
  showWrapupRequired: false,
  ifShowWrapupButton: false,
  isUnfinishedWrapup: false,
  ifWrapupLoading: false,
  ifWrapupSubmitSuccess: false,
  wrapupSubmitError: '',
  ifWrapupHistoryLoading: false,
  autoRefreshDataFlag: false,
  ifHistoryDetailLoading: false,
  ifHistoryListLoading: false,
  wrapupDetailsOpenId: -1,
  ifPopTranslation: false,
  enableTranslation: false,
  translationLanguage: 0,
  endTime: null,
  g2mStartUrl: '',
  errorMessageForChat: '',
  scrollTop: -1,
  ifShowNotify: false,
  audioVideoChatWidth: 500,
  ifVisitorSupportWebrtc: true,
  ifShowGotoMeetingNotify: false,
  files: [],
});

const addSystemMessageGen = (mapFn = _.identity, needNotify = false) =>
  /**
   * Add one system message to message list.
   *
   * @param {Immutable.Set} chat - Chat state
   * @param {{payload:object}} action - Action payload
   * @return {Immutable.Set} new state.
   */
  (chat, { payload }, isReading = false) => {
    const message = _.pick(payload, ['id', 'time', 'type', 'message']);
    message.type = messageType.system;
    if (message.message === '' || message.message === null) {
      return chat;
    }
    const updatedChat = chat.update('messages', messages => messages.push([mapFn(message, payload)]));
    if (needNotify) {
      if (isReading) {
        return updatedChat.set('unreadMessages', 0);
      }
      return updatedChat.update('unreadMessages', unreadMessages => (unreadMessages + 1));
    }
    return updatedChat;
  };

const mergeMessages = (messages, message) =>
  messages.update(
    messages.size - 1,
    messageGroup => (messageGroup ? messageGroup.concat(message) : [message])
  );

const shouldMerge = (lastMessageGroup, message) => {
  if (!lastMessageGroup) return false;
  const lastMessage = lastMessageGroup[0];
  if (!lastMessage) return false;
  if (lastMessage.type !== message.type) return false;
  if (message.type === messageType.agent && message.sender !== lastMessage.sender) return false;
  if (time.dateSubs(message.time, lastMessage.time) > 60 * 1000) return false;
  return true;
};

const addAgentMessage = (chat, message) => {
  const lastMessageGroup = chat.get('messages').last();
  return (
    chat.update('messages', (msg) => {
      if (shouldMerge(lastMessageGroup, message)) return mergeMessages(msg, message);
      return msg.push([message]);
    })
      .set('lastRepliedTime', null)
      .set('scrollTop', -1)
  );
};

const addVisitorMessage = (chat, message, isReading) => {
  const lastMessageGroup = chat.get('messages').last();
  return (
    chat.update('messages', (msg) => {
      if (shouldMerge(lastMessageGroup, message)) return mergeMessages(msg, message);
      return msg.push([message]);
    })
      .update('unreadMessages', unreadMessages => (isReading ? 0 : unreadMessages + 1))
      .update('lastRepliedTime', lastRepliedTime => lastRepliedTime || new Date())
      .set('typingMessage', '')
      .set('ifShowNotify', true)
  );
};

const addTextMessageHoF = isAgent => (chat, { payload }, isReading) => {
  const message = _.pick(payload, ['id', 'time', 'message', 'translatedMessage', 'sender']);
  message.type = isAgent ? messageType.agent : messageType.visitor;
  return isAgent ? addAgentMessage(chat, message) : addVisitorMessage(chat, message, isReading);
};

const enableTranslation = (chat, { payload }) =>
  addSystemMessageGen()(chat, { payload })
    .set('enableTranslation', true)
    .set('translationLanguage', parseInt(payload.sender, 0));

const disableTranlation = (chat, { payload }) =>
  addSystemMessageGen()(chat, { payload })
    .set('enableTranslation', false);

const addVisitorSubmitSecureForm = (chat, { payload }, isReading) => {
  const { sender: token, ...message } = _.pick(payload, ['sender', 'id', 'time', 'type', 'message']);
  message.type = messageType.system;
  const rsaKey = chat.get('secureFormKeys') === undefined ? null :
    chat.get('secureFormKeys').get(token);
  return (
    chat.update('messages', (messages) => {
      if (rsaKey != null) {
        return messages.push([message])
          .push([
            {
              id: token,
              time: message.time,
              type: messageType.secureForm,
              message: {
                status: secureFormStatus.init,
                token,
                rsaKey,
                content: null,
              },
            },
          ]);
      }
      return messages.push([message]);
    })
      .update('unreadMessages', unreadMessages => (isReading ? 0 : unreadMessages + 1))
  );
};

const addVisitorTextMessage = addTextMessageHoF(false);
const addAgentTextMessage = addTextMessageHoF(true);


const addAgentAddInvitationText = (chat, { payload }, isReading) => {
  const message = _.pick(payload, ['id', 'time', 'message', 'translatedMessage', 'sender']);
  message.type = messageType.agent;
  return chat.set('messages', Immutable.List([[message]]))
    .set('unreadMessages', (isReading ? 0 : 1));
};

const addAgentSendImageStart = (chat, { payload }) => {
  const message = _.pick(payload, ['id', 'time']);
  message.guid = payload.message;
  message.sender = payload.sender;// .slit(splitter);
  message.type = messageType.agent;
  return addAgentMessage(chat, message);
};

const addAgentSentImage = (chat, { payload }) => {
  const message = _.pick(payload, ['id', 'time']);
  const [url, fileName, sender] = payload.sender.split(splitter);
  const guid = url.substr(url.indexOf('file=') + 5, 32);
  _.assign(message, { url, fileName, sender, isImage: true, type: messageType.agent, guid });
  const index = chat.get('messages')
    .findIndex(messageGroup => messageGroup.some(msg => msg.guid === guid));
  if (!index) return chat.update('messages', messages => messages.push(message));
  return chat.update('messages', messages => messages.update(
    index,
    messageGroup => messageGroup.map(msg => (msg.guid === guid ? message : msg))
  ));
};

const addVisitorSentImage = (chat, { payload }) => {
  const message = _.pick(payload, ['id', 'time']);
  const [url, fileName, sender] = payload.sender.split(splitter);
  const guid = url.substr(url.indexOf('file=') + 5, 32);
  _.assign(message, { url, type: messageType.visitor, isImage: true, sender, fileName, guid });
  return addVisitorMessage(chat, message);
};

const addSentAttachmentHoF = isAgent => (chat, { payload }, isReading) => {
  const [url, fileName, sender] = payload.sender.split(splitter);
  const message = _.pick(payload, ['id', 'time']);
  _.assign(message, {
    url,
    fileName,
    sender,
    isImage: isImageSuffix(fileName),
    type: isAgent ? messageType.agent : messageType.visitor,
  });
  return isAgent ? addAgentMessage(chat, message) : addVisitorMessage(chat, message, isReading);
};

const updateTypingMessage = (chat, { payload }) =>
  chat.set('typingMessage', payload.message);

const getPromoteOfflineMessage = (message, payload) => {
  const text = message.message;
  return Object.assign(message, {
    message: text.replace(new RegExp(language.macroOfflineMessageLinkText, 'ig'), payload.sender),
  });
};

function setChatIfSupportWebrtc(chat, { payload }) {
  return chat.set('ifVisitorSupportWebrtc', payload.message.toLowerCase() === 'true');
}

function startP2PChat(chat) {
  const p2pChat = chat.get('p2pChat');
  if (p2pChat) {
    p2pChat.start();
  }
  return chat;
}

function stopP2PChat(chat) {
  const p2pChat = chat.get('p2pChat');
  if (p2pChat) {
    p2pChat.stop();
    return chat.set('p2pChat', null);
  }
  return chat;
}

function handleAudioVideoChatAction(avStatus, needNotify) {
  return (chat, action, isReading, currentAgent) => {
    let chat1 = addSystemMessageGen(undefined, needNotify)(chat, action, isReading);
    const payload = action.payload;
    if (!ifSupportWebrtc) return chat1;

    // unfinished wrapup
    if (currentAgent == null) return chat1;

    if (payload.type === chatAction.agentAudioChatRequest
      || payload.type === chatAction.agentVideoChatRequest
      || payload.type === chatAction.agentAudioChatAccept
      || payload.type === chatAction.agentVideoChatAccept) {
      chat1 = chat1.set('audioVideoChatAgentId', payload.sender);
    }

    const id = parseInt(chat1.get('audioVideoChatAgentId'), 10);
    if (!id || (id === currentAgent.id)) {
      // 是我的聊天
      if (avStatus === audioVideoChatStatus.audioChatting
        || avStatus === audioVideoChatStatus.videoChatting) {
        return startP2PChat(chat1);
      }

      if (avStatus === audioVideoChatStatus.notStart) {
        return stopP2PChat(chat1.set('audioVideoChatAgentId', 0));
      }
    } else {
      // 不是我的聊天
      if (avStatus === audioVideoChatStatus.notStart) {
        return stopP2PChat(chat1.set('audioVideoChatAgentId', ''));
      }
      return chat1;
    }

    return chat1;
  };
}

