const mapMessage = (chat, action, isReading, currentAgent, visitor) => {
  if (!chatActionMapping[action.payload.type]) return chat;
  return chatActionMapping[action.payload.type](chat, action, isReading, currentAgent, visitor);
};

const messageUpdate = (state, action, chatId, isReading, currentAgent, visitor) => {
  const chat = getChat(state, chatId);
  const maxMessageId = chat.get('maxMessageId');
  const payload = action.payload;
  const messageId = payload.id;
  if (messageId > maxMessageId) {
    if (!chat) return state;
    const newChat = mapMessage(chat, action, isReading, currentAgent, visitor)
      .update('sourceMessages', msgs => _.concat(msgs, payload))
      .set('maxMessageId', messageId);
    return state.mergeIn([chatId], newChat);
  }
  return state;
};

function newChatWithDefault(chatId, visitorId) {
  return Immutable.Map(Object.assign(defaultChat(), {
    id: chatId,
    visitorId,
  }));
}

function newChatFromUnfinishedWrapup(chatId, visitor) {
  return newChatWithDefault(chatId, visitor.id)
    .set('ifShowWrapupButton', true)
    .set('isUnfinishedWrapup', true);
}

/* Mapping from action.type to corresponding state manipulation function */
const stateMapping = {
  [types.updateScrollTop](state, action) {  // timeout call
    const chat = getChat(state, action.meta);
    if (chat) {
      return state.setIn([action.meta, 'scrollTop'], action.payload);
    }
    return state;
  },
  [types.newChat](state, action) {
    const chatId = action.meta;
    const visitorId = action.payload;
    let chat = newChatWithDefault(chatId, visitorId);
    const entry = state.findEntry(c => c.get('visitorId') === visitorId &&
      !c.get('isUnfinishedWrapup'));
    if (entry != null) {
      chat = chat.set('isPinned', entry[1].get('isPinned'));
      return state.delete(entry[0]).set(chatId, chat);
    }
    return state.set(chatId, chat);
  },
  [types.deleteChat](state, action) {
    return state.delete(action.meta);
  },
  [types.newUnfinishedWrapup](state, action) {
    const { visitor, message } = action.payload;
    const chat = newChatFromUnfinishedWrapup(visitor.chatId, visitor);
    const commandTypeMapReverse = reverseSchema(commandTypeMap);
    const newChat = message.reduce((rchat, msg) =>
      mapMessage(rchat,
        { payload: Object.assign({}, msg, { type: commandTypeMapReverse[msg.type] }) }, true)
      , chat);
    return state.set(visitor.chatId, newChat);
  },
  [types.chatEnded](state, action, totalState) {
    const chatId = action.meta;
    const chat = getChat(state, chatId);
    if (!chat) return state;
    const newState = state.setIn([action.meta, 'endTime'], Date.now())
                          .setIn([action.meta, 'typingMessage'], '');
    if (selectors.ifRequireWrapup(totalState, chatId)) {
      if (!chat.get('wrapupSubmitted')) {
        memoryStore.set(memoryStoreKey.cacheWrapupTabSelected, true, chatId);
      }
      return newState.setIn([action.meta, 'ifShowWrapupButton'], true)
                     .setIn([action.meta, 'isUnfinishedWrapup'], true);
    }
    return newState;
  },
  [types.tryCloseChat](state, action, totalState) {
    if (!selectors.getIsUnfinishedWrapup(totalState, action.meta) ||
      selectors.getWrapupSubmitted(totalState, action.meta)) {
      return state.delete(action.meta);
    }
    return state.setIn([action.meta, 'showWrapupRequired'], true);
  },
  [chatListActions.togglePin](state, action) {
    return state.updateIn([action.payload, 'isPinned'], flag => !flag);
  },
  [chatListActions.setSelected](state, action) {
    return state.setIn([action.payload, 'unreadMessages'], 0);
  },
  [wrapupTypes.handleSubmitWrapup](state, action) {
    const chat = getChat(state, action.meta);
    if (!chat) return state;
    const newChat = chat.merge(action.payload);
    return state.mergeIn([action.meta], newChat);
  },
  [wrapupTypes.wrapupHistory](state, action) {
    const chat = getChat(state, action.meta);
    if (!chat) return state;
    const newChat = chat.set('ifWrapupHistoryLoading', action.payload.ifWrapupHistoryLoading)
      .set('autoRefreshDataFlag', true);
    return state.mergeIn([action.meta], newChat);
  },
  [wrapupTypes.wrapupDetails](state, action) {
    const detailsId = state.getIn([action.meta, 'wrapupDetailsOpenId']);
    if (detailsId === action.payload.wrapupDetailsOpenId) {
      return state.setIn([action.meta, 'wrapupDetailsOpenId'], -1);
    }
    return state.setIn([action.meta, 'wrapupDetailsOpenId'], action.payload.wrapupDetailsOpenId);
  },
  [types.requestedSecureForm](state, action) {
    const { token, rsaKey } = action.payload;
    return state.setIn([action.meta, 'secureFormKeys', token], rsaKey);
  },
  [types.getSecureFormContent](state, action, totalState) {
    const { token, content } = action.payload;
    const chatId = action.meta;
    const chat = getChat(state, chatId);
    const isReading = selectors.getIsReading(totalState, chatId);
    const newChat = chat
      .update('messages', (messages) => {
        const index = messages.findIndex((msgs) => {
          const firstMessage = msgs[0];
          return firstMessage.type === messageType.secureForm && firstMessage.id === token;
        });
        return messages.update(index, (msgs) => {
          const firstMessage = msgs[0];
          return [
            Object.assign(
              {},
              firstMessage,
              { message: { status: secureFormStatus.downloaded, content } }
            ),
          ];
        });
      })
      .update('unreadMessages', unreadMessages => (isReading ? 0 : unreadMessages + 1));
    return state.mergeIn([action.meta], newChat);
  },
  [types.updateIfPopTranslation](state, action) {
    return state.updateIn([action.payload, 'ifPopTranslation'], () => true);
  },
  [types.setErrorMessageForChat](state, action) {
    return state.setIn([action.meta, 'errorMessageForChat'], action.payload);
  },
  [types.clearG2mStartUrl](state, action) {
    return state.setIn([action.meta, 'g2mStartUrl'], '')
      .setIn([action.meta, 'ifShowGotoMeetingNotify'], false);
  },
  [types.updateIfShowNotify](state, action) {
    return state.setIn([action.meta, 'ifShowNotify'], action.payload);
  },
  [types.audioAndVideoWidthChanged](state, action) {
    return state.setIn([action.meta, 'audioVideoChatWidth'], action.payload);
  },
  [types.setP2PChat](state, action) {
    return state.setIn([action.meta, 'p2pChat'], action.payload);
  },
  [`${switchServer}`](state, action) {
    const recoverVisitors = action.payload.recoverVistiorIds;
    return state.filter((chat, id) => recoverVisitors.find(v => v.oldChatId === id) !== undefined)
      .mapEntries(([id, chat]) => {
        const recoveredVisitor = recoverVisitors.find(v => v.oldChatId === id);
        if (recoveredVisitor !== undefined) {
          const newChat = chat.update('id', () => recoveredVisitor.chatId)
            .update('visitorId', () => recoveredVisitor.visitorId);
          return [recoveredVisitor.chatId, newChat];
        }
        return [id, chat];
      });
  },
  [types.startUpload](state, action) {
    return state.updateIn([action.meta, 'files'], files => files.concat(action.payload));
  },
  [types.uploadFile](state, action) {
    return state.updateIn([action.meta, 'files'], (files) => {
      let inserted = false;
      const ret = files.map((file) => {
        if (file.guid === action.payload.guid && file.filename === action.payload.filename) {
          if (file.percentage !== action.payload.percentage) {
            inserted = true;
            return action.payload;
          }
        }
        return file;
      });
      if (inserted) return ret;
      return files;
    });
  },
  [types.cancelUpload](state, action) {
    return state.updateIn(
      [action.meta, 'files'],
      (files) => {
        const ret = files.filter(
          file => file.guid !== action.payload.guid || file.filename !== action.payload.filename
        );
        if (ret.length === files.length) return files;
        return ret;
      }
    );
  },
};

export default (state = defaultState, action, totalState) => {
  if (action.type.indexOf(web.webResponse) === 0) {
    if (action.type.indexOf(`${web.webResponse}/chat/`) === 0) {
      const chatId = selectors.getVisitorChatId(totalState, action.payload.visitorId);
      return messageUpdate(state, action, chatId,
        selectors.getIsReading(totalState, chatId),
        selectors.getCurrentAgent(totalState),
        selectors.getVisitorByChatId(totalState, chatId));
    }
    if (action.type === `${web.webResponse}/${chatAction.agentDetectLanguage}`) {
      const chatId = selectors.getVisitorChatId(totalState, action.payload.visitorId);
      const chat = getChat(state, chatId);
      if (!chat) return state;
      const newChat = chat.set('translationLanguage', action.payload.language);
      return state.mergeIn([chatId], newChat);
    }
    if (action.type === `${web.webResponse}/${chatAction.agentSubmitWrapup}`) {
      const chat = getChat(state, action.payload.chatId);
      if (!chat) return state;
      const newChat = chat.set('ifWrapupLoading', false)
        .set('wrapupSubmitted', true)
        .set('ifWrapupSubmitSuccess', true);
      return state.mergeIn([action.payload.chatId], newChat);
    }
    if (action.type === `${web.webResponse}/${chatAction.agentGetWrapupHistory}`) {
      const payload = action.payload;
      return state.map((chat) => {
        if (chat.get('visitorId') === payload.visitorId) {
          return chat.update('visitor', (visitor) => {
            if (visitor != null) {
              return Object.assign({}, visitor, { wrapups: payload.wrapups });
            }
            return undefined;
          })
            .set('ifWrapupHistoryLoading', false)
            .set('wrapupDetailsOpenId', -1);
        }
        return chat;
      });
    }
    if (action.type === `${web.webResponse}/${chatAction.agentCreateG2MMeeting}`) {
      const { joinUrl, visitorId } = action.payload;
      const chatId = selectors.getVisitorChatId(totalState, visitorId);
      const chat = getChat(state, chatId);
      if (!chat) return state;
      const newChat = chat
        .set(
          'ifShowGotoMeetingNotify',
          joinUrl !== chat.get('g2mStartUrl') ? true : chat.get('ifShowGotoMeetingNotify')
        )
        .set('g2mStartUrl', joinUrl);
      return state.mergeIn([chatId], newChat);
    }

    return state;
  }
  if (action.type === layoutConstants.switchPage &&
    action.payload.page === pages.chats) {
    const selectedChatId = selectors.getSelectedChatId(totalState);
    if (state.has(selectedChatId)) {
      return state.setIn([selectedChatId, 'unreadMessages'], 0);
    }
  }
  if (action.type === `${web.webError}/${chatAction.agentGetVisitorContactWrapupDetails}`) {
    const payload = action.payload;
    if (!payload.sourceAction) return state;
    const visitorId = Number(payload.sourceAction.sender);
    return state.map((chat) => {
      if (chat.get('visitorId') === visitorId) {
        return chat.set('wrapupDetailsOpenId', -1);
      }
      return chat;
    });
  }
  if (stateMapping[action.type] === undefined) return state;
  return stateMapping[action.type](state, action, totalState);
};



