import * as _ from 'lodash';
import { chatStatus } from 'src/constants/enums';
import * as customVariableType from 'src/constants/enums/customVariableType';
import * as visitorStatus from 'src/constants/enums/visitorStatus';
import * as columnType from 'src/constants/enums/columnType';
import * as perf from 'src/utils/perf';
import * as selectors from './index';

/** @todo should use `enums` instead of using .get('xxx') */

const simpleCompare = (a, b) => {
  if (a < b) return -1;
  else if (a > b) return 1;
  return 0;
};
const compareNumber = simpleCompare;
const compareString = simpleCompare;
const compareTime = (a, b) => {
  const result = simpleCompare(a, b);
  /**
   * V8 optimization:
   * V8 will deoptimize, if return value is minus zero. Thus, force the correct sign here.
   */
  if (result < 0) return 1;
  if (result > 0) return -1;
  return 0;
};

const sortBySessionStartTime = iteratee => (visitorA, visitorB) => {
  const timeA = visitorA.get('sessionStartTime');
  const timeB = visitorB.get('sessionStartTime');
  return iteratee(compareTime(timeA, timeB));
};

const simpleSortWrapper = (key, compareFunc = simpleCompare) =>
(visitors, iteratee) => (idA, idB) => {
  const visitorA = visitors.get(idA);
  const visitorB = visitors.get(idB);
  const propA = visitorA.get(key);
  const propB = visitorB.get(key);
  const result = compareFunc(propA, propB);
  if (result === 0) {
    return sortBySessionStartTime(iteratee)(visitorA, visitorB);
  }
  return iteratee(result);
};

const sortWrapper = baseSortFunc => (visitors, iteratee, totalState) => (idA, idB) => {
  const visitorA = visitors.get(idA);
  const visitorB = visitors.get(idB);
  const result = baseSortFunc(visitorA, visitorB, totalState);
  if (result === 0) return sortBySessionStartTime(iteratee)(visitorA, visitorB);
  return iteratee(result);
};

const getStatusSortingWeight = (status) => {
  if (status === visitorStatus.transferring) {
    return -1;
  }
  if (status === visitorStatus.manuallyInvitedAndWaitingVisitor) {
    return visitorStatus.invited - 0.1;
  }
  return status;
};

const sortByStatus = simpleSortWrapper(
  'status',
  (a, b) => compareNumber(getStatusSortingWeight(a), getStatusSortingWeight(b))
);

const sortByAgents = sortWrapper((visitorA, visitorB, totalState) => {
  const involvedAgentNamesA = selectors.getInvolvedAgentNames(totalState, visitorA.get('id'));
  const involvedAgentNamesB = selectors.getInvolvedAgentNames(totalState, visitorB.get('id'));
  return compareString(involvedAgentNamesA, involvedAgentNamesB);
});

const sortByCampaign = sortWrapper((visitorA, visitorB, totalState) => {
  const campaignIdA = visitorA.get('codePlanId');
  const campaignIdB = visitorB.get('codePlanId');
  const campaignNameA = selectors.getCampaignName(totalState, campaignIdA);
  const campaignNameB = selectors.getCampaignName(totalState, campaignIdB);
  return compareString(campaignNameA, campaignNameB);
});

const smallestDate = new Date(0);

const sortByWaitTime = sortWrapper((visitorA, visitorB) => {
  let timeA = smallestDate;
  let timeB = smallestDate;
  const statusA = visitorA.get('status');
  const statusB = visitorB.get('status');
  const chatBeginTimeA = visitorA.get('chatBeginTime');
  const chatBeginTimeB = visitorB.get('chatBeginTime');
  if ((statusA === chatStatus.waitingForChat || statusA === chatStatus.transferring) &&
 chatBeginTimeA) {
    timeA = chatBeginTimeA;
  }
  if ((statusB === chatStatus.waitingForChat || statusB === chatStatus.transferring) &&
  chatBeginTimeB) {
    timeB = chatBeginTimeB;
  }
  return compareTime(timeA, timeB);
});

const sortByChatTime = sortWrapper((visitorA, visitorB) => {
  let timeA = smallestDate;
  let timeB = smallestDate;
  const statusA = visitorA.get('status');
  const statusB = visitorB.get('status');
  const chatBeginTimeA = visitorA.get('chatBeginTime');
  const chatBeginTimeB = visitorB.get('chatBeginTime');
  if ((
    statusA === chatStatus.chatting ||
    statusA === chatStatus.transferring
  ) && chatBeginTimeA) {
    timeA = chatBeginTimeA;
  }
  if ((
    statusB === chatStatus.chatting ||
    statusB === chatStatus.transferring
  ) && chatBeginTimeB) {
    timeB = chatBeginTimeB;
  }
  return compareTime(timeA, timeB);
});

const sortByCustomerSegmentation = (visitors, iteratee, totalState) => {
  const sortByStatusFn = sortByStatus(visitors, iteratee);
  return (idA, idB) => {
    const visitorA = visitors.get(idA);
    const visitorB = visitors.get(idB);
    const segmentsA = visitorA.get('segmentIds');
    const segmentsB = visitorB.get('segmentIds');
    const hasA = (segmentsA && segmentsA.length > 0);
    const hasB = (segmentsB && segmentsB.length > 0);
    if (!hasA && !hasB) return iteratee(sortByStatusFn(idA, idB));
    if (!hasA) return iteratee(1);
    if (!hasB) return iteratee(-1);
    const segmentA = selectors.getSegmentationById(totalState, segmentsA[0]);
    const segmentB = selectors.getSegmentationById(totalState, segmentsB[0]);
    const result = compareNumber(
      segmentA.priority ? segmentA.priority : -1,
      segmentB.priority ? segmentB.priority : -1
    );
    if (result === 0) return sortByStatusFn(idA, idB);
    return iteratee(result);
  };
};

const sortByCustomVariable = (visitors, iteratee, totalState, customVariableId) => {
  const sortByStatusFn = sortByStatus(visitors, iteratee);
  return (idA, idB) => {
    const customVariable = selectors.getCustomVariableById(totalState, customVariableId);
    if (!customVariable) return sortByStatusFn(idA, idB);
    const visitorA = visitors.get(idA);
    const visitorB = visitors.get(idB);
    const customVariableA =
      _.find(visitorA.get('customVariables'), cv => cv.name === customVariable.name);
    const customVariableB =
      _.find(visitorB.get('customVariables'), cv => cv.name === customVariable.name);
    if (!customVariableA && !customVariableB) return sortByStatusFn(idA, idB);
    if (!customVariableA) return 1;
    if (!customVariableB) return -1;
    const type = customVariable.type;
    if (type === customVariableType.integer || type === customVariableType.decimal) {
      const numA = +customVariableA.value;
      const numB = +customVariableB.value;
      if (_.isNaN(numA) && _.isNaN(numB)) return sortByStatusFn(idA, idB);
      if (_.isNaN(numA)) return 1;
      if (_.isNaN(numB)) return -1;
      if (Math.abs(numA - numB) < 1e-5) return sortByStatusFn(idA, idB);
      return iteratee(compareNumber(numA, numB));
    }
    const result = compareString(customVariableA.value, customVariableB.value);
    if (result === 0) return sortByStatusFn(idA, idB);
    return iteratee(result);
  };
};

const sortFuncMapping = {
  [columnType.customVariable]: sortByCustomVariable,
  [columnType.status]: sortByStatus,
  [columnType.contactUserIcon]: () => () => 0, // TOOD
  [columnType.name]: simpleSortWrapper('latestName', compareString),
  [columnType.email]: simpleSortWrapper('latestEmail', compareString),
  [columnType.ticketId]: simpleSortWrapper('ticketId', compareNumber),
  [columnType.department]: simpleSortWrapper('departmentId', compareNumber),
  [columnType.visits]: simpleSortWrapper('visitorTimes', compareNumber),
  [columnType.productService]: simpleSortWrapper('prechatProductService', compareString),
  [columnType.chats]: simpleSortWrapper('chatTimes', compareNumber),
  [columnType.currentPage]: simpleSortWrapper('currentPage', compareString),
  [columnType.currentPageBrowsingTime]: simpleSortWrapper('currentPageEnterTime', compareTime),
  [columnType.referrer]: simpleSortWrapper('referrer', compareString),
  [columnType.searchEngine]: simpleSortWrapper('searchEngine', compareString),
  [columnType.keywords]: simpleSortWrapper('searchEngineKeywords', compareString),
  [columnType.autoInvitation]: simpleSortWrapper('autoInvitationId', compareNumber),
  [columnType.campaign]: sortByCampaign,
  [columnType.landingPage]: simpleSortWrapper('landingPageUrl', compareString),
  [columnType.agents]: sortByAgents,
  [columnType.visitTime]: simpleSortWrapper('sessionStartTime', compareNumber),
  [columnType.waitTime]: sortByWaitTime,
  [columnType.chatTime]: sortByChatTime,
  /**
   * [NOTICE]:
   * `totalTime` also depends on sessionStartTime.
   * However, the smaller sessionStartTime is (i.e. earlier landing on site), the bigger totalTime
   * is. That means, totalTime should be sorted almost the same as visitTime, which is also based on
   * sessionStartTime. The only difference is, totalTime should have a reversed result instead.
   *
   * Thus, `totalTime` is compared using compareTime, while `visitTime` using compareNumber`.
   */
  [columnType.totalTime]: simpleSortWrapper('sessionStartTime', compareTime),
  [columnType.timezone]: simpleSortWrapper('timeZone', compareString),
  [columnType.city]: simpleSortWrapper('city', compareString),
  [columnType.state]: simpleSortWrapper('state', compareString),
  [columnType.countryRegion]: simpleSortWrapper('country', compareString),
  [columnType.numOfPages]: simpleSortWrapper('numOfPages', compareNumber),
  [columnType.visitorInfo]: simpleSortWrapper('latestName', compareString),
  [columnType.visitorSegmentation]: sortByCustomerSegmentation,
};

/**
 * High order function to generate function for sorting visitor list.
 *
 * @param {Immutable.Map} visitors - Visitors in state.
 * @param {{enumColumn:number,customVariableId:number}} column - Selected column for sorting.
 * @param {"desc" | "asc"} sortOrder - Sort order.
 * @param {object} totalState - Total state.
 *
 * @return {function(number,number):number} sort function.
 */
const sortGen = (visitors, column, sortOrder, totalState) => {
  const type = column.enumColumn;
  if (sortFuncMapping[type] === undefined) return () => 0;

  return sortFuncMapping[type](
    visitors,
    sortOrder === 'asc' ? _.identity : num => num * -1,
    totalState,
    column.customVariableId
  );
};

/**
 * @todo
 * sort has potential issues.
 * Basically, we don't want to re-sort list whenever state changes.
 * However, currently only `list`, `sortByColumn` and `sortOrder` are used.
 * Other info such as custom variable, segmentation are used, but not explicitly required here.
 *
 * This means, if custom variable, segmentation, agent name or other info that is used has been
 * changed, re-sort will not be triggered.
 */

let cachedList;
let cachedSortColumn;
let cachedSortOrder;
let cachedResult;

export default (list, sortByColumn, sortOrder, state) => {
  if (list === cachedList && sortByColumn === cachedSortColumn && sortOrder === cachedSortOrder) {
    return cachedResult;
  }
  cachedList = list;
  cachedSortColumn = sortByColumn;
  cachedSortOrder = sortOrder;
  const visitors = selectors.getVisitors(state);
  perf.markBegin('Sort Visitors');
  cachedResult = list.slice(0).sort(sortGen(visitors, sortByColumn, sortOrder, state));
  perf.markEnd('Sort Visitors');
  return cachedResult;
};



