import { ifCurrentAgentChatting } from '../../reducers/selectors';
import { updateVisitorsAndChatsPeriod } from '../../constants/web';
import * as actions from '../../actions/agent';
import heartbeatSubscriber from './heartbeatSubscriber';

let vistorChatHeartbeatFlag = true;

export default heartbeatSubscriber({
  onHeartbeat: (store) => {
    vistorChatHeartbeatFlag = !vistorChatHeartbeatFlag;
    if (vistorChatHeartbeatFlag) {
      store.dispatch(actions.getVisitors());
    } else if (ifCurrentAgentChatting(store.getState())) {
      store.dispatch(actions.getChats());
    }
  },
  getPeriod: () => updateVisitorsAndChatsPeriod,
});



