import * as web from '../constants/web';
import * as chatAction from '../constants/enums/chatAction';
import { getErrorMessage } from '../utils/helper';
import * as actions from '../actions/ui';
import { isNullOrEmpty } from '../utils/str';

const showErrorType = [
  `${web.webError}/${chatAction.agentSaveG2MAccount}`,
  `${web.webError}/${chatAction.agentTransferChatToDepartment}`,
  `${web.webError}/${chatAction.agentTransferChat}`,
  `${web.webError}/${chatAction.agentAudioChatAccept}`,
  `${web.webError}/${chatAction.agentVideoChatAccept}`,
  `${web.webError}/${chatAction.agentAudioChatRequest}`,
  `${web.webError}/${chatAction.agentVideoChatRequest}`,
  `${web.webError}/${chatAction.agentAudioChatStop}`,
  `${web.webError}/${chatAction.agentVideoChatStop}`,
];
export default () => next => (action) => {
  if ((action.type === web.webError &&
    action.payload.errorCode === web.errorCode.connectionError) ||
    showErrorType.indexOf(action.type) >= 0) {
    next(actions.switchErrorMessagePanel(true, getErrorMessage(action.payload.errorCode,
      action.payload.errorString)));
  } else if (action.type === `${web.webResponse}/${chatAction.agentCheckSFAutoIntegrationResult}`) {
    if (!isNullOrEmpty(action.payload.message)) {
      next(actions.switchErrorMessagePanel(true, getErrorMessage(0, action.payload.message)));
    }
  }
  return next(action);
};



