import _ from 'lodash';

const totrot = [1, 2, 4, 6, 8, 10, 12, 14, 15, 17, 19, 21, 23, 25, 27, 28];

export function encrypt(plainText) {
  let ent = '';
  const count = plainText.length;

  if (count > 0 && count <= 16) {
    for (let i = 0; i < count; i += 1) {
      const code = plainText.charCodeAt(i);
      ent += (code + totrot[i]).toString();
      ent += '-';
    }
  }

  if (ent.length > 0) {
    ent = ent.substr(0, ent.length - 1);
  }
  return ent;
}

export function decrypt(ent) {
  if (ent.length === 0) return ent;
  return ent.split('-').map((code, i) =>
     String.fromCharCode(_.parseInt(code) - totrot[i])
  ).join('');
}



