import React from 'react';
import classnames from 'classnames';
import _ from 'lodash';
import RadioButtonStyle from './RadioButton.css';
import CommonStyle from '../CommonResources/common.css';

class RadioButton extends React.Component {
  constructor() {
    super();
    this.state = {

    };
  }
  shouldComponentUpdate(nextProps) {
    return !_.isEqual(this.props.radioOption, nextProps.radioOption) ||
      this.props.selectedVal !== nextProps.selectedVal;
  }
  render() {
    return (
      <div className={CommonStyle.clearfix}>
        {
          this.props.radioOption.map((item, index) => (
            /** @todo remove following eslint disable */
            // eslint-disable-next-line
            <label
              key={index}
              className={
                classnames(
                  RadioButtonStyle.control,
                  RadioButtonStyle.controlRadio,
                  this.props.className,
                  this.props.horizontal ? RadioButtonStyle.horizontal : ''
                )
              }
            >
              {item.text || item}
              <input
                type="radio"
                name={this.props.name}
                value={item.value || item}
                checked={this.props.selectedVal === item.value || item.checked}
                onChange={this.props.handleChange ? this.props.handleChange : () => {}}
              />

              <div className={RadioButtonStyle.controlIndicator} />
              {item.component ? item.component : ''}
            </label>
          ))
        }
      </div>

    );
  }
}

RadioButton.propTypes = {
  name: React.PropTypes.string.isRequired,
  radioOption: React.PropTypes.arrayOf(React.PropTypes.shape({
    text: React.PropTypes.string,
    value: React.PropTypes.string,
    checked: React.PropTypes.bool,
  })).isRequired,
  selectedVal: React.PropTypes.string,
  handleChange: React.PropTypes.func.isRequired,
  className: React.PropTypes.string,
  horizontal: React.PropTypes.bool,
  // name: React.PropTypes.string.isRequired,
  // value: React.PropTypes.string.isRequired,
  // checked: React.PropTypes.string,
  // component: React.PropTypes.element,
};
RadioButton.examples = {
  DefaultExample: {
    description: 'Default Example',
    component: <RadioButton
      name="banvisitor"
      radioOption={
      [
        {
          text: 'Ban visitor from this Visitor Id.',
          value: '0',
          checked: true,
        },
        {
          text: 'Ban visitor from this IP',
          value: '1',
        },
      ]
      }
      handleChange={event => console.log(event.target.value)}
    />,
  },
};

export default RadioButton;



