import * as chatAction from '../constants/enums/chatAction';
import { webRequest } from '../constants/web';
import { throwRequired } from '../utils/helper';

export const createWebRequest = (type, payload, next) =>
  ({
    type: `${webRequest}/${type}`,
    payload,
    next,
  });

const requireVisitorId = type =>
  (visitorId = throwRequired('visitorId')) => createWebRequest(type, visitorId);

export const getVisitors = () => createWebRequest(chatAction.agentGetVisitors);
export const getChats = () => createWebRequest(chatAction.agentGetChats);
export const logout = () => createWebRequest(chatAction.agentLogout);
export const getAgents = () => createWebRequest(chatAction.agentGetAgents);

export const getCannedMessages = () => createWebRequest(chatAction.agentGetCannedMessages);

export const sendText = (visitorId, message) =>
  createWebRequest(chatAction.agentSendText, { visitorId, message });
export const agentUseCannedMessage = (chatId, id) =>
  createWebRequest(chatAction.agentUseCannedMessage, { chatId, id });

export const sendImageStart = (visitorId, imageId, next) =>
  createWebRequest(chatAction.agentSendImageStart, { visitorId, imageId }, next);

export const sendPrivateMessageToAgent = (agentId, message) =>
  createWebRequest(chatAction.agentSendPrivateMessageToAgent, { agentId, message });
export const sendFileToAgent = (agentId, fileName, guid) =>
  createWebRequest(chatAction.agentSendFileToAgent, { agentId, fileName, guid });

export const agentSendFile = (visitorId, fileName, guid) =>
  createWebRequest(chatAction.agentSendFile, { visitorId, fileName, guid });

export const agentSetStatus = status =>
  createWebRequest(chatAction.agentSetStatus, status);
export const setPreference = () => createWebRequest(chatAction.agentSetPreference);

export const checkG2MAccount = (email, password) =>
  createWebRequest(chatAction.agentCheckG2MAccount, { email, password });
export const connectG2MAccount = (email, password) =>
  createWebRequest(chatAction.agentSaveG2MAccount, { email, password });
export const createG2MMeeting = requireVisitorId(chatAction.agentCreateG2MMeeting);


/**
 * @param {number} visitorId - visitor's id
 * @param {object} preChat - system fields for pre chat form
 * {
 *  name: '',
 *  email: '',
 *  phone: '',
 *  company: '',
 *  productService: '',
 *  departmentId: '',
 * }
 * @returns {object} action
 */
export const setPreChatForm = (visitorId, preChat) =>
  createWebRequest(chatAction.agentSetPreChatForm, { visitorId, preChat });

/**
 * @param {number} visitorId - visitor's id
 * @param {array} fields - custom fields for pre chat form
 * [{
 *    id: 1,
 *    name: '',
 *    value: '',
 * }]
 * @returns {object} action
 */
export const setCustomFields = (visitorId, fields) =>
  createWebRequest(chatAction.agentSetCustomFields, { visitorId, fields });

export const setAgentComment = (visitorId, comment) =>
  createWebRequest(chatAction.agentSetComment, { visitorId, comment });
/**
 * @param {number} visitorId - visitor's id
 * @param {object} wrapup - wrapup content
 * {
 *  visitorId: 1,
 *  chatId: 1,
 *  categoryId: 1,
 *  customFields: [{
 *    id: 1,
 *    name: '',
 *    value: '',
 *  }],
 *  comment: '',
 *  categoryList: [1, 2],
 * }
 * @returns {object} action
 */
export const setWrapupForm = (visitorId, wrapup) =>
  createWebRequest(chatAction.agentSubmitWrapup, { visitorId, wrapup });

/**
 * @param {number} visitorId - visitors' id
 * @param {object} ban - ban info
 * {
 *  banType: 1,
 *  ip: '',
 *  comment: '',
 * }
 * @returns {object} action
 */
export const banVisitor = (visitorId, ban) =>
  createWebRequest(chatAction.agentBanVisitor, { visitorId, ban });

export const promoteToUser = requireVisitorId(chatAction.agentPromoteToUser);
export const promoteToContact = requireVisitorId(chatAction.agentPromoteToContact);

export const transferChatToAgent = (visitorId, agentId, ifLeaveChat = false) =>
  createWebRequest(chatAction.agentTransferChat, { visitorId, agentId, ifLeaveChat });

export const transferChatToDepartment = (visitorId, departmentId, ifLeaveChat = false) =>
  createWebRequest(chatAction.agentTransferChatToDepartment,
    { visitorId, departmentId, ifLeaveChat });

export const acceptChat = requireVisitorId(chatAction.agentAcceptChat);
export const refuseChat = requireVisitorId(chatAction.agentRefuseChat);
export const leaveChat = requireVisitorId(chatAction.agentEndChat);
export const acceptTransfer = requireVisitorId(chatAction.agentAcceptTransfer);
export const refuseTransfer = requireVisitorId(chatAction.agentRefuseTransfer);
export const joinChat = (visitorId, next) =>
  createWebRequest(chatAction.agentJoinChat, visitorId, next);
export const monitorChat = (visitorId, next) =>
  createWebRequest(chatAction.agentMonitorChat, visitorId, next);
export const captureVisitor = requireVisitorId(chatAction.agentCaptureVisitor);
export const releaseVisitor = requireVisitorId(chatAction.agentReleaseVisitor);
export const cancelManuallyInvited = requireVisitorId(chatAction.agentCancelManuallyInvited);
export const inviteVisitorWithText = (visitorId, message, next) =>
  createWebRequest(chatAction.agentInviteChatWithText, { visitorId, message }, next);
export const enableTranslation = (visitorId, language) =>
  createWebRequest(chatAction.agentEnableTranslate, { visitorId, language });
export const disableTranlsation = requireVisitorId(chatAction.agentDisableTranslate);

export const setAnotherAgentStatus = (agentId, status) =>
  createWebRequest(chatAction.agentSetAnotherAgentStatus, { agentId, status });

export const getNavigation = requireVisitorId(chatAction.agentGetNavigations);
export const getChatHistory = requireVisitorId(chatAction.agentGetChatHistory);
export const getOfflineMessageHistory = requireVisitorId(chatAction.agentGetOfflineMessageHistory);
export const getSessionHistory = requireVisitorId(chatAction.agentGetSessionHistory);
export const getWrapupHistory = requireVisitorId(chatAction.agentGetWrapupHistory);
export const getChatHistoryDetails = (visitorId, chatId) =>
  createWebRequest(chatAction.agentGetChatDetails, { visitorId, chatId });
export const getOfflineMessageDetails = (visitorId, offlineMessageId) =>
  createWebRequest(chatAction.agentGetOfflineMessageDetails, { visitorId, offlineMessageId });
export const getNavigationDetails = (visitorId, contactId) =>
  createWebRequest(chatAction.agentGetNavigationDetails, { visitorId, contactId });
export const getWrapupDetails = (visitorId, chatId) =>
  createWebRequest(chatAction.agentGetVisitorContactWrapupDetails, { visitorId, chatId });

export const attachNewTicket = requireVisitorId(chatAction.agentAttachNewTicket);
export const attachToExistTicket = (visitorId, ticketId) =>
  createWebRequest(chatAction.agentAttachExistingTicket, { visitorId, ticketId });
export const sendEmail = (chatId, email, next) =>
  createWebRequest(chatAction.agentSendEmail, { chatId, email }, next);
export const requestSecureForm = (visitorId, secureFormName, token) =>
  createWebRequest(chatAction.agentRequestSecureForm, { visitorId, secureFormName, token });

export const agentExtendChargeDate = () => createWebRequest(chatAction.agentExtendChargeDate);

export const agentSetDashboardMessageNotShow = ids =>
  createWebRequest(chatAction.agentSetDashboardMessageNotShow, ids);

export const agentMonitorAllChats = () => createWebRequest(chatAction.agentMonitorAllChats);

export const onRequestVideoChat = requireVisitorId(chatAction.agentVideoChatRequest);
export const onRequestAudioChat = requireVisitorId(chatAction.agentAudioChatRequest);
export const onAcceptAudioChat = requireVisitorId(chatAction.agentAudioChatAccept);
export const onAcceptVideoChat = requireVisitorId(chatAction.agentVideoChatAccept);
export const onRefuseAudioChat = requireVisitorId(chatAction.agentAudioChatRefuse);
export const onRefuseVideoChat = requireVisitorId(chatAction.agentVideoChatRefuse);
export const onCancelAudioChatRequest = requireVisitorId(chatAction.agentAudioChatCancelRequest);
export const onCancelVideoChatRequest = requireVisitorId(chatAction.agentVideoChatCancelRequest);
export const onStopAudioChat = requireVisitorId(chatAction.agentAudioChatStop);
export const onStopVideoChat = requireVisitorId(chatAction.agentVideoChatStop);



