import * as _ from 'lodash';
import { serverNotAvailable, webResponse, webError, errorCode } from '../../constants/web';
import * as chatAction from '../../constants/enums/chatAction';
import * as selectors from '../../reducers/selectors';
import { historyType, sessionType } from '../../constants/enums';
import { objectType } from '../../constants/salesforce';
import { deserializationLookupResult } from '../../utils/serialization';

const responseAction = (type, payload) =>
  ({ type: `${webResponse}/${type}`, payload });

const responseActionSFObjectManager = (type, resp) =>
  responseAction(type,
    {
      visitorId: resp.sourceAction.visitorId,
      sfObject: resp.extend.sfObject,
    });

const responseDataMapping = {
  [chatAction.agentGetAgents]: resp =>
    responseAction(chatAction.agentGetAgents, resp.extend.agents),
  [chatAction.agentGetDepartments]: resp =>
    responseAction(chatAction.agentGetDepartments, resp.extend.departments),
  [chatAction.agentGetNavigations]: resp =>
    responseAction(chatAction.agentGetNavigations,
      {
        visitorId: resp.visitorId,
        navigation: resp.extend.navigations,
      }),
  [chatAction.agentGetHistories]: resp =>
    responseAction(chatAction.agentGetHistories, resp),
  [chatAction.agentGetChatHistory]: resp =>
    responseAction(chatAction.agentGetChatHistory,
      {
        visitorId: resp.visitorId,
        chats: resp.extend.history,
      }),
  [chatAction.agentGetOfflineMessageHistory]: resp =>
    responseAction(
      chatAction.agentGetOfflineMessageHistory,
      {
        visitorId: resp.visitorId,
        offlineMessages: resp.extend.history,
      }),
  [chatAction.agentGetSessionHistory]: resp =>
    responseAction(chatAction.agentGetSessionHistory,
      {
        visitorId: resp.visitorId,
        sessions: resp.extend.history,
      }),
  [chatAction.agentGetWrapupHistory]: resp =>
    responseAction(chatAction.agentGetWrapupHistory,
      {
        visitorId: resp.visitorId,
        wrapups: resp.extend.history,
      }),
  [chatAction.agentGetVisitorContactHistoryDetails]: resp =>
    responseAction(chatAction.agentGetVisitorContactHistoryDetails, resp),
  [chatAction.agentGetChatDetails]: resp =>
    responseAction(chatAction.agentGetChatDetails,
      {
        visitorId: parseInt(resp.sourceAction.sender, 0),
        chat: resp.extend.history.shift(),
      }),
  [chatAction.agentGetOfflineMessageDetails]: resp =>
    responseAction(
      chatAction.agentGetOfflineMessageDetails,
      {
        visitorId: parseInt(resp.sourceAction.sender, 0),
        offlineMessage: resp.extend.history.shift(),
      }),
  [chatAction.agentGetNavigationDetails]: resp =>
    responseAction(
      chatAction.agentGetNavigationDetails,
      {
        visitorId: resp.visitorId,
        session: resp.extend.history.shift(),
      }),
  [chatAction.agentGetVisitorContactWrapupDetails]: resp =>
    responseAction(chatAction.agentGetVisitorContactWrapupDetails,
      {
        visitorId: parseInt(resp.sourceAction.sender, 0),
        wrapup: resp.extend.history.shift(),
      }),
  [chatAction.agentGetCannedMessages]: resp =>
    responseAction(chatAction.agentGetCannedMessages,
      {
        categories: resp.extend.categories,
        cannedMessages: resp.extend.cannedMessages,
      }),
  [chatAction.agentSubmitWrapup]: resp =>
    responseAction(chatAction.agentSubmitWrapup,
      {
        visitorId: resp.sourceAction.visitorId,
        chatId: resp.visitorId,
      }),
  [chatAction.agentInviteChatWithText]: (resp) => {
    const payload = {
      visitorId: resp.visitorId,
      showChatWindow: resp.errorString === '1',
    };
    if (resp.sourceAction.next) {
      resp.sourceAction.next(payload);
    }
    return responseAction(chatAction.agentInviteChatWithText, payload);
  },
  [chatAction.agentDetectLanguage]: resp =>
    responseAction(chatAction.agentDetectLanguage,
      {
        visitorId: resp.sourceAction.visitorId,
        language: parseInt(resp.errorString, 0),
      }),
  [chatAction.agentCreateG2MMeeting]: resp =>
    responseAction(chatAction.agentCreateG2MMeeting,
      {
        visitorId: resp.sourceAction.visitorId,
        joinUrl: resp.errorString,
      }),
  [chatAction.agentCheckG2MAccount]: resp =>
    responseAction(chatAction.agentCheckG2MAccount,
      {
        ifTestG2ASuccess: resp.errorString === '',
        errorMsg: resp.errorString,
      }),
  [chatAction.agentSaveG2MAccount]: resp =>
    responseAction(chatAction.agentSaveG2MAccount,
      {
        ifG2MConnected: resp.errorCode === 'noerror'
        && resp.errorString === 'true',
      }),
  [chatAction.agentSendImageStart]: (resp) => {
    if (resp.sourceAction.next) {
      resp.sourceAction.next(resp.errorString);
    }
    return responseAction(chatAction.agentSendImageStart,
      {
        visitorId: resp.sourceAction.visitorId,
        imageId: resp.sourceAction.message,
        guid: resp.errorString,
      });
  },
  [chatAction.agentSendEmail]: (resp) => {
    const payload = {
      errorCode: resp.errorCode,
      chatId: resp.sourceAction.visitorId,
    };
    if (resp.sourceAction.next) {
      resp.sourceAction.next(payload);
    }
    return responseAction(chatAction.agentSendEmail, payload);
  },

  [chatAction.agentGetUnfinishedWrapups]: resp =>
    responseAction(chatAction.agentGetUnfinishedWrapups, resp.extend.unfinishedWrapups),

  // salesforce
  [chatAction.agentGetSFIntegrationConfig]: resp => responseAction(resp.type, resp.extend.sfConfig),
  [chatAction.agentGetSFObjectFields]: (resp) => {
    const type = resp.sourceAction.message;
    if (type === `${objectType.account}`) {
      return responseAction(chatAction.agentGetSFAccountFields, resp.extend.sfObjectFields);
    } else if (type === `${objectType.contact}`) {
      return responseAction(chatAction.agentGetSFContactFields, resp.extend.sfObjectFields);
    } else if (type === `${objectType.lead}`) {
      return responseAction(chatAction.agentGetSFLeadFields, resp.extend.sfObjectFields);
    }
    return responseAction(chatAction.agentGetSFObjectFields, resp.extend.sfObjectFields);
  },
  [chatAction.agentCreateSFContact]: resp => responseActionSFObjectManager(resp.type, resp),
  [chatAction.agentEditSFContact]: resp => responseActionSFObjectManager(resp.type, resp),
  [chatAction.agentCreateSFLead]: resp => responseActionSFObjectManager(resp.type, resp),
  [chatAction.agentEditSFLead]: resp => responseActionSFObjectManager(resp.type, resp),
  [chatAction.agentCreateSFCase]: resp => responseAction(resp.type, resp.sourceAction.visitorId),
  [chatAction.agentAttachSFCaseToContact]: resp => responseAction(resp.type,
    resp.sourceAction.visitorId),
  [chatAction.agentAttachSFTaskToLead]: resp => responseAction(resp.type,
    resp.sourceAction.visitorId),
  [chatAction.agentLookupSFObject]: (resp) => {
    const result = deserializationLookupResult(resp.extend.sfLookupResult);
    if (resp.sourceAction.next) {
      resp.sourceAction.next(result);
    }
    responseAction(resp.type, result);
  },
  [chatAction.agentCheckSFAutoIntegrationResult]: resp => responseAction(resp.type,
    { visitorId: resp.sourceAction.visitorId, message: resp.errorString }),
  [chatAction.agentJoinChat](resp) {
    if (resp.sourceAction.next) {
      resp.sourceAction.next(resp);
    }
    return responseAction(resp.type, resp);
  },
  [chatAction.agentMonitorChat](resp) {
    if (resp.sourceAction.next) {
      resp.sourceAction.next(resp);
    }
    return responseAction(resp.type, resp);
  },
};

const chatMessageFilter = (store, dispatchCommands, msg) => {
  if (msg.type === chatAction.visitorAddTextMessage) {
    const state = store.getState();

    const ifEnableTranslation = selectors.getAgentIfEnableTranslation(state);
    if (!ifEnableTranslation) return;

    const visitorId = msg.visitorId;
    const visitor = selectors.getVisitorObject(state, visitorId);
    const agentId = selectors.getCurrentAgentId(state);
    if (!selectors.isChattingWithAgent(visitor, agentId)) return;

    const chatId = selectors.getVisitorChatId(state, visitorId);
    if (selectors.getIfChatEnableTransaltion(state, chatId)) return;

    const visitorLanguage = selectors.getVisitorLanguage(state, chatId);
    const agentLanguage = selectors.getAgentTranslationLanguage(state);
    const detected = visitorLanguage !== 0 && visitorLanguage !== agentLanguage;
    const countVisitorMessage = selectors.getVisitorMessageCount(state, chatId);
    if (countVisitorMessage < 5 && !detected) {
      dispatchCommands(true,
        [{
          type: chatAction.agentDetectLanguage,
          visitorId,
          message: msg.message,
        }]
      );
    }
  }
};

const checkIfHaveMonitorPermission = (state) => {
  if (selectors.ifEnableAutoMonitor(state)) {
    if (selectors.ifHavePermissionMonitorChat(state)) {
      return { enable: true, isMonitorChat: true };
    } else if (selectors.getifDepartmentEnabled(state) &&
               selectors.ifHavePermissionMonitorDepartment(state)) {
      return { enable: true, isMonitorChat: false };
    }
  }
  return { enable: false, isMonitorChat: false };
};

// Auto Monitor chats
const autoMonitorChat = (store, dispatchCommands, { visitors: { full, moreDiff } }) => {
  const state = store.getState();
  const { enable, isMonitorChat } = checkIfHaveMonitorPermission(state);
  if (enable) {
    const ownerDepartments = [];
    const agentId = selectors.getCurrentAgentId(state);
    if (!isMonitorChat) {
      selectors.getOnlineDepartments(state).reduce((ids, dpt) => {
        if (dpt.agentIds.indexOf(agentId) >= 0) {
          ids.push(dpt.id);
        }
        return ids;
      }, ownerDepartments);
    }
    const moreDiffUpdateVisitors = _.concat(full, moreDiff);
    if (moreDiffUpdateVisitors.length > 0) {
      const autoMonitorCommands = moreDiffUpdateVisitors
        .filter((v) => {
          if (!isMonitorChat && ownerDepartments.indexOf(v.departmentId) < 0) {
            return false;
          }
          const oldVisitor = selectors.getVisitor(state, v.id);
          const oldVistiorNotChatting = oldVisitor === undefined ||
            !selectors.checkIfVisitorStatusChatting(oldVisitor.get('status'));
          if (!oldVistiorNotChatting) return false;
          /** checkVisitorCanMonitor API accepts immutable, use following workaround to mock API */
          const fakeVisitor = {
            get(key) { return v[key]; },
          };
          return oldVistiorNotChatting &&
            selectors.checkVisitorCanMonitor(state, fakeVisitor, agentId);
        })
        .map(v => ({
          type: chatAction.agentMonitorChat,
          visitorId: v.id,
        }));
      if (autoMonitorCommands.length > 0) {
        dispatchCommands(false,
          _.concat(autoMonitorCommands, { type: chatAction.agentGetVisitors }));
      }
    }
  }
};


const typeMapping = {
  [chatAction.agentGetHistories]: (sourceAction) => {
    const type = sourceAction.message;
    if (type === `${historyType.chat}`) {
      return chatAction.agentGetChatHistory;
    } else if (type === `${historyType.offlineMessage}`) {
      return chatAction.agentGetOfflineMessageHistory;
    } else if (type === `${historyType.navigation}`) {
      return chatAction.agentGetSessionHistory;
    } else if (type === `${historyType.wrapup}`) {
      return chatAction.agentGetWrapupHistory;
    }
    return chatAction.agentGetHistories;
  },
  [chatAction.agentGetVisitorContactHistoryDetails]: (sourceAction) => {
    const type = sourceAction.message;
    if (type === `${sessionType.chat}`) {
      return chatAction.agentGetChatDetails;
    } else if (type === `${sessionType.offlineMessage}`) {
      return chatAction.agentGetOfflineMessageDetails;
    } else if (type === `${sessionType.navigation}`) {
      return chatAction.agentGetNavigationDetails;
    }
    return chatAction.agentGetVisitorContactHistoryDetails;
  },
};

const getVisitorIdFromAction = action => ({ visitorId: action.visitorId });
const getIdsFromHistoryDetailAction = action => ({
  visitorId: parseInt(action.sender, 0),
  id: action.visitorId,
});

const errorActionMapping = {
  [chatAction.agentCreateSFContact]: getVisitorIdFromAction,
  [chatAction.agentCreateSFLead]: getVisitorIdFromAction,
  [chatAction.agentCreateSFCase]: getVisitorIdFromAction,
  [chatAction.agentEditSFContact]: getVisitorIdFromAction,
  [chatAction.agentEditSFLead]: getVisitorIdFromAction,
  [chatAction.agentAttachSFCaseToContact]: getVisitorIdFromAction,
  [chatAction.agentAttachSFTaskToLead]: getVisitorIdFromAction,
  [chatAction.agentGetChatHistory]: getVisitorIdFromAction,
  [chatAction.agentGetOfflineMessageHistory]: getVisitorIdFromAction,
  [chatAction.agentGetSessionHistory]: getVisitorIdFromAction,
  [chatAction.agentGetWrapupHistory]: getVisitorIdFromAction,
  [chatAction.agentGetChatDetails]: getIdsFromHistoryDetailAction,
  [chatAction.agentGetOfflineMessageDetails]: getIdsFromHistoryDetailAction,
  [chatAction.agentGetNavigationDetails]: getIdsFromHistoryDetailAction,
};
// const mapChatMessage()


/**
 * Receive a dispatch function from `redux` for dispatching pure actions;
 * Then returns a function to receive all web response. The response will be tranformed to pure
 * actions first, then be dispatched to `redux`.
 *
 * @param {Function} store - `redux` store
 * @param {Function} next - Dispatch function from `redux`
 * @return {Function} A function to accept web response
 */

export default (store, next, dispatchCommands) => (error, resp) => {
  /**
   * @todo
   * mixing error and data together for `next` is not a good design.
   * Proper fix will be: next(error, data). use error === null when no error occurs.
   */
  if (error) {
    if (error.errorCode === errorCode.systemSwitchServer ||
        error.errorCode === errorCode.currentAgentNotOnline) {
      return;
    } else if (error.errorCode === errorCode.maxonServerCheck) {
      next({ type: `${serverNotAvailable}` });
      return;
    }

    if (error.sourceAction == null) {
      next({ type: `${webError}`, payload: error });
    } else {
      const type = typeMapping[error.sourceAction.type] != null ?
        typeMapping[error.sourceAction.type](error.sourceAction) : error.sourceAction.type;
      if (errorActionMapping[type] != null) {
        next({
          type: `${webError}/${type}`,
          payload: Object.assign({
            errorCode: error.errorCode,
            errorString: error.errorString,
          }, errorActionMapping[type](error.sourceAction)),
        });
      } else {
        next({ type: `${webError}/${type}`, payload: error });
      }
      if (error.sourceAction.next) {
        error.sourceAction.next({ errorCode: error.errorCode, errorString: error.errorString });
      }
    }
    return;
  }

  const type = typeMapping[resp.type] != null ?
    typeMapping[resp.type](resp.sourceAction) : resp.type;

  if (type === chatAction.agentGetVisitors) {
    autoMonitorChat(store, dispatchCommands, resp);
  }

  if (type === chatAction.agentGetChats) {
    next({
      type: 'batch',
      payload: _.map(resp.chatMessages, (msg) => {
        chatMessageFilter(store, dispatchCommands, msg);
        return { type: `${webResponse}/chat/${msg.type}`, payload: msg };
      }).concat(
        _.map(resp.privateMessages, msg => ({
          type: `${webResponse}/agentchat/${msg.type}`, payload: msg,
        }))
      ),
    });

    // _.each(resp.chatMessages, (msg) => {
    //   chatMessageFilter(store, dispatchCommands, msg);
    //   next({ type: `${webResponse}/chat/${msg.type}`, payload: msg });
    // });

    // _.each(resp.privateMessages, (msg) => {
    //   next({ type: `${webResponse}/agentchat/${msg.type}`, payload: msg });
    // });
  } else if (responseDataMapping[type] != null) {
    next(responseDataMapping[type](resp));
  } else {
    next({ type: `${webResponse}/${type}`, payload: resp });
  }
};



