import * as _ from 'lodash';
import * as perf from 'src/utils/perf';

export default (reducers, order = []) => {
  const keys = order.filter(key => typeof reducers[key] === 'function');
  /* istanbul ignore if */
  if (process.env.NODE_ENV !== 'production' && order.length !== keys.length) {
    console.warn('Order has one or more keys that are not in reducers.');
  }
  _.each(Object.keys(reducers), (key) => {
    if (keys.indexOf(key) >= 0) return;
    keys.push(key);
  });
  function reducer(oldState = { }, action) {
    perf.markBegin(`reduce: ${action.type}`);
    const state = Object.assign({}, oldState);
    let hasChanged = false;
    const newState = keys.reduce((currentState, key) => {
      const currentPart = reducers[key](currentState[key], action, currentState);
      hasChanged = hasChanged || currentPart !== currentState[key];
      currentState[key] = currentPart;  // eslint-disable-line no-param-reassign
      return currentState;
    }, state);
    perf.markEnd(`reduce: ${action.type}`);
    if (!hasChanged) return oldState;
    return newState;
  }
  return (oldState, action) => {
    if (action.type === 'batch') {
      return _.reduce(
        action.payload,
        (prevState, currAction) => reducer(prevState, currAction),
        oldState
      );
    }
    return reducer(oldState, action);
  };
};



