import { initialize } from 'redux-form';
import * as visitorDetails from '../constants/types/visitorDetails';

export const onDetailsTabSelect = tab => ({
  type: visitorDetails.detailsTabSelect,
  payload: {
    visitorDetailsTabSelected: tab,
  },
});

export const onDetailsWidthChanged = width => ({
  type: visitorDetails.chatDetailsWidthChanged,
  payload: {
    chatDetailsWidth: width,
  },
});

export const onPreChatEditIconClick = () => ({
  type: visitorDetails.infoState,
  payload: {
    ifpreChatEdit: true,
  },
});

export const onPreChatCancelClick = () => ({
  type: visitorDetails.infoState,
  payload: {
    ifpreChatEdit: false,
  },
});

export const handleSubmitpreChat = () => ({
  type: visitorDetails.infoState,
  payload: {
    ifpreChatLoading: true,
  },
});

export const handleSubmitCustomFields = () => ({
  type: visitorDetails.infoState,
  payload: {
    ifcustomFieldLoading: true,
  },
});

export const handleSubmitComment = () => ({
  type: visitorDetails.infoState,
  payload: {
    isNoteLoading: true,
  },
});

export const initPreChatForm = initialValues => initialize('infoPrechat', initialValues);

export const onCustomFieldEditIconClick = () => ({
  type: visitorDetails.infoState,
  payload: {
    ifcustomFieldEdit: true,
  },
});

export const onCustomFieldCancelClick = () => ({
  type: visitorDetails.infoState,
  payload: {
    ifcustomFieldEdit: false,
  },
});

export const resetForms = () => ({
  type: visitorDetails.infoState,
  payload: {
    ifpreChatEdit: false,
    ifcustomFieldEdit: false,
    isNoteEdit: false,
    ifpreChatLoading: false,
    ifcustomFieldLoading: false,
    isNoteLoading: false,
  },
});

export const onCurrentBrowsingIconClick = flag => ({
  type: visitorDetails.infoState,
  payload: {
    isCurrentBrowsingShowTitle: flag === 0 || flag === 2,
    isCurrentBrowsingShowUrl: flag === 1 || flag === 2,
  },
});

export const onCommentEditIconClick = () => ({
  type: visitorDetails.infoState,
  payload: {
    isNoteEdit: true,
  },
});

export const onCommentCancelClick = () => ({
  type: visitorDetails.infoState,
  payload: {
    isNoteEdit: false,
  },
});

export const initCommentForm = initialValues => initialize('AgentComment', initialValues);



