import { updateAgentsPeriod } from '../../constants/web';
import * as actions from '../../actions/agent';
import heartbeatSubscriber from './heartbeatSubscriber';
import { getIsAgentTab } from '../../reducers/selectors';

let cursor = 0;

export default heartbeatSubscriber({
  onHeartbeat: (store) => {
    cursor = (cursor + 1) % 6;
    if (getIsAgentTab(store.getState()) || cursor === 0) {
      store.dispatch(actions.getAgents());
    }
  },
  getPeriod: () => updateAgentsPeriod,
});



