import React, { PropTypes } from 'react';
import classnames from 'classnames';
import styles from './AgentTab.css';
import { agentTabs } from '../../constants/enums';
import PromptNumber from '../Icons/PromptNumber';
import { shouldComponentUpdateGen } from '../../utils/helper';

const shouldComponentUpdateHelper = shouldComponentUpdateGen(
  'PageTab',
  ['currentTab', 'visitorNumber', 'chatNumber', 'agentNumber', 'ifPopupWindowForVisitors']
);

// const AgentTab = props => (
class AgentTab extends React.Component {

  shouldComponentUpdate(nextProps) {
    return shouldComponentUpdateHelper(this.props, nextProps);
  }

  render() {
    const props = this.props;
    return (
      <div className={styles.agenttabwraper}>
        <ul className={styles.ultab}>
          {!props.ifPopupWindowForVisitors && (
            <li
              onClick={() => props.clickTab(agentTabs.visitor)}
              className={classnames(styles.tabItem,
                props.currentTab === agentTabs.visitor && styles.selected)}
            >
              {props.currentTab !== agentTabs.visitor && (
                <PromptNumber
                  customClass={styles.info}
                  number={props.visitorNumber}
                />
              )}
                          Visitors
            </li>
          )}
          <li
            onClick={() => props.clickTab(agentTabs.chat)}
            className={classnames(styles.tabItem,
              props.currentTab === agentTabs.chat && styles.selected)}
          >
            {props.currentTab !== agentTabs.chat && (
              <PromptNumber
                customClass={styles.info}
                number={props.chatNumber}
              />
            )}
                        My Chats
          </li>
          <li
            onClick={() => props.clickTab(agentTabs.agent)}
            className={classnames(styles.tabItem,
              props.currentTab === agentTabs.agent && styles.selected)}
          >
            {props.currentTab !== agentTabs.agent && (
              <PromptNumber
                customClass={styles.info}
                number={props.agentNumber}
              />
            )}
                        Agents
          </li>
        </ul>
      </div>
    );
  }
}

AgentTab.propTypes = {
  children: PropTypes.element,
  visitorNumber: PropTypes.number,
  chatNumber: PropTypes.number,
  agentNumber: PropTypes.number,
  currentTab: PropTypes.string,
  clickTab: PropTypes.func.isRequired,
  ifPopupWindowForVisitors: PropTypes.bool,
};
AgentTab.defaultProps = {
  visitorElement: <span>visitors</span>,
  chatElement: <span>chats</span>,
  agentElement: <span>agents</span>,
  visitorNumber: 0,
  chatNumber: 0,
  agentNumber: 0,
  currentTab: agentTabs.vistor,
};
AgentTab.examples = {
  'Default Example': {
    description: 'AgentTab',
    component:
  <AgentTab
    visitorNumber={5}
    currentTab={agentTabs.agent}
    clickTab={tab => tab}
  />,
  },
};
export default AgentTab;



