import 'babel-polyfill';
import 'moment/src/locale/en-gb';
import './assets/manifest.json';
import './assets/logo256x256.png';
import * as serviceWorker from './services/serviceWorker';
import configureStore from './store/configureStore';
import uiInitialize from './ui';
import createSubscription from './subscriptions/create';
import { initData } from './store/windowsManager';

serviceWorker.initialize();

const store = configureStore();

uiInitialize(store);
createSubscription(store);
initData(store);

/**
 * Following global event is to prevent default behavior of dropping file to redirect browser.
 * Fix issue 19442.
 *
 * @todo consider moving this to separate module.
 */
window.addEventListener('dragover', (e) => {
  const event = e || window.event;
  event.preventDefault();
});

window.addEventListener('drop', (e) => {
  if (process.env.NODE_ENV !== 'production') {
    console.log('Prevent drop event on window');
  }
  const event = e || window.event;
  event.preventDefault();
});



