import _ from 'lodash';
import * as reporter from 'src/utils/reporter';
import memoryStore from './memoryStore';
import * as memoryStoreKey from '../constants/memoryStore';
import * as helper from '../utils/helper';
import Notification from '../utils/Notification';
import image from '../components/Image/logo60.png';
import { ipcRenderer } from '../services/electronAPI';
import * as channelsType from '../constants/enums/channels';

let isActive = true;
let timer = null;
if (process.env.IS_WEB_APP) {
  Notification.requestPermission().then((permission) => {
    if (process.env.NODE_ENV !== 'production') {
      console.log('permission:', permission);
    }
  });
}
window.onfocus = () => {
  isActive = true;
};

window.onblur = () => {
  isActive = false;
};
const notifyQueue = (function () {
  let callbackDic = {};
  let queue = [];
  let playing = false;

  function play(obj) {
    if (memoryStore.get(memoryStoreKey.ifLogout)) {
      callbackDic = {};
      queue = [];
      playing = false;
    }
    obj()
      .then(() => {
        if (queue.length > 0) {
          play(queue.shift());
        } else {
          playing = false;
        }
      })
      .catch((err) => {
        reporter.error(err, 'notifyManager', 40, 0);
      });
  }

  function push(obj) {
    if (playing) {
      queue.push(obj);
    } else {
      playing = true;
      play(obj);
    }
  }

  function setHandle(key, handle) {
    callbackDic[key] = handle;
    return key;
  }
  function getHandle(key) {
    const handle = callbackDic[key];
    if (_.isFunction(handle)) {
      delete callbackDic[key];
    }
    return handle;
  }
  return {
    push,
    setHandle,
    getHandle,
  };
}());

ipcRenderer.on(channelsType.clickNotification, (event, key) => {
  const callbackHandle = notifyQueue.getHandle(key);
  if (_.isFunction(callbackHandle)) {
    callbackHandle();
  }
});

export const playSound = (soundType, volume, callBack) => {
  const audios = memoryStore.get(memoryStoreKey.cacheSounds);
  if (!audios) {
    if (process.env.NODE_ENV !== 'production') {
      console.debug('[NOTIFICATION]: audios is missing');
    }
    return;
  }
  const audio = audios[soundType];
  if (!_.isUndefined(audio)) {
    audio.volume = volume;
    if (audio.paused) {
      audio.play();
      if (_.isFunction(callBack)) {
        audio.onended = callBack;
      }
    } else {
      setTimeout(() => {
        if (audio.paused) {
          audio.play();
          if (_.isFunction(callBack)) {
            audio.onended = callBack;
          }
        } else if (_.isFunction(callBack)) {
          callBack();
        }
      }, 300);
    }
    if (process.env.NODE_ENV !== 'production') {
      console.debug('[NOTIFICATION]: sound played.', soundType);
    }
  } else if (process.env.NODE_ENV !== 'production') {
    console.debug('[NOTIFICATION]: given audio cannot be found', soundType);
  }
};
export const showNotification = ({
  title = '    ',
  soundType = 0,
  volume = 1,
  message = '',
  segments = [],
  popupOn = true,
  soundOn = false,
}, onClick) => {
  /**
   * @todo
   * if directly call resolve() at the end of callback function, this promise is useless.
   * more reasonable implementation will be, having resolve as a callback in playSound,
   * or call resolve when notify.onshow, etc.
   */
  notifyQueue.push(() => new Promise((resolve) => {
    if (soundOn) {
      playSound(soundType, volume, () => {
      });
    }
    if (popupOn && !isActive) {
      if (process.env.IS_WEB_APP) {
        if (Notification.permission === 'granted') {
          const notify = new Notification(title, {
            icon: image,
            body: message +
            (segments.length > 0 ? segments.map(seg => seg.name).join(', ') : ''),
            tag: 'comm100_notify',
            silent: true,
          });
          if (process.env.NODE_ENV !== 'production') {
            console.debug('[NOTIFICATION]: popup has been triggered', soundType);
          }
          notify.onclick = () => {
            window.focus();
            notify.close();
            if (onClick) onClick();
            if (!process.env.IS_WEB_APP) {
              ipcRenderer.send(channelsType.activeDesktopApp);
            }
          };
          notify.onshow = () => {
            if (timer !== null) {
              clearTimeout(timer);
            }
            timer = setTimeout(() => {
              notify.close();
            }, 5000);
          };
        }
      } else {
        const safeMessage = helper.htmlEncode(message);
        const safeSegments = _.map(segments, item => ({
          color: item.color,
          description: item.name,
        }));
        const key = `${soundType}_${Date.now()}`;
        notifyQueue.setHandle(key, onClick);
        ipcRenderer.send(channelsType.showNotification, {
          key,
          title,
          message: safeMessage,
          segments: safeSegments,
        });
      }
      ipcRenderer.send(channelsType.highlightTaskBar);
    }
    resolve();
  }));
};



