import _ from 'lodash';
import * as pages from '../../constants/enums/page';
import { agentTabs, userAgentNotSupportNotification } from '../../constants/enums';
import { getNumber } from '../../utils/localStorage';
import * as localStorageKeys from '../../constants/localStorage';

export const childWindowIsReady = state => state.ui.childWindowIsReady;
export const getResponseErrorMessage = state => state.ui.mychats.responseErrorMessage;
export const getSendEmailSuccessMessage = state => state.ui.mychats.sendEmailSuccessMessage;
export const getSelectedChatId = state => state.ui.mychats.selected;
export const getFontSize = state => state.ui.mychats.fontSize;
export const getPage = state => state.ui.page;
export const getIsReading = (state, chatId) =>
  getPage(state) === pages.chats && getSelectedChatId(state) === chatId;

export const getCurrentTab = (state) => {
  if (state.ui.page === pages.chats) {
    return agentTabs.chat;
  } else if (state.ui.page === pages.agents) {
    return agentTabs.agent;
  }
  return agentTabs.visitor;
};

export const getIsAgentTab = state => getPage(state) === pages.agents;

export const getVisitorDetailsSelectedTab = state =>
  (getPage(state) === pages.chats
    ? state.ui.mychats.visitorDetailsTabSelected
    : state.ui.visitors.visitorDetailsTabSelected);

export const getSelectedVisitorId = state => state.ui.visitors.selected;
export const getIfShowDetailsInVisitorList = state => state.ui.visitors.showDetails;
export const getVisitorsHistoryChatId = state => state.ui.visitors.historyChatId;
export const getVisitorsHistoryMessageId = state => state.ui.visitors.historyMessageId;
export const getVisitorsHistoryRefreshing = state => state.ui.visitors.historyRefreshing;
export const getVisitorsHistoryErrorInfo = state =>
({
  getChatHistoryError: state.ui.visitors.getChatHistoryError,
  getOfflineMessageHistoryError: state.ui.visitors.getOfflineMessageHistoryError,
  getChatHistoryDetailsError: state.ui.visitors.getChatHistoryDetailsError,
  getOfflineMessageDetailsError: state.ui.visitors.getOfflineMessageDetailsError,
});

export const getHistoryViewType = state => state.ui.visitors.historyViewType;

export const getIfNavigationLoading = state => state.ui.visitors.ifNavigationLoading;

export const getSelectedIdForAgents = state => state.ui.agents.selectedId;
export const getAgentsListWidth = () => getNumber(localStorageKeys.agentsListWidth, 230);

/** @todo this is not selector, should not be here */

export const getListVisitorsWidth = () => getNumber(localStorageKeys.listVisitorsWidth, 230);
export const getAgentsOpenedItems = state => state.ui.agents.openedItems;
export const ifAgentChatWithOthers = (state, agentId) => {
  if (getPage(state) !== pages.agents) return false;
  const selectedId = getSelectedIdForAgents(state);
  const arrTemp = _.isString(selectedId) ? selectedId.split('_') : [];
  if (arrTemp.length === 0) return false;
  return arrTemp.length > 1 ? arrTemp[1] === agentId.toString() : false;
};
export const getChatDetailsWidth = state => state.ui.mychats.chatDetailsWidth;
/**
 * Get config for visitor list
 *
 * @param {object} state - Total state.
 * @return {{selected:number,sortOrder:string,sortColumn:number,filter:string,customFilter:{}}} conf
 */
export const getVisitorsUiConfig = state => state.ui.visitors;


/**
 * Get sort order.
 *
 * @param {object} state - Total state.
 * @return {"desc"|"asc"} sort order.
 */
export const getVisitorUiSortOrder = state => state.ui.visitors.sortOrder;
export const getVisitorUiSortColumnConfig = state => state.ui.visitors.sortColumn;
export const getLeaveMessageProps = state => ({
  ifOpen: state.ui.leaveConfirm,
  title: 'Comm100 Live Chat',
  message: state.ui.leaveMessage,
});

export const getConfirmBeforeCloseProps = state => ({
  ifOpen: state.ui.confirmBeforeClose,
  message: state.ui.confirmBeforeCloseMessage,
  type: state.ui.confirmBeforeCloseData.type,
  data: state.ui.confirmBeforeCloseData.data,
});

export const getCloseWindowFlag = state => state.ui.ifOpenCloseWindow;
export const getDashboardWindowFlag = state => state.ui.ifOpenDashboardWindow;

export const getShowErrorOnHeaderFlag = state => state.ui.ifShowErrorOnHeader;
export const getErrorMessageOnHeader = state => state.ui.errorMessageOnHeader;

export const getIfSupportNotification = () => {
  const userAgent = navigator.userAgent;
  let ifSupport = true;
  for (const key of Object.keys(userAgentNotSupportNotification)) {
    if (userAgent.toLowerCase().indexOf(userAgentNotSupportNotification[key]) > 0) {
      ifSupport = false;
      break;
    }
  }
  return ifSupport;
};
export const getIfPopupWindowForVisitors = state =>
  state.ui.ifPopupWindowForVisitors;
export const getHasUpdate = state => state.ui.hasUpdate;



