import * as _ from 'lodash';
import Client from './client';
import moderator from '../../services/moderator';
import { errorCode } from '../../constants/web';
import * as selectors from '../../reducers/selectors';
import * as chatAction from '../../constants/enums/chatAction';
import { serializationRecoverChat } from '../../utils/serialization';
import { agentStatus } from '../../constants/enums';
import autoLogin from './autoLogin';

const switchServerCommands = (state) => {
  const status = selectors.getCurrentAgentStatus(state);
  const recoverChats = selectors.getRecoverChats(state);
  const loginCommand = autoLogin(state, agentStatus.away);
  const recoverChatsCommand = {
    type: chatAction.agentRestoreChats,
    message: serializationRecoverChat(recoverChats),
  };
  const getVisitors = {
    type: chatAction.agentGetVisitors,
    visitorId: -1,
  };
  const switchStatus = {
    type: chatAction.agentSetStatus,
    message: `${status}`,
  };
  return [loginCommand, recoverChatsCommand, getVisitors, switchStatus];
};
/**
 * {
 *  errorCode: 0,
 *  errorString: '',
 *  switched: false;
 *  isUseMainServer: false,  // isUseMainServer,
 *  newSession: '',
 *  recoverVisitorIds: [
 *    {
 *      oldVisitorId: 1,
 *      visitorId: 2,
 *      chatId: 3,
 *    }
 *  ],
 *  visitors: {},
 * }
 */
/* eslint-disable no-param-reassign */
/* eslint-disable import/prefer-default-export */
export const switchServer = (state) => {
  const server = selectors.getModeratorServer(state);
  const siteId = selectors.getSiteId(state);
  const isUseMainServer = selectors.getIfUseMainServer(state);
  const switchResult = {};
  return new Promise((resolve) => {
    moderator(server, siteId)
      .then((isCurrentUseMainServer) => {
        if (isUseMainServer !== isCurrentUseMainServer) {
          switchResult.switched = true;
          switchResult.isUseMainServer = isCurrentUseMainServer;
          const chatServerUrl = selectors.getLiveChatHandler3(state, isCurrentUseMainServer);
          const commands = switchServerCommands(state);
          _.each(commands, (cmd, i) => { cmd.id = i + 2; });
          const req = {
            commands,
            session: '',
            siteId: 0,
            localChats: [],
            agentId: 0,
          };
          const client = new Client(`${chatServerUrl}?siteId=${siteId}`);
          client.promiseSend(req)
            .then((resp) => {
              if (resp.errorCode !== errorCode.noerror) {
                switchResult.errorCode = resp.errorCode;
                switchResult.errorString = resp.errorString;
                resolve(switchResult);
              }

              _.each(resp.commands, (cmd) => {
                if (cmd.errorCode === errorCode.noerror) {
                  if (cmd.type === chatAction.agentLogin ||
                    cmd.type === chatAction.agentAutoLogin) {
                    switchResult.newSession = cmd.errorString;
                  }
                  if (cmd.type === chatAction.agentRestoreChats) {
                    switchResult.recoverVisitorIds = cmd.extend.recoverVisitorIds;
                  }
                  if (cmd.type === chatAction.agentGetVisitors) {
                    switchResult.visitors = cmd.visitors;
                  }
                } else {
                  switchResult.errorCode = cmd.errorCode;
                  switchResult.errorString = cmd.errorString;
                }
              });
              resolve(switchResult);
            })
            .catch(() => {
              switchResult.switched = false;
              resolve(switchResult);
            });
        } else {
          switchResult.switched = false;
          resolve(switchResult);
        }
      });
  });
};




