export const getTimezone = state => state.config.preference.timeZone;
export const getTimezoneOffset = state => state.config.preference.offset;
export const getDateTimeFormat = state => state.config.preference.dateTimeFormat;
export const textDirectionIfRTL = state => state.config.preference.textDirectionIfRTL;
export const getIfEnableTranslate = state => state.config.preference.ifEnableTranslate;

export const getIfSalesforceManually = state =>
  getSiteInfo(state).ifEnableSFIntegration
  && state.config.salesforce
  && state.config.salesforce.integrationConfig
  && state.config.salesforce.integrationConfig.ifManually;
export const ifEnableSFIntegration = state =>
  state.config.settings.siteInfo
  && state.config.settings.siteInfo.ifEnableSFIntegration;
export const getIfAutoAway = state => state.config.preference.others.ifAutoAway;
export const getAutoAwayMinutes = state => state.config.preference.others.autoAwayMinutes;
export const getIfAutoLogoff = state => state.config.preference.others.ifAutoLogoff;
export const getAutoLogoffMinutes = state => state.config.preference.others.autoLogoffMinutes;



