import { createStore, applyMiddleware, compose } from 'redux';
import * as _ from 'lodash';
import promise from 'redux-promise';
import * as config from 'src/config';
import web from './web/index';
import reducer from '../reducers/index';
import { webRequest } from '../constants/web';
// import subscribeOptimizer from './subscribeOptimizer';
import notification from './notification';
import chatFilter from './chatFilter';
import globalError from './globalError';
import windowsManager from './windowsManager';
// import batchWrapper from './batchWrapper';
import { isChildWindow } from '../utils/helper';

let cachedConfig = config;

function getDebugSessionKey() {
  // You can write custom logic here!
  // By default we try to read the key from ?debug_session=<key> in the address bar
  const matches = window.location.href.match(/[?&]debug_session=([^&#]+)\b/);
  return (matches && matches.length > 0) ? matches[1] : null;
}

const isDebugMode = window.location.search.slice(1).split('&').some(key => key === 'debug');

let enhancer;
if (process.env.NODE_ENV === 'production') {
  let middleware = null;
  if (isChildWindow()) {
    middleware = applyMiddleware(
      promise,
      windowsManager
    );
  } else {
    middleware = applyMiddleware(
      promise,
      web,
      chatFilter,
      windowsManager,
      notification,
      globalError
    );
  }
  if (isDebugMode) {
    enhancer = compose(
      middleware,
      require('./actionLogger').default, // eslint-disable-line global-require
      // subscribeOptimizer
    );
  } else {
    enhancer = compose(
      middleware,
      // subscribeOptimizer
    );
  }
} else {
  const { persistState } = require('redux-devtools'); // eslint-disable-line
  const createLogger = require('redux-logger').default; // eslint-disable-line
  const DevTools = require('../containers/DevTools/index').default; // eslint-disable-line

  const loggerWebRequest = createLogger({
    duration: true,
    collapsed: true,
    predicate: ($, action) => cachedConfig.logWebRequest && action.type === webRequest,
  });

  const logger = createLogger({
    duration: true,
    collapsed: true,
    predicate: ($, action) =>
      cachedConfig.logActions &&
      action.type.indexOf('chat/update-scrollTop') < 0 &&
      (cachedConfig.logTypingActions || action.type.indexOf('editor/update-content') < 0) &&
      (cachedConfig.logReduxFormAction || action.type.indexOf('redux-form') < 0) &&
      (cachedConfig.logWebResponse || action.type.indexOf('webResponse') < 0) &&
      (action.type !== 'batch' || action.payload.length > 0),
    stateTransformer: (state) => {
      const newState = Object.assign({}, state);
      newState.chats = state.chats.toJS();
      newState.agentChats = state.agentChats.toJS();
      newState.visitor = Object.assign({}, state.visitor);
      newState.visitor.visitors = state.visitor.visitors.toJS();
      return newState;
    },
  });
  if (process.env.IS_WEB_APP) {
    enhancer = compose(
      applyMiddleware(
        promise,
        loggerWebRequest,
        web,
        chatFilter,
        notification,
        globalError,
        logger
      ),
      // subscribeOptimizer,
      cachedConfig.useReduxDevToolsComponent ? DevTools.instrument() : _.identity,
      persistState(getDebugSessionKey())
    );
  } else {
    let middleware = null;
    if (isChildWindow()) {
      middleware = applyMiddleware(
        promise,
        windowsManager
      );
      enhancer = compose(
        middleware,
        cachedConfig.useReduxDevToolsComponent ? DevTools.instrument() : _.identity,
        persistState(getDebugSessionKey())
      );
    } else {
      middleware = applyMiddleware(
        promise,
        loggerWebRequest,
        web,
        chatFilter,
        windowsManager,
        notification,
        globalError,
        logger
      );
      enhancer = compose(
        middleware,
        require('./actionLogger').default, // eslint-disable-line global-require
        cachedConfig.useReduxDevToolsComponent ? DevTools.instrument() : _.identity,
        persistState(getDebugSessionKey())
      );
    }
  }
}

export default function configureStore(initialState) {
  const store = createStore(
    reducer,
    initialState,
    enhancer
  );

  if (module.hot) {
    module.hot.accept('../reducers/index', () => {
      store.replaceReducer(require('../reducers/index').default); // eslint-disable-line
    });
    module.hot.accept('../config', () => {
      const previousUseReduxDevToolsComponent = cachedConfig.useReduxDevToolsComponent;
      cachedConfig = require('../config'); // eslint-disable-line
      if (previousUseReduxDevToolsComponent !== cachedConfig.useReduxDevToolsComponent) {
        throw new Error('Full reload is required when useReduxDevToolsComponent is updated.');
      }
    });
  }

  if (process.env.NODE_ENV !== 'production') {
    window.store = store;
  }

  return store;
}



