import { connect } from 'react-redux';
import * as selectors from '../reducers/selectors';
import Confirmation from '../components/Modal/Confirmation/Confirmation';
import * as uiActions from '../actions/ui';
import * as agentAction from '../actions/agent';

const mapStateToProps = state => (selectors.getConfirmBeforeCloseProps(state));

const mergeProps = (stateProps, dispatchProps) => {
  const { dispatch } = dispatchProps;
  const dispatchObject = {
    onConfirm() {
      if (stateProps.type === 'ManuallyInvited') {
        dispatch(agentAction.cancelManuallyInvited(stateProps.data.visitorId));
        dispatch(uiActions.switchConfirmBeforeCloseWindow(false));
      }
    },
    onCancel() {
      dispatch(uiActions.switchConfirmBeforeCloseWindow(false));
    },
  };
  return {
    ...stateProps,
    ...dispatchObject,
  };
};

export default connect(mapStateToProps, null, mergeProps)(Confirmation);



