// 维护heartbeat时钟，发送heartbeat请求，记录visitorListVersion，每一个聊天的maxMessageId
// TODO (Roy): 移到subscriptions/heartbeat.js
import * as _ from 'lodash';
import * as chatAction from '../../constants/enums/chatAction';
import * as selectors from '../../reducers/selectors';
import * as serializer from '../../utils/serialization';
import { historyType, sessionType } from '../../constants/enums';
import { objectType } from '../../constants/salesforce';

// 部分命令需要马上更新visitorList，在这个命令后面加上heartbeat命令
// 处理不同的action实际网络发包情况

const createCommands = (action, ...types) =>
  _.concat(action, types.map(type => ({ type })), { type: chatAction.agentGetChats });

const createVisitorCommands = (type, visitorId, next) =>
  createCommands({ type, visitorId, next }, chatAction.agentGetVisitors);

const createChatCommands = (type, { visitorId, message }) =>
  createCommands({ type, visitorId, message });

const createPreChatCommands = (state, visitorId, data) => {
  const preChat = Object.assign(
    selectors.getVisitorPreChatForm(state, visitorId),
    data);
  return createCommands({
    type: chatAction.agentSetPreChatForm,
    visitorId,
    message: serializer.serializationPreChat(preChat),
  },
    chatAction.agentGetVisitors);
};

// should use another network queue
const createHistoryCommands = (visitorId, type) =>
  [{
    type: chatAction.agentGetHistories,
    visitorId,
    message: `${type}`,
  }];

const createHistoryDetailCommands = (visitorId, historyId, type) =>
  [{
    type: chatAction.agentGetVisitorContactHistoryDetails,
    visitorId: historyId,
    message: `${type}`,
    sender: `${visitorId}`,
  }];

const createSFObjectFieldCommands = (siteId, fieldType) =>
  [{
    type: chatAction.agentGetSFObjectFields,
    visitorId: siteId,
    message: `${fieldType}`,
  }];

const createSFObjectManagerCommands = (type, visitorId, fieldValues) =>
  [{
    type,
    visitorId,
    message: serializer.serializationSFFieldValues(fieldValues),
  }];

const actionMapping = {
  [chatAction.agentLogout]() {
    return [{
      type: chatAction.agentLogout,
    }];
  },
  [chatAction.agentSendText](state, payload) {
    return createChatCommands(chatAction.agentSendText, payload);
  },
  [chatAction.agentSendImageStart](state, { visitorId, imageId }, next) {
    return createCommands({
      type: chatAction.agentSendImageStart,
      visitorId,
      message: `${imageId}`,
      next,
    });
  },
  [chatAction.agentPromoteToUser](state, visitorId) {
    return createVisitorCommands(chatAction.agentPromoteToUser, visitorId);
  },
  [chatAction.agentPromoteToContact](state, visitorId) {
    return createVisitorCommands(chatAction.agentPromoteToContact, visitorId);
  },
  // visitor actions
  [chatAction.agentAcceptChat](state, visitorId) {
    return createVisitorCommands(chatAction.agentAcceptChat, visitorId);
  },
  [chatAction.agentRefuseChat](state, visitorId) {
    return createVisitorCommands(chatAction.agentRefuseChat, visitorId);
  },
  [chatAction.agentEndChat](state, visitorId) {
    return createVisitorCommands(chatAction.agentEndChat, visitorId);
  },
  [chatAction.agentAcceptTransfer](state, visitorId) {
    return createVisitorCommands(chatAction.agentAcceptTransfer, visitorId);
  },
  [chatAction.agentRefuseTransfer](state, visitorId) {
    return createVisitorCommands(chatAction.agentRefuseTransfer, visitorId);
  },
  [chatAction.agentJoinChat](state, visitorId, next) {
    return createVisitorCommands(chatAction.agentJoinChat, visitorId, next);
  },
  [chatAction.agentMonitorChat](state, visitorId, next) {
    return createVisitorCommands(chatAction.agentMonitorChat, visitorId, next);
  },
  [chatAction.agentVideoChatRequest](state, visitorId) {
    return createVisitorCommands(chatAction.agentVideoChatRequest, visitorId);
  },
  [chatAction.agentAudioChatRequest](state, visitorId) {
    return createVisitorCommands(chatAction.agentAudioChatRequest, visitorId);
  },
  [chatAction.agentAudioChatAccept](state, visitorId) {
    return createVisitorCommands(chatAction.agentAudioChatAccept, visitorId);
  },
  [chatAction.agentVideoChatAccept](state, visitorId) {
    return createVisitorCommands(chatAction.agentVideoChatAccept, visitorId);
  },
  [chatAction.agentAudioChatRefuse](state, visitorId) {
    return createVisitorCommands(chatAction.agentAudioChatRefuse, visitorId);
  },
  [chatAction.agentVideoChatRefuse](state, visitorId) {
    return createVisitorCommands(chatAction.agentVideoChatRefuse, visitorId);
  },
  [chatAction.agentAudioChatCancelRequest](state, visitorId) {
    return createVisitorCommands(chatAction.agentAudioChatCancelRequest, visitorId);
  },
  [chatAction.agentVideoChatCancelRequest](state, visitorId) {
    return createVisitorCommands(chatAction.agentVideoChatCancelRequest, visitorId);
  },
  [chatAction.agentAudioChatStop](state, visitorId) {
    return createVisitorCommands(chatAction.agentAudioChatStop, visitorId);
  },
  [chatAction.agentVideoChatStop](state, visitorId) {
    return createVisitorCommands(chatAction.agentVideoChatStop, visitorId);
  },
  [chatAction.agentCancelManuallyInvited](state, visitorId) {
    return createCommands({ type: chatAction.agentCancelManuallyInvited, visitorId });
  },
  [chatAction.agentTransferChat](state, { visitorId, agentId, ifLeaveChat }) {
    return createCommands(
      { type: chatAction.agentTransferChat, visitorId, message: `${agentId}`, sender: `${ifLeaveChat}` },
      chatAction.agentGetVisitors
    );
  },
  [chatAction.agentTransferChatToDepartment](state, { visitorId, departmentId, ifLeaveChat }) {
    return createCommands(
      { type: chatAction.agentTransferChatToDepartment, visitorId, message: `${departmentId}`, sender: `${ifLeaveChat}` },
      chatAction.agentGetVisitors
    );
  },
  [chatAction.agentCaptureVisitor](state, visitorId) {
    return createCommands(
      { type: chatAction.agentCaptureVisitor, visitorId, message: 'true' },
      chatAction.agentGetVisitors
    );
  },
  [chatAction.agentReleaseVisitor](state, visitorId) {
    return createCommands(
      { type: chatAction.agentCaptureVisitor, visitorId, message: 'false' },
      chatAction.agentGetVisitors
    );
  },
  [chatAction.agentBanVisitor](state, { visitorId, ban }) {
    return createCommands(
      {
        type: chatAction.agentBanVisitor,
        visitorId,
        message: serializer.serializationBan(ban),
      },
      chatAction.agentGetVisitors,
    );
  },
  [chatAction.agentInviteChatWithText](state, { visitorId, message }, next) {
    return createCommands(
      {
        type: chatAction.agentInviteChatWithText,
        visitorId,
        message,
        next,
      },
      chatAction.agentGetVisitors,
    );
  },
  [chatAction.agentEnableTranslate](state, { visitorId, language }) {
    return createChatCommands(chatAction.agentEnableTranslate,
      { visitorId, message: `${language}` });
  },
  [chatAction.agentDisableTranslate](state, visitorId) {
    return createChatCommands(chatAction.agentDisableTranslate, { visitorId, message: '' });
  },

  [chatAction.agentGetVisitors]() {
    return createCommands({ type: chatAction.agentGetVisitors });
  },
  [chatAction.agentGetChats]() {
    return [{ type: chatAction.agentGetChats }];
  },
  [chatAction.agentSetPreference](state) {
    const message = serializer.serializationPreference(selectors.getCurrentAgent(state));
    return createCommands({ type: chatAction.agentSetPreference, message });
  },
  [chatAction.agentCheckG2MAccount](state, { email, password }) {
    return createCommands({ type: chatAction.agentCheckG2MAccount, message: `${email}\u2299${password}` });
  },
  [chatAction.agentSaveG2MAccount](state, { email, password }) {
    return createCommands({ type: chatAction.agentSaveG2MAccount, message: `${email}\u2299${password}` });
  },
  [chatAction.agentCreateG2MMeeting](state, visitorId) {
    return createCommands({ type: chatAction.agentCreateG2MMeeting, visitorId });
  },
  [chatAction.agentSetStatus](state, status) {
    return createCommands({ type: chatAction.agentSetStatus, message: `${status}` });
  },
  [chatAction.agentGetAgents]() {
    return createCommands({ type: chatAction.agentGetDepartments }, chatAction.agentGetAgents);
  },
  [chatAction.agentUseCannedMessage](state, { chatId, id }) {
    const siteId = selectors.getSiteId(state);
    const agentId = selectors.getCurrentAgentId(state);
    return createCommands({
      type: chatAction.agentUseCannedMessage,
      message: serializer.serializationUseCannedMessageRecord(siteId, agentId, chatId, id),
    });
  },
  [chatAction.agentSetPreChatForm](state, { visitorId, preChat }) {
    return createPreChatCommands(state, visitorId, preChat);
  },
  [chatAction.agentSetCustomFields](state, { visitorId, fields }) {
    return createPreChatCommands(state, visitorId, { customFields: fields });
  },
  [chatAction.agentSetComment](state, { visitorId, comment }) {
    return createPreChatCommands(state, visitorId, { comment });
  },
  [chatAction.agentSubmitWrapup](state, { visitorId, wrapup }) {
    return createCommands({
      type: chatAction.agentSubmitWrapup,
      visitorId,
      message: serializer.serializationWrapup(wrapup),
    });
  },
  [chatAction.agentSendPrivateMessageToAgent](state, { agentId, message }) {
    return createCommands({
      type: chatAction.agentSendPrivateMessageToAgent,
      visitorId: agentId,
      message,
    });
  },
  [chatAction.agentSendFileToAgent](state, { agentId, fileName, guid }) {
    return createCommands({
      type: chatAction.agentSendFileToAgent,
      visitorId: agentId,
      message: fileName,
      sender: guid,
    });
  },
  [chatAction.agentSetAnotherAgentStatus](state, { agentId, status }) {
    return createCommands(
      {
        type: chatAction.agentSetAnotherAgentStatus,
        visitorId: agentId,
        message: `${status}`,
      },
      chatAction.agentGetAgents,
    );
  },
  [chatAction.agentGetNavigations](state, visitorId) {
    return createCommands({ type: chatAction.agentGetNavigations, visitorId });
  },

  [chatAction.agentAttachNewTicket](state, visitorId) {
    return createCommands({
      type: chatAction.agentAttachNewTicket,
      visitorId,
    });
  },
  [chatAction.agentAttachExistingTicket](state, { visitorId, ticketId }) {
    return createCommands({
      type: chatAction.agentAttachExistingTicket,
      visitorId,
      message: ticketId,
    });
  },
  [chatAction.agentSendEmail](state, { chatId, email }, next) {
    return createCommands({
      type: chatAction.agentSendEmail,
      visitorId: chatId,
      message: serializer.serializationChatEmail(email),
      next,
    });
  },
  [chatAction.agentRequestSecureForm](state, { visitorId, secureFormName, token }) {
    return createCommands(
      {
        type: chatAction.agentRequestSecureForm,
        visitorId,
        message: secureFormName,
        sender: token,
      },
      chatAction.agentGetChats,
    );
  },


  [chatAction.agentGetSFIntegrationConfig](state, siteId) {
    return createCommands({ type: chatAction.agentGetSFIntegrationConfig, visitorId: siteId });
  },
  [chatAction.agentCheckSFAutoIntegrationResult](state, visitorId) {
    return createCommands({ type: chatAction.agentCheckSFAutoIntegrationResult, visitorId });
  },
  [chatAction.agentSetDashboardMessageNotShow](state, ids) {
    return createCommands({ type: chatAction.agentSetDashboardMessageNotShow, message: ids.join(':') });
  },
  [chatAction.agentMonitorAllChats](state) {
    return selectors.getAllCanMonitorChatVisitors(state)
      .map(visitorId => ({ type: chatAction.agentMonitorChat, visitorId }))
      .toArray()
      .concat({ type: chatAction.agentGetVisitors }, { type: chatAction.agentGetChats });
  },


  // long time requests
  [chatAction.agentGetChatHistory](state, visitorId) {
    return createHistoryCommands(visitorId, historyType.chat);
  },
  [chatAction.agentGetOfflineMessageHistory](state, visitorId) {
    return createHistoryCommands(visitorId, historyType.offlineMessage);
  },
  [chatAction.agentGetSessionHistory](state, visitorId) {
    return createHistoryCommands(visitorId, historyType.navigation);
  },
  [chatAction.agentGetWrapupHistory](state, visitorId) {
    return createHistoryCommands(visitorId, historyType.wrapup);
  },
  [chatAction.agentGetChatDetails](state, { visitorId, chatId }) {
    return createHistoryDetailCommands(visitorId, chatId, sessionType.chat);
  },
  [chatAction.agentGetOfflineMessageDetails](state, { visitorId, offlineMessageId }) {
    return createHistoryDetailCommands(visitorId, offlineMessageId, sessionType.offlineMessage);
  },
  [chatAction.agentGetNavigationDetails](state, { visitorId, contactId }) {
    return createHistoryDetailCommands(visitorId, contactId, sessionType.navigation);
  },
  [chatAction.agentGetVisitorContactWrapupDetails](state, { visitorId, chatId }) {
    return [{
      type: chatAction.agentGetVisitorContactWrapupDetails,
      visitorId: chatId,
      message: `${historyType.chat}`,
      sender: `${visitorId}`,
    }];
  },

  [chatAction.agentGetSFContactFields](state, siteId) {
    return createSFObjectFieldCommands(siteId, objectType.contact);
  },
  [chatAction.agentGetSFLeadFields](state, siteId) {
    return createSFObjectFieldCommands(siteId, objectType.lead);
  },
  [chatAction.agentGetSFAccountFields](state, siteId) {
    return createSFObjectFieldCommands(siteId, objectType.account);
  },
  [chatAction.agentCreateSFContact](state, { visitorId, fieldValues }) {
    return createSFObjectManagerCommands(chatAction.agentCreateSFContact,
      visitorId, fieldValues);
  },
  [chatAction.agentEditSFContact](state, { visitorId, fieldValues }) {
    return createSFObjectManagerCommands(chatAction.agentEditSFContact,
      visitorId, fieldValues);
  },
  [chatAction.agentCreateSFLead](state, { visitorId, fieldValues }) {
    return createSFObjectManagerCommands(chatAction.agentCreateSFLead,
      visitorId, fieldValues);
  },
  [chatAction.agentEditSFLead](state, { visitorId, fieldValues }) {
    return createSFObjectManagerCommands(chatAction.agentEditSFLead,
      visitorId, fieldValues);
  },
  [chatAction.agentCreateSFCase](state, visitorId) {
    return [{ type: chatAction.agentCreateSFCase, visitorId }];
  },
  [chatAction.agentAttachSFCaseToContact](state, { visitorId, contactId }) {
    return [{ type: chatAction.agentAttachSFCaseToContact, visitorId, message: `${contactId}` }];
  },
  [chatAction.agentAttachSFTaskToLead](state, { visitorId, leadId }) {
    return [{ type: chatAction.agentAttachSFTaskToLead, visitorId, message: `${leadId}` }];
  },
  [chatAction.agentLookupSFObject](state, { type, keyword }, next) {
    return [{ type: chatAction.agentLookupSFObject, message: `${type},${keyword}`, next }];
  },
};


export function adapter(state, action) {  // eslint-disable-line
  if (actionMapping[action.type] == null) {
    return createCommands({ type: action.type });
  }
  return actionMapping[action.type](state, action.payload, action.next);
}

const longTimeRequests = [
  chatAction.agentGetSFContactFields,
  chatAction.agentGetSFLeadFields,
  chatAction.agentGetSFAccountFields,
  chatAction.agentCreateSFContact,
  chatAction.agentEditSFContact,
  chatAction.agentCreateSFLead,
  chatAction.agentEditSFLead,
  chatAction.agentCreateSFCase,
  chatAction.agentAttachSFCaseToContact,
  chatAction.agentAttachSFTaskToLead,
  chatAction.agentLookupSFObject,
  chatAction.agentGetChatHistory,
  chatAction.agentGetOfflineMessageHistory,
  chatAction.agentGetSessionHistory,
  chatAction.agentGetWrapupHistory,
  chatAction.agentGetChatDetails,
  chatAction.agentGetOfflineMessageDetails,
  chatAction.agentGetNavigationDetails,
  chatAction.agentGetVisitorContactWrapupDetails,
];

export const isLongTimeRequest = action =>
  longTimeRequests.indexOf(action.type) !== -1;




