import React from 'react';
import { Provider } from 'react-redux';
import * as config from 'src/config';
import App from './App';
import DevTools from './DevTools/index';

let devToolComponent = null;

if (process.env.NODE_ENV !== 'production') {
  devToolComponent = <DevTools />;
}

const Root = ({ store }) => (
  <Provider store={store}>
    <div style={{ height: '100%' }}>
      <App key={Date.now()} />
      {config.useReduxDevToolsComponent && devToolComponent}
    </div>
  </Provider>
);

Root.propTypes = {
  store: React.PropTypes.shape({}).isRequired,
};

export default Root;



