import React from 'react';
import Modal from '../Modal';
import ModalStyle from '../Modal.css';
// import Button from '../../Button/Button';

const ConfirmationModal = props => (
  props.ifOpen && <Modal
    closeModal={props.onCancel}
    className={ModalStyle.forceLoginModal}
    title={props.title}
    actions={{
      okProps: { text: 'OK', eventClick: props.onConfirm },
      cancelProps: { text: 'Cancel' },
    }}
  >
    <p dangerouslySetInnerHTML={{ __html: props.message }} />
  </Modal>
);

export default ConfirmationModal;

ConfirmationModal.propTypes = {
  ifOpen: React.PropTypes.bool.isRequired,
  title: React.PropTypes.string.isRequired,
  message: React.PropTypes.string.isRequired,
  onConfirm: React.PropTypes.func.isRequired,
  onCancel: React.PropTypes.func.isRequired,
};

ConfirmationModal.defaultProps = {
  title: 'Comm100 Live Chat',
};



