import React, { PropTypes } from 'react';
import * as _ from 'lodash';
import IconButton from '../Button/Button';
import styles from './Header.css';
import { agentStatus, icons } from '../../constants/enums';
import MenuItem from './MenuItem';
import MenuContent from './MenuContent';
import Icon from '../Icon/Icon';
import TrialInfo from './TrialInfo';
import Upgrade from './Upgrade';
import * as selectors from '../../reducers/selectors';
import CommonStyle from '../CommonResources/common.css';
import Logo from '../Image/Logo';
import { portalLivechatUrl } from '../../constants/login';

class Header extends React.Component {
  constructor(props) {
    super(props);
    this.openOldVersion = this.openOldVersion.bind(this);
  }


  shouldComponentUpdate(nextProps) {
    return !_.isEqual(nextProps, this.props);
  }

  openOldVersion() {
    if (process.env.IS_WEB_APP) {
      window.location.href =
      `${portalLivechatUrl}/VisitorMonitor.aspx?siteId=${this.props.siteId}&forceLogin=true&agent=${this.props.agent}&token=${this.props.token}&fromAgentConsole=true&ls=1`;
    }
  }

  render() {
    const props = this.props;
    const settingElement = (<MenuContent
      items={props.settings}
      clickHandle={props.clickSetting}
    />);
    const statusElement = (<MenuContent
      items={props.agentStatus}
      clickHandle={props.clickStatus}
      hasIcon
    />);
    const t = _.find(props.agentStatus, item => item.code === props.statusCode);
    const statusDescription = t ? t.desc : undefined;
    const [iconType, iconColor, statusDesc] = selectors.getIconByCode(props.statusCode,
      statusDescription);
    return (
      <div className={CommonStyle.page}>
        <div className={styles.headSection}>
          {!_.isEmpty(props.agentInfo.trialRemind) && (
            <TrialInfo
              trialInfo={props.agentInfo}
              clickPurchase={props.clickPurchase}
              showBuyNow={props.showBuyNow}
            />
          )}
          <div className={styles.headLeft}>
            <Logo
              className={styles.logoStyle}
            />
          </div>
          <div className={styles.headRight}>
            {props.hasUpdate && <div className={styles.menuItem}>
              <Upgrade />
            </div>}
            {window.beta && (<div className={styles.menuItem}>
              <IconButton
                text="Switch to the Old Version"
                className={styles.iconButton}
                onClick={() => {
                  this.openOldVersion();
                }}
              >
                <Icon
                  type={icons.versionSwitch}
                  size={16}
                  className={styles.statusIcon}
                />
              </IconButton>
            </div>)}

            <MenuItem
              content={settingElement}
              text={'Settings'}
              type={icons.setting}
              buttonClass={styles.iconButton}
              className={styles.settingWraper}
            />
            <div className={styles.menuItem}>
              <IconButton
                text="Control Panel"
                className={styles.iconButton}
                onClick={() => {
                  props.gotoDashBoard(props.agentInfo);
                }}
              >
                <Icon
                  type={icons.controlPanel}
                  size={16}
                  className={styles.statusIcon}
                />
              </IconButton>
            </div>
            <MenuItem
              content={statusElement}
              type={iconType}
              color={iconColor}
              buttonClass={styles.iconButton}
              className={styles.statusWraper}
              agentStatus={{ name: props.agentName, status: statusDesc }}
            />
          </div>
        </div>
        <div className={CommonStyle.content}>
          {props.children}
        </div>
      </div>
    );
  }
}
Header.propTypes = {
  siteId: PropTypes.number.isRequired,
  agent: PropTypes.string.isRequired,
  token: PropTypes.string.isRequired,
  children: PropTypes.oneOfType([
    PropTypes.element,
    PropTypes.arrayOf(PropTypes.element),
    PropTypes.any,
  ]),
  agentStatus: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  settings: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  clickSetting: PropTypes.func.isRequired,
  clickStatus: PropTypes.func.isRequired,
  agentName: PropTypes.string.isRequired,
  statusCode: PropTypes.number,
  /* following props for trial info */
  agentInfo: PropTypes.shape({
    isWeb: PropTypes.bool,
    dashboardUrl: PropTypes.string,
    purchaseUrl: PropTypes.string,
    loginUrl: PropTypes.string,
    autoLoginUrl: PropTypes.string,
    trialRemind: PropTypes.string,
  }).isRequired,
  clickPurchase: PropTypes.func.isRequired,
  gotoDashBoard: PropTypes.func.isRequired,
};
Header.examples = {
  'Default Example': {
    description: [
      'Use `agentStatus` to pass in agent`s status',
      'Use `settings` to pass in the config info',
    ].join(''),
    component:
  <Header
    agentStatus={[
          { code: agentStatus.online, desc: 'Online' },
          { code: agentStatus.away, desc: 'Away' },
    ]}
    settings={[{ desc: 'Chat Setting' }, { desc: 'language' }]}
    agentName="Andy"
    statusCode={agentStatus.online}
  />,
  },
};
export default Header;



