import * as constants from '../constants/layout';
import * as page from '../constants/enums/page';
import * as ui from '../constants/types/ui';

export const closeError = () => ({
  type: ui.closeError,
});

export const operateGroupArrow = id => ({
  type: ui.operateGroupArrow,
  payload: id,
});

export const showLogin = () => ({
  type: constants.switchPage,
  payload: {
    page: page.login,
  },
});

export const showVisitor = () => ({
  type: constants.switchPage,
  payload: {
    page: page.visitors,
  },
});

export const showAgent = () => ({
  type: constants.switchPage,
  payload: {
    page: page.agents,
  },
});

export const showChat = () => ({
  type: constants.switchPage,
  payload: {
    page: page.chats,
  },
});

export const changeFontSize = selectedFontSize => ({
  type: ui.changeFontSize,
  payload: selectedFontSize,
});

export const changeVisitorListOrderBy = (enumColumn, customVariableId, order) => ({
  type: ui.changeVisitorListOrderBy,
  payload: order,
  meta: {
    enumColumn,
    customVariableId,
  },
});

export const changeVisitorListFilter = index => ({
  type: ui.changeVisitorListFilter,
  payload: index,
  meta: index,
});

export const selectVisitorListRow = index => ({
  type: ui.selectVisitorListRow,
  payload: index,
  meta: index,
});

export const toggleVisitorDetails = () => ({
  type: ui.toggleVisitorDetails,
});

export const showForceLogin = ifShowForceLogin => ({
  type: ui.showForceLogin,
  payload: {
    ifShowForceLogin,
  },
});

export const showSiteSelect = sites => ({
  type: ui.showSiteSelect,
  payload: {
    ifShowSiteSelect: true,
    sites,
  },
});

export const hideSiteSelect = () => ({
  type: ui.hideSiteSelect,
  payload: {
    siteClick: false,
    ifShowSiteSelect: false,
  },
});

export const handleSiteSelect = siteId => ({
  type: ui.handleSiteSelect,
  payload: {
    siteClick: true,
    siteId,
  },
});

export const showSettingsModal = selectedTab => ({
  type: ui.showSettingsModal,
  payload: {
    ifOpen: true,
    selectedTab,
    isTesting: false,
  },
});

export const hideSettingsModal = () => ({
  type: ui.hideSettingsModal,
  payload: {
    ifOpen: false,
    isTesting: false,
    ifTestG2ASuccess: false,
  },
});

export const testG2MConnection = () => ({
  type: ui.testG2MConnection,
  payload: {
    isTesting: true,
  },
});
export const formValuesChange = newValues => ({
  type: ui.activeIP,
  payload: {
    formValues: newValues,
  },
});
export const preStartSubmitLogin = () => ({
  type: ui.activeIP,
  payload: {
    agentLogout: 0,
    billUrl: '',
    ifShowSuggestion: false,
    loginSubmitError: '',
  },
});
export const startSubmitLogin = () => ({
  type: ui.activeIP,
  payload: {
    agentLogout: 0,
    billUrl: '',
    ifShowSuggestion: false,
    ifLoginSubmitting: true,
    loginSubmitError: '',
  },
});
export const loginFail = error => ({
  type: ui.activeIP,
  payload: {
    ifLoginSubmitting: false,
    loginSubmitError: error,
  },
});
export const loginSuccess = () => ({
  type: ui.activeIP,
  payload: {
    ifLoginSubmitting: false,
    loginSubmitError: '',
  },
});
export const saveSiteId = siteId => ({
  type: ui.saveSiteId,
  payload: {
    siteId,
  },
});
export const showVerificationCode = (verificationCodeUrl, sessionId) => ({
  type: ui.showVerificationCode,
  payload: {
    ifShowVerificationCode: true,
    verificationCodeUrl,
    sessionId,
  },
});

export const showChangePassword = hasExpired => ({
  type: ui.showChangePassword,
  payload: {
    ifShowChangePassword: true,
    hasExpired,
  },
});

export const hideChangePassword = () => ({
  type: ui.showChangePassword,
  payload: {
    ifShowChangePassword: false,
    changePasswordSuccess: false,
  },
});

export const saveUrls = urls => ({
  type: ui.saveUrls,
  payload: {
    urls,
  },
});

export const showModalLoading = () => ({
  type: ui.modalLoading,
  payload: true,
});

export const hideModalLoading = () => ({
  type: ui.modalLoading,
  payload: false,
});

export const refreshVCode = r => ({
  type: ui.refreshVCode,
  payload: r,
});

export const changePasswordSuccess = () => ({
  type: ui.changePasswordSuccess,
  payload: true,
});

export const showActiveIP = isAdmin => ({
  type: ui.activeIP,
  payload: {
    ifShowActiveIP: true,
    sendActivationIPEmail: false,
    isAdmin,
  },
});
export const showActiveIPLoading = () => ({
  type: ui.activeIP,
  payload: {
    activeIPLoading: true,
    activeIPError: '',
  },
});
export const hideActiveIP = () => ({
  type: ui.activeIP,
  payload: {
    ifShowActiveIP: false,
    activeIPLoading: false,
  },
});
export const activeIPSuccess = () => ({
  type: ui.activeIP,
  payload: {
    activeIPLoading: false,
    sendActivationIPEmail: true,
  },
});

export const activeIPError = errorString => ({
  type: ui.activeIP,
  payload: {
    activeIPLoading: false,
    activeIPError: errorString,
  },
});

export const switchLeaveMessageWindow = flag => ({
  type: ui.switchLeaveMessageWindow,
  payload: flag,
});

export const switchConfirmBeforeCloseWindow = (flag, type = '', message = '', data = {}) => ({
  type: ui.switchConfirmBeforeCloseWindow,
  payload: flag,
  meta: {
    type,
    message,
    data,
  },
});

export const openLeaveMessageConfirm = (leaveConfirm, leaveMessage) => ({
  type: ui.openLeaveMessageConfirm,
  payload: { leaveConfirm, leaveMessage },
});
export const siteNotActive = billUrl => ({
  type: ui.activeIP,
  payload: {
    billUrl,
  },
});

export const showTrialExpired = (isExpired, trialPeriodReminderDays) => ({
  type: ui.activeIP,
  payload: {
    ifShowTrialExpired: true,
    isTrialExpired: isExpired,
    expireDaysRemaining: trialPeriodReminderDays || 0,
  },
});

export const hideTrialExpired = () => ({
  type: ui.activeIP,
  payload: {
    ifShowTrialExpired: false,
  },
});

export const showSuggestion = () => ({
  type: ui.activeIP,
  payload: {
    ifShowSuggestion: true,
  },
});

export const showDunning = () => ({
  type: ui.activeIP,
  payload: {
    ifShowDunning: true,
  },
});
export const hideDunning = () => ({
  type: ui.activeIP,
  payload: {
    ifShowDunning: false,
  },
});

export const switchCloseWindow = flag => ({
  type: ui.switchCloseWindow,
  payload: flag,
});

export const refreshCannedMessage = () => ({
  type: ui.refreshCannedMessage,
  payload: {
    ifCannedMessageLoading: true,
  },
});

export const notificationMessage = (id, soundType) => ({
  type: ui.notificationMessage,
  payload: {
    id,
    soundType,
  },
});

export const hideErrorMessage = () => ({
  type: ui.activeIP,
  payload: {
    ifShowErrorMessage: false,
  },
});

export const showErrorMessage = (ifShowDismiss, errorMsg) => ({
  type: ui.activeIP,
  payload: {
    ifShowErrorMessage: true,
    ifShowDismiss,
    errorMessage: errorMsg,
  },
});

export const onExtendClick = () => ({
  type: ui.activeIP,
  payload: {
    ifExtendLoading: true,
  },
});

export const switchDashboardMessagesWindow = flag => ({
  type: ui.switchDashboardMessagesWindow,
  payload: flag,
});

export const switchErrorMessagePanel = (flag, message) => ({
  type: ui.switchErrorMessagePanel,
  payload: message,
  meta: flag,
});

// export const setErrorMessageForChat = errorMessage => ({
//   type: ui.setErrorMessageForChat,
//   payload: errorMessage,
// });

export const setResponseErrorMessage = errorMessage => ({
  type: ui.setResponseErrorMessage,
  payload: errorMessage,
});

export const handleCustomFilterModal = ifOpen => ({
  type: ui.handleCustomFilterModal,
  payload: ifOpen,
});

export const setSendEmailSuccessMessage = successMessage => ({
  type: ui.setSendEmailSuccessMessage,
  payload: successMessage,
});

export const openNewWindow = () => ({
  type: ui.openNewWindow,
});

export const updateAgentInfoForChildWindow = info => ({
  type: ui.updateAgentInfoForChildWindow,
  payload: info,
});

export const embetChatWindowInParent = () => ({
  type: ui.embetChatWindowInParent,
});

export const updateAvailable = version => ({
  type: ui.updateAvailable,
  payload: version,
});



