import * as _ from 'lodash';
/**
 * @module utils/localStorage
 */

/**
 * Get Value
 * @param {string} key - key
 * @param {?string} defaultValue - Default value if value not found using given key
 * @return {string} Value from localStorage
 */
export function get(key, defaultValue = '') {
  const value = localStorage.getItem(key);
  if (value === null) return defaultValue;
  return value;
}

/**
 * Set value
 * @param {string} key - key
 * @param {string} value - Value
 */
export function set(key, value) {
  localStorage.setItem(key, value);
}

export function setObject(key, object) {
  if (_.isObject(object)) {
    localStorage.setItem(key, JSON.stringify(object));
    return true;
  }
  return false;
}

export function getNumber(key, defaultValue = -1) {
  const val = localStorage.getItem(key);
  if (isNaN(val) || val === null) {
    return defaultValue === -1 ? val : defaultValue;
  }
  return _.parseInt(val);
}

export function getObject(key, defaultValue = null) {
  const val = localStorage.getItem(key);
  return _.isString(val) ? JSON.parse(val) : defaultValue;
}

export function getBool(key) {
  const value = localStorage.getItem(key);
  if (value === 'true') return true;
  return false;
}
export function deleteItem(key) {
  localStorage.removeItem(key);
}



