import config from 'config.json';
/* eslint-disable */
export const loginSubmit = 'login/submit';
export const loginSuccess = 'login/success';
export const loginFailure = 'login/failure';

export const loginResultCode = {
  loginSuccess: 0,
  agentIPBlock: 1,
  adminIPBlock: 2,
  normalError: 3,
  agentPasswordExpired: 4,
  agentPasswordWillExpired: 5,
  agentIPBlockAndShowVCode: 6,
  adminIPBlockAndShowVCode: 7,
  normalErrorAndShowVCode: 8,
  agentPasswordExpiredAndShowVCode: 9,
  agentPasswordWillExpiredAndShowVCode: 10,
  multipleSites: 11,
  verificationCodeNotRightAndShowVCode: 12,
  verificationCodeNotRight: 13,
  sitePurged: 14,
  agentExceed: 15,
  siteInactivate: 16,
};

let routeServerUrl = window.mainRouteServerUrl;
let backupRouteServerUrl = window.backupRouteServerUrl;
let forgotPassword = window.forgotPassword;
if (!process.env.IS_WEB_APP) {
  routeServerUrl = require('electron').remote.process.env.routeServer || routeServerUrl;
  backupRouteServerUrl = require('electron').remote.process.env.backupRouteServer || backupRouteServerUrl;
  forgotPassword = require('electron').remote.process.env.forgotPasswordUrl || forgotPassword;
}
export const routeServer = routeServerUrl;
export const backupRouteServer = backupRouteServerUrl;
export const portalLivechatUrl = window.portalLivechatUrl;

export const liveChatVisitorMonitorVersion = process.env.version;

export const liveChatUrl = config.liveChatUrl;
export const forgotPasswordUrl = forgotPassword;
export const copyright = config.copyright;
export const brandingName = config.brandingName;
export const brandingLiveChatName = config.brandingLiveChatName;
export const sales_email_address = config.sales_email_address;
export const branding_portal_url = config.branding_portal_url;
export const edition_team = config.edition_team;
export const edition_business = config.edition_business;
export const edition_businessWithMaxon = config.edition_businessWithMaxon;
export const edition_ent = config.edition_ent;

export const kb_help = config.kb_help;
export const kb_login_error = config.kb_login_error;
export const kb_login_help = config.kb_login_help;
export const kb_profile_help = config.kb_profile_help;
export const kb_cancel_account = config.kb_cancel_account;


