import * as selectors from '../../reducers/selectors';
import { webVersion } from '../../constants/web';
import { getLoginAction } from '../../utils/common';
import * as chatAction from '../../constants/enums/chatAction';
import * as str from '../../utils/str';

export default (state, status) => {
  const loginInfo = selectors.getLogInInfo(state);
  const loginAction = getLoginAction(status, true);
  const siteId = selectors.getSiteId(state);
  if (state.agent.loginWithWebSession === true) {
    return {
      type: chatAction.agentAutoLogin,
      visitorId: siteId,
      message: str.joincolon(loginAction, loginInfo.token),
      sender: str.joincolon(webVersion, loginInfo.agent),
    };
  }
  return {
    type: chatAction.agentLogin,
    visitorId: siteId,
    message: str.joincolon('', loginAction,
      str.passwordConvert(selectors.getCurrentAgentPassword(state)), '', ''),
    sender: str.joincolon(webVersion, loginInfo.agent),
  };
};



