import * as agentsTypes from '../constants/types/agents';

export const updateAgent = (id, isKickOff = false) => ({
  type: agentsTypes.updateAgent,
  payload: id,
  meta: isKickOff,
});
export const updateDepartment = id => ({
  type: agentsTypes.updateDepartment,
  payload: id,
});
export const changedAgentsListWidth = width => ({
  type: agentsTypes.changedAgentsListWidth,
  payload: width,
});



