import React, { Component } from 'react';
import _ from 'lodash';
import Modal from '../Modal';
import ModalStyle from '../Modal.css';
import Checkbox from '../../Checkbox/Checkbox';

class DashboardPageModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ifOpen: props.ifOpen,
      currentIndex: 0,
      showPrevious: false,
      showNext: false,
      selectedIds: [],
    };
    this.viewPreviousMsg = this.viewPreviousMsg.bind(this);
    this.viewNextMsg = this.viewNextMsg.bind(this);
    this.enableButton = this.enableButton.bind(this);
    this.closeWindow = this.closeWindow.bind(this);
    this.switchCheckboxStatus = this.switchCheckboxStatus.bind(this);
  }
  componentDidMount() {
    this.enableButton(this.props.messages);
  }
  viewPreviousMsg() {
    const index = this.state.currentIndex - 1;
    this.enableButton(this.props.messages, index);
  }

  viewNextMsg() {
    const index = this.state.currentIndex + 1;
    this.enableButton(this.props.messages, index);
  }

  enableButton(messages, index = -1) {
    const count = messages.length;
    const currentIndex = index === -1 ? this.state.currentIndex : index;
    if (count === 1) {
      this.setState({ showPrevious: false, showNext: false, currentIndex });
      return;
    }
    if (currentIndex <= 0) {
      this.setState({ showPrevious: false, showNext: true, currentIndex });
    } else if (currentIndex + 1 >= count) {
      this.setState({ showPrevious: true, showNext: false, currentIndex });
    } else {
      this.setState({ showPrevious: true, showNext: true, currentIndex });
    }
  }

  closeWindow() {
    this.setState({
      ifOpen: false,
    });
    this.props.onClose(this.state.selectedIds);
  }

  switchCheckboxStatus(id) {
    const selectedIds = this.state.selectedIds;
    const index = selectedIds.indexOf(id);
    if (index >= 0) {
      selectedIds.splice(index, 1);
    } else {
      selectedIds.push(id);
    }
    this.setState({ selectedIds });
  }
  render() {
    const message = this.props.messages[this.state.currentIndex];
    const actionPrevious = this.state.showPrevious ? { text: 'Previous', eventClick: this.viewPreviousMsg } : {};
    const actionNext = this.state.showNext ? { text: 'Next', eventClick: this.viewNextMsg } : {};
    let okProps = [];
    okProps = !_.isEmpty(actionPrevious) ? okProps.concat([actionPrevious]) : okProps;
    okProps = !_.isEmpty(actionNext) ? okProps.concat([actionNext]) : okProps;
    return (
      this.state.ifOpen && <Modal
        closeModal={this.closeWindow}
        className={ModalStyle.dashboard}
        overlayClassName={ModalStyle.dashboardoverLay}
        title={message.title}
        actions={{
          className: ModalStyle.buttonsPanel,
          okProps,
          cancelProps: { text: 'Close' },
        }}
      >
        <p dangerouslySetInnerHTML={{ __html: message.content }} />
        <div className={ModalStyle.action}>
          <div className={ModalStyle.checkboxPanel}>
            <Checkbox
              input={{
                value: this.state.selectedIds.indexOf(message.id) >= 0,
                onChange: () => {
                  this.switchCheckboxStatus(message.id);
                },
              }}
              text="Do not show this message next time."
            />
          </div>
        </div>
      </Modal>
    );
  }
}

export default DashboardPageModal;

DashboardPageModal.propTypes = {
  messages: React.PropTypes.arrayOf(React.PropTypes.shape({
    id: React.PropTypes.number,
    title: React.PropTypes.string,
    content: React.PropTypes.string,
  })),
  ifOpen: React.PropTypes.bool.isRequired,
  onClose: React.PropTypes.func.isRequired,
};

DashboardPageModal.defaultProps = {
  title: 'Comm100 Live Chat',
};



