import React from 'react';
import Modal from '../Modal';
import Link from '../../Link/Link';
import ModalStyle from '../Modal.css';
import * as utils from '../../../utils/time';
import * as loginConstants from '../../../constants/login';

class SiteSelectModal extends React.Component {
  shouldComponentUpdate(nextProps) {
    if (this.props.siteClick !== nextProps.siteClick ||
      this.props.ifOpen !== nextProps.ifOpen) {
      return true;
    }
    return false;
  }
  componentDidUpdate(prevProps) {
    if (this.props.siteClick && this.props.siteClick !== prevProps.siteClick) {
      this.props.siteSelected();
    }
  }
  render() {
    return (
      <Modal
        closeModal={this.props.closeModal}
        className={ModalStyle.siteSelectModal}
      >
        <p>
          There are
          {this.props.sites.length}
          accounts associated with your email. Please choose one to log in.
        </p>
        <Link newWindow href={loginConstants.kb_cancel_account}>
          May I keep one and close others?
        </Link>
        <ul className={ModalStyle.siteList}>
          {
            this.props.sites.map((site, i) => (
              <li
                key={i}
                onClick={() => {
                  this.props.siteSelect(site.siteId);
                }}
              >
                <p>Site ID:<span className={ModalStyle.siteId}>{site.siteId}</span></p>
                <p className={ModalStyle.lastLogin}>Last Login: { utils.format(utils.parseDate(site.lastLoginTime), 'MM/dd/yyyy') }</p>
                <p className={ModalStyle.accountCreate}>Account Created: { utils.format(utils.parseDate(site.registerTime), 'MM/dd/yyyy') }</p>
              </li>
            ))
          }
        </ul>
      </Modal>
    );
  }
}

export default SiteSelectModal;

SiteSelectModal.propTypes = {
  ifOpen: React.PropTypes.bool.isRequired,
  siteSelect: React.PropTypes.func.isRequired,
  closeModal: React.PropTypes.func.isRequired,
  sites: React.PropTypes.arrayOf(
    React.PropTypes.shape({
      siteId: React.PropTypes.number.isRequired,
      lastLoginTime: React.PropTypes.string,
      registerTime: React.PropTypes.string,
    })
  ).isRequired,
  siteClick: React.PropTypes.bool,
  siteSelected: React.PropTypes.func,
};



