import React, { PropTypes } from 'react';
import classnames from 'classnames';
import PopupMenu from '../Popup/PopupMenu';
import IconButton from '../Button/Button';

import styles from './Header.css';
import Icon from '../Icon/Icon';


class MenuItem extends React.Component {
  constructor() {
    super();
    this.state = {
      isHover: false,
    };
    this.updateHoverStatus = this.updateHoverStatus.bind(this);
  }

  updateHoverStatus(flag) {
    this.setState({
      isHover: flag,
    });
  }
  render() {
    return (
      <div
        className={classnames(styles.menuItem,
                              (this.state.isHover && styles.mouseover),
                              this.props.className)}
        onMouseEnter={() => this.updateHoverStatus(true)}
        onMouseLeave={() => this.updateHoverStatus(false)}
        onClick={() => this.updateHoverStatus(!this.state.isHover)}
      >
        <IconButton
          text={this.props.text ? this.props.text : ''}
          className={this.props.buttonClass}
        >
          <Icon
            type={this.props.type}
            size={16}
            color={this.props.color}
            className={styles.statusIcon}
          />
          {
            this.props.agentStatus &&
            <span>
              <span className={styles.menuItemAgent}>
                {this.props.agentStatus.name}
              </span>
              <span className={styles.menuItemStatus}>
                {` - ${this.props.agentStatus.status}`}
              </span>
            </span>
          }
        </IconButton>
        <PopupMenu
          childElement={this.props.content}
          containerClass={styles.popupStyle}
        />
      </div>
    );
  }
}

MenuItem.propTypes = {
  content: PropTypes.element.isRequired,
  text: PropTypes.string,
  iconClass: PropTypes.string,
  className: PropTypes.string,
  type: PropTypes.string,
  color: PropTypes.string,
  buttonClass: PropTypes.string,
  agentStatus: PropTypes.shape({
    name: PropTypes.string,
    status: PropTypes.string,
  }),
};
export default MenuItem;



