import React from 'react';
import { shell } from '../../services/electronAPI';

class Link extends React.Component {
  shouldComponentUpdate(nextProps) {
    return nextProps.disabled !== this.props.disabled ||
      nextProps.href !== this.props.href ||
      nextProps.className !== this.props.className ||
      nextProps.children !== this.props.children;
  }
  render() {
    const {
      children,
      href,
      newWindow,
      onClick,
      disabled,
      ...rest
    } = this.props;
    const useBlank = !disabled && newWindow && href;
    let onClickFn = disabled ? undefined : onClick;
    if (!disabled && href && !onClick) {
      onClickFn = (event) => {
        event.preventDefault();
        shell.openExternal(href);
      };
    }
    return (
      <a
        {...rest}
        href={disabled ? undefined : href}
        target={useBlank ? '_blank' : undefined}
        rel={useBlank ? 'noopener noreferrer' : undefined}
        onClick={onClickFn}
      >
        {children}
      </a>
    );
  }
}

Link.propTypes = {
  href: React.PropTypes.string,
  newWindow: React.PropTypes.bool,
  onClick: React.PropTypes.func,
  className: React.PropTypes.string,
  children: React.PropTypes.oneOfType([
    React.PropTypes.any,
  ]).isRequired,
  disabled: React.PropTypes.bool,
};

export default Link;



