import React from 'react';
import Icon from './Icon';
import style from '../Icons/Icon.css';
import * as enums from '../../constants/enums';

export default (
  <div
    style={{
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      width: '100%',
      height: '100%',
    }}
  >
    <Icon type={enums.icons.loadingBlue} className={style.loadingBlue} />
  </div>
);



