import { connect } from 'react-redux';
import Login from '../components/Widget/Login';
import * as uiActions from '../actions/ui';
import * as guid from '../utils/guid';
import chatServerRequest from '../services/login/chatServer';
import * as chatAction from '../constants/enums/chatAction';
import memoryStore from '../services/memoryStore';
import * as selectors from '../reducers/selectors';
import { encrypt } from '../utils/simpleEncrypt';
import { gotoBuyNow } from './Business/common';
import * as agentActions from '../actions/agent';
import * as loginForm from './LoginForm';

const getAutoLoginUrls = (state) => {
  const siteId = state.ui.login.siteId;
  const email = state.ui.login.formValues.email || '';
  const password = escape(encrypt(state.ui.login.formValues.password || ''));
  const loginUrl = selectors.getLoginUrl(state);
  const autoLoginUrl =
    `${selectors.getAutoLoginManagerUrl(state)}?siteId=${siteId}&email=${email}&password=${password}`;
  return {
    loginUrl,
    autoLoginUrl,
  };
};

const mapStateToProps = state => ({
  ifShowForceLogin: state.ui.login.ifShowForceLogin,
  ifShowSiteSelect: state.ui.login.ifShowSiteSelect,
  ifShowActiveIP: state.ui.login.ifShowActiveIP,
  ifShowChangePassword: state.ui.login.ifShowChangePassword,
  sites: state.ui.login.sites,
  siteId: state.ui.login.siteId,
  ifShowVerificationCode: state.ui.login.ifShowVerificationCode,
  verificationCodeUrl: state.ui.login.verificationCodeUrl,
  changePasswordSuccess: state.ui.login.changePasswordSuccess,
  hasExpired: state.ui.login.hasExpired,
  isAdmin: state.ui.login.isAdmin,
  sendActivationIPEmail: state.ui.login.sendActivationIPEmail,
  agentLogout: state.ui.login.agentLogout,
  sessionId: state.ui.login.sessionId,
  siteClick: state.ui.login.siteClick,
  email: state.ui.login.formValues.email || '',
  password: state.ui.login.formValues.password || '',
  chatServer: state.config.settings.urls.chatServer,
  standbyChatServer: state.config.settings.urls.standbyChatServer,
  activeIPLoading: state.ui.login.activeIPLoading,
  activeIPError: state.ui.login.activeIPError,
  billUrl: state.ui.login.billUrl,
  ifShowTrialExpired: state.ui.login.ifShowTrialExpired,
  isTrialExpired: state.ui.login.isTrialExpired,
  expireDaysRemaining: state.ui.login.expireDaysRemaining,
  ifShowSuggestion: state.ui.login.ifShowSuggestion,
  loginUrls: getAutoLoginUrls(state),
  ifShowDunning: state.ui.login.ifShowDunning,
  dunningInfoStatus: state.config.settings.dunningInfo && state.config.settings.dunningInfo.status,
  ifExtendLoading: state.ui.login.ifExtendLoading,
  message: state.config.settings.dunningInfo && state.config.settings.dunningInfo.message,
  ifShowErrorMessage: state.ui.login.ifShowErrorMessage,
  ifShowDismiss: state.ui.login.ifShowDismiss,
  errorMessage: state.ui.login.errorMessage,
  ifLoginSubmitting: state.ui.login.ifLoginSubmitting,
  loginSubmitError: state.ui.login.loginSubmitError,
  formValues: state.ui.login.formValues,
  hasUpdate: selectors.getHasUpdate(state),
});
const mergeProps = (stateProps, dispatchProps) => {
  const { dispatch } = dispatchProps;

  const dispatchObject = {
    closeModal() {
      setTimeout(() => {
        dispatch(uiActions.showForceLogin(false));
        dispatch(uiActions.hideSiteSelect());
        dispatch(uiActions.hideActiveIP());
        if (stateProps.verificationCodeUrl) {
          const uid = guid.createGUID();
          dispatch(uiActions.refreshVCode(uid));
        }
      }, 300);
    },
    closeErrorMessage() {
      dispatch(uiActions.hideErrorMessage());
    },
    showError(errorMessage) {
      dispatch(uiActions.showErrorMessage(true, errorMessage));
    },
    closeTrialExpiredModal() {
      setTimeout(() => {
        dispatch(uiActions.hideTrialExpired());
        if (!stateProps.isTrialExpired) {
          dispatch(uiActions.showVisitor());
          memoryStore.set('loginResponse', null);
        } else if (stateProps.verificationCodeUrl) {
          const uid = guid.createGUID();
          dispatch(uiActions.refreshVCode(uid));
        }
      }, 300);
    },
    closeDunningModal() {
      dispatch(uiActions.hideDunning());
      dispatch(uiActions.showVisitor());
      memoryStore.set('loginResponse', null);
    },
    closeChangePasswordModal() {
      dispatch(uiActions.hideChangePassword());
      if (!stateProps.hasExpired) {
        // dispatch(uiActions.showVisitor());
        loginForm.onSubmitSuccess(memoryStore.get('loginResponse'), dispatch);
        // success
      } else if (stateProps.verificationCodeUrl) {
        const uid = guid.createGUID();
        dispatch(uiActions.refreshVCode(uid));
      }
    },
    handleForceLogin() {
      setTimeout(() => {
        dispatch(uiActions.showForceLogin(false));
        loginForm.onloginForm(stateProps.formValues, dispatch, stateProps);
      }, 300);
    },
    handleSiteSelect(siteId) {
      dispatch(uiActions.handleSiteSelect(siteId));
    },
    handleSiteSelected() {
      dispatch(uiActions.hideSiteSelect());
      loginForm.onloginForm(stateProps.formValues, dispatch, stateProps);
    },
    handleSubmitChangePassword: async (form) => {
      const options = {
        email: stateProps.email,
        oldPassword: stateProps.password,
        siteId: stateProps.siteId,
        newPassword: form.newPassword,
        chatServer: stateProps.chatServer,
        standbyChatServer: stateProps.standbyChatServer,
      };
      const resp = await chatServerRequest(options, chatAction.agentChangePassword);
      if (stateProps.hasExpired) {
        dispatch(uiActions.formValuesChange(
          Object.assign({}, stateProps.formValues, { password: form.newPassword })
        ));
      }
      return resp;
    },
    refreshVCode() {
      if (stateProps.verificationCodeUrl) {
        const uid = guid.createGUID();
        dispatch(uiActions.refreshVCode(uid));
      }
    },
    changPasswordOKClick() {
      dispatch(uiActions.hideChangePassword());
      if (stateProps.hasExpired) {
        loginForm.onloginForm(stateProps.formValues, dispatch, stateProps);
      } else {
        // dispatch(uiActions.showVisitor());
        // success
        loginForm.onSubmitSuccess(memoryStore.get('loginResponse'), dispatch);
      }
    },
    onExtendClick() {
      dispatch(uiActions.onExtendClick());
      dispatch(agentActions.agentExtendChargeDate());
    },
    handleActivateIP: async () => {
      const options = {
        email: stateProps.email,
        siteId: stateProps.siteId,
        chatServer: stateProps.chatServer,
        standbyChatServer: stateProps.standbyChatServer,
      };
      try {
        dispatch(uiActions.showActiveIPLoading());
        await chatServerRequest(options, chatAction.agentSendEmailActivateIP);
        dispatch(uiActions.activeIPSuccess());
      } catch (e) {
        dispatch(uiActions.activeIPError(e.data.errorString || e.data.message || e.message));
      }
    },
    handleBuyNow: () => {
      const { controlPanel } = memoryStore.get('urls', stateProps.siteId, null);
      const billurl = `${controlPanel}/billing/BuyNowRedirect.aspx?siteid=${stateProps.siteId}`;
      gotoBuyNow({ isWeb: false,
        buyNowUrl: billurl,
        loginUrl: stateProps.loginUrls.loginUrl,
        autoLoginUrl: stateProps.loginUrls.autoLoginUrl,
      });
    },
    formValuesChange: (newValuse) => {
      dispatch(uiActions.formValuesChange(Object.assign({}, newValuse, { autoWebLogin: false, agentId: 0, token: '', siteId: 0 })));
    },
    handleSubmit: async () => {
      await loginForm.onloginForm(stateProps.formValues, dispatch, stateProps);
    },
    autoLogin: () => {
      loginForm.onloginForm(stateProps.formValues, dispatch, stateProps, true);
    },
    autoWebLogin: (options, isForceLogin) => {
      loginForm.onloginForm(options, dispatch, stateProps, false, isForceLogin);
    },
    preHandleSubmit: () => {
      dispatch(uiActions.preStartSubmitLogin());
    },
  };
  return {
    ...stateProps,
    ...dispatchObject,
  };
};

export default connect(mapStateToProps, null, mergeProps)(Login);



