export const getAgentChats = state => state.agentChats;
export const getAgentChat = (state, agentId) => state.agentChats.get(agentId);
export const getAgentUnreadMessages = (state, agentId) => {
  const chat = getAgentChat(state, agentId);
  return chat === undefined ? 0 : getAgentChat(state, agentId).get('unreadMessages');
};
export const getAgentChatEditorState = (state, id) => {
  const chat = getAgentChat(state, id);
  return chat === undefined ? undefined : getAgentChat(state, id).get('editorState');
};
export const getAgentChatUploadFiles = (state, id) => getAgentChat(state, id).get('files');



