import React, { PropTypes } from 'react';
import classnames from 'classnames';
import styles from './PopupMenu.css';

const PopupMenu = props => (
  <div
    className={classnames(styles.openPopup,
        props.containerClass)}
  >
    {props.childElement}
  </div>
);
PopupMenu.propTypes = {
  childElement: PropTypes.element.isRequired,
  containerClass: PropTypes.string,
};
PopupMenu.examples = {
  'Default Example': {
    description: 'PopupMenu',
    component:
  <PopupMenu isOpen childComponent={(<div>test</div>)} />
    ,
  },
};
export default PopupMenu;



