/**
 * Whether to log which prop has caused component to update
 */
export const logShouldComponentUpdate = false;
/**
 * Whether to log if there is any mis-config of logShouldComponentUpdate.
 * It will log detailed info, if component has prop/state changes, but return `false` instead.
 */
export const logShouldComponentUpdatePossibleIssue = false;
/**
 * Whether to log web request related actions.
 */
export const logWebRequest = false;
/**
 * Whether to log web response related actions.
 */
export const logWebResponse = false;
/**
 * Whether to log typing actions.
 */
export const logTypingActions = false;
/**
 * Whether use redux <DevTools />
 * NOTICE: updating this attribute will reuqire full reload
 */
export const useReduxDevToolsComponent = false;
/**
 * Whether log redux form related actions
 */
export const logReduxFormAction = false;
/**
 * Whether log redux action at all.
 * NOTICE: logging redux action, especially output before and after state will increate memory usage
 */
export const logActions = true;



