import React from 'react';
import Modal from '../Modal';
import ModalStyle from '../Modal.css';
import RadioButton from '../../RadioButton/RadioButton';
import Checkbox from '../../Checkbox/Checkbox';

class CloseWindow extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      actionType: '0',
      askMeAgain: false,
    };
    this.handleRadioChange = this.handleRadioChange.bind(this);
    this.clickSure = this.clickSure.bind(this);
    this.clickDonotAskMeAgain = this.clickDonotAskMeAgain.bind(this);
  }
  handleRadioChange(event) {
    this.setState({
      actionType: event.target.value,
    });
  }
  clickSure() {
    this.props.onConfirm(this.state.actionType, this.props.attachedData);
    this.props.onCancel();
  }
  clickDonotAskMeAgain(v) {
    this.setState({ askMeAgain: v });
    this.props.clickDonotAskMeAgain(v);
  }
  render() {
    const currentThis = this;
    return (
      this.props.ifOpen && <Modal
        closeModal={this.props.onCancel}
        title="Do you want to"
        actions={{
          okProps: { text: 'OK', eventClick: this.clickSure },
          cancelProps: { text: 'Cancel' },
        }}
      >
        <RadioButton
          name="closeWindow"
          radioOption={
          [
            {
              text: 'Exit program',
              value: '0',
              checked: this.state.actionType === '0',
            },
            {
              text: 'Minimize to system tray',
              value: '1',
              checked: this.state.actionType === '1',
            },
          ]
          }
          handleChange={this.handleRadioChange}
        />
        <div className={ModalStyle.toolbar}>
          <Checkbox
            text="Don't ask me again"
            input={{
              value: currentThis.state.askMeAgain,
              onChange: currentThis.clickDonotAskMeAgain,
            }}
          />
        </div>
      </Modal>
    );
  }

}

export default CloseWindow;

CloseWindow.propTypes = {
  attachedData: React.PropTypes.shape({}),
  ifOpen: React.PropTypes.bool.isRequired,
  onConfirm: React.PropTypes.func.isRequired,
  onCancel: React.PropTypes.func.isRequired,
  clickDonotAskMeAgain: React.PropTypes.func.isRequired,
};




