import React from 'react';
import { reduxForm, Field, SubmissionError } from 'redux-form';
import IconInput from '../../Input/IconInput';
import * as enums from '../../../constants/enums';
import Icon from '../../Icon/Icon';
import style from './ChangePassword.css';
import Button from '../../Button/Button';
import * as uiActions from '../../../actions/ui';

const ChangePasswordForm = props => (
  <form onSubmit={props.handleSubmit}>
    <div className={style.errorMessage} style={{ display: props.error ? 'block' : 'none' }}>
      <span>{props.error}</span>
    </div>
    <div className={style.text}>
      { !props.hasExpired &&
      <p>
        Your password will be expired soon. Please change your password.
      </p>
      }
      { props.hasExpired &&
        <p>
          Your password has expired and must be changed.
        </p>
      }
    </div>
    <div className={style.line}>
      <Field
        name="newPassword"
        component={IconInput}
        className={style.inputWidth}
        icon={<Icon type={enums.icons.loginPassword} color="#aaa" size="larger" />}
        type="password"
        placeholder="New Password"
      />
    </div>
    <div className={style.line}>
      <Field
        name="rePassword"
        component={IconInput}
        className={style.inputWidth}
        icon={<Icon type={enums.icons.loginPassword} color="#aaa" size="larger" />}
        type="password"
        placeholder="Confirm Password"
      />
    </div>
    <div className={style.buttonWrapper}>
      <Button
        className={style.marginRight20}
        onClick={props.handleSubmit}
        disabled={props.submitting || props.pristine}
        loading={props.submitting}
        text={props.hasExpired ? 'Change' : 'Change Now'}
        type="primary"
        htmlType="submit"
      />
      <Button
        onClick={props.closeModal}
        text={props.hasExpired ? 'Cancel' : 'Change Later'}
        htmlType="button"
      />
    </div>
  </form>
);


ChangePasswordForm.propTypes = {
  hasExpired: React.PropTypes.bool.isRequired,
  closeModal: React.PropTypes.func.isRequired,
  handleSubmitChangePassword: React.PropTypes.func,
  handleSubmit: React.PropTypes.func,
  submitting: React.PropTypes.bool,
  pristine: React.PropTypes.bool, /** @todo unsure about this propType */
  error: React.PropTypes.string,
};

const validate = (values) => {
  const errors = {};
  if (!values.newPassword) {
    errors.newPassword = 'New Password is Required!';
  }
  if (!values.rePassword) {
    errors.rePassword = 'Confirm Password is Required!';
  }
  return errors;
};
const submitValidate = (values) => {
  if (values.newPassword !== values.rePassword) {
    throw new SubmissionError({ _error: 'The two password you typed don\'t match each other.' });
  }
  if (!values.newPassword) {
    throw new SubmissionError({ _error: 'Please enter your new password.' });
  }
};
export default reduxForm({
  form: 'changePasswordForm',
  validate,
  destroyOnUnmount: true,
  onSubmit: async (form, dispatch, props) => {
    submitValidate(form, props);
    if (props.handleSubmitChangePassword) {
      try {
        await props.handleSubmitChangePassword(form);
      } catch (e) {
        if (e instanceof SubmissionError) throw e;
        if (e.type === 'chatServerError') {
          if (e.data.errorString) {
            throw new SubmissionError({ _error: e.data.errorString });
          } else if (e.data.message) {
            throw new SubmissionError({ _error: e.data.message });
          }
        } else {
          throw new SubmissionError({ _error: e.message });
        }
      }
    }
  },
  onSubmitSuccess: (result, dispatch) => {
    dispatch(uiActions.changePasswordSuccess());
  },
})(ChangePasswordForm);



