import axios from 'axios';
import * as helper from '../utils/helper';
import * as config from '../constants/config';

export const feedback = (server, siteId, agentId, mainAvailable) => {
  const mainDisconnected = mainAvailable ? '0' : '1';
  const url = `${server}/moderator.aspx?action=3&siteId=${siteId}&operatorId=${agentId}&mainDisconnected=${mainDisconnected}`;
  axios.get(helper.getUrl(url, config.isSSL), { timeout: 30 * 1000 });
};


export default (server, siteId) =>
  new Promise((resolve) => {
    const url = `${server}/moderator.aspx?action=1&siteId=${siteId}`;
    axios.get(helper.getUrl(url, config.isSSL), { timeout: 30 * 1000 })
      .then((res) => {
        resolve(res.data !== 0);
      })
      .catch(() => {
        resolve(true);
      });
  });



