import * as config from '../../constants/config';
import { getSiteId } from './config';
import { getCurrentAgentSession } from './agents';
import { getUrl } from '../../utils/helper';

export const ifUseMainServer = state => state.config.ifUseMainServer;
export const getControlPanelURL = state => state.config.settings.urls.controlPanel;
export const getChatServerURL = state => state.config.settings.urls.chatServer;
export const getStandbyChatServerURL = state => state.config.settings.urls.standbyChatServer;
export const getBillingURL = state => `${getControlPanelURL(state)}/billing`;
export const getAdminLogin = state => `${getControlPanelURL(state)}/adminManage`;
export const getLiveChatSettingURL = state => `${getControlPanelURL(state)}/livechat`;
export const getLiveChatReportURL = state => `${getControlPanelURL(state)}/livechatreport`;
export const getTicketURL = state => `${getControlPanelURL(state)}/tickets`;

export const getAdminAgentAvatarUrl = state =>
  getUrl(`${getAdminLogin(state)}/adminpanel/operatoravatarforvm.aspx`, config.isSSL);

export const getAdminAddAgentsUrl = state =>
  getUrl(`${getAdminLogin(state)}/adminpanel/OperatorList.aspx`, config.isSSL);

export const getAutoLoginManagerUrl = state =>
  getUrl(`${getAdminLogin(state)}/AutoLoginManager.ashx`, config.isSSL);

export const getLoginUrl = state =>
  getUrl(`${getAdminLogin(state)}/login.aspx`, true);

export const getAttachTicketURL = state =>
  getUrl(`${getTicketURL(state)}/ticket.aspx`, false);

export const getLiveChatDashboardUrl = state =>
  getUrl(`${getLiveChatSettingURL(state)}/Dashboard.aspx`, true);

export const getFileUploadUrl = (state) => {
  if (ifUseMainServer(state)) {
    return getUrl(`${getChatServerURL(state)}/FileUploadHandler.ashx`, config.isSSL);
  }
  return getUrl(`${getStandbyChatServerURL(state)}/FileUploadHandler.ashx`, config.isSSL);
};
export const getFileDownloadUrl = (state) => {
  if (ifUseMainServer(state)) {
    return getUrl(`${getChatServerURL(state)}/download.ashx`, config.isSSL);
  }
  return getUrl(`${getStandbyChatServerURL(state)}/download.ashx`, config.isSSL);
};

export const getTranscriptsDetailsWithoutBaseUrl = (state) => {
  if (ifUseMainServer(state)) {
    return getUrl(`${getLiveChatReportURL(state)}/TranscriptsDetailsWithoutBase.aspx`, config.isSSL);
  }
  return getUrl(`${getLiveChatReportURL(state)}/TranscriptsDetailsWithoutBase.aspx`, config.isSSL);
};

export const appendParamsToDownloadUrl = (state, name, guid, privateMessage) => {
  const downloadUrl = getFileDownloadUrl(state);
  const sessionId = getCurrentAgentSession(state);
  const siteId = getSiteId(state);

  return `${downloadUrl}?sessionId=${sessionId}&privateMessage=${privateMessage}&siteId=${siteId}&guid=${guid}&displayName=${name}`;
};

export const getSoundUrl = (state) => {
  if (ifUseMainServer(state)) {
    return getUrl(`${getChatServerURL(state)}/DBResource/DBSound.ashx`, config.isSSL);
  }
  return getUrl(`${getStandbyChatServerURL(state)}/DBResource/DBSound.ashx`, config.isSSL);
};

export const getServerSoundUrl = (state, soundId) => {
  if (ifUseMainServer(state)) {
    return getUrl(`${getChatServerURL(state)}/DBResource/DBSound.ashx?soundId=${soundId}`, config.isSSL);
  }
  return getUrl(`${getStandbyChatServerURL(state)}/DBResource/DBSound.ashx?soundId=${soundId}`, config.isSSL);
};

export const getChatHistoryUrl = (state, historyChatId, sessionType, sessionId, siteId) => {
  const transcriptsDetailsWithoutBaseUrl = getTranscriptsDetailsWithoutBaseUrl(state);
  return `${transcriptsDetailsWithoutBaseUrl}?date=${historyChatId}_${sessionType}_${sessionId}&siteId=${siteId}`;
};

const removeScheme = url => url.replace(/^https?:\/\//, '');
export const getWebrtcSignalingServiceUrl = state => `${removeScheme(getControlPanelURL(state))}/webrtcSignalingService/signaling.ashx`;



