import * as _ from 'lodash';
import * as axios from 'axios';
import * as perf from 'src/utils/perf';
import { commandTypeMap } from '../../constants/enums';
import { extractError, connectionErrorTimeout } from '../../constants/web';
import { convert, reverseSchema } from '../../utils/schema';
import { mapObject } from '../../utils/helper';
import { parseDate } from '../../utils/time';
import { agentRequest, agentResponse } from '../../constants/schema';

const commandTypeMapReverse = reverseSchema(commandTypeMap);

function encodeInput(input) {
  const encoded = convert(Object.assign({}, input, {
    commands: input.commands.map(command => _.omit(command, 'next')),
  }), agentRequest);
  /* eslint-disable no-param-reassign */
  _.each(encoded.m, (cmd) => { cmd.d = commandTypeMap[cmd.d]; });
  return encoded;
}

const decodeOutput = (output) => {
  perf.markBegin('Web: Decode Response');
  /**
   * @todo convert date should not be here
   * Redux action should be JSON-friendly. Put it in middleware
   */

  const tryParseDate = val => parseDate(val) || val;
  const newoutput = mapObject(output, tryParseDate);
  let decoded = newoutput;
  if (!decoded.hasConverted) {
    decoded = convert(decoded, agentResponse);
  }
  if (decoded.commands !== null) {
    _.each(decoded.commands, (cmd) => {
      cmd.errorCode = extractError(cmd.errorCode);
      cmd.type = commandTypeMapReverse[cmd.type];
    });
  }
  if (_.isArray(decoded.chatMessages)) {
    _.each(decoded.chatMessages, (msg) => {
      msg.type = commandTypeMapReverse[msg.type];
    });
  }
  decoded.errorCode = extractError(decoded.errorCode);
  perf.markEnd('Web: Decode Response');

  return decoded;
};

export default class Client {
  constructor(url, timeout) {
    this.url = url;
    this.timeout = timeout || connectionErrorTimeout;
  }

  async send(input) {
    const body = JSON.stringify(encodeInput(input));
    const response = await axios.post(
      this.url,
      body,
      { timeout: this.timeout },
    );
    return decodeOutput(response.data);
  }

  promiseSend(input) {
    const body = JSON.stringify(encodeInput(input));
    return new Promise((resolve, reject) => {
      axios.post(this.url, body, { timeout: this.timeout })
        .then(resp => resolve(decodeOutput(resp.data)))
        .catch((err) => { reject(err); });
    });
  }
}




