import * as schema from '../constants/schema';
import { convert, reverseSchema } from '../utils/schema';
import { commandTypeMap } from '../constants/enums';
import * as salesforce from '../constants/salesforce';

export const serializationPreference = preference =>
  JSON.stringify(
    convert(
    Object.assign({},
      preference,
      { others: JSON.stringify(
            convert(
              Object.assign({},
              preference.others,
              { customFilterSettings: JSON.stringify(preference.others.customFilterSettings) }
        ), reverseSchema(schema.agentOtherPreference))),
      }
  ), reverseSchema(schema.currentAgent)));

export const deserializationOtherPreference = (input) => {
  const others = JSON.parse(input || '{}');
  return Object.assign({},
    schema.defaultOtherPreference,
    convert(others, schema.agentOtherPreference),
    { customFilterSettings: JSON.parse(others.cfs || '[]') }
  );
};

const convertSFObjectList = (type, objs) => {
  if (type === 'Contact') {
    return convert(objs, salesforce.contact);
  } else if (type === 'Account') {
    return convert(objs, salesforce.account);
  } else if (type === 'User') {
    return convert(objs, salesforce.user);
  }
  return convert(objs, salesforce.object);
};

export const deserializationLookupResult = (input) => {
  if (input == null) return null;
  const result = JSON.parse(input || '{}');
  const { errorcode: errorCode, errorstring: errorString } = result;
  const type = result.objecttype;
  const list = result.objs == null ? null : convertSFObjectList(type, result.objs);
  const props = result.columnnames === null ? null :
    result.columnnames.map(prop => ({ name: prop.name.toLowerCase(), label: prop.label }));
  return { errorCode, errorString, type, list, props };
};


export const serializationBan = ban =>
  JSON.stringify(convert(ban, schema.ban));

export const serializationPreChat = preChat =>
  JSON.stringify(convert(preChat, schema.preChatForm));

export const serializationWrapup = wrapup =>
  JSON.stringify(convert(wrapup, schema.wrapupForm));

export const serializationChatEmail = email =>
  JSON.stringify(convert(email, schema.chatEmail));

export const serializationRecoverChat = chats =>
  JSON.stringify(convert(chats.map(chat =>
    Object.assign({},
      chat,
      {
        messages: convert(
          chat.messages.map(message => Object.assign({}, message,
            { type: commandTypeMap[message.type] })),
          reverseSchema(schema.message)),
      }
   )), schema.recoverChat));

export const serializationSFFieldValues = fieldValues =>
  JSON.stringify(convert(fieldValues, reverseSchema(schema.sfFieldValue)));

export const serializationUseCannedMessageRecord = (siteId, agentId, chatId, id) =>
  JSON.stringify({
    a: siteId,
    b: chatId,
    c: agentId,
    d: id,
  });



