import * as _ from 'lodash';
import * as visitorStatus from 'src/constants/enums/visitorStatus';
import * as selectors from '../reducers/selectors';
import { webResponse } from '../constants/web';
import { agentGetVisitors, agentGetUnfinishedWrapups } from '../constants/enums/chatAction';
import * as helper from '../utils/common';
import * as chatChangeAction from '../actions/chat';
import { initConfig } from '../constants/agent';


const checkIfCreateChat = (state, v, department, agentId) => {
  const status = v.status;
  const isWaiting = selectors.isWaitingStatus(state, v, agentId);
  const isChatting = selectors.isChattingWithAgent(v, agentId);
  const isWaitingAcceptInvitationFromMe =
    selectors.checkIfWaitingVisitorAcceptInvitationFromMe(v, agentId);
  const isMonitoring = selectors.isMonitoringChat(v, agentId);
  const isHavePermission = helper.isHavePermissionDealChat(status, v.chatInvolvedAgents,
    department, agentId);
  return (isWaiting && isHavePermission) ||
    isChatting ||
    isWaitingAcceptInvitationFromMe ||
    isMonitoring;
};

const checkIfChatVisitorLeaved = (state, visitorId, prev) => {
  if (!selectors.isVisitorOnline(state, visitorId)) {
    prev.push(chatChangeAction.chatVisitorLeaved(visitorId));
  }
};

const getPinnedStatus = (state, chatId, agentId) => {
  const visitor = selectors.getVisitorByChatId(state, chatId);
  if (!visitor) return false;
  if (selectors.getChatIsPinned(state, chatId)) {
    if (helper.isAgentInMonitorList(visitor, agentId) &&
        !helper.isAgentInInvolvedList(visitor, agentId)) {
      return false;
    }
    return true;
  }
  return false;
};

const checkIfChatShouldDelete = (oldState, state, chatId, visitorId, agentId, prev) => {
  const oldVisitor = selectors.getVisitor(oldState, visitorId);
  const visitor = selectors.getVisitor(state, visitorId);
  const department = visitor == null ? null :
    selectors.getDepartmentById(state, visitor.get('departmentId'));
  const notChat = !checkIfCreateChat(state, {
    status: visitor.get('status'),
    chatActiveAgents: visitor.get('chatActiveAgents'),
    chatMonitorAgents: visitor.get('chatMonitorAgents'),
    chatInvolvedAgents: visitor.get('chatInvolvedAgents'),
    departmentId: visitor.get('departmentId'),
  }, department, agentId);
  if (helper.isWaiting(oldVisitor, agentId, selectors.getDepartments(oldState))) {
    if (visitor == null || notChat) {
      prev.push(chatChangeAction.deleteChat(chatId));
    }
  }
  if (!getPinnedStatus(oldState, chatId, agentId) &&
    helper.isMonitorWithAgent(oldVisitor, agentId)) {
    if (visitor == null ||
        (notChat &&
         !helper.isAgentInMonitorList(visitor, agentId) &&
         !helper.isAgentInInvolvedList(visitor, agentId))) {
      prev.push(chatChangeAction.deleteChat(chatId));
    }
  }

  if (
    helper.checkIfWaitingVisitorAcceptInvitationFromMe(oldVisitor, agentId)) {
    if (visitor == null ||
        (notChat && visitor.get('status') !== visitorStatus.refusedByVisitor)) {
      prev.push(chatChangeAction.deleteChat(chatId));
    }
  }
};

const checkIfChatEnded = (oldState, state, chatId, visitorId, agentId, prev) => {
  const oldVisitor = selectors.getVisitor(oldState, visitorId);
  const visitor = selectors.getVisitor(state, visitorId);
  const chat = selectors.getChat(state, chatId);
  if (chat
    && (helper.isChattingWithAgent(oldVisitor, agentId)
        || helper.isMonitorWithAgent(oldVisitor, agentId)
        || (helper.isChatting(oldVisitor) && helper.isChattedWithAgent(oldVisitor, agentId)))
    && (!helper.isChatting(visitor)  // chat have ended..
        || visitor.get('chatId') !== chatId)) {    // start another chat..
    prev.push(chatChangeAction.chatEnded(chatId));
  }
};
const checkIfChatShouldAutoDelete = (state, chat, prev) => {
  if (selectors.ifEnableAutoCloseChat(state)) {
    const delay = selectors.autoCloseChatWindowDelay(state) * 60 * 1000;
    const chatId = chat.get('id');
    if (!chat.get('isPinned') &&
        chat.get('endTime') != null &&
        Date.now() - chat.get('endTime') > delay &&
        (!chat.get('isUnfinishedWrapup') || chat.get('wrapupSubmitted'))) {
      prev.push(chatChangeAction.deleteChat(chatId));
    }
  }
};


function getExtraActions(oldState, state, action) {
  if (action.type === `${webResponse}/${agentGetVisitors}`) {
    const payload = [];
    const chats = selectors.getChats(state);
    const { visitors: { full, moreDiff, noUpdates } } = action.payload;

    const agentId = selectors.getCurrentAgentId(state);
    chats.forEach((chat) => {
      const visitorId = chat.get('visitorId');
      const chatId = chat.get('id');
      if (!chat.get('isUnfinishedWrapup')) {
        if (noUpdates.indexOf(visitorId) === -1) {
          checkIfChatVisitorLeaved(state, visitorId, payload);
          checkIfChatEnded(oldState, state, chatId, visitorId, agentId, payload);
          checkIfChatShouldDelete(oldState, state, chatId, visitorId, agentId, payload);
        }
        checkIfChatShouldAutoDelete(state, chat, payload);
      }
    });

    const moreDiffUpdateVisitors = _.concat(full, moreDiff);
    _.each(moreDiffUpdateVisitors, (v) => {
      const chatId = v.chatId;
      const department = selectors.getDepartmentById(state, v.departmentId);
      if (checkIfCreateChat(state, v, department, agentId)) {
        if (!chats.has(chatId)) {
          payload.push(chatChangeAction.newChat(chatId, v.id));
        }
      }
    });

    return payload;
  }
  if (action.type === initConfig) {
    const unfinishedWrapups = action.payload.settings.unfinishedWrapups;
    if (unfinishedWrapups.length > 0) {
      return _.map(unfinishedWrapups, u => chatChangeAction.newUnfinishedWrapup(u));
    }
  }
  if (action.type === `${webResponse}/${agentGetUnfinishedWrapups}`) {
    const unfinishedWrapups = action.payload;
    if (unfinishedWrapups.length > 0) {
      return _.map(unfinishedWrapups, u => chatChangeAction.newUnfinishedWrapup(u));
    }
  }
  return [];
}

export default store => next => (action) => {
  const oldState = store.getState();
  const result = next(action);
  const newState = store.getState();
  const getFromAction = getExtraActions.bind(null, oldState, newState);

  let payload;
  if (action.type === 'batch') {
    payload = action.payload.reduce((prev, curr) => prev.concat(getFromAction(curr)), []);
  } else {
    payload = getFromAction(action);
  }
  if (payload.length > 0) {
    return next({
      type: 'batch',
      payload,
    });
  }

  return result;
};



