import { connect } from 'react-redux';
import * as selectors from '../reducers/selectors';
import Confirmation from '../components/Modal/Confirmation/Confirmation';
import * as uiActions from '../actions/ui';
import { logout } from '../actions/agent';
import { ipcRenderer } from '../services/electronAPI';
import * as channelsType from '../constants/enums/channels';
import memoryStore from '../services/memoryStore';
import * as memoryStoreKey from '../constants/memoryStore';
import { agentStatus } from '../constants/enums';

const mapStateToProps = state => (selectors.getLeaveMessageProps(state));

const mapDispatchToProps = dispatch => ({
  onConfirm() {
    dispatch(uiActions.showModalLoading());
    dispatch(uiActions.switchLeaveMessageWindow(false));
    dispatch(logout());
    if (memoryStore.get(memoryStoreKey.leaveMessageStatus) === agentStatus.exit) {
      ipcRenderer.send(channelsType.sendExitFlag);
    }
  },
  onCancel() {
    memoryStore.set(memoryStoreKey.leaveMessageStatus, -1);
    dispatch(uiActions.switchLeaveMessageWindow(false));
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(Confirmation);



