import React from 'react';
import * as _ from 'lodash';
import Upgrade from 'src/components/Header/Upgrade';
import headStyle from 'src/components/Header/Header.css';
import Button from 'src/components/Button/Button';
import * as electronAPI from 'src/services/electronAPI';
import LoginForm from './LoginForm';
import Logo from '../Image/Logo';
import * as enums from '../../constants/enums';
import Icon from '../Icon/Icon';
import ForceLoginModal from '../Modal/ForceLogin/ForceLoginModal';
import SiteSelectModal from '../Modal/SiteSelect/SiteSelectModal';
import ChangePasswordModal from '../Modal/ChangePassword/ChangePasswordModal';
import ActivateIPModal from '../Modal/ActivateIP/ActivateIPModal';
import TrialExpiredModal from '../Modal/TrialExpired/TrialExpiredModal';
import SiteDunningModal from '../Modal/SiteDunning/SiteDunningModal';
import style from './Page.css';
import * as loginConstants from '../../constants/login';
import * as localStorage from '../../utils/localStorage';
import * as localStorageKeys from '../../constants/localStorage';

class Login extends React.Component {
  componentDidMount() {
    window.document.title = ` Comm100 Live Chat Agent Console - ${loginConstants.liveChatVisitorMonitorVersion}`;
    const agentId = typeof getAgentId === 'undefined' ? '' : window.getAgentId();
    const token = typeof getToken === 'undefined' ? '' : window.getToken();
    const siteId = typeof getSiteId === 'undefined' ? '' : window.getSiteId();
    const email = typeof getAgentEmail === 'undefined' ? '' : window.getAgentEmail();
    const isForceLogin = typeof getForceLogin === 'undefined' ? '' : window.getForceLogin();
    if (this.props.agentLogout === 0 && process.env.IS_WEB_APP && agentId && token && siteId) {
      this.props.autoWebLogin({ agentId, token, siteId, email, autoWebLogin: true }, isForceLogin === 'true');
    } else if (this.props.agentLogout === 0 && localStorage.getBool(localStorageKeys.rememberMe)) {
      this.props.autoLogin();
    }
  }

  shouldComponentUpdate(nextProps) {
    if (!_.isEqual(this.props, nextProps)) {
      return true;
    }
    return false;
  }

  render() {
    const { errorMessage, handleForceLogin, closeModal, closeTrialExpiredModal, sites,
      isAdmin, sendActivationIPEmail, handleActivateIP, handleSiteSelect,
      ifShowChangePassword, ifShowActiveIP, handleSiteSelected,
      changePasswordSuccess, hasExpired, handleSubmitChangePassword, changPasswordOKClick,
      password, closeChangePasswordModal, activeIPLoading, activeIPError,
      ifShowTrialExpired, isTrialExpired, expireDaysRemaining, handleBuyNow,
      ifShowDunning, dunningInfoStatus, message, onExtendClick, closeDunningModal,
      ifShowDismiss, ifShowErrorMessage, closeErrorMessage, ifExtendLoading, showError,
      hasUpdate,
      ...rest } = this.props;

    return (<div className={style.page}>
      <div className={style.container}>
        <div className={style.content}>
          <div className={headStyle.headSection}>
            <div className={headStyle.headLeft}>
              <Logo className={headStyle.logoStyle} />
            </div>
            <div className={headStyle.headRight}>
              {hasUpdate &&
                <div className={headStyle.menuItem}>
                  <Upgrade />
                </div>
              }
              <div className={headStyle.menuItem}>
                <Button
                  text="GET HELP"
                  className={headStyle.iconButton}
                  onClick={() => {
                    electronAPI.shell.openExternal(loginConstants.kb_help);
                  }}
                >
                  <Icon
                    type={enums.icons.loginHelp}
                    size={16}
                    className={headStyle.statusIcon}
                  />
                </Button>
              </div>
            </div>
          </div>
          <div className={style.globalErrorMessageWrapper}>
            <div className={style.globalErrorMessage} style={{ display: errorMessage && ifShowErrorMessage ? 'inline-block' : 'none' }}>
              <div className={style.closeImage} onClick={closeErrorMessage} />
              <span>{errorMessage}</span>
              {ifShowDismiss && (
                <a className={style.dismiss} onClick={closeErrorMessage}>Dismiss</a>
              )}
            </div>
          </div>
        </div>
        <LoginForm {...rest} />
        <div className={style.footer}>
          {`${loginConstants.copyright} ${loginConstants.brandingName} | version ${loginConstants.liveChatVisitorMonitorVersion}`}
        </div>
      </div>
      {rest.ifShowForceLogin &&
        <ForceLoginModal
          handleForceLogin={handleForceLogin}
          closeModal={closeModal}
        />
      }
      {rest.ifShowSiteSelect &&
        <SiteSelectModal
          siteSelect={handleSiteSelect}
          closeModal={closeModal}
          sites={sites}
          siteClick={rest.siteClick}
          siteSelected={handleSiteSelected}
        />
      }
      {ifShowChangePassword &&
        <ChangePasswordModal
          changePasswordSuccess={changePasswordSuccess}
          closeModal={closeChangePasswordModal}
          hasExpired={hasExpired}
          handleSubmitChangePassword={handleSubmitChangePassword}
          changPasswordOKClick={changPasswordOKClick}
          oldPassword={password}
        />
      }
      {ifShowActiveIP &&
        <ActivateIPModal
          handleActivateIP={handleActivateIP}
          closeModal={closeModal}
          isAdmin={isAdmin}
          sendActivationIPEmail={sendActivationIPEmail}
          activeIPLoading={activeIPLoading}
          activeIPError={activeIPError}
        />
      }
      {ifShowTrialExpired &&
        <TrialExpiredModal
          handleBuyNow={handleBuyNow}
          closeModal={closeTrialExpiredModal}
          isExpired={isTrialExpired}
          expireDaysRemaining={expireDaysRemaining}
        />
      }
      {ifShowDunning &&
        <SiteDunningModal
          onExtendClick={onExtendClick}
          closeModal={closeDunningModal}
          dunningInfoStatus={dunningInfoStatus}
          message={message}
          ifExtendLoading={ifExtendLoading}
          showError={showError}
        />
      }
    </div>);
  }
}
Login.propTypes = {
  errorMessage: React.PropTypes.string,
  ifShowDunning: React.PropTypes.bool,
  dunningInfoStatus: React.PropTypes.number,
  message: React.PropTypes.string,
  onExtendClick: React.PropTypes.func,
  closeDunningModal: React.PropTypes.func,
  ifShowDismiss: React.PropTypes.bool,
  ifShowErrorMessage: React.PropTypes.bool,
  closeErrorMessage: React.PropTypes.func,
  ifExtendLoading: React.PropTypes.bool,
  showError: React.PropTypes.func,
  className: React.PropTypes.string,
  ifShowForceLogin: React.PropTypes.bool,
  closeModal: React.PropTypes.func,
  closeTrialExpiredModal: React.PropTypes.func,
  closeChangePasswordModal: React.PropTypes.func,
  handleForceLogin: React.PropTypes.func,
  ifShowSiteSelect: React.PropTypes.bool,
  sites: React.PropTypes.arrayOf(
    React.PropTypes.shape({
      siteId: React.PropTypes.number.isRequired,
      lastLoginTime: React.PropTypes.string,
      registerTime: React.PropTypes.string,
    })
  ),
  ifShowChangePassword: React.PropTypes.bool,
  ifShowActiveIP: React.PropTypes.bool,
  isAdmin: React.PropTypes.bool,
  sendActivationIPEmail: React.PropTypes.bool,
  handleActivateIP: React.PropTypes.func,
  handleSiteSelect: React.PropTypes.func,
  siteId: React.PropTypes.number,
  handleSiteSelected: React.PropTypes.func,
  changePasswordSuccess: React.PropTypes.bool.isRequired,
  hasExpired: React.PropTypes.bool.isRequired,
  handleSubmitChangePassword: React.PropTypes.func,
  changPasswordOKClick: React.PropTypes.func,
  verificationCodeUrl: React.PropTypes.string,
  siteClick: React.PropTypes.bool,
  agentLogout: React.PropTypes.number,
  handleSubmit: React.PropTypes.func,
  password: React.PropTypes.string,
  activeIPLoading: React.PropTypes.bool,
  activeIPError: React.PropTypes.string,
  billUrl: React.PropTypes.string,
  ifShowTrialExpired: React.PropTypes.bool.isRequired,
  handleBuyNow: React.PropTypes.func.isRequired,
  isTrialExpired: React.PropTypes.bool,
  expireDaysRemaining: React.PropTypes.number,
  ifShowSuggestion: React.PropTypes.bool,
  autoLogin: React.PropTypes.func,
  autoWebLogin: React.PropTypes.func,
  hasUpdate: React.PropTypes.bool,
};

export default Login;



