import * as _ from 'lodash';
import * as chatAction from '../../constants/enums/chatAction';
import { errorCode } from '../../constants/web';
import ConnectionErrorReporter from './connectionErrorReporter';
import { everyfns } from '../../utils/func';
// import { log } from '../../utils/helper';

function selfIncrease(start = 1) {
  let cnt = start - 1;
  return () => {
    cnt += 1;
    return cnt;
  };
}

// 保证一次请求里有且只有一个heartbeat命令
function removeDuplicates(commands) {
  const commandTypes = [chatAction.agentGetVisitors, chatAction.agentGetChats,
    chatAction.agentLogin, chatAction.agentAutoLogin];
  const preds = commandTypes.map(type => cmd => cmd.type !== type);

  const reversed = commands.reverse();

  return commands
    .filter(everyfns(...preds))
    .concat(preds.map(p => reversed.find(_.negate(p))).filter(_.negate(_.isUndefined)))
    .sort((a, b) => a.id - b.id);
}

export default class Queue {
  constructor(options) {
    Object.assign(this, options);
    this.subscriptions = [];

    // pending是将要发送的网络包，收到command后先放到pending里
    this.pending = [];

    // sending是正在发送的网络包，不为空表示正在发送，同一时刻只有一个网络包在发送
    this.sending = [];

    this.switching = false;

    this.getCommandId = selfIncrease(this.startCommandId);

    this.errorReporter = new ConnectionErrorReporter({
      onreport: this.dispatchSubscriptions.bind(this),
      connectionErrorTimeout: this.connectionErrorTimeout,
      connectionErrorCount: this.connectionErrorCount,
      maxonServerErrorCheckPoint: this.maxonServerErrorCheckPoint,
    });
  }

  newSendPacket() {
    const commands = removeDuplicates(this.pending);
    _.each(commands, (command) => {
      if (command.type === chatAction.agentGetVisitors) {
        command.visitorId = this.getVisitorListVersion(); // eslint-disable-line no-param-reassign
      }
    });
    return {
      commands,
      session: this.session,
      localChats: this.getLocalChats(),
    };
  }

  async send() {
    if (this.switching || this.pending.length === 0) {
      return;
    }

    if (this.sending.length === 0) {
      const tosend = this.newSendPacket();
      this.sending = tosend.commands;
      this.pending = [];

      try {
        const resp = await this.client.send(tosend);

        this.errorReporter.reset();
        if (resp.errorCode !== errorCode.noerror) {
          this.sending = [];
          this.dispatchSubscriptions({
            errorCode: resp.errorCode,
            errorString: resp.errorString,
          });
          return;
        }

        const sending = this.sending;
        const accepts = resp.commands.filter(pkt => sending.some(pkt2 => pkt.id === pkt2.id));
        this.sending = sending.filter(pkt => !accepts.some(pkt2 => pkt.id === pkt2.id));
        if (accepts.length > 0) {
          // 一个命令一个命令分发
          /**
           * @todo
           * if merge all these commands into one action, should be able to reduce some renders
           * it's not necessary if performance is not an issue
           */
          _.each(accepts, (cmd) => {
            if (cmd.errorCode === errorCode.noerror) {
              const cmdresp =
                cmd.type === chatAction.agentGetChats // getChats命令的返回数据在外层对象里
                  ? Object.assign({
                    chatMessages: resp.chatMessages,
                    privateMessages: resp.privateMessages,
                  }, cmd)
                  : cmd;
              cmdresp.sourceAction = sending.find(c => c.id === cmdresp.id);
              this.dispatchSubscriptions(null, cmdresp);
            } else {
              this.dispatchSubscriptions({
                errorCode: cmd.errorCode,
                errorString: cmd.errorString,
                sourceAction: sending.find(c => c.id === cmd.id),
              });
            }
          });
        }
      } catch (err) {
        // catch到网络操作返回错误
        this.discardSending();
        this.errorReporter.record();
      }
    }
  }

  // 内部使用
  dispatchSubscriptions(err, resp) {
    _.each(this.subscriptions, sub => sub(err, resp));
  }

  // 把sending重新放回到pending这样下个heartbeat时自动重试
  discardSending() {
    this.pending = this.sending.concat(this.pending);
    this.sending = [];
  }

  switchingServer() {
    this.switching = true;
    this.discardSending();
  }

  switchedServer() {
    this.switching = false;
  }

  getIsSwitchingServer() {
    return this.switching;
  }
  /**
   * Dispatch network related action to queue
   *
   * @param {Array} commands - network related commands
   *
   * @memberOf Queue
   */
  dispatch(...commands) {
    this.pending = this.pending.concat(
      commands.map(cmd => Object.assign({}, cmd, {
        id: this.getCommandId(),
      }))
    );
    this.send();
  }

  insertCommands(...commands) {
    this.pending = _.concat(
      commands.map(cmd => Object.assign({}, cmd, {
        id: this.getCommandId(),
      })), this.pending);
  }

  /**
   * Subscribe callback for new arriving network responses
   *
   * @param {Function} subscriber - Function that accept new JSON responses as param
   *
   * @memberOf Queue
   */
  subscribe(subscriber) {
    this.subscriptions.push(subscriber);
  }

  // for unit test
  getSending() {
    return this.sending;
  }

  // for unit test
  getPending() {
    return this.pending;
  }
}



