import { reducer as formReducer } from 'redux-form';
import agent from './agent';
import ui from './ui';
import { webResponse, webError, errorCode } from '../constants/web';
import * as chatAction from '../constants/enums/chatAction';
import chats from './chats';
import visitor from './visitor';
import config from './config';
import debug from './debug';
import agentChats from './agentChats';
import editor from './editor';
import reduceReducers from './reduceReducers';
import memoryStore from '../services/memoryStore';
import * as memoryStoreKey from '../constants/memoryStore';
import { ipcRenderer } from '../services/electronAPI';
import * as channelsType from '../constants/enums/channels';
import * as localStorage from '../utils/localStorage';
import * as localStorageKeys from '../constants/localStorage';
import * as enums from '../constants/enums';
import { ifSupportWebrtc } from '../utils/common';

const reducers = {
  form: formReducer,
  agent,
  visitor,
  chats,
  agentChats,
  editor,
  ui,
  config,
};

if (process.env.NODE_ENV !== 'production') {
  reducers.debug = debug;
}

const reducer = reduceReducers(
  reducers,
  ['config', 'visitor', 'chats', 'agentChats', 'ui']
);

const getResetValue = () => {
  if (process.env.IS_WEB_APP) {
    const email = typeof getAgentEmail === 'undefined' ? '' : window.getAgentEmail();
    if (email) {
      return {
        status: enums.agentStatus.online,
        rememberMe: false,
        email,
        password: '',
      };
    }
  }
  const cache = localStorage.getObject(localStorageKeys.loginForm);
  const initValues =
    {
      status: enums.agentStatus.online,
      rememberMe: false,
      email: '',
      password: '',
    };
  if (cache && cache.email) {
    initValues.email = cache.email;
  }
  return initValues;
};

function stopP2PChat(state) {
  if (!ifSupportWebrtc) return;

  state.chats.forEach((c) => {
    const p2pChat = c.get('p2pChat');
    if (p2pChat != null) p2pChat.stop();
  });
}


export default (state, action) => {
  if (process.env.NODE_ENV !== 'production' && action.type === 'debug/replay') {
    stopP2PChat(state);

    // for development mode only, restart state
    return reducer(undefined, action);
  }
  if (
    action.type === `${webResponse}/${chatAction.agentLogout}` ||
    (action.type.indexOf(webError) === 0 && action.payload.errorCode === errorCode.sessionTimeout)
  ) {
    stopP2PChat(state);

    const status = memoryStore.get(memoryStoreKey.leaveMessageStatus);
    ipcRenderer.send(channelsType.changeStatus, { code: status });
    memoryStore.set(memoryStoreKey.ifLogout, true);

    let newState = reducer(undefined, action);
    newState = Object.assign({}, newState, {
      ui: Object.assign({}, newState.ui, {
        agents: Object.assign({}, newState.ui.agents, {
          selectedId: null,
          openedItems: [],
          agentsListWidth: 0,
        }),
      }),
    });
    if (process.env.NODE_ENV !== 'production') {
      newState.debug = state.debug;
    }
    let agentLogoutFlag = 2;
    if (state.ui.modalLoading) {
      agentLogoutFlag = 1;
    }
    return Object.assign({}, newState, {
      ui: Object.assign({}, newState.ui, {
        modalLoading: false,
        login: Object.assign({}, newState.ui.login, {
          agentLogout: agentLogoutFlag,
          formValues: getResetValue(),
        }),
      }),
    });
  }

  return reducer(state, action);
};



