import * as _ from 'lodash';
import moment from 'moment';

export function parseDate(s) {
  if (_.isDate(s)) return s;
  if (typeof s === 'string') {
    const m = /\/Date\(([-+\d]+)\)\//.exec(s);
    if (m && m.length > 1) {
      return new Date(parseInt(m[1], 10));
    }
  }
  return null;
}
export const dateSubs = (to, from) => (to.getTime() - parseDate(from).getTime());
export const dateSubsToString = (to, from, delay = 0) => {
  const diff = Math.max(0, dateSubs(to, from) - (delay * 1e3));
  const hour = Math.floor(diff / 36e5);
  const minute = Math.floor((diff % 36e5) / 6e4);
  const second = Math.floor((diff % 6e4) / 1e3);
  return `${_.padStart(hour, 2, '0')}:${_.padStart(minute, 2, '0')}:${_.padStart(second, 2, '0')}`;
};
export function checkTimeout(from, duration) {
  return dateSubs(new Date(), from) > duration;
}
export const toHHMM = date =>
  `${_.padStart(date.getHours(), 2, '0')}:${_.padStart(date.getMinutes() + 1, 2, '0')}`;

export const toHHMMSS = date =>
  `${_.padStart(date.getHours(), 2, '0')}:${_.padStart(date.getMinutes() + 1, 2, '0')}:${_.padStart(date.getSeconds(), 2, '0')}`;

export const dateToString = (date, format, timezone, timezoneOffset) => {
  let offset = 0;
  if (timezone >= 0) {
    offset = (new Date()).getTimezoneOffset() - timezoneOffset;
  }
  const newDate = new Date(date);
  newDate.setMinutes(newDate.getMinutes() + offset);
  return moment(newDate).format(format.replace('dd', 'DD').replace('yyyy', 'YYYY'));
};

const toJSONWarining = _.once(() => {
  console.warn('[WARNING]: Date.toJSON() has been modified. Do not expect it to behave normally.');
});

Date.prototype.toJSON = function dateToJSON() { // eslint-disable-line no-extend-native
  if (process.env.NODE_ENV !== 'production') {
    toJSONWarining();
  }
  return `/Date(${this.getTime()})/`;
};
export function formatWaitingTimeWithMillsecond(ticks) {
  const seconds = ticks / 1000;
  if (seconds < 0) {
    return '';
  } else if (seconds < 60) {
    return `${seconds.toFixed(0)}s`;
  } else if (seconds < 3600) {
    return `${(seconds / 60).toFixed(0)}m`;
  }
  return `${(seconds / 3600).toFixed(0)}h`;
}


const timeUnits = [
  // { unit: 60 * 60 * 24, stuf: 'd ' },
  { unit: 60 * 60, stuf: 'h ' },
  { unit: 60, stuf: 'm ' },
  { unit: 1, stuf: 's ' },
];

// 将毫秒转换成天，时，分，秒，不足一秒返回 1s.
export function formatTimeWithMillsecond(ticks) {
  let second = ticks / 1000;
  if (second < 1) return '1s';
  let flag = false;
  let ret = '';
  _.forEach(timeUnits, (val) => {
    const value = (second / (val.unit)).toFixed(0);
    flag = value === '0' ? flag : true;
    second %= val.unit;
    if (flag) ret += (value + val.stuf);
  });
  return ret;
}

export function formatTimeWithMillSecondKeep2Unit(ticks) {
  let second = ticks / 1000;
  if (second < 1) return '1s';
  let unitCount = 0;
  let flag = false;
  let ret = '';
  _.forEach(timeUnits, (val) => {
    const value = Math.floor(second / (val.unit));
    flag = value !== 0;
    second %= val.unit;
    if (flag && unitCount < 2) {
      unitCount += 1;
      ret += (value + val.stuf);
    }
  });
  return ret;
}

// 对Date的扩展，将 Date 转化为指定格式的String
// 月(M)、日(d)、小时(h)、分(m)、秒(s)、季度(q) 可以用 1-2 个占位符，
// 年(y)可以用 1-4 个占位符，毫秒(S)只能用 1 个占位符(是 1-3 位的数字)
// 例子：
// (new Date()).Format("yyyy-MM-dd hh:mm:ss.S") ==> 2006-07-02 08:09:04.423
// (new Date()).Format("yyyy-M-d h:m:s.S")      ==> 2006-7-2 8:9:4.18
export const format = function (date, fmt) { // author: meizz
  const o = {
    'M+': date.getMonth() + 1, // 月份
    'd+': date.getDate(), // 日
    'h+': date.getHours() % 12 === 0 ? 12 : date.getHours() % 12, // 12小时制
    'H+': date.getHours(), // 24小时制
    'm+': date.getMinutes(), // 分
    's+': date.getSeconds(), // 秒
    'q+': Math.floor((date.getMonth() + 3) / 3), // 季度
    S: date.getMilliseconds(), // 毫秒
  };
  let result = fmt;
  if (/(y+)/.test(fmt)) {
    result = result.replace(RegExp.$1, (`${date.getFullYear()}`).substr(4 - RegExp.$1.length));
  }
  return Object.keys(o).reduce((ret, key) => {
    if (new RegExp(`(${key})`).test(result)) {
      return ret.replace(
        RegExp.$1,
        (RegExp.$1.length === 1) ? (o[key]) : ((`00${o[key]}`).substr((`${o[key]}`).length))
      );
    }
    return ret;
  }, result);
};

const getLocalTimeFromUtc = (dateUTC, timezone, timezoneoffset) => {
  const fromTime = parseDate(dateUTC);
  const dateTimeLocal = new Date(fromTime.getTime());
  let localOffset;
  if (timezone >= 0) {
    localOffset = fromTime.getTimezoneOffset() - timezoneoffset;
  } else {
    localOffset = 0;
  }
  return new Date(dateTimeLocal.getTime() + (localOffset * 60000));
};

export const formatTimeForHistory = (timezone, timezoneoffset, dateUTC, fmt) => {
  let shortFmt = fmt;
  const optTime = getLocalTimeFromUtc(dateUTC, timezone, timezoneoffset);
  const optNow = getLocalTimeFromUtc(new Date(), timezone, timezoneoffset);
  if (optTime.getFullYear() === optNow.getFullYear()) {
    shortFmt = shortFmt.replace(/[\W]?y+[\S]?/g, '');
    if (optTime.getMonth() === optNow.getMonth()
      && optTime.getDate() === optNow.getDate()) {
      shortFmt = shortFmt.replace(/[\W]?M+[\W]?/g, '');
      shortFmt = shortFmt.replace(/[\W]?d+[\S]?/g, '');
      shortFmt = shortFmt.replace(/[\W]?s+[\S]?/g, '');
    }
  }
  shortFmt = shortFmt.trim();
  return format(optTime, shortFmt);
};


// timezoneoffset 是秒钟所以要 * 1000
