// timezoneoffset 是秒钟所以要 * 1000
export const formatDateFromSpamTime = (seconds, timezone, timezoneoffset) => {
  let date = new Date((seconds * 1000));
  if (timezone !== -1) {
    date = getLocalTimeFromUtc(date, timezone, timezoneoffset);
  }
  return format(date, 'HH:mm:ss');
};



