import * as _ from 'lodash';
import * as helper from 'src/utils/common';
import * as selectors from 'src/reducers/selectors';
import * as pages from 'src/constants/enums/page';
import * as action from 'src/actions/chatList';
import * as uiAction from 'src/actions/ui';
import * as visitorDetailsActions from '../actions/visitorDetails';
import { visitorDetailsTabs, chatStatus } from '../constants/enums';
import memoryStore from '../services/memoryStore';

let currentKeydownHandler;

const checkOnGoingChat = (state, chatId) => {
  const visitor = selectors.getVisitorByChatId(state, chatId);
  const status = visitor.get('status');
  if (status === chatStatus.chatting ||
    (status === chatStatus.transferring &&
      selectors.checkIfVisitorStatusChatting(status) &&
      helper.isAgentInChatActiveList(visitor, selectors.getCurrentAgentId(state))
    )) {
    return true;
  }
  return false;
};

const hasUnreadMessages = (state, chatId) => {
  const chat = selectors.getChat(state, chatId);
  const visitor = selectors.getVisitorByChatId(state, chatId);
  if (visitor &&
      visitor.get('status') === chatStatus.chatting &&
      chat.get('unreadMessages') > 0) {
    return true;
  }
  return false;
};

const goToNextChat = (state, dispatch, checkHandle) => {
  const chats = selectors.getChats(state).toArray().reverse();
  if (chats.length <= 1) return;
  const selected = selectors.getSelectedChatId(state);
  const index = _.findIndex(chats, chat => chat.get('id') === selected);
  if (index < 0) {
    const selectedIndex = _.findIndex(chats, chat => checkHandle(state, chat.get('id')));
    if (selectedIndex >= 0) {
      dispatch(action.setSelected(chats[selectedIndex].get('id')));
    }
    return;
  }
  for (let i = 1; i < chats.length; i += 1) {
    const chat = chats[(i + index) % chats.length];
    if (checkHandle(state, chat.get('id'))) {
      dispatch(action.setSelected(chat.get('id')));
      return;
    }
  }
};

const selectNextChat = (state, dispatch) => {
  goToNextChat(state, dispatch, checkOnGoingChat);
};

const selectNextResponse = (state, dispatch) => {
  goToNextChat(state, dispatch, hasUnreadMessages);
};

const selectCannedMessageTab = (state, dispatch) => {
  memoryStore.set('shouldFocusInput', true);
  dispatch(visitorDetailsActions.onDetailsTabSelect(visitorDetailsTabs.cannedMessage));
};

const selectVisitor = step => (state, dispatch) => {
  const selected = selectors.getSelectedVisitorId(state);
  if (!selected) return;
  const visitorList = selectors.getVisibleVisitorsList(state);
  const index = _.indexOf(visitorList, selected);
  if ((index === 0 && step === -1) || (index === visitorList.length - 1 && step === 1)) {
    return;
  }
  dispatch(uiAction.selectVisitorListRow(visitorList[index + step]));
};

const genHandleKeyDown = (totalConfig, store) => (event) => {
  const state = store.getState();
  const page = selectors.getPage(state);
  const config = totalConfig[page];
  if (!config) return;

  const keyCode = event.keyCode;
  for (let i = 0; i < config.length; i += 1) {
    const { shortcut, callback } = config[i];
    if (shortcut.key === keyCode &&
    !!shortcut.alt === event.altKey &&
    !!shortcut.ctrl === event.ctrlKey &&
    !!shortcut.meta === event.metaKey &&
    !!shortcut.shift === event.shiftKey) {
      callback(store.getState(), store.dispatch);
      event.preventDefault();
      event.stopPropagation();
      return;
    }
  }
};

const CTRL_KEY = 17;
const ALT_KEY = 18;
const SHIFT_KEY = 16;
const ARROW_UP = 38;
const ARROW_DOWN = 40;

function convertShortcut(config) {
  const shortcut = {
    alt: _.includes(config, ALT_KEY),
    ctrl: _.includes(config, CTRL_KEY),
    meta: false, // not in use currently
    shift: _.includes(config, SHIFT_KEY),
    key: _.find(config, code => !_.includes([ALT_KEY, CTRL_KEY, SHIFT_KEY], code)),
  };
  return shortcut;
}

function getShortcutConfig(state) {
  const cannedMessage = convertShortcut(selectors.getCannedMessageShortcut(state));
  const nextChat = convertShortcut(selectors.getNextChatShortcut(state));
  const nextResponse = convertShortcut(selectors.getNextResponseShortcut(state));
  return {
    cannedMessage,
    nextChat,
    nextResponse,
  };
}

function shouldShortcutUpdate(prevState, state) {
  return (
    selectors.getCannedMessageShortcut(prevState) !== selectors.getCannedMessageShortcut(state) ||
    selectors.getNextChatShortcut(prevState) !== selectors.getNextChatShortcut(state) ||
    selectors.getNextResponseShortcut(prevState) !== selectors.getNextResponseShortcut(state)
  );
}


export default (store) => {
  let previousState = store.getState();

  const staticConfig = {
    [pages.visitors]: [
      {
        shortcut: {
          key: ARROW_UP,
        },
        callback: selectVisitor(-1),
      },
      {
        shortcut: {
          key: ARROW_DOWN,
        },
        callback: selectVisitor(1),
      },
    ],
  };

  const unsubscribe = store.subscribe(() => {
    const state = store.getState();
    if (!shouldShortcutUpdate(previousState, state)) {
      previousState = state;
      return;
    }
    previousState = state;
    const { cannedMessage, nextChat, nextResponse } = getShortcutConfig(state);
    const config = Object.assign({
      [pages.chats]: [
        {
          shortcut: nextResponse,
          callback: selectNextResponse,
        },
        {
          shortcut: nextChat,
          callback: selectNextChat,
        },
        {
          shortcut: cannedMessage,
          callback: selectCannedMessageTab,
        },
      ],
    }, staticConfig);
    window.removeEventListener('keydown', currentKeydownHandler);
    currentKeydownHandler = genHandleKeyDown(config, store);
    window.addEventListener('keydown', currentKeydownHandler);
  });
  return () => {
    window.removeEventListener('keydown', currentKeydownHandler);
    unsubscribe();
  };
};



