import React from 'react';
import Modal from '../Modal';
import ModalStyle from '../Modal.css';

const ActivateIPModal = props => (
  <Modal
    closeModal={props.closeModal}
    className={ModalStyle.loginModal}
    ifShowCloseIcon={false}
    actions={
      (props.isAdmin && !props.sendActivationIPEmail) ? {
        okProps: { text: 'Authorize This IP', disabled: props.activeIPLoading, eventClick: props.handleActivateIP, loading: props.activeIPLoading },
        cancelProps: { text: 'Close' },
      }
      :
      {
        cancelProps: { text: 'OK' },
      }
    }
  >
    <div className={ModalStyle.errorMessage} style={{ display: props.activeIPError ? 'block' : 'none' }}>
      <span>{props.activeIPError}</span>
    </div>
    { !props.isAdmin && !props.sendActivationIPEmail &&
      <p>
        {[
          'You\'re logging in from an unauthotized IP address.',
          'Please check with your account administrator and try again.',
        ].join(' ')}
      </p>
    }
    { props.isAdmin && !props.sendActivationIPEmail &&
      <p>
        {'You\'re logging in from an unauthotized IP address.'}
      </p>
    }
    { props.sendActivationIPEmail &&
      <p>
        Please check your email for IP authorization instructions and then try the login again.
      </p>
    }
  </Modal>
);

export default ActivateIPModal;

ActivateIPModal.propTypes = {
  // ifOpen: React.PropTypes.bool.isRequired,
  handleActivateIP: React.PropTypes.func.isRequired,
  closeModal: React.PropTypes.func.isRequired,
  isAdmin: React.PropTypes.bool,
  sendActivationIPEmail: React.PropTypes.bool,
  activeIPLoading: React.PropTypes.bool,
  activeIPError: React.PropTypes.string,
};



