import Immutable from 'immutable';
import * as _ from 'lodash';
import * as web from '../constants/web';
import * as selectors from './selectors';
import * as time from '../utils/time';
import * as chatType from '../constants/enums/agentChatType';
import * as agentsTypes from '../constants/types/agents';
import * as agentChatTypes from '../constants/types/agentChat';
import { isImage } from '../utils/seq';
import memoryStore from '../services/memoryStore';
import * as memoryStoreKey from '../constants/memoryStore';
import { updateCheckUnreadMsgFlag } from '../containers/Business/agents';
import * as pages from '../constants/enums/page';
import * as layoutConstants from '../constants/layout';

/* the structure of agent's chat */
const defaultChat = (id, isAgent = true) => ({
  id,
  isAgent,
  messages: Immutable.List(),
  editorState: null,
  typingMessage: '',
  scrollTop: -1,
  unreadMessages: 0,
  errorMessageForChat: '',
  files: [],
});

const defaultState = Immutable.Map({});

function getSelectedId(totalState) {
  if (!totalState.ui) return [false];
  const sId = selectors.getSelectedIdForAgents(totalState);
  let selectedId = -1;
  if (sId || sId === 0) {
    const ids = sId.toString().split('_');
    if (ids.length > 1) {
      selectedId = _.parseInt(ids[1]);
      if (_.findIndex(selectors.getAgents(totalState), agent => agent.id === selectedId) >= 0) {
        return [true, true, selectedId];
      }
    }
    selectedId = _.parseInt(ids[0]);
    if (_.findIndex(selectors.getDepartments(totalState), dep => dep.id === selectedId) >= 0) {
      return [true, false, selectedId];
    }
  }
  return [false];
}

const initState = (state, totalState, isAgentsTab = false) => {
  const [isAvailable, isAgent, id] = getSelectedId(totalState);
  if (!isAvailable) return state;
  if (!state.get(id)) {
    return state.set(id, Immutable.Map(defaultChat(id, isAgent)));
  }
  if (isAgentsTab) return state.setIn([id, 'unreadMessages'], 0);
  return state;
};

const mergeMessages = (messages, message) =>
  messages.update(
    messages.size - 1,
    messageGroup => (messageGroup ? messageGroup.concat(message) : [message])
  );

const shouldMerge = (lastMessageGroup, message) => {
  if (!lastMessageGroup) return false;
  const lastMessage = lastMessageGroup[0];
  if (!lastMessage) return false;
  if (lastMessage.type !== message.type) return false;
  if (message.agentId !== lastMessage.agentId) return false;
  if (time.dateSubs(message.time, lastMessage.time) > 30 * 1000) return false;
  return true;
};

const addMessage = (chat, message, isReading) => {
  const lastMessageGroup = chat.get('messages').last();
  if (!isReading) {
    updateCheckUnreadMsgFlag(true);
  }
  return (
    chat.update('messages', (msg) => {
      if (shouldMerge(lastMessageGroup, message)) {
        return mergeMessages(msg, message);
      }
      return msg.push([message]);
    })
    .set('scrollTop', -1)
    .update('unreadMessages', unreadMessages => (isReading ? 0 : unreadMessages + 1))
  );
};

const messageUpdate = (chat, msg, isReading, totalState) => {
  let message = null;
  switch (msg.type) {
    case chatType.messageTypeSendFile:
      message = _.pick(msg, ['agentId', 'time', 'fileName', 'type', 'url', 'isImage']);
      return addMessage(chat, message, isReading);
    case chatType.messageTypeFile:
      message = _.pick(msg, ['agentId', 'time', 'message', 'type', 'guid']);
      message.isImage = isImage(message.message);
      message.fileName = message.message;
      message.url = selectors.appendParamsToDownloadUrl(totalState,
                    message.message,
                    message.guid,
                    1);
      delete message.message;
      return addMessage(chat, message, isReading);
    case chatType.messageTypeText:
      message = _.pick(msg, ['agentId', 'time', 'message', 'type']);
      return addMessage(chat, message, isReading);
    default:
      if (process.env.NODE_ENV !== 'production') {
        if (chatType.messageTypeBeginSendImage === msg.type) {
          console.log('messageTypeBeginSendImage', msg);
        } else if (chatType.messageTypeEndSendImage === msg.type) {
          console.log('messageTypeEndSendImage', msg);
        }
      }
      return chat;
  }
};
function updateChat(state, id, isAgent = true) {
  if (!state.has(id)) {
    return state.set(id, Immutable.Map(defaultChat(id, isAgent)));
  }
  return state.setIn([id, 'unreadMessages'], 0);
}
function initStateFromMemoryCache(state, totalState) {
  if (!_.isUndefined(totalState.agent) && totalState.agent.id !== 0) {
    if (state.size === 0) {
      return memoryStore.get(memoryStoreKey.agentChats, totalState.agent.id, Immutable.Map({}));
    }
    memoryStore.set(memoryStoreKey.agentChats, state, totalState.agent.id);
  }
  return state;
}
const stateMapping = {
  [agentsTypes.updateAgent](state, action) {
    const agentId = _.parseInt(action.payload.toString().split('_')[1]);
    return updateChat(state, agentId);
  },
  [agentsTypes.updateDepartment](state, action) {
    const dptId = _.parseInt(action.payload);
    return updateChat(state, dptId, false);
  },
  [agentChatTypes.updateContent](state, action) {
    if (!state.get(action.meta).get('editorState')) {
      return state.setIn([action.meta, 'editorState'], action.payload);
    }
    return state.mergeIn([action.meta, 'editorState'], action.payload);
  },
  [agentChatTypes.updateScrollTop](state, action) {
    return state.setIn([action.meta, 'scrollTop'], action.payload);
  },
  [agentChatTypes.setErrorMessageForAgentChat](state, action) {
    return state.setIn([action.meta, 'errorMessageForChat'], action.payload);
  },
  [agentChatTypes.startUpload](state, action) {
    return state.updateIn([action.meta, 'files'], files => files.concat(action.payload));
  },
  [agentChatTypes.uploadFile](state, action) {
    return state.updateIn([action.meta, 'files'], (files) => {
      let inserted = false;
      const ret = files.map((file) => {
        if (file.guid === action.payload.guid && file.filename === action.payload.filename) {
          if (file.percentage !== action.payload.percentage) {
            inserted = true;
            return action.payload;
          }
        }
        return file;
      });
      if (inserted) return ret;
      return files;
    });
  },
  [agentChatTypes.cancelUpload](state, action) {
    return state.updateIn(
      [action.meta, 'files'],
      (files) => {
        const ret = files.filter(
          file => file.guid !== action.payload.guid || file.filename !== action.payload.filename
        );
        if (ret.length === files.length) return files;
        return ret;
      }
    );
  },
};
/* eslint-disable no-param-reassign*/
export default (preState = defaultState, action, totalState) => {
  /**
   * @todo CANNOT do this. Refactor this when have time, move it to middleware if possible.
   * Reason: Reading external variable/data inside reducer makes it not pure. This is anti-pattern.
   */
  let state = initStateFromMemoryCache(preState, totalState);
  if (action.type.indexOf(web.webResponse) === 0) {
    if (action.type.indexOf(`${web.webResponse}/agentchat/`) === 0) {
      const agentId = action.payload.agentId;
      let chat = selectors.getAgentChat(totalState, agentId);
      if (!chat) {
        chat = Immutable.Map(defaultChat(agentId));
      }
      return state.set(agentId,
        messageUpdate(chat,
          action.payload,
          selectors.ifAgentChatWithOthers(totalState, agentId),
          totalState));
    }
    return state;
  }
  if (action.type === agentChatTypes.appendOwnMessage) {
    let newState = state;
    _.forEach(action.meta, (id) => {
      const chat = newState.get(id) || Immutable.Map(defaultChat(id));
      const newMsg = {
        agentId: selectors.getCurrentAgentId(totalState),
        time: new Date(),
        message: action.payload,
        type: chatType.messageTypeText,
      };
      newState = newState.set(id, messageUpdate(chat,
          newMsg,
          true));
    });
    return newState;
  }
  if (action.type === agentChatTypes.appendFile) {
    let newState = state;
    _.forEach(action.meta, (id) => {
      const chat = newState.get(id) || Immutable.Map(defaultChat(id));
      const newMsg = Object.assign({
        agentId: selectors.getCurrentAgentId(totalState),
        time: new Date(),
        type: chatType.messageTypeSendFile,
      }, action.payload);
      newState = newState.set(id, messageUpdate(chat,
          newMsg,
          true));
    });
    return newState;
  }
  if (action.type === layoutConstants.switchPage &&
      action.payload.page === pages.agents) {
    return initState(state, totalState, true);
  }
  state = initState(state, totalState);
  if (stateMapping[action.type] === undefined) return state;
  return stateMapping[action.type](state, action);
};



