import React from 'react';
import ChangePasswordForm from './ChangePasswordForm';
import Modal from '../Modal';
import style from './ChangePassword.css';
import Button from '../../Button/Button';
import ModalStyle from '../Modal.css';

const ChangePasswordModal = (props) => {
  const { changPasswordOKClick, changePasswordSuccess, ...rest } = props;
  return (
    <Modal
      closeModal={rest.closeModal}
      className={ModalStyle.loginModal}
      ifShowCloseIcon={false}
      shouldCloseOnOverlayClick={false}
    >
      {
        !changePasswordSuccess && React.createElement(ChangePasswordForm, rest)
      }
      {
        changePasswordSuccess &&
        (
          <div className={style.changedWrapper}>
            <p>
                Your password has been changed.
            </p>
            <Button
              onClick={changPasswordOKClick}
              text="OK"
              type="primary"
            />
          </div>
        )
      }
    </Modal>
  );
};
ChangePasswordModal.propTypes = {
  // ifOpen: React.PropTypes.bool.isRequired,
  changePasswordSuccess: React.PropTypes.bool.isRequired,
  hasExpired: React.PropTypes.bool.isRequired,
  closeModal: React.PropTypes.func.isRequired,
  handleSubmitChangePassword: React.PropTypes.func,
  changPasswordOKClick: React.PropTypes.func,
};

export default ChangePasswordModal;



