import _ from 'lodash';
import * as uiActionType from '../constants/types/ui';
import * as uiAction from '../actions/ui';
import * as channel from '../constants/enums/windowsChannel';
import { isChildWindow as getIfChildWindow } from '../utils/helper';
import { webRequest, webResponse, webError, errorCode } from '../constants/web';
import * as chatListTypes from '../constants/types/chatList';
import * as chatAction from '../constants/enums/chatAction';
import * as configAction from '../constants/types/config';
import * as loginConstants from '../constants/login';
import * as constants from '../constants/agent';
import { ipcRenderer } from '../services/electronAPI';
import * as channelsType from '../constants/enums/channels';
import * as layoutType from '../constants/layout';
import * as page from '../constants/enums/page';

let currentStore = null;
let childWindow = null;
let timer = null;
const dataType = 'JSONFORMAT';

const formatParams = (type, data = '') => (JSON.stringify({
  format: dataType,
  type,
  data,
}));

const receiveMessage = (event) => {
  if (!event.data) return;
  if (event.data && event.data.type) {
    if (event.data.type === 'webpackErrors') {
      console.log('There were some errors', event.data.data);
    }
    if (event.data.type === 'webpackOk') {
      console.log('webpackOk');
    }
    return;
  }
  if (!event.data.indexOf || event.data.indexOf(dataType) < 0) return;
  const transferData = JSON.parse(event.data);
  if (_.isUndefined(transferData.type)) return;
  /* eslint-disable default-case*/
  switch (transferData.type) {
    case channel.checkConnection:
      event.source.postMessage(formatParams(channel.connectionSuccess), event.origin);
      break;
    case channel.connectionSuccess:
      if (timer !== null) {
        clearInterval(timer);
        timer = null;
      }
      event.source.postMessage(formatParams(channel.sendAgentInfoToChildWindow,
       currentStore.getState()), event.origin);
      break;
    case channel.sendAgentInfoToChildWindow:
      window.document.title = ` Comm100 Live Chat Agent Console - ${loginConstants.liveChatVisitorMonitorVersion} - Agent ${transferData.data.agent.name}`;
      currentStore.dispatch(uiAction.updateAgentInfoForChildWindow(transferData.data));
      break;
    case channel.updateChildVisitors:
      currentStore.dispatch(transferData.data);
      break;
    case channel.postAction:
      if (chatListTypes.setSelected === transferData.data.type) {
        ipcRenderer.send(channelsType.activeDesktopApp);
      }
      currentStore.dispatch(transferData.data);
      break;
    case channel.embetChatWindowInParent:
      childWindow = null;
      currentStore.dispatch(uiAction.embetChatWindowInParent());
      break;
  }
};
window.addEventListener('message', receiveMessage, false);

const checkConnection = (win) => {
  timer = setInterval(() => {
    win.postMessage(formatParams(channel.checkConnection));
  }, 1000);
};
const ifNeedToTransfer = (action) => {
  const transferType = [
    chatListTypes.setSelected,
    configAction.updateCustomFilter,
    configAction.changeVisitorListColumnSize,
    configAction.changeVisitorListColumnOrder,
    uiActionType.closeError,
    uiActionType.changeVisitorListOrderBy,
    uiActionType.selectVisitorListRow,
  ];
  const abortActions = [
    chatListTypes.setSelected,
  ];
  const isWebRequest = action.type.indexOf(webRequest) >= 0;
  const isAbortAction = abortActions.indexOf(action.type) >= 0;
  const isNeedToSyncAction = transferType.indexOf(action.type) >= 0;
  return [isWebRequest || isNeedToSyncAction, isWebRequest || isAbortAction];
};

export const openNewWindowForVisitorList = () => {
  if (childWindow !== null) {
    childWindow.close();
    childWindow = null;
  }
  const url = window.location.href.replace('index.html', 'visitor-popup.html');
  childWindow = window.open(url, 'visitorpopupwindow', `width=${window.outerWidth},height=${window.outerHeight}`);
};
export const embedMainWindow = () => {
  window.opener.postMessage(formatParams(channel.embetChatWindowInParent));
};
export const initData = (store) => {
  currentStore = store;
};
export default store => next => (action) => {
  const oldState = store.getState();
  const isChildWindow = getIfChildWindow();

  if (isChildWindow) {
    if (action.type === uiActionType.openNewWindow) {
      embedMainWindow();
      window.close();
      return oldState;
    }
    const [needTransfer, isWebRequst] = ifNeedToTransfer(action);
    if (needTransfer) {
      window.opener.postMessage(formatParams(channel.postAction,
        action));
      if (isWebRequst) return oldState;
    }
  }
  if (!isChildWindow && action.type === uiActionType.openNewWindow) {
    openNewWindowForVisitorList();
    checkConnection(childWindow);
  }
  if (!isChildWindow && childWindow !== null) {
    if (action.type === `${webResponse}/${chatAction.agentLogout}` ||
    (action.type.indexOf(webError) === 0 &&
     action.payload.errorCode === errorCode.sessionTimeout)) {
      childWindow.close();
    } else if (action.type === `${webResponse}/${chatAction.agentGetVisitors}`) {
      childWindow.postMessage(formatParams(channel.updateChildVisitors,
        action));
    } else if (action.type.indexOf(webResponse) >= 0) {
      childWindow.postMessage(formatParams(channel.updateChildVisitors,
        action));
    } else if (action.type === constants.updatePreference) {
      childWindow.postMessage(formatParams(channel.updateChildVisitors,
        action));
    } else if (action.type === layoutType.switchPage &&
      action.payload.page === page.visitors) {
      ipcRenderer.send(channelsType.activeChildWindow, 'visitorpopupwindow');
      return oldState;
    }
  }
  return next(action);
};



