import * as _ from 'lodash';
import chatServerLogin from './chatServer';
import route from './route';
import getSettings from './settings';
import * as serviceWorker from '../serviceWorker';
import { deserializationOtherPreference } from '../../utils/serialization';
import * as uiActions from '../../actions/ui';
import * as chatAction from '../../constants/enums/chatAction';
import memoryStore from '../../services/memoryStore';
import { getUrl } from '../../utils/helper';
import * as config from '../../constants/config';

let cache;

export default async function login(options, dispatch) {
  let routeResp;
  if (options.forceLogin && cache) {
    routeResp = cache;
    cache = null;
  } else {
    routeResp = await route(options);
    cache = routeResp;
  }
  const { chatServer, siteId, standbyChatServer, controlPanel, liveChatFunc } = routeResp;
  dispatch(uiActions.saveSiteId(siteId));
  /** @todo seems this action is unnecessary, ln 25 redefined urls */
  const chatServerUrl = getUrl(chatServer, config.isSSL);
  const standbyChatServerUrl = getUrl(standbyChatServer, config.isSSL);
  dispatch(uiActions.saveUrls({ chatServer: chatServerUrl,
    standbyChatServer: standbyChatServerUrl,
    controlPanel,
    liveChatFunc }));
  serviceWorker.post({
    type: 'url',
    payload: {
      chatServer: chatServerUrl,
      standbyChatServer: standbyChatServerUrl,
    },
  });
  memoryStore.set('urls', { chatServer, standbyChatServer, controlPanel, liveChatFunc }, siteId);
  const verificationCode = '';
  const {
    agent, session, startCommandId, siteInfo, trialInfo, loginResponse, server, ifUseMainServer,
  } = await chatServerLogin(
      Object.assign({}, options, { chatServer, standbyChatServer, siteId, verificationCode }),
      options.autoWebLogin ? chatAction.agentAutoLogin : chatAction.agentLogin
    );
  const { settings, visitors } = await getSettings({ chatServer: server, session, siteId });
  cache = null;
  const preference = _.omit(agent, ['id', 'name', 'status', 'others']);
  preference.others = deserializationOtherPreference(agent.others);

  settings.urls = { chatServer, standbyChatServer, controlPanel, liveChatFunc };
  settings.siteInfo = siteInfo;
  settings.trialInfo = trialInfo;
  return {
    agent: {
      id: agent.id,
      name: agent.name,
      status: agent.status,
      email: agent.email,
      password: options.password,
      siteId,
      session,
      startCommandId,
    },
    visitors,
    settings,
    preference,
    loginResponse,
    ifUseMainServer,
  };
}



