import React from 'react';
import Modal from '../Modal';
import ModalStyle from '../Modal.css';
import * as loginConstants from '../../../constants/login';

const TrialExpiredModal = props => (
  <Modal
    closeModal={props.closeModal}
    className={ModalStyle.trialExpiredModal}
    actions={{
      okProps: { text: 'Buy Now', eventClick: props.handleBuyNow },
    }}
  >
    <div className={ModalStyle.expiredImageWrapper} />
    <div className={ModalStyle.contextWrapper}>
      { props.isExpired &&
        <h3>
          Your free trial has expired.
        </h3>
      }
      { !props.isExpired && props.expireDaysRemaining !== 0 &&
        <h3>
          Your free trial will expire in
          <span className={ModalStyle.expireDays}>{props.expireDaysRemaining}</span>
          days.
        </h3>
      }
      { !props.isExpired && props.expireDaysRemaining <= 0 &&
        <h3 className={ModalStyle.expireDays}>
          Your free trial will expire today.
        </h3>
      }
      <p className={ModalStyle.expiredText}>
        If you want to extend the trial period, please contact {loginConstants.sales_email_address}.
      </p>
    </div>
  </Modal>
);

export default TrialExpiredModal;

TrialExpiredModal.propTypes = {
  ifOpen: React.PropTypes.bool.isRequired,
  handleBuyNow: React.PropTypes.func.isRequired,
  closeModal: React.PropTypes.func.isRequired,
  isExpired: React.PropTypes.bool,
  expireDaysRemaining: React.PropTypes.number,
};



