import React from 'react';
import * as electronAPI from 'src/services/electronAPI';
import Modal from '../Modal';
import style from '../Modal.css';

const quitAndInstall = function () {
  window.onbeforeunload = null; // disable default leave confirm
  electronAPI.quitAndInstall();
};

const ConfirmUpgrade = (props) => {
  if (!props.ifOpen) return <div />;
  return (
    <Modal
      closeModal={props.onCancel}
      className={style.forceLoginModal}
      title="Confirm Upgrade"
      actions={{
        okProps: { text: 'Exit & Upgrade now', eventClick: quitAndInstall },
        cancelProps: { text: 'Cancel' },
      }}
    >
      <p>
        {[
          'You need to exit the app before continuing the upgrade. Any chats you have now ongoing',
          'will be interrupted when you exit. Hence, we suggest you do the upgrade when all chats',
          'have been completed.',
        ].join('')}
      </p>
    </Modal>
  );
};

export default ConfirmUpgrade;

ConfirmUpgrade.propTypes = {
  ifOpen: React.PropTypes.bool.isRequired,
  onCancel: React.PropTypes.func.isRequired,
};



