import React from 'react';

let DevToolsDock; // eslint-disable-line

if (process.env.NODE_ENV !== 'production') {
  // Exported from redux-devtools
  const { createDevTools } = require('redux-devtools'); // eslint-disable-line
  // Monitors are separate packages, and you can make a custom one
  const LogMonitor = require('redux-devtools-log-monitor').default; // eslint-disable-line
  const DockMonitor = require('redux-devtools-dock-monitor').default; // eslint-disable-line

  // createDevTools takes a monitor and produces a DevTools component
  DevToolsDock = createDevTools(
    // Monitors are individually adjustable with props.
    // Consult their repositories to learn about those props.
    // Here, we put LogMonitor inside a DockMonitor.
    // Note: DockMonitor is visible by default.
    <DockMonitor
      toggleVisibilityKey="ctrl-h"
      changePositionKey="ctrl-q"
      defaultIsVisible={false}
    >
      <LogMonitor theme="tomorrow" />
    </DockMonitor>
  );
}

export default DevToolsDock;



