import React from 'react';
import Modal from '../Modal';
import ModalStyle from '../Modal.css';

class ForceLoginModal extends React.Component {
  constructor(props) {
    super(props);
    // this.state = {
    //   open: props.ifOpen,
    // };
    this.closeModal = this.closeModal.bind(this);
    this.handleForceLogin = this.handleForceLogin.bind(this);
  }

  closeModal() {
    // this.setState({
    //   open: false,
    // });
    this.props.closeModal();
  }

  handleForceLogin() {
    // this.setState({
    //   open: false,
    // });
    this.props.handleForceLogin();
  }

  render() {
    return (
      <Modal
        closeModal={this.closeModal}
        className={ModalStyle.forceLoginModal}
        title="Force Login?"
        actions={{
          okProps: { text: 'OK', eventClick: this.handleForceLogin },
          cancelProps: { text: 'Cancel' },
        }}
      >
        <p>
          {[
            'You\'ve logged in from another place. Do you want to log out',
            'the previous one and log in from here?',
          ].join(' ')}
        </p>
      </Modal>
    );
  }
}

export default ForceLoginModal;

ForceLoginModal.propTypes = {
  // ifOpen: React.PropTypes.bool.isRequired,
  handleForceLogin: React.PropTypes.func.isRequired,
  closeModal: React.PropTypes.func.isRequired,
};



