// 访问route server得到siteId和chat server url
import * as _ from 'lodash';
import * as axios from 'axios';
import querystring from 'querystring';
import LoginError from './LoginError';
import * as str from '../../utils/str';
import { routeRequest, routeResponse } from '../../constants/schema';
import { convert } from '../../utils/schema';
import { errorCode, extractError } from '../../constants/web';
import * as localStorage from '../../utils/localStorage';

let cache;

async function auth(server, backupServer, email, password, verificationCode, loginSession) {
  const encryptedPassword = str.passwordConvert(password);
  const query = querystring.encode(convert({
    email,
    encryptedPassword,
    verificationCode,
    loginSession,
  }, routeRequest));
  const doGet = async s => (await axios.get(`${s}/urlhandler.ashx?${query}`)).data;
  try {
    return await doGet(server);
  } catch (error) {
    try {
      return await doGet(backupServer);
    } catch (ex) {
      // return the cache data
      const localStorageKey = email + encryptedPassword;
      const cacheData = localStorage.getObject(localStorageKey);
      if (cacheData) return cacheData;
      throw error;
    }
  }
}

async function authBySiteId(server, backupServer, siteId) {
  const query = `siteId=${siteId}`;
  const doGet = async s => (await axios.get(`${s}/urlhandler.ashx?${query}`)).data;
  try {
    return await doGet(server);
  } catch (error) {
    try {
      return await doGet(backupServer);
    } catch (ex) {
      // return the cache data
      const localStorageKey = query;
      const cacheData = localStorage.getObject(localStorageKey);
      if (cacheData) return cacheData;
      throw error;
    }
  }
}

export default async function route({
  email,
  password,
  routeServer,
  backupRouteServer,
  siteId,
  verificationCode,
  session,
  autoWebLogin = false,
}) {
  if (siteId && siteId > 0 && cache) {
    const site = _.find(cache, o => o.siteId === siteId);
    if (site) {
      cache = null;
      return site;
    }
  }
  let routeResponseData;
  if (autoWebLogin && siteId) {
    routeResponseData = await authBySiteId(routeServer, backupRouteServer, siteId);
    if (routeResponseData.a === siteId) { // success
      const siteInfo = Object.assign({}, routeResponseData);
      routeResponseData = {
        c: 0,
        d: '',
        e: [siteInfo],
        f: null,
      };
    }
  } else {
    routeResponseData =
      await auth(routeServer, backupRouteServer, email, password, verificationCode, session);
  }
  const resp = convert(routeResponseData, routeResponse);
  resp.errorCode = extractError(resp.errorCode);
  // TODO (Roy): check mutiple sites and handle require verfication code
  // if resp.sites.length > 1 , dispatch mutiple sites
  if (resp.errorCode === errorCode.noerror) {
    let localStorageKey;
    if (autoWebLogin) {
      localStorageKey = `siteId=${siteId}`;
    } else {
      const encryptedPassword = str.passwordConvert(password);
      localStorageKey = email + encryptedPassword;
    }
    localStorage.setObject(localStorageKey, routeResponseData);
    if (resp.sites.length > 1) {
      if (siteId !== 0) {
        const site = _.find(resp.sites, o => o.siteId === siteId);
        if (site) {
          cache = null;
          return site;
        }
      }
      cache = resp.sites;
      throw new LoginError('routeServerError', errorCode.multipleSites, resp.sites);
    }
    cache = null;
    return resp.sites[0];
  }
  throw new LoginError('routeServerError', resp.errorCode, resp);
}



