import Immutable from 'immutable';
import * as _ from 'lodash';
import * as visitorDetails from 'src/constants/types/visitorDetails';
import * as perf from 'src/utils/perf';
import * as visitorStatus from '../constants/enums/visitorStatus';
import * as selectors from './selectors';
import * as columnType from '../constants/enums/columnType';
import * as web from '../constants/web';
import * as actions from '../constants/enums/chatAction';
import * as visitorAction from '../constants/types/visitors';
import { updateIn } from '../utils/seq';
import * as chatTypes from '../constants/types/chat';
import { switchServer } from '../constants/agent';
import * as uiTypes from '../constants/types/ui';

/* Reducer Definition Area */

// REVIEW (Roy): 使用immutable.js
/* Default state for initialization */
const defaultState = {
  version: -1,  // 服务器差量传输用
  list: [],
  visitors: Immutable.Map({
  }), // id map
};

const updateVisitors = (state, action, totalState) => {
  const { visitors: { full, lessDiff, moreDiff, noUpdates }, visitorId } = action.payload;

  perf.markBegin('Reducers: Update Visitors');

  const concatList = full.concat(lessDiff).concat(moreDiff);

  perf.markEnd('Reducers: Update Visitors');

  const newVisitors = _.reduce(
    concatList,
    (prev, data) => {
      const exists = prev.get(data.id);
      if (!exists) return prev.set(data.id, Immutable.Map(data));
      return prev.set(data.id, exists.merge(Immutable.Map(data)));
    },
    state.visitors
  );

  if (full.length === 0 && lessDiff.length === 0 && moreDiff.length === 0 &&
  noUpdates.length === state.list.length) {
    return {
      version: visitorId,
      visitors: newVisitors,
      list: state.list,
    };
  }

  const sortByColumn = selectors.getVisitorUiSortColumn(totalState);

  const sortByColumnType = sortByColumn.enumColumn;

  if (full.length === 0) {
    if ((lessDiff.length + moreDiff.length + noUpdates.length === state.list.length) && (
    sortByColumnType === columnType.timezone ||
    sortByColumnType === columnType.city ||
    sortByColumnType === columnType.state ||
    sortByColumnType === columnType.countryRegion ||
    sortByColumnType === columnType.visitTime ||
    sortByColumnType === columnType.visits ||
    sortByColumnType === columnType.referrer ||
    sortByColumnType === columnType.searchEngine ||
    sortByColumnType === columnType.keywords ||
    sortByColumnType === columnType.landingPage ||
    sortByColumnType === columnType.totalTime)) {
      return { version: visitorId, visitors: newVisitors, list: state.list };
    }

    if (moreDiff.length === 0) {
      if ((lessDiff.length + noUpdates.length === state.list.length) && (
      sortByColumnType === columnType.customVariable ||
      sortByColumnType === columnType.contactUserIcon ||
      sortByColumnType === columnType.name ||
      sortByColumnType === columnType.email ||
      sortByColumnType === columnType.ticketId ||
      sortByColumnType === columnType.department ||
      sortByColumnType === columnType.productService ||
      sortByColumnType === columnType.chats ||
      sortByColumnType === columnType.autoInvitation ||
      sortByColumnType === columnType.campaign ||
      sortByColumnType === columnType.agents ||
      sortByColumnType === columnType.visitorInfo ||
      sortByColumnType === columnType.visitorSegmentation)) {
        return { version: visitorId, visitors: newVisitors, list: state.list };
      }
    }
  }

  const list = _.map(concatList, x => x.id).concat(noUpdates);

  return {
    version: visitorId,
    visitors: newVisitors,
    list,
  };
};

const updateVisitorInfo = (state, value, visitorId, ...path) => {
  /*
  const visitor = state.visitors.get(visitorId);
  if (visitor != null) {
    return Object.assign({}, state, {
      visitors: state.visitors.setIn(_.concat(visitorId, path), value),
    });
  }
  */
  const mapIter = state.visitors.entries();
  let prev = state.visitors;
  for (const obj of mapIter) {
    const key = obj[0];
    const data = obj[1];
    if (data.get('id') === visitorId) {
      prev = prev.set(key, data.setIn(path, value));
    }
  }
  return Object.assign({}, state, {
    visitors: prev,
  });
};

const recoverVisitorIds = (state, action, totalState) => {
  const recoverVistiors = action.payload.recoverVistiorIds
    .map(ids =>
      selectors.getVisitor(totalState, ids.oldVisitorId)
        .update('id', () => ids.visitorId)
        .update('chatId', () => ids.chatId))
    .reduce((vs, v) => vs.set(v.get('id'), v), Immutable.Map({}));

  return Object.assign({}, state, {
    visitors: state.visitors.merge(recoverVistiors),
  });
};

const updateVisitorSFContactFields = (state, action) => {
  const vId = action.payload.visitorId;
  const sfContact = action.payload.sfObject ? action.payload.sfObject.contact : null;
  if (sfContact) {
    return updateVisitorInfo(state, sfContact, vId, 'salesforceContactFields');
  }
  return state;
};
const updateVisitorSFLeadFields = (state, action) => {
  const vId = action.payload.visitorId;
  const sfLead = action.payload.sfObject ? action.payload.sfObject.Lead : null;
  if (sfLead) {
    return updateVisitorInfo(state, sfLead, vId, 'salesforceLeadFields');
  }
  return state;
};


// 设置获取历史数据时的错误信息
const updateChatHistoryError = (state, vId, errorInfo) =>
   updateVisitorInfo(state, errorInfo, vId, 'chatHistoryError');
const updateOfflineMessageHistoryError = (state, vId, errorInfo) =>
  updateVisitorInfo(state, errorInfo, vId, 'offlineMessageHistoryError');
const updateChatHistoryDetailsError = (state, vId, errorInfo) =>
  updateVisitorInfo(state, errorInfo, vId, 'chatHistoryDetailsError');
const updateOfflineMessageDetailsError = (state, vId, errorInfo) =>
  updateVisitorInfo(state, errorInfo, vId, 'offlineMessageDetailsError');

/* Mapping from action.type to corresponding state manipulation function */
const stateMapping = {
  [uiTypes.updateAgentInfoForChildWindow](state, action) {
    let visitors = Immutable.Map({});
    action.payload.visitor.list.forEach((v) => {
      visitors = visitors.set(v, Immutable.Map(action.payload.visitor.visitors[v]));
    });
    return Object.assign({}, action.payload.visitor, {
      visitors,
    });
  },
  [chatTypes.chatEnded](state, action, totalState) {
    const chat = selectors.getChat(totalState, action.meta);
    if (chat && selectors.ifRequireWrapup(totalState, action.meta)) {
      const visitorId = chat.get('visitorId');
      const newVisitor = Immutable.Map(state.visitors.get(visitorId));
      return {
        version: state.version,
        visitors: state.visitors.set(`v${visitorId}c${action.meta}`, newVisitor),
        list: state.list,
      };
    }
    return state;
  },
  [`${web.webResponse}/${actions.agentGetVisitors}`]: updateVisitors,
  [`${web.webResponse}/${actions.agentGetNavigations}`](state, action) {
    const payload = action.payload;
    return updateVisitorInfo(state, payload.navigation, payload.visitorId, 'navigation');
  },

  [`${web.webResponse}/${actions.agentGetChatHistory}`](state, action) {
    const payload = action.payload;
    const tempState = updateVisitorInfo(state, '', payload.visitorId, 'chatHistoryError');
    return updateVisitorInfo(tempState, payload.chats, payload.visitorId, 'history', 'chats');
  },
  [`${web.webResponse}/${actions.agentGetOfflineMessageHistory}`](state, action) {
    const payload = action.payload;
    const tempState = updateVisitorInfo(state, '', payload.visitorId, 'offlineMessageHistoryError');
    return updateVisitorInfo(tempState, payload.offlineMessages,
      payload.visitorId, 'history', 'offlineMessages');
  },
  [`${web.webResponse}/${actions.agentGetSessionHistory}`](state, action) {
    const payload = action.payload;
    return updateVisitorInfo(state, payload.sessions, payload.visitorId, 'history', 'sessions');
  },
  [`${web.webResponse}/${actions.agentGetChatDetails}`](state, action) {
    const payload = action.payload;
    const tempState = updateVisitorInfo(state, undefined, payload.visitorId, 'chatHistoryDetailsError');
    const key = `c${payload.chat.id}`;
    return updateVisitorInfo(tempState, payload.chat, payload.visitorId,
      'history', 'details', key);
  },
  [`${web.webResponse}/${actions.agentGetOfflineMessageDetails}`](state, action) {
    const payload = action.payload;
    const tempState = updateVisitorInfo(state, undefined, payload.visitorId, 'offlineMessageDetailsError');
    const key = `o${payload.offlineMessage.id}`;
    return updateVisitorInfo(tempState, payload.offlineMessage, payload.visitorId,
      'history', 'details', key);
  },
  [`${web.webResponse}/${actions.agentGetWrapupHistory}`](state, action) {
    const payload = action.payload;
    return updateVisitorInfo(state, payload.wrapups, payload.visitorId, 'wrapups');
  },
  [`${web.webResponse}/${actions.agentGetVisitorContactWrapupDetails}`](state, action, totalState) {
    const payload = action.payload;
    const visitor = selectors.getVisitor(totalState, payload.visitorId) ||
       state.visitors.find(v => v.get('id') === payload.visitorId);
    if (visitor != null) {
      const wrapups = visitor.get('wrapups');
      const index = _.findIndex(wrapups, wrapup => wrapup.id === payload.wrapup.id);
      payload.wrapup.sessionId = wrapups[index].sessionId;
      payload.wrapup.chatCategory = wrapups[index].chatCategory;
      const newWrapups = _.fill(wrapups, payload.wrapup, index, index + 1);
      return updateVisitorInfo(state, newWrapups, payload.visitorId, 'wrapups');
    }
    return state;
  },
  [`${web.webError}/${actions.agentGetVisitorContactWrapupDetails}`](state, action, totalState) {
    const payload = action.payload;
    if (!payload.sourceAction) return state;
    const visitorId = Number(payload.sourceAction.sender);
    const id = payload.sourceAction.visitorId;
    const visitor = selectors.getVisitor(totalState, visitorId) ||
       state.visitors.find(v => v.get('id') === visitorId);
    if (visitor != null) {
      const wrapups = visitor.get('wrapups');
      const index = _.findIndex(wrapups, wrapup => wrapup.id === id);
      const oldwrapup = wrapups[index];
      const newWrapup = Object.assign({}, oldwrapup, {
        wrapupDetailsErrorString: payload.errorString,
      });
      const newWrapups = _.fill(wrapups, newWrapup, index, index + 1);
      return updateVisitorInfo(state, newWrapups, visitorId, 'wrapups');
    }
    return state;
  },
  [chatTypes.newUnfinishedWrapup](state, action) {
    const { visitor } = action.payload;
    const key = `v${visitor.id}c${visitor.chatId}`;
    const newVisitors = state.visitors.set(key, Immutable.Map(visitor));
    return {
      version: state.version,
      list: state.list,
      visitors: newVisitors,
    };
  },
  [chatTypes.chatVisitorLeaved](state, action) {
    return updateIn(state, 'visitors', state.visitors.setIn([action.meta, 'status'], visitorStatus.outOfSite));
  },
  [visitorDetails.navigationUpdate](state, action) {
    return updateIn(state, 'visitors', state.visitors.mergeIn([action.payload, 'navigation'], []));
  },
  [visitorDetails.updateIsViewAllChats](state, action) {
    return updateIn(state, 'visitors', state.visitors.setIn([action.meta, 'isViewAllChats'], action.payload));
  },
  [visitorDetails.updateIsViewAllMessages](state, action) {
    return updateIn(state, 'visitors', state.visitors.setIn([action.meta, 'isViewAllMessages'], action.payload));
  },
  [visitorAction.removeVisitors](state, action) {
    const list = action.payload;
    let newVisitors = state.visitors;
    _.each(list, (id) => {
      newVisitors = newVisitors.delete(id);
    });
    return {
      version: state.version,
      list: state.list,
      visitors: newVisitors,
    };
  },
  [`${switchServer}`]: recoverVisitorIds,
  [`${web.webResponse}/${actions.agentCreateSFContact}`]: updateVisitorSFContactFields,
  [`${web.webResponse}/${actions.agentEditSFContact}`]: updateVisitorSFContactFields,
  [`${web.webResponse}/${actions.agentCreateSFLead}`]: updateVisitorSFLeadFields,
  [`${web.webResponse}/${actions.agentEditSFLead}`]: updateVisitorSFLeadFields,


  // visitor history info get error handles.
  [`${web.webError}/${actions.agentGetChatHistory}`](state, action) {
    return updateChatHistoryError(state,
    action.payload.visitorId,
    action.payload.errorString);
  },
  [`${web.webError}/${actions.agentGetOfflineMessageHistory}`](state, action) {
    return updateOfflineMessageHistoryError(state,
    action.payload.visitorId,
    action.payload.errorString);
  },
  [`${web.webError}/${actions.agentGetChatDetails}`](state, action) {
    return updateChatHistoryDetailsError(state,
    action.payload.visitorId,
    action.payload);
  },
  [`${web.webError}/${actions.agentGetOfflineMessageDetails}`](state, action) {
    return updateOfflineMessageDetailsError(state,
    action.payload.visitorId,
    action.payload);
  },
};

export default (state = defaultState, action, totalState) => {
  if (stateMapping[action.type] === undefined) return state;
  return stateMapping[action.type](state, action, totalState);
};




