import { maxonFeedbackPeriod } from '../../constants/web';
import heartbeatSubscriber from './heartbeatSubscriber';
import { feedback } from '../../services/moderator';
import * as selectors from '../../reducers/selectors';

export default heartbeatSubscriber({
  onHeartbeat: (store) => {
    const state = store.getState();
    if (selectors.ifEnableMaxOn(state)) {
      feedback(
        selectors.getStandbyChatServer(state),
        selectors.getSiteId(state),
        selectors.getAgentId(state),
        selectors.getIsMainServerAvaiable(state));
    }
  },
  getPeriod: () => maxonFeedbackPeriod,
});



