import React from 'react';
import { connect } from 'react-redux';
// import electronContextMenu from 'electron-context-menu';

import LazilyLoad, { importLazy } from 'src/components/LazilyLoad/LazilyLoad';
import loadingComponent from 'src/components/Icons/LoadingComponent';
import Reload from 'src/components/Widget/Reload';
import ModalReload from 'src/components/Modal/ModalReload';

import Login from './Login';
import Header from './Header';
import PageTab from './PageTab';
import CommonStyle from '../components/CommonResources/common.css';
import * as chatListActionCreater from '../actions/chatList';

import * as enumPage from '../constants/enums/page';
import * as selectors from '../reducers/selectors';
import * as visitorDetailsActions from '../actions/visitorDetails';
import * as AgentsCreater from '../actions/agents';
import { shouldComponentUpdateGen } from '../utils/helper';
import ModalLoading from '../components/Modal/ModalLoading';
import LeaveConfirm from './LeaveConfirm';
import ConfirmBeforeClose from './ConfirmBeforeClose';
import CloseWindow from './CloseWindow';
import DashboardPage from './DashboardPage';
import * as uiActions from '../actions/ui';
import ErrorMessage from './ErrorMessage';
import pageStyles from '../components/Widget/Page.css';


/**
 * @todo Considering using react-resizable-box instead
 */
require.include('../components/Resizer/ResizeContainer.jsx');

function getChatsTabContent(selectedChatId,
                            chatItemWidth,
                            onChatItemStopResize,
                            chatDetailWidth,
                            onChatDetailStopResize) {
  return (
    <LazilyLoad
      modules={{ SplitterChat: () => importLazy(new Promise((resolve, reject) => {
        require.ensure(['./SplitterChat'], (require) => {
          resolve(require('./SplitterChat'));
        }, 'chat').catch(reject);
      })) }}
      loading={loadingComponent}
      retry={Reload}
    >
      {
        ({ SplitterChat }) => (
          <SplitterChat
            selectedChatId={selectedChatId}
            chatItemWidth={chatItemWidth}
            onChatItemStopResize={onChatItemStopResize}
            chatDetailWidth={chatDetailWidth}
            onChatDetailStopResize={onChatDetailStopResize}
          />
        )
      }
    </LazilyLoad>
  );
}

function getAgentsTabContent(agentsWidth,
                            agentNumber,
                            selectedAgentChatId,
                            onAgentsStopResize) {
  return (
    <LazilyLoad
      modules={{ SplitterAgentChat: () => importLazy(new Promise((resolve, reject) => {
        require.ensure(['./SplitterAgentChat'], (require) => {
          resolve(require('./SplitterAgentChat'));
        }, 'agent-chat').catch(reject);
      })) }}
      loading={loadingComponent}
      retry={Reload}
    >
      {
        ({ SplitterAgentChat }) => (
          <SplitterAgentChat
            agentsWidth={agentsWidth}
            agentNumber={agentNumber}
            selectedAgentChatId={selectedAgentChatId}
            onAgentsStopResize={onAgentsStopResize}
          />
        )
      }
    </LazilyLoad>
  );
}

const shouldComponentUpdateHelper = shouldComponentUpdateGen(
  'App',
  [
    'page',
    'chatItemWidth',
    'agentsWidth',
    'chatDetailWidth',
    'selectedAgentChatId',
    'modalLoading',
    'visitorListShowDetails',
    'hasVisitors',
    'agentNumber',
    'isOpenPreference',
    'ifShowErrorOnHeader',
    'ifOpenDashboardWindow',
    'ifPopupWindowForVisitors',
  ]
);
class App extends React.Component {
  componentDidMount() {
    // const { app, BrowserWindow } = require('electron');
    // if (!process.env.IS_WEB_APP) {
    //   electronContextMenu({
    //     labels: {
    //       cut: 'Cut',
    //       copy: 'Copy',
    //       paste: 'Paste',
    //       save: 'Save Image',
    //       copyLink: 'Copy Link',
    //       // inspect: 'Inspect',
    //     },
    //     // append: (params, browserWindow) => [{
    //     //   role: 'selectall',
    //     //   label: 'Select All',
    //     //   // only show it when right-clicking images
    //     //   // enabled: params.selectionText,
    //     // }],
    //     // showInspectElement: false,
    //   });
    // }
  }
  shouldComponentUpdate(nextProps) {
    return shouldComponentUpdateHelper(this.props, nextProps) || (
      !!this.props.selectedChatId !== !!nextProps.selectedChatId
    );
  }

  render() {
    const {
      page,
      agentNumber,
      selectedChatId,
      selectedAgentChatId,
      chatItemWidth,
      onChatItemStopResize,
      agentsWidth,
      onAgentsStopResize,
      chatDetailWidth,
      onChatDetailStopResize,
      visitorListShowDetails,
      hasVisitors,
      isOpenPreference,
      ifShowErrorOnHeader,
      ifOpenDashboardWindow,
      ifPopupWindowForVisitors,
      onHideVisitorListDetailsClick,
    } = this.props;
    if (page === enumPage.login) {
      return (
        <div className={pageStyles.page}>


          <Login />
          {ifShowErrorOnHeader && (<ErrorMessage />)}
        </div>
      );
    }
    return (
      <div>

        <Header>
          <div className={CommonStyle.layoutContainer}>
            <PageTab />
            {page === enumPage.chats && (
              getChatsTabContent(
                selectedChatId,
                chatItemWidth,
                onChatItemStopResize,
                chatDetailWidth,
                onChatDetailStopResize
              )
            )}

            {page === enumPage.agents && (
              getAgentsTabContent(agentsWidth,
                                  agentNumber,
                                  selectedAgentChatId,
                                  onAgentsStopResize)
            )}

            {!ifPopupWindowForVisitors && page === enumPage.visitors &&
              <LazilyLoad
                modules={{
                  SplitterVisitorList: () => importLazy(new Promise((resolve, reject) => {
                    require.ensure(['./SplitterVisitorList'], (require) => {
                      resolve(require('./SplitterVisitorList'));
                    }, 'visitor').catch(reject);
                  })),
                }}
                loading={loadingComponent}
                retry={Reload}
              >
                {
                  ({ SplitterVisitorList }) => (
                    <SplitterVisitorList
                      visitorListShowDetails={visitorListShowDetails}
                      hasVisitors={hasVisitors}
                      onHideVisitorListDetailsClick={onHideVisitorListDetailsClick}
                    />
                  )
                }
              </LazilyLoad>
            }
          </div>
          {
            isOpenPreference && (
              <LazilyLoad
                modules={{
                  Settings: () => importLazy(new Promise((resolve, reject) => {
                    require.ensure(['./Settings'], (require) => {
                      resolve(require('./Settings'));
                    }, 'settings').catch(reject);
                  })) }}
                loading={<ModalLoading ifOpen />}
                retry={ModalReload}
              >
                {
                  ({ Settings }) => (
                    <Settings />
                  )
                }
              </LazilyLoad>
            )
          }
        </Header>


        {this.props.modalLoading &&
          <ModalLoading ifOpen={this.props.modalLoading} />
        }
        <LeaveConfirm />
        <CloseWindow />
        <ConfirmBeforeClose />
        {ifOpenDashboardWindow && (<DashboardPage />)}
        {ifShowErrorOnHeader && (<ErrorMessage />)}

        {window.beta && <LazilyLoad
          modules={{
            SplitterBetaFeature: () => importLazy(new Promise((resolve, reject) => {
              require.ensure(['./SplitterBetaFeature'], (require) => {
                resolve(require('./SplitterBetaFeature'));
              }, 'beta').catch(reject);
            })),
          }}
        >
          {
            ({ SplitterBetaFeature }) => (
              <SplitterBetaFeature siteId={this.props.siteId} agentId={this.props.agentId} />
            )
          }
        </LazilyLoad>}
      </div>);
  }
}

App.propTypes = {
  siteId: React.PropTypes.number.isRequired,
  agentId: React.PropTypes.number.isRequired,
  page: React.PropTypes.string.isRequired,
  agentNumber: React.PropTypes.number.isRequired,
  selectedChatId: React.PropTypes.number,
  selectedAgentChatId: React.PropTypes.oneOfType([
    React.PropTypes.number,
    React.PropTypes.string,
  ]),
  chatItemWidth: React.PropTypes.number.isRequired,
  agentsWidth: React.PropTypes.number.isRequired,
  onAgentsStopResize: React.PropTypes.func.isRequired,
  onChatItemStopResize: React.PropTypes.func.isRequired,
  chatDetailWidth: React.PropTypes.number.isRequired,
  onChatDetailStopResize: React.PropTypes.func.isRequired,
  modalLoading: React.PropTypes.bool,
  visitorListShowDetails: React.PropTypes.bool.isRequired,
  hasVisitors: React.PropTypes.bool.isRequired,
  isOpenPreference: React.PropTypes.bool,
  onHideVisitorListDetailsClick: React.PropTypes.func.isRequired,
  ifShowErrorOnHeader: React.PropTypes.bool.isRequired,
  ifOpenDashboardWindow: React.PropTypes.bool.isRequired,
  ifPopupWindowForVisitors: React.PropTypes.bool.isRequired,
};

const mapStateToProps = state => ({
  siteId: selectors.getSiteId(state),
  agentId: selectors.getAgentId(state),
  hasVisitors: (selectors.getVisitorList(state) || []).length > 0,
  page: selectors.getPage(state),
  agentNumber: selectors.getAgents(state).length,
  visitorListShowDetails: selectors.getIfShowDetailsInVisitorList(state),
  selectedChatId: selectors.getSelectedChatId(state),
  selectedAgentChatId: selectors.getSelectedIdForAgents(state),
  chatItemWidth: selectors.getListVisitorsWidth(),
  agentsWidth: selectors.getAgentsListWidth(),
  chatDetailWidth: selectors.getChatDetailsWidth(state),
  modalLoading: state.ui.modalLoading,
  isOpenPreference: state.ui.preference.ifOpen,
  ifShowErrorOnHeader: selectors.getShowErrorOnHeaderFlag(state),
  ifOpenDashboardWindow: selectors.getDashboardWindowFlag(state),
  ifPopupWindowForVisitors: selectors.getIfPopupWindowForVisitors(state),
});

const mapDispatchToProps = dispatch => ({
  onChatItemStopResize(info) {
    dispatch(chatListActionCreater.chatItemWidthChanged(info));
  },
  onAgentsStopResize(info) {
    dispatch(AgentsCreater.changedAgentsListWidth(info));
  },
  onChatDetailStopResize(info) {
    dispatch(visitorDetailsActions.onDetailsWidthChanged(info));
  },
  onHideVisitorListDetailsClick() {
    dispatch(uiActions.toggleVisitorDetails());
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(App);



