import * as _ from 'lodash';
import * as visitorStatus from '../constants/enums/visitorStatus';
import * as language from '../constants/language';
import { agentStatus } from '../constants/enums';

export const isChatting = (visitor) => {
  const status = visitor.get('status');
  return (status === visitorStatus.chatting ||
    status === visitorStatus.transferring);
};

export const isChatEnded = (visitor) => {
  const status = visitor.get('status');
  return (
    status !== visitorStatus.chatting &&
    status !== visitorStatus.waitingForChat &&
    status !== visitorStatus.transferring &&
    status !== visitorStatus.manuallyInvitedAndWaitingVisitor
  );
};

export const isAgentInChatActiveList = (visitor, agentId) =>
  _.indexOf(visitor.get('chatActiveAgents'), agentId) >= 0;

export const isAgentInMonitorList = (visitor, agentId) =>
  _.indexOf(visitor.get('chatMonitorAgents'), agentId) >= 0;

export const isAgentInInvolvedList = (visitor, agentId) =>
  _.indexOf(visitor.get('chatInvolvedAgents'), agentId) >= 0;

export const isVisitorChattedWithAgent = (visitor, agentId) => {
  const status = visitor.get('status');
  return (status === visitorStatus.chatEnded || isChatting(visitor)) &&
    isAgentInInvolvedList(visitor, agentId);
};

/**
 * Whether sending message to given visitor is allowed. This is used to control whether canned
 * message can be sent out and whether editor should be enabled.
 *
 * @param {Immutable.Map} visitor - Specific visitor.
 * @param {number} agentId - Current Agent Id.
 * @return {boolean} Whether is allowed.
 */
export const isSendingMessageAllowed = (visitor, agentId) =>
  (isChatting(visitor) && isAgentInChatActiveList(visitor, agentId)) ||
  visitor.get('status') === visitorStatus.manuallyInvitedAndWaitingVisitor;

/** @todo code review of following func is required. */

export const isAttachTicketAllowed = (visitor, chatExists, agentId) => {
  if (!chatExists) return false;
  const status = visitor.get('status');
  const isChattingOrVoiceChatting = status === visitorStatus.chatting;
  const inActiveList = isAgentInChatActiveList(visitor, agentId);
  if (isChattingOrVoiceChatting && inActiveList) return true;
  if ((isChattingOrVoiceChatting || status === visitorStatus.chatEnded) &&
    isAgentInInvolvedList(visitor, agentId)) return true;
  return false;
};

export const isWaiting = (visitor, agentId, departments) => {
  const status = visitor.get('status');
  if (status === visitorStatus.transferring) {
    if (isAgentInChatActiveList(visitor, agentId)) return false;
    const departmentId = visitor.get('departmentId');
    const department = _.find(departments, dep => dep.id === departmentId);
    if (department && department.agentIds.indexOf(agentId) < 0) {
      return false;
    }
    return true;
  } else if (status === visitorStatus.waitingForChat) {
    return true;
  }
  return false;
};

export const getAgentNames = (visitor, agentId, agents) => {
  if (isAgentInMonitorList(visitor, agentId) &&
    (isChatting || visitor.get('status') === visitorStatus.chatEnded)) {
    const agentIds = visitor.get('chatInvolvedAgents');
    /** @todo can be improved by searching from 'agentIds' instead of 'agents'? */
    return agents.filter(agent => agentIds.indexOf(agent.id) >= 0)
      .map(agent => agent.name)
      .join(', ');
  }
  return '';
};

export const isChattingWithAgent = (visitor, agentId) =>
  (visitor.get('status') === visitorStatus.chatEnded || isChatting(visitor)) &&
  isAgentInChatActiveList(visitor, agentId);

export const isChattedWithAgent = (visitor, agentId) =>
  (visitor.get('status') === visitorStatus.chatEnded ||
    isChatting(visitor)) &&
  isAgentInInvolvedList(visitor, agentId);

export const isMonitorWithAgent = (visitor, agentId) =>
  isAgentInMonitorList(visitor, agentId);

export const isMonitorChatEndedWithAgent = (visitor, agentId) =>
  visitor.get('status') === visitorStatus.chatEnded &&
  _.isArray(visitor.get('chatMonitorAgents')) &&
  isAgentInMonitorList(visitor, agentId);

export const checkIfWaitingVisitorAcceptInvitationFromMe = (visitor, agentId) =>
  visitor.get('status') === visitorStatus.manuallyInvitedAndWaitingVisitor &&
  isAgentInChatActiveList(visitor, agentId);

export const checkIfVisitorRefuseMyInvitation = (visitor, agentId) =>
  visitor.get('status') === visitorStatus.refusedByVisitor &&
  isAgentInChatActiveList(visitor, agentId);

export const isHavePermissionDealChat = (status, chatInvolvedAgents, department, agentId) => {
  if (department !== undefined && department !== null) {
    if (_.indexOf(department.agentIds, agentId) >= 0) {
      return true;
    }
    return false;
  }

  if (status === visitorStatus.waitingForChat || status === visitorStatus.transferring) {
    if (chatInvolvedAgents.length > 0) {
      if (_.indexOf(chatInvolvedAgents, agentId) >= 0) {
        return true;
      }
      return false;
    }
    return true;
  }

  if (status === visitorStatus.chatting) {
    return true;
  }
  return false;
};

export const showGoToChatButton = (visitor, agentId) => {
  const isVisitorChatting = isChatting(visitor);
  if (!isVisitorChatting) return false;
  const isAgentActive = isAgentInChatActiveList(visitor, agentId);
  const isAgentInvolvedInMonitor = isAgentInMonitorList(visitor, agentId);
  return (isAgentActive || isAgentInvolvedInMonitor);
};

export const showAcceptButton = (visitor, department, agentId) => {
  const status = visitor.get('status');
  const chatInvolvedAgents = visitor.get('chatInvolvedAgents');
  const ifHavePermissionDealChat = isHavePermissionDealChat(status,
    chatInvolvedAgents, department, agentId);

  const chatActiveAgents = visitor.get('chatActiveAgents');
  const isAgentInvolvedInMonitor = isAgentInMonitorList(visitor, agentId);
  const isAgentActive = _.indexOf(chatActiveAgents, agentId) >= 0;
  return ifHavePermissionDealChat && !isAgentActive &&
    (status === visitorStatus.waitingForChat ||
      (status === visitorStatus.transferring && !isAgentInvolvedInMonitor)
    );
};

const patterns = {};
const replace = (text, from, to) => {
  if (!patterns[from]) {
    patterns[from] = new RegExp(from, 'g');
  }
  const pattern = patterns[from];
  return text.replace(pattern, to);
};
const visitorReplaceConfig = {
  [language.macroVisitorName]: 'latestName',
  [language.macroVisitorEmail]: 'latestEmail',
  [language.macroVisitorIp]: 'ip',
  [language.macroVisitorCountryRegion]: 'country',
  [language.macroVisitorState]: 'state',
  [language.macroVisitorCity]: 'city',
  [language.macroPreChatCompany]: 'prechatCompany',
  [language.macroPreChatPhone]: 'prechatPhone',
  [language.macroPreChatProductService]: 'prechatProductService',
};

/**
 * For historical reasons, support both: {!Agent.Name} and {!Operator.Name} here
 */
const agentPattern = '(?:Agent|Operator)';
const agentNamePattern = new RegExp(language.macroAgentName.replace('Agent', agentPattern), 'g');
const agentEmailPattern =
  new RegExp(language.macroAgentEmail.replace('Agent', agentPattern), 'g');
const customFieldPattern = new RegExp(`{!${language.macroPreChatPrefix}\\.(\\w+)}`, 'g');
const customVariablePattern = new RegExp(`{!${language.macroCustomVariablePrefix}\\.(\\w+)}`, 'g');

export const replaceVisitorInfoMacros = (text, visitor) =>
  _.reduce(
    Object.keys(visitorReplaceConfig),
    (result, macro) => replace(result, macro, visitor.get(visitorReplaceConfig[macro])),
    text
  );

/**
 * Replace macro based on state info, such as visitor, etc.
 * @param {string} text - Original text.
 * @param {Immutable.Map} visitor - Visitor state
 * @param {{name:string,email:string}} agent - Agent info including name and email.
 *
 * @return {string} Text after replacing the macros.
 */
export const replaceMacros = (text, visitor, agent) => {
  const afterAgentInfoUpdate = text
    .replace(agentNamePattern, agent.name)
    .replace(agentEmailPattern, agent.email);
  const afterVisitorInfoUpdate = replaceVisitorInfoMacros(afterAgentInfoUpdate, visitor);
  const customVariables = visitor.get('customVariables');
  const customFields = visitor.get('customFields');
  const afterCustomFieldsInfoUpdate =
    afterVisitorInfoUpdate.replace(customFieldPattern, (macro, fieldName) => {
      const field = _.find(customFields, f => f.name === fieldName);
      if (!field) return '';
      return field.value;
    });
  const afterCustomVariablesInfoUpdate =
    afterCustomFieldsInfoUpdate.replace(customVariablePattern, (macro, variableName) => {
      const variable = _.find(customVariables, f => f.name === variableName);
      if (!variable) return '';
      return variable.value;
    });
  return afterCustomVariablesInfoUpdate.replace(/⊙/g, ',');
};

export const getLoginAction = (status, forceLogin) => {
  const ifOnline = (status === agentStatus.online);
  if (ifOnline && forceLogin) return 4;
  else if (ifOnline && !forceLogin) return 0;
  else if (!ifOnline && forceLogin) return 5;
  return 2;
};

export const getOSInfo = () => {
  const sUserAgent = navigator.userAgent;
  const isWin = (navigator.platform === 'Win32') || (navigator.platform === 'Windows');
  const isMac = (navigator.platform === 'Mac68K')
    || (navigator.platform === 'MacPPC')
    || (navigator.platform === 'Macintosh')
    || (navigator.platform === 'MacIntel');
  if (isMac) return 'Mac';
  const isUnix = (navigator.platform === 'X11') && !isWin && !isMac;
  if (isUnix) return 'Unix';
  const isLinux = (String(navigator.platform).indexOf('Linux') > -1);
  if (isLinux) return 'Linux';
  if (isWin) {
    const isWin2K = sUserAgent.indexOf('Windows NT 5.0') > -1
      || sUserAgent.indexOf('Windows 2000') > -1;
    if (isWin2K) return 'Win2000';
    const isWinXP = sUserAgent.indexOf('Windows NT 5.1') > -1
      || sUserAgent.indexOf('Windows XP') > -1;
    if (isWinXP) return 'WinXP';
    const isWin2003 = sUserAgent.indexOf('Windows NT 5.2') > -1
      || sUserAgent.indexOf('Windows 2003') > -1;
    if (isWin2003) return 'Win2003';
    const isWinVista = sUserAgent.indexOf('Windows NT 6.0') > -1
      || sUserAgent.indexOf('Windows Vista') > -1;
    if (isWinVista) return 'WinVista';
    const isWin7 = sUserAgent.indexOf('Windows NT 6.1') > -1
      || sUserAgent.indexOf('Windows 7') > -1;
    if (isWin7) return 'Win7';
    const isWin10 = sUserAgent.indexOf('Windows NT 10') > -1
      || sUserAgent.indexOf('Windows 10') > -1;
    if (isWin10) return 'Win10';
  }
  return 'other';
};


export const getBrowserInfo = () => {
  let tem;
  const ua = navigator.userAgent;
  let M = ua.match(/(opera|chrome|safari|firefox|msie|trident(?=\/))\/?\s*(\d+)/i) || [];
  if (/trident/i.test(M[1])) {
    tem = /\brv[ :]+(\d+)/g.exec(ua) || [];
    return `IE ${tem[1] || ''}`;
  }
  if (M[1] === 'Chrome') {
    tem = ua.match(/\b(OPR|Edge)\/(\d+)/);
    if (tem) return tem.slice(1).join(' ').replace('OPR', 'Opera');
  }
  M = M[2] ? [M[1], M[2]] : [navigator.appName, navigator.appVersion, '-?'];
  tem = ua.match(/version\/(\d+)/i);
  if (tem) {
    M.splice(1, 1, tem[1]);
  }
  return M.join(' ');
};

export const getResolution = () => `h: ${window.screen.height}; w: ${window.screen.width};`;


const PC = window.RTCPeerConnection
  || window.mozRTCPeerConnection
  || window.webkitRTCPeerConnection;
const getUserMedia = navigator.getUserMedia
  || navigator.webkitGetUserMedia
  || navigator.msGetUserMedia
  || navigator.mozGetUserMedia;
const edge = navigator.userAgent.indexOf(' Edge') > 0;

export const ifSupportWebrtc = !!PC && !!getUserMedia && !edge;


// FIXME (Roy): _.get seems broken when path contains more than one level
export function getIn(o, path) {
  const parts = _.isArray(path) ? path : path.split('.');
  switch (parts.length) {
    case 1: return o[parts[0]];
    case 2: return o[parts[0]][parts[1]];
    case 3: return o[parts[0]][parts[1]][parts[2]];
    case 4: return o[parts[0]][parts[1]][parts[2]][parts[3]];
    case 5: return o[parts[0]][parts[1]][parts[2]][parts[3]][parts[4]];
    default:
      throw new Error('too deep');
  }
}

