import { connect } from 'react-redux';
import * as selectors from 'src/reducers/selectors';
import AgentTab from '../components/Tabs/AgentTab';
import { agentTabs } from '../constants/enums';
import { showAgent, showVisitor, showChat } from '../actions/ui';
import { getChatNumber, getAgentNumber } from './Business/common';
import { getAgents } from '../actions/agent';
import { resetForms } from '../actions/visitorDetails';
import * as localStorage from '../utils/localStorage';

const mapStateToProps = state => ({
  visitorNumber: 0,
  chatNumber: getChatNumber(state),
  agentNumber: getAgentNumber(state),
  currentTab: selectors.getCurrentTab(state),
  ifPopupWindowForVisitors: selectors.getIfPopupWindowForVisitors(state),
});

const mapDispatchToProps = dispatch => ({
  clickTab(tab) {
    if (tab === agentTabs.agent) {
      dispatch(showAgent());
      dispatch(getAgents());
    } else if (tab === agentTabs.visitor) {
      dispatch(resetForms());
      dispatch(showVisitor());
      localStorage.deleteItem('prechatFormState');
      localStorage.deleteItem('customFields_FormState');
    } else if (tab === agentTabs.chat) {
      dispatch(resetForms());
      dispatch(showChat());
      localStorage.deleteItem('prechatFormState');
      localStorage.deleteItem('customFields_FormState');
    }
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(AgentTab);



