import React from 'react';
import Link from 'src/components/Link/Link';

class LazilyLoad extends React.Component {

  constructor(props) {
    super(props);
    this.isComponentMounted = false;
    this.state = {
      isLoaded: false,
    };
    this.load = this.load.bind(this);
  }

  componentDidMount() {
    this.isComponentMounted = true;
    this.load();
  }

  componentDidUpdate(previous) {
    /** simple check here, if module names are the same, modules are the same */
    if (Object.keys(this.props.modules).every(key => previous.modules[key])) return;
    this.load();
  }

  componentWillUnmount() {
    this.isComponentMounted = false;
  }

  load() {
    this.setState({
      isLoaded: false,
    });

    const { modules } = this.props;
    const keys = Object.keys(modules);

    Promise.all(keys.map(key => modules[key]()))
      .then(values => (keys.reduce((agg, key, index) => {
        agg[key] = values[index]; // eslint-disable-line no-param-reassign
        return agg;
      }, {})))
      .catch((error) => {
        /**
         * render retry component, if loading module encounters any issue.
         * (usually caused by network issue)
         */
        this.setState({ isLoaded: true, error });
      })
      .then((result) => {
        /** caused by an error (which is being catched by previous .catch) */
        if (!result) return;
        /**
         * render lazily-loaded component if ready.
         *
         * NOTICE: retry component will not be shown if rendering lazily-loaded component causing
         * any issue. As if rendering component throws error, it will throws error no matter how
         * many times retried.
         */
        if (!this.isComponentMounted) return;
        this.setState({ modules: result, isLoaded: true, error: null });
      });
  }

  render() {
    if (!this.state.isLoaded) return this.props.loading || null;
    if (this.state.error) {
      return this.props.retry ?
      React.createElement(this.props.retry, { error: this.state.error, onRetry: this.load }) :
      (
        <div>Click <Link onClick={this.load}>here</Link> to retry</div>
      );
    }
    return React.Children.only(this.props.children(this.state.modules));
  }
}

LazilyLoad.propTypes = {
  /** Component that will be used when script is loading. */
  loading: React.PropTypes.element,
  /** Modules that should be loaded. */
  modules: React.PropTypes.shape({}),
  /** render function that will be called when script successfully loaded. */
  children: React.PropTypes.func.isRequired,
  /**
   * Element that will be shown when error occurs and retry required.
   * If no specific element provided here, a default ugly one will be used instead.
   */
  retry: React.PropTypes.func,
};

export const LazilyLoadFactory = (Component, modules) => props => (
  <LazilyLoad modules={modules}>
    {mods => <Component {...mods} {...props} />}
  </LazilyLoad>
);

export const importLazy = promise => (
  promise.then(result => result.default)
);

export default LazilyLoad;



