import { reverseSchema } from '../utils/schema';

const seconds = 1000;
const minutes = 60 * seconds;

export const updateVisitorsAndChatsPeriod = 2.5 * seconds;
export const updateAgentsPeriod = 10 * seconds;
export const maxonFeedbackPeriod = 1 * minutes;

export const networkTimeout = 1 * minutes;
export const connectionErrorTimeout = 1 * minutes;
export const connectionErrorCount = 20;
export const maxonServerErrorCheckPoint = 30 * seconds;

export const serverNotAvailable = 'serverNotAvailable';
export const webRequest = 'webRequest';
export const webResponse = 'webResponse';
export const webError = 'webError';
export const webVersion = '7.6';

export const errorCodeMap = {
  maxonServerCheck: -100010,
  multipleSites: -1,
  noerror: 0,
  serverException: 1,
  sessionTimeout: 5,
  connectionError: 12,
  systemSwitchServer: 314,
  currentAgentNotOnline: 1001,
  agentNotOnline: 1003,
  agentAlreadyLogin: 1007,
  desktopLoginNotAllowed: 1030,
  systemUpgrade: 170001,
  trialExpired: 1035,
  serverSiteNotActive: 1002,
  agentLoginError: 3124,
};


const errorCodeMapReverse = reverseSchema(errorCodeMap);

export const extractError = (code) => {
  const error = errorCodeMapReverse[code];
  return error === undefined ? `${code}` : error;
};

export const errorCode =
  Object.keys(errorCodeMap).reduce((o, k) => {
    const next = o;
    next[k] = k;
    return next;
  }, {});




