import _ from 'lodash';
import * as constants from '../constants/agent';
import { agentStatus } from '../constants/enums';
import { getReducerHandler } from '../utils/helper';
import * as web from '../constants/web';
import * as chatAction from '../constants/enums/chatAction';
import * as uiActionTypes from '../constants/types/ui';

/* Reducer Definition Area */

/* Default state for initialzation */
const defaultState = {
  id: 0,
  siteId: 0,
  session: '',
  email: '',
  name: '',
  password: '',
  status: agentStatus.offline,
  ifAudioVideoChatting: false,
};

/* Mapping from action.type to corresponding state manipulation function */
const stateMapping = {
  [uiActionTypes.updateAgentInfoForChildWindow](state, action) {
    return Object.assign({}, action.payload.agent);
  },
  [constants.initCurrentAgent](state, action) {
    return Object.assign({}, state, action.payload);
  },
  [constants.switchServer](state, action) {
    return Object.assign({}, state, { session: action.payload.session });
  },
};

export default (state = defaultState, action) => {
  if (`${web.webResponse}/${chatAction.agentGetAgents}` === action.type) {
    const agent = _.find(action.payload, agt => agt.id === state.id);
    if (agent) {
      return Object.assign({}, state, { status: agent.status });
    }
    return state;
  }
  const handler = getReducerHandler(action, stateMapping);
  if (handler == null) return state;
  return handler(state, action);
};




