import _ from 'lodash';
import { ipcRenderer } from '../services/electronAPI';
import * as channelsType from '../constants/enums/channels';
import { agentStatus } from '../constants/enums';
import { logout, agentSetStatus, initCurrentAgent } from '../actions/agent';
import memoryStore from '../services/memoryStore';
import * as memoryStoreKey from '../constants/memoryStore';
import * as localStorage from '../utils/localStorage';
import * as localStorageKeys from '../constants/localStorage';
import * as uiActions from '../actions/ui';
import * as selectors from '../reducers/selectors';
import * as productInfo from '../constants/login';
import { isChildWindow } from '../utils/helper';
import { embedMainWindow } from '../store/windowsManager';

let dispatchHandle = null;
export const dispatchChangedStatus = (status) => {
  const leaveMessage = memoryStore.get(memoryStoreKey.leaveMessageConfirm);
  memoryStore.set(memoryStoreKey.leaveMessageStatus, status);

  if (leaveMessage && leaveMessage.hasMessage) {
    if (status === agentStatus.logout) {
      dispatchHandle(uiActions.openLeaveMessageConfirm(true,
        (!_.isEmpty(leaveMessage.chattingMessage_logout) ?
                      leaveMessage.chattingMessage_logout :
                      leaveMessage.unWrapupMessage_logout),
      ));
      return true;
    } else if (status === agentStatus.exit) {
      dispatchHandle(uiActions.openLeaveMessageConfirm(true,
        (!_.isEmpty(leaveMessage.chattingMessage_exist) ?
                      leaveMessage.chattingMessage_exist :
                      leaveMessage.unWrapupMessage_exist),
      ));
      return true;
    }
  }
  if (status === agentStatus.exit) {
    dispatchHandle(uiActions.showModalLoading());
    dispatchHandle(logout());
    ipcRenderer.send(channelsType.sendExitFlag);
  } else if (status === agentStatus.logout) {
    dispatchHandle(uiActions.showModalLoading());
    dispatchHandle(logout());
  } else {
    dispatchHandle(agentSetStatus(status));
    dispatchHandle(initCurrentAgent({ status }));
  }
  return false;
};

function ifHasMessageInChatsTab() {
  const leaveMessage = memoryStore.get(memoryStoreKey.leaveMessageConfirm);
  memoryStore.set(memoryStoreKey.leaveMessageStatus,
                agentStatus.exit);
  if (leaveMessage && leaveMessage.hasMessage) {
    dispatchHandle(uiActions.openLeaveMessageConfirm(true,
      (!_.isEmpty(leaveMessage.chattingMessage_exist) ?
                    leaveMessage.chattingMessage_exist :
                    leaveMessage.unWrapupMessage_exist),
    ));
    return true;
  }
  return false;
}
ipcRenderer.on(channelsType.changeStatus, (event, item) => {
  if (dispatchChangedStatus(item.code)) {
    return;
  }
});
let getConfig = () => {};
const checkConfig = store => () => {
  const state = store.getState();
  return {
    ifAutoAway: selectors.getIfAutoAway(state),
    autoAwayMinutes: selectors.getAutoAwayMinutes(state),
    ifAutoLogoff: selectors.getIfAutoLogoff(state),
    autoLogoffMinutes: selectors.getAutoLogoffMinutes(state),
  };
};
ipcRenderer.on(channelsType.systemIdleTime, (event, idle) => {
  if (memoryStore.get(memoryStoreKey.ifLogout, null, true)) return;

  const {
    ifAutoAway,
    autoAwayMinutes,
    ifAutoLogoff,
    autoLogoffMinutes } = getConfig();
  const idleMinutes = idle / 60000;
  if (ifAutoAway && autoAwayMinutes < idleMinutes) {
    dispatchHandle(agentSetStatus(agentStatus.away));
    dispatchHandle(initCurrentAgent({ status: agentStatus.away }));
  }
  if (ifAutoLogoff && autoLogoffMinutes < idleMinutes) {
    dispatchHandle(uiActions.showModalLoading());
    dispatchHandle(logout());
  }
});
if (process.env.NODE_ENV === 'production') {
  window.onbeforeunload = (error) => {
    const e = error;
    if (!process.env.IS_WEB_APP && isChildWindow()) {
      embedMainWindow();
      window.close();
      return;
    }

    if (!process.env.IS_WEB_APP && !isChildWindow()) {
      const ifDonotAskMeAgain = localStorage.getObject(localStorageKeys.ifDonotAskMeAgain, false);
      const ifExitCloseWindow = localStorage.getObject(localStorageKeys.ifExitCloseWindow, true);
      if (ifDonotAskMeAgain) {
        if (ifExitCloseWindow) {
          const ifHasMessage = ifHasMessageInChatsTab();
          if (!ifHasMessage) {
            dispatchHandle(uiActions.switchCloseWindow(false));
            dispatchHandle(logout());
          }
        } else {
          ipcRenderer.send(channelsType.minimize);
        }
        e.returnValue = false;
        return;
      }
      const ifLogout = memoryStore.get(memoryStoreKey.ifLogout, null, true);
      if (!ifLogout) {
        dispatchHandle(uiActions.switchCloseWindow(true));
        e.returnValue = false;
      } else {
        ipcRenderer.send(channelsType.changeStatus, { code: agentStatus.exit });
        e.returnValue = true;
      }
    } else {
      const leaveMessage = memoryStore.get(memoryStoreKey.leaveMessageConfirm);
      if (leaveMessage && leaveMessage.hasMessage &&
          memoryStore.get(memoryStoreKey.leaveMessageStatus) !== agentStatus.exit) {
        e.returnValue = (!_.isEmpty(leaveMessage.chattingMessage_exist) ?
                        leaveMessage.chattingMessage_exist :
                        leaveMessage.unWrapupMessage_exist).replace('<br/>', '');
      }
    }
  };
}

const getLeaveMessageConfirm = (state) => {
  const leaveMessage = {
    hasMessage: true,
    chattingMessage_logout: '',
    chattingMessage_exist: '',
    unWrapupMessage_logout: '',
    unWrapupMessage_exist: '',
  };
  if (selectors.ifCurrentAgentChatting(state)) {
    leaveMessage.chattingMessage_logout = `You are chatting with visitors now. <br/>Are you sure you want to log out of ${productInfo.brandingLiveChatName}?`;
    leaveMessage.chattingMessage_exist = `You are chatting with visitors now. <br/>Are you sure you want to close ${productInfo.brandingLiveChatName}?`;
  } else if (selectors.hasUnfinishedWrapup(state)) {
    leaveMessage.unWrapupMessage_logout = `You still have wrap-up forms left unfinished. <br/>Are you sure you want to log out of ${productInfo.brandingLiveChatName}?`;
    leaveMessage.unWrapupMessage_exist = `You still have wrap-up forms left unfinished. <br/>Are you sure you want to close ${productInfo.brandingLiveChatName}?`;
  } else {
    leaveMessage.hasMessage = false;
  }
  return leaveMessage;
};

export default (store) => {
  getConfig = checkConfig(store);
  dispatchHandle = store.dispatch;
  ipcRenderer.on(channelsType.updateAvailable, (event, version) => {
    dispatchHandle(uiActions.updateAvailable(version));
  });
  const unsubscribe = store.subscribe(() => {
    memoryStore.set(memoryStoreKey.leaveMessageConfirm,
                    getLeaveMessageConfirm(store.getState()));
  });
  return () => {
    unsubscribe();
  };
};



