import Client from './client';
import Queue from './queue';
import input from './input';
import output from './output';
import { switchServer } from './maxon';
import * as actions from '../../actions/agent';
import * as chatAction from '../../constants/enums/chatAction';
import * as selectors from '../../reducers/selectors';
import { adapter, isLongTimeRequest } from './adapter';
import {
  networkTimeout,
  connectionErrorTimeout,
  connectionErrorCount,
  maxonServerErrorCheckPoint,
  errorCode,
} from '../../constants/web';
import autoLogin from './autoLogin';

function createQueue(store, timeout) {
  const state = store.getState();
  const siteId = selectors.getSiteId(state);
  const session = selectors.getCurrentAgentSession(state);
  const chatServerUrl = selectors.getLiveChatHandler3(state);

  return new Queue({
    session,
    client: new Client(`${chatServerUrl}?siteId=${siteId}`, timeout),
    startCommandId: state.agent.startCommandId || 100,
    getLocalChats: () => selectors.getLocalChats(store.getState()),
    getVisitorListVersion: () => selectors.getVisitorVersion(store.getState()),
    connectionErrorTimeout,
    connectionErrorCount,
    maxonServerErrorCheckPoint,
  });
}


export default store => (next) => {
  let queue = null;
  let longTimeQueue = null;

  const reset = () => {
    queue = null;
    longTimeQueue = null;
  };

  const dispatchCommands = (isLongTime, commands) => {
    if (isLongTime) {
      longTimeQueue.dispatch(...commands);
    } else {
      queue.dispatch(...commands);
      longTimeQueue.dispatch();
    }
  };

  const switchingServer = () => {
    queue.switchingServer();
    longTimeQueue.switchingServer();
  };

  const endSwitchServer = () => {
    queue.switchedServer();
    longTimeQueue.switchedServer();
  };

  const switchedServer = ({ newSession, isUseMainServer }) => {
    const chatServerUrl = selectors.getLiveChatHandler3(store.getState(), isUseMainServer);
    const siteId = selectors.getSiteId(store.getState());
    const newChatServerUrl = `${chatServerUrl}?siteId=${siteId}`;
    queue.session = newSession;
    queue.client = new Client(newChatServerUrl, networkTimeout);
    queue.switchedServer();

    longTimeQueue.session = newSession;
    longTimeQueue.client = new Client(newChatServerUrl, networkTimeout);
    longTimeQueue.switchedServer();
  };

  const doSwitchServer = () => {
    if (queue.getIsSwitchingServer()) {
      return;
    }
    switchingServer();
    switchServer(store.getState())
      .then((result) => {
        if (result.switched) {
          const recoverVistiorIds = result.recoverVisitorIds.map(ids =>
            Object.assign({}, ids, {
              oldChatId: selectors.getVisitorChatId(store.getState(), ids.oldVisitorId),
            })
          );
          next(actions.serverSwitched(result.isUseMainServer,
            result.newSession, recoverVistiorIds));
          next(actions.initVisitors(result.visitors));
          switchedServer(result);
          dispatchCommands(false, [{ type: chatAction.agentGetUnfinishedWrapups }]);
        } else {
          endSwitchServer(result);
        }
      })
      .catch(() => {
        endSwitchServer();
      });
  };

  const checkSwitchError = () => (err) => {
    if (err) {
      if (err.errorCode === errorCode.systemSwitchServer
        || err.errorCode === errorCode.maxonServerCheck) {
        doSwitchServer();
      }
    }
  };

  const checkQueue = () => {
    if (queue === null) {
      queue = createQueue(store, networkTimeout);
      queue.subscribe((err, resp) => {
        if (err != null && err.errorCode === errorCode.sessionTimeout) {
          reset();
        }
        if (resp != null && resp.type === chatAction.agentLogout) {
          reset();
        }
        if (err != null && err.errorCode === errorCode.currentAgentNotOnline) {
          const state = store.getState();
          queue.insertCommands(autoLogin(state, selectors.getCurrentAgentStatus(state)));
        }
        if (resp != null &&
          (resp.type === chatAction.agentLogin || resp.type === chatAction.agentAutoLogin)) {
          const newSession = resp.errorString;
          queue.session = newSession;
          longTimeQueue.session = newSession;
        }
      });
      queue.subscribe(checkSwitchError());
      queue.subscribe(output(store, next, dispatchCommands));

      longTimeQueue = createQueue(store, networkTimeout * 3);
      longTimeQueue.subscribe(output(store, next, dispatchCommands));
    }
  };

  const receive = input((action) => {
    const commands = adapter(store.getState(), action);
    checkQueue();
    const longTimeRequest = isLongTimeRequest(action);
    dispatchCommands(longTimeRequest, commands);
  });

  return (action) => {
    if (receive(action)) return store.getState();
    return next(action);
  };
};



