import React from 'react';
import classnames from 'classnames';
import styles from './Icon.css';
import { formatPromptNumber } from '../../utils/helper';

const PromptNumber = props => (
  <i
    className={classnames(styles.promptNumber,
      props.customClass,
      (props.number > 0 ? styles.promptDisplay : styles.promptHide))}
  >
    {formatPromptNumber(props.number)}
  </i>
);

PromptNumber.propTypes = {
  customClass: React.PropTypes.string.isRequired,
  number: React.PropTypes.number,
};
PromptNumber.defaultProps = {
  number: 0,
};

export default PromptNumber;



