import * as reporter from 'src/utils/reporter';
import Client from '../../store/web/client';
import LoginError from './LoginError';
import { errorCode, webVersion } from '../../constants/web';
import { throwRequired, getUrl } from '../../utils/helper';
import { agentStatus } from '../../constants/enums';
import * as str from '../../utils/str';
import * as chatAction from '../../constants/enums/chatAction';
import moderator from '../moderator';
import * as config from '../../constants/config';
import { getLoginAction, ifSupportWebrtc } from '../../utils/common';

const loginCommands = ({
  siteId = throwRequired('siteId'),
  email = throwRequired('email'),
  password = throwRequired('password'),
  status = agentStatus.online,
  forceLogin = false,
  deviceTag = '',
  verificationCode = '',
  session = '',
}) => {
  const encryptedPassword = str.passwordConvert(password);
  const loginAction = getLoginAction(status, forceLogin);
  return {
    type: chatAction.agentLogin,
    visitorId: siteId,
    message: str.joincolon(
      deviceTag,
      loginAction,
      encryptedPassword,
      verificationCode,
      session,
      ifSupportWebrtc,
    ),
    sender: str.joincolon(webVersion, email),
  };
};
const autoLoginCommands = ({
  siteId = throwRequired('siteId'),
  agentId = throwRequired('agentId'),
  token = throwRequired('token'),
  status = agentStatus.online,
  forceLogin = false,
}) => {
  const loginAction = getLoginAction(status, forceLogin);
  return {
    type: chatAction.agentAutoLogin,
    visitorId: siteId,
    message: str.joincolon(loginAction, token),
    sender: str.joincolon(webVersion, agentId),
  };
};
const changePasswordCommands = ({
  siteId = throwRequired('siteId'),
  email = throwRequired('email'),
  newPassword = throwRequired('newPassword'),
  oldPassword = throwRequired('newPassword'),
}) => {
  const encryptedNewPassword = str.passwordConvert(newPassword);
  const encryptedOldPassword = str.passwordConvert(oldPassword);
  return {
    type: chatAction.agentChangePassword,
    visitorId: siteId,
    message: str.joincolon(
      encryptedNewPassword,
      encryptedOldPassword,
    ),
    sender: email,
  };
};

const sendEmailActivateIPCommands = ({
  siteId = throwRequired('siteId'),
  email = throwRequired('email'),
}) => ({ type: chatAction.agentSendEmailActivateIP, visitorId: siteId, message: email });

let cache = {};

export default async function login(options, type, ifSwitchServer) {
  const { chatServer, standbyChatServer, siteId } = options;
  let liveChatHandler3 = `${ifSwitchServer ? standbyChatServer : chatServer}/liveChatHandler3.ashx`;
  liveChatHandler3 = getUrl(liveChatHandler3, config.isSSL);
  const input = {
    commands: [],
    session: '',
  };
  if (type === chatAction.agentLogin) {
    input.commands = [loginCommands(options)];
  } else if (type === chatAction.agentChangePassword) {
    input.commands = [changePasswordCommands(options)];
  } else if (type === chatAction.agentSendEmailActivateIP) {
    input.commands = [sendEmailActivateIPCommands(options)];
  } else if (type === chatAction.agentAutoLogin) {
    input.commands = [autoLoginCommands(options)];
  }
  let resp = null;
  let error = {};
  try {
    const client = new Client(`${liveChatHandler3}?siteId=${siteId}`);
    resp = await client.send(input);
  } catch (err) {
    // catch到网络操作返回错误
    reporter.error(err, 'chatServer', 110, 0);
    error = err;
  }
  if (!resp || !resp.errorCode ||
    (resp && (
      resp.errorCode === errorCode.systemSwitchServer ||
      resp.errorCode === errorCode.connectionError ||
      resp.errorCode === errorCode.serverException
    ))
  ) {
    if (!ifSwitchServer) {
      if (resp && resp.errorCode) {
        error = {
          errorCode: resp.errorCode,
          errorString: resp.errorString,
          siteId,
          loginResponse: null,
        };
      }
      cache = error;
      const isNotCurrentUseMainServer = await moderator(standbyChatServer, siteId);
      if (!isNotCurrentUseMainServer) {
        return await login(options, type, true);
      }
    }
    throw new LoginError('chatServerError', cache.errorCode, cache);
  } else {
    const command = resp.commands[0];
    if (command.errorCode === errorCode.noerror) {
      if (type === chatAction.agentSendEmailActivateIP) {
        return {};
      }
      return {
        agent: command.extend.currentAgent,
        session: command.errorString,
        startCommandId: command.visitorId,
        siteInfo: command.extend.siteInfo,
        trialInfo: command.extend.trialInfo,
        loginResponse: command.extend.loginResponse,
        server: ifSwitchServer ? standbyChatServer : chatServer,
        ifUseMainServer: !ifSwitchServer,
      };
    }
    error = {
      errorCode: command.errorCode,
      errorString: command.errorString,
      siteId,
      loginResponse: command.extend ? command.extend.loginResponse : null,
    };
    throw new LoginError('chatServerError', command.errorCode, error);
  }
  /*
  else if (command.errorCode === errorCode.agentAlreadyLogin) {
    // TODO (Roy): force login, throw a exception here
    options.forceLogin = true;
    return await login(options);
  }

  throw new Error(command.errorString);
  */
}



