import { getIsAuthenticated } from '../../reducers/selectors';

// 只有在agent login时才启动，getPeriod变了时钟会重启
export default ({
  onHeartbeat,
  getPeriod,
}) => (store) => {
  let timer = null;
  let interval = null;

  const start = () => {
    if (timer === null) {
      onHeartbeat(store);
      timer = setInterval(onHeartbeat.bind(null, store), interval);
    }
  };

  const stop = () => {
    if (timer !== null) {
      clearInterval(timer);
      timer = null;
    }
  };

  const unsubscribe = store.subscribe(() => {
    const state = store.getState();
    if (process.env.NODE_ENV !== 'production' && state.debug.isReplaying) return;
    if (getIsAuthenticated(state)) {
      const newInterval = getPeriod(state);
      if (interval !== newInterval) {
        interval = newInterval;
        stop();
      }
      start();
    } else {
      stop();
    }
  });

  return () => {
    unsubscribe();
    stop();
  };
};



