import { connect } from 'react-redux';
import * as selectors from '../reducers/selectors';
import DashboardPage from '../components/Modal/DashboardPage/DashboardPage';
import * as uiActions from '../actions/ui';
import * as agentActions from '../actions/agent';

const mapStateToProps = state => ({
  ifOpen: selectors.getDashboardWindowFlag(state),
  messages: selectors.getDashboardMessages(state),
});

const mapDispatchToProps = dispatch => ({
  onClose(ids) {
    setTimeout(() => {
      dispatch(uiActions.switchDashboardMessagesWindow(false));
      dispatch(agentActions.agentSetDashboardMessageNotShow(ids));
    }, 300);
  },
});
export default connect(mapStateToProps, mapDispatchToProps)(DashboardPage);



