import React, { PropTypes } from 'react';
import styles from './Header.css';

const TrialInfo = props => (
  <div className={styles.trialInfoWraper}>
    <span className={styles.label}>{props.trialInfo.trialRemind}</span>
    {
      props.showBuyNow &&
      <span
        className={styles.purchase}
        onClick={() => props.clickPurchase(props.trialInfo)}
      >
        Buy Now
      </span>
    }
  </div>
);
TrialInfo.propTypes = {
  trialInfo: PropTypes.shape({
    isWeb: PropTypes.bool,
    purchaseUrl: PropTypes.string,
    loginUrl: PropTypes.string,
    autoLoginUrl: PropTypes.string,
    trialRemind: PropTypes.string,
  }).isRequired,
  clickPurchase: PropTypes.func.isRequired,
  showBuyNow: PropTypes.bool,
};
export default TrialInfo;



