import React from 'react';
import Button from 'src/components/Button/Button';

const Reload = ({
  onRetry,
}) => (
  <div
    style={{
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      height: '100%',
      width: '100%',
    }}
  >
    <div style={{ textAlign: 'center' }}>
      <h1
        style={{ fontSize: 25, marginTop: 20, marginBottom: 20 }}
      >
        Oops. Error loading the module.
      </h1>
      <Button onClick={onRetry} text="Reload" type="primary" size="small" />
    </div>
  </div>
);

Reload.propTypes = {
  onRetry: React.PropTypes.func.isRequired,
};

export default Reload;



