import { updateAgents, updateVisitors, maxonFeedback } from './heartbeat/index';
import shortcut from './shortcut';
import housekeeper from './housekeeper';
import channelCenter from './channelCenter';
import salesforce from './salesforce';

let result; // eslint-disable-line import/no-mutable-exports

if (process.env.IS_WEB_APP) {
  result = [
    updateAgents,
    updateVisitors,
    maxonFeedback,
    shortcut,
    housekeeper,
    channelCenter,
    salesforce,
  ];
} else {
  result = [
    updateAgents,
    updateVisitors,
    maxonFeedback,
    shortcut,
    housekeeper,
    channelCenter,
    salesforce,
    require('./contextMenu').default, // eslint-disable-line global-require
  ];
}

export default result;



