import React from 'react';
import Modal from '../Modal';
import ModalStyle from '../Modal.css';
import * as enums from '../../../constants/enums';

let cache = '';
class SiteDunningModal extends React.Component {
  shouldComponentUpdate(nextProps) {
    if (this.props.dunningInfoStatus !== enums.siteDunningInfoEnum.ExtendFailed &&
      nextProps.dunningInfoStatus === enums.siteDunningInfoEnum.ExtendFailed) {
      cache = this.props.message;
    }
    if (this.props.ifOpen !== nextProps.ifOpen ||
      this.props.message !== nextProps.message ||
      this.props.dunningInfoStatus !== nextProps.dunningInfoStatus ||
      this.props.ifExtendLoading !== nextProps.ifExtendLoading) {
      return true;
    }
    return false;
  }
  componentDidUpdate() {
    if (this.props.dunningInfoStatus === enums.siteDunningInfoEnum.ExtendFailed) {
      this.props.showError(this.props.message);
    }
  }
  render() {
    const props = this.props;
    const showExtend = (props.dunningInfoStatus === enums.siteDunningInfoEnum.RemindAndShowExtend ||
      props.dunningInfoStatus === enums.siteDunningInfoEnum.ExtendFailed);
    let okProps = [{ text: 'OK', eventClick: props.closeModal, className: ModalStyle.dunningOKButton }];
    okProps = showExtend ? okProps.concat([{
      text: 'Extend for 5 days',
      eventClick: props.onExtendClick,
      disabled: props.ifExtendLoading,
      loading: props.ifExtendLoading,
    }]) : okProps;
    return (
      <Modal
        closeModal={props.closeModal}
        className={ModalStyle.siteDunningModal}
        shouldCloseOnOverlayClick={false}
        title="Notice"
        actions={{
          className: ModalStyle.trialExpiredButton,
          okProps,
        }}
      >
        <div>
          <div className={ModalStyle.dunningImageWrapper} />
          <div>
            {props.dunningInfoStatus !== enums.siteDunningInfoEnum.ExtendFailed ?
                props.message : cache}
          </div>
        </div>
      </Modal>);
  }
}

export default SiteDunningModal;

SiteDunningModal.propTypes = {
  ifOpen: React.PropTypes.bool.isRequired,
  onExtendClick: React.PropTypes.func.isRequired,
  closeModal: React.PropTypes.func.isRequired,
  dunningInfoStatus: React.PropTypes.number,
  message: React.PropTypes.string,
  ifExtendLoading: React.PropTypes.bool,
  showError: React.PropTypes.func,
};



