import * as _ from 'lodash';
import * as visitorStatus from 'src/constants/enums/visitorStatus';
import { chatStatus } from 'src/constants/enums';
import { getDepartmentById } from './index';

export const getVisitors = state => state.visitor.visitors;
export const getVisitor = (state, id) => getVisitors(state).get(id);
export const getVisitorObject = (state, id) => {
  /** DO NOT use this function, use `getVisitor` instead */
  const visitor = getVisitor(state, id);
  return visitor === undefined ? undefined : visitor.toJS();
};

export const getVisitorVersion = state => state.visitor.version;
export const getAllVisitors = state => getVisitors(state).toList();
export const getVisitorList = state => state.visitor.list;
export const isVisitorOnline = (state, id) => {
  const inList = state.visitor.list.indexOf(id) >= 0;
  const visitor = getVisitor(state, id);
  const isOnline = visitor === undefined ? false : visitor.get('status') !== visitorStatus.outOfSite;
  return inList && isOnline;
};

export const getHistoryRefreshingStatus = (state, visitorId) =>
  getVisitor(state, visitorId).get('historyRefreshing');

export const getHistoryMessageId = (state, visitorId) =>
  getVisitor(state, visitorId).get('historyMessageId');

export const getHistoryChatId = (state, visitorId) =>
  getVisitor(state, visitorId).get('historyChatId');

const getHistory = (state, visitorId) => {
  const visitor = getVisitors(state).find(val => val.get('id') === visitorId);
  return visitor === undefined ? undefined : visitor.get('history');
};

export const getVisitorChatHistory = (state, visitorId) => {
  const history = getHistory(state, visitorId);
  return history === undefined ? undefined : history.get('chats');
};


const matchErrorInfo = (visitor, name, id) => {
  const info = visitor.get(name);
  return (info !== undefined && info.id === id) ? info.errorString : '';
};

export const getHistoryError = (state, visitorId, chatId, messageId) => {
  const visitor = getVisitor(state, visitorId);
  // const history = getHistory(state, visitorId);
  return visitor === undefined ?
    {}
    : {
      getChatHistoryError: visitor.get('chatHistoryError'),
      getOfflineMessageHistoryError: visitor.get('offlineMessageHistoryError'),
      getChatHistoryDetailsError: matchErrorInfo(visitor, 'chatHistoryDetailsError', chatId),
      getOfflineMessageDetailsError:
      matchErrorInfo(visitor, 'offlineMessageDetailsError', messageId),
    };
};

export const getVisitorMessageHistory = (state, visitorId) => {
  const history = getHistory(state, visitorId);
  return history === undefined ? undefined : history.get('offlineMessages');
};
export const getChatDetails = (state, visitorId, chatId) => {
  const history = getHistory(state, visitorId);
  const details = history === undefined ? undefined : history.get('details');
  const key = `c${chatId}`;
  return details === undefined ? undefined : details.get(key);
};

export const getMessageDetails = (state, visitorId, messageId) => {
  const history = getHistory(state, visitorId);
  const details = history === undefined ? undefined : history.get('details');
  const key = `o${messageId}`;
  return details === undefined ? undefined : details.get(key);
};

export const getVisitorPreChatForm = (state, id) => {
  const visitor = getVisitor(state, id);
  return {
    comment: visitor.get('agentComment'),
    name: visitor.get('prechatName'),
    email: visitor.get('prechatEmail'),
    company: visitor.get('prechatCompany'),
    phone: visitor.get('prechatPhone'),
    productService: visitor.get('prechatProductService'),
    departmentId: visitor.get('departmentId'),
    customFields: visitor.get('customFields'),
  };
};
/**
 * Get certain property of visitor. Should only use this to get property returned by web server.
 *
 * _[NOTICE]_: Please use other `selector` if you want any property that does not exist in web
 * response, as these property might be changed later.
 *
 * @param {object} state - State from redux
 * @param {number} id - Visitor id
 * @param {string} prop - Visitor Property (exists in web response)
 *
 * @return {any} value, undefined if not found or visitor not exists.
 */
export const getVisitorProp = (state, id, prop) => {
  const visitor = getVisitor(state, id);
  if (!visitor) return undefined;
  return visitor.get(prop);
};
export const getVisitorChatId = (state, id) => getVisitorProp(state, id, 'chatId');

export const getHitoryIsViewAllChats = (state, id) => getVisitorProp(state, id, 'isViewAllChats');
export const getHitoryIsViewAllMessages = (state, id) => getVisitorProp(state, id, 'isViewAllMessages');

function checkVisitorStatus(visitor, statusList) {
  return statusList.some(status => status === visitor.status);
}

const chattingStatus = [chatStatus.chatting, chatStatus.transferring];
export const ifVisitorChatting = visitor =>
  checkVisitorStatus(
    visitor,
    chattingStatus
  );

/* format the name of agents with comma */
export const formatAgents = (agentIds = [], agents = []) =>
  agents.filter(agt =>
    agentIds.indexOf(agt.id) >= 0
  )
    .map(agent => agent.name)
    .join(', ');

export const checkIfVisitorStatusChatting = status =>
  status === chatStatus.chatting ||
  status === chatStatus.transferring;

export const checkIfAgentInVisitorMonitorIds = (visitor, agentId) =>
  visitor.chatMonitorAgents != null &&
  visitor.chatMonitorAgents.indexOf(agentId) >= 0;

export const checkIfAgentInVisitorActiveIds = (visitor, agentId) =>
  visitor.chatActiveAgents != null &&
  visitor.chatActiveAgents.indexOf(agentId) >= 0;

export const checkIfAgentInVisitorInvolveIds = (visitor, agentId) =>
  visitor.chatInvolvedAgents != null &&
  visitor.chatInvolvedAgents.indexOf(agentId) >= 0;

export const checkIfWaitingVisitorAcceptInvitation = status =>
  status === chatStatus.manuallyInvitedAndWatingVisitor;

export const checkIfWaitingVisitorAcceptInvitationFromMe = (visitor, agentId) =>
  checkIfWaitingVisitorAcceptInvitation(visitor.status) &&
  checkIfAgentInVisitorActiveIds(visitor, agentId);

export const checkIfRefuseByVisitor = visitor =>
  visitor.status === chatStatus.refusedByVisitor;

export const checkIfVisitorRefuseMyInvitation = (visitor, agentId) =>
  visitor.status === chatStatus.refusedByVisitor &&
  checkIfAgentInVisitorActiveIds(visitor, agentId);

export const checkIfVisitorStatusChatEndedVisitor = status =>
  status === chatStatus.chatEnded;

export const getAgentNames = (visitor, agentId, agents) => {
  if ((checkIfVisitorStatusChatting(visitor.status) &&
    checkIfAgentInVisitorMonitorIds(visitor, agentId)) ||
    (checkIfVisitorStatusChatEndedVisitor(visitor.status) &&
      checkIfAgentInVisitorMonitorIds(visitor, agentId))) {
    return formatAgents(visitor.chatInvolvedAgents, agents);
  }
  return '';
};

export const isChatEnded = status =>
  status !== chatStatus.waitingForChat &&
  status !== chatStatus.transferring &&
  status !== chatStatus.chatting &&
  status !== chatStatus.manuallyInvitedAndWatingVisitor;

export const checkIfVisitorDepartmentContainsCurrentAgent = (
  state,
  visitor,
  agentId) => {
  const department = getDepartmentById(state, visitor.departmentId);
  if (typeof (department) === 'undefined' || department.agentIds.indexOf(agentId) >= 0) {
    return true;
  }
  return false;
};

export const isWaitingStatus = (state, visitor, agentId) => {
  if (visitor.status === chatStatus.transferring) {
    if (checkIfAgentInVisitorActiveIds(visitor, agentId) ||
      !checkIfVisitorDepartmentContainsCurrentAgent(state,
        visitor,
        agentId)
    ) {
      return false;
    }
    return true;
  } else if (visitor.status === chatStatus.waitingForChat) {
    return true;
  }
  return false;
};
export const checkIfVisitorChattedWithCurrentAgent = (visitor, agentId) =>
  (visitor.status === chatStatus.chatEnded ||
    checkIfVisitorStatusChatting(visitor.status)) &&
  checkIfAgentInVisitorInvolveIds(visitor, agentId);

/** @todo use reselect here to cache result */

export const getInsiteVisitorNumber = (state) => {
  const visitorList = getVisitorList(state);
  const visitors = getVisitors(state);
  return _.filter(
    visitorList,
    id => visitors.get(id).get('status') !== visitorStatus.outOfSite
  ).length;
};

export const isChattingWithAgent = (v, agentId) =>
  checkIfVisitorStatusChatting(v.status) &&
  checkIfAgentInVisitorActiveIds(v, agentId);

export const isMonitoringChat = (v, agentId) =>
  checkIfVisitorStatusChatting(v.status) &&
  checkIfAgentInVisitorMonitorIds(v, agentId);

export const isWaitingAcceptInvitationForMe = (v, agentId) =>
  v.status === chatStatus.manuallyInvitedAndWatingVisitor &&
  checkIfAgentInVisitorActiveIds(v, agentId);

export const isMonitorChatEnded = (v, agentId) => {
  if (v.status === chatStatus.chatEnded &&
    _.isArray(v.chatMonitorAgents) &&
    v.chatMonitorAgents.length > 0) {
    return checkIfAgentInVisitorMonitorIds(v, agentId);
  }
  return false;
};

export const isVisitorExists = (state, id) => state.visitor.list.indexOf(id) >= 0;



