import _ from 'lodash';

export function updateIn(xs, ...paths) {
  if (paths.length < 1) {
    return xs;
  }

  if (paths.length === 1) {
    return paths[0];
  }

  const [key, ...rest] = paths;
  const next = xs == null ? null : xs[key];
  return Object.assign({}, xs, { [key]: updateIn(next, ...rest) });
}

export const isImage = (fileName) => {
  if (!_.isString(fileName)) return false;
  return _.endsWith(fileName.toLowerCase(), '.jpg') ||
        _.endsWith(fileName.toLowerCase(), '.png') ||
        _.endsWith(fileName.toLowerCase(), '.jpeg') ||
        _.endsWith(fileName.toLowerCase(), '.gif');
};



