import * as _ from 'lodash';
import * as constants from '../constants/agent';
import * as chatListActionTypes from '../constants/types/chatList';
import { chatListConfig } from '../constants/enums';
import { updateIn } from '../utils/seq';
import * as web from '../constants/web';
import * as chatAction from '../constants/enums/chatAction';
import * as uiTypes from '../constants/types/ui';
import * as configType from '../constants/types/config';
import memoryStore from '../services/memoryStore';
import * as memoryStoreKey from '../constants/memoryStore';
import * as agentsSelector from './selectors/agents';
import { parseDate } from '../utils/time';

/* Default state for initialization */
const defaultState = {
  ifUseMainServer: true,
  isMainServerAvailable: true,
  timeSync: {
    delay: 0,
    times: 0,
  },
  preference: {
    others: {},
  },
  settings: {
    urls: {
      chatServer: 'https://chatserver.comm100.com',
      standbyChatServer: 'https://hostedmax.comm100.com/chatserver',
      controlPanel: 'https://hosted.comm100.com',
    },
  },
  salesforce: {},
};

function initConfig(state, { payload }) {
  return Object.assign({}, state, payload);
}

function updateChatListConfig(state, { payload }) {
  const others = Object.assign({}, state.preference.others);
  if (payload === chatListConfig.showUnreposiveTime) {
    others.ifShowUnresponseTime = !state.preference.others.ifShowUnresponseTime;
  } else if (payload === chatListConfig.showDepartment) {
    others.ifShowDepartment = !state.preference.others.ifShowDepartment;
  } else if (payload === chatListConfig.showByStatus) {
    others.ifGroupChats = !state.preference.others.ifGroupChats;
  }
  return updateIn(state, 'preference', 'others', others);
}

function updatePreference(state, { payload }, totalState) {
  const preference = _.cloneDeep(state.preference);

  preference.shortcutSend = payload.chatInfo.shortcutSend;
  preference.messageWhenAcceptChat = payload.chatInfo.messageWhenAcceptChat;

  preference.others.shortcutNextChat = payload.chatInfo.shortcutNextChat;
  preference.others.shortcutNextResponse = payload.chatInfo.shortcutNextResponse;
  preference.others.shortcutCannedMessage = payload.chatInfo.shortcutCannedMessage;
  preference.ifAutoSendMessage = payload.chatInfo.ifAutoSendMessage;

  preference.textDirectionIfRTL = payload.languageInfo.textDirectionIfRTL;
  preference.ifEnableSpellCheck = payload.languageInfo.ifEnableSpellCheck;
  preference.languageSpellCheck = payload.languageInfo.languageSpellCheck;
  preference.ifEnableTranslate = payload.languageInfo.ifEnableTranslate;
  preference.translateLanguage = payload.languageInfo.translateLanguage;

  Object.assign(preference, _.pick(payload.soundpopupInfo, [
    'visitorEnterSiteSoundId',
    'visitorEnterSiteSoundVolume',
    'ifVisitorEnterSitePopup',
    'ifVisitorEnterSiteSoundOn',
    'newChatRequestSoundId',
    'newChatRequestSoundVolume',
    'ifNewChatRequestPopup',
    'ifNewChatRequestSoundOn',
    'chatInQueueSoundId',
    'chatInQueueSoundVolume',
    'ifChatInQueuePopup',
    'ifChatInQueueSoundOn',
    'newResponseSoundId',
    'newResponseSoundVolume',
    'ifNewResponsePopup',
    'ifNewResponseSoundOn',
    'chatEndedSoundId',
    'chatEndedSoundVolume',
    'ifChatEndedPopup',
    'ifChatEndedSoundOn',
    'transferSoundId',
    'transferSoundVolume',
    'ifTransferPopup',
    'ifTransferSoundOn',
    'privateMessageSoundId',
    'privateMessageSoundVolume',
    'ifPrivateMessageSoundOn',
    'ifPrivateMessagePopup',
    'exceptionSoundId',
    'exceptionSoundVolume',
    'ifExceptionPopup',
    'ifExceptionSoundOn',

  ]));
  Object.assign(preference.others, _.pick(payload.soundpopupInfo, [
    'segmentationSoundId',
    'segmentationSoundVolume',
    'ifSegmentationPopup',
    'ifSegmentationSoundOn',
    'audioVideoChatRequestSoundId',
    'audioVideoChatRequestSoundVolume',
    'ifAudioVideoChatRequestPopup',
    'ifAudioVideoChatRequestSoundOn',
    'audioVideoChatEndSoundId',
    'audioVideoChatEndSoundVolume',
    'ifAudioVideoChatEndPopup',
    'ifAudioVideoChatEndSoundOn',
  ]));


  preference.visitorMonitorColumns = payload.columnsInfo;

  // others tab
  preference.others.ifEnableAutoMonitor = payload.othersInfo.ifEnableAutoMonitor;
  preference.others.autoCloseChatWindow = payload.othersInfo.autoCloseChatWindow;
  preference.others.autoCloseChatDelayMinutes = payload.othersInfo.autoCloseChatDelayMinutes;
  preference.g2mEmail = payload.othersInfo.g2mEmail;
  preference.g2mPassword = payload.othersInfo.g2mPassword;
  preference.ifG2MConnected = payload.othersInfo.ifG2MConnected;
  preference.others.ifAutoAway = payload.othersInfo.ifAutoAway;
  preference.others.autoAwayMinutes = payload.othersInfo.autoAwayMinutes;
  preference.others.ifAutoLogoff = payload.othersInfo.ifAutoLogoff;
  preference.others.autoLogoffMinutes = payload.othersInfo.autoLogoffMinutes;

  const currentAgentId = agentsSelector.getCurrentAgentId(totalState);
  memoryStore.set(memoryStoreKey.cacheSounds, {}, currentAgentId);

  return updateIn(state, 'preference', preference);
}

function updateSendType(state, { payload }) {
  return updateIn(state, 'preference',
    Object.assign({}, state.preference, { shortcutSend: payload }));
}

function updateCannedMessages(state, { payload }) {
  return updateIn(state, 'settings', Object.assign({}, state.settings,
    { cannedMessages: payload.cannedMessages, categories: payload.categories }));
}

function updateDepartments(state, { payload }) {
  return updateIn(state, 'settings', 'departments', payload);
}

function updateAgents(state, { payload }) {
  return updateIn(state, 'settings', 'agents', payload);
}

function updateServerStatus(state, mainServerAvailable = true) {
  if (state.ifUseMainServer) {
    return updateIn(state, 'isMainServerAvailable', mainServerAvailable);
  }
  return state;
}

function syncTime(state, { payload }) {
  const { delay, times } = state.timeSync;
  const newState = updateServerStatus(state);
  if (times < 100) {
    let { visitors: { time: serverTime } } = payload;
    serverTime = parseDate(serverTime);
    const timeDelay = Date.now() - serverTime.getTime();
    const averageDelay = ((delay * times) + timeDelay) / (times + 1);
    return updateIn(newState, 'timeSync', { delay: averageDelay, times: times + 1 });
  }
  return newState;
}

function changeVisitorListColumnOrder(state, { payload, meta }) {
  const { startEnumColumn, endEnumColumn, startCustomVariableId, endCustomVariableId } = payload;
  if (startEnumColumn === endEnumColumn && startCustomVariableId === endCustomVariableId) {
    return state;
  }
  const { leftToRight } = meta;
  const columns = state.preference.visitorMonitorColumns.slice();

  let cached;
  const step = leftToRight ? 1 : -1;
  for (
    let i = leftToRight ? 0 : columns.length - 1;
    i >= 0 && i < columns.length;
    i += step
  ) {
    const column = columns[i];
    if (column.enumColumn === startEnumColumn &&
      column.customVariableId === startCustomVariableId) {
      cached = column;
    } else if (column.enumColumn === endEnumColumn &&
      column.customVariableId === endCustomVariableId) {
      columns[i] = cached;
      cached = undefined;
    }
    if (cached) {
      columns[i] = columns[i + step];
    }
  }


  return Object.assign({}, state, {
    preference: Object.assign({}, state.preference, {
      visitorMonitorColumns: columns,
    }),
  });
}

function changeVisitorListColumnSize(state, { payload, meta }) {
  const { enumColumn, customVariableId } = meta;
  return Object.assign({}, state, {
    preference: Object.assign({}, state.preference, {
      visitorMonitorColumns: state.preference.visitorMonitorColumns.map((column) => {
        if (column.enumColumn !== enumColumn || column.customVariableId !== customVariableId) {
          return column;
        }
        return Object.assign({}, column, { width: payload });
      }),
    }),
  });
}
function updateSettingUrls(state, { payload }) {
  return updateIn(state, 'settings', 'urls', payload.urls);
}
function updatePreferenceG2MInfo(state, { payload }) {
  if (payload.ifG2MConnected) {
    return updateIn(state, 'preference', Object.assign({}, state.preference,
      { ifG2MConnected: true }));
  }
  return updateIn(state, 'preference', Object.assign({}, state.preference,
    { ifG2MConnected: false, g2mEmail: '', g2mPassword: '' }));
}

/* Mapping from action.type to corresponding state manipulation function */
const stateMapping = {
  [uiTypes.updateAgentInfoForChildWindow](state, action) {
    return Object.assign({}, action.payload.config);
  },
  [constants.initConfig]: initConfig,
  [chatListActionTypes.showUnreposiveTime]: updateChatListConfig,
  [chatListActionTypes.showDepartment]: updateChatListConfig,
  [chatListActionTypes.showByStatus]: updateChatListConfig,
  [constants.updatePreference]: updatePreference,
  [configType.changeSendType]: updateSendType,
  [`${web.webResponse}/${chatAction.agentSaveG2MAccount}`]: updatePreferenceG2MInfo,
  [`${web.webError}/${chatAction.agentSaveG2MAccount}`]: updatePreferenceG2MInfo,
  [`${web.webResponse}/${chatAction.agentGetCannedMessages}`]: updateCannedMessages,
  [`${web.webResponse}/${chatAction.agentGetDepartments}`]: updateDepartments,
  [`${web.webResponse}/${chatAction.agentGetAgents}`]: updateAgents,
  [`${web.webResponse}/${chatAction.agentGetVisitors}`]: syncTime,
  [uiTypes.saveUrls]: updateSettingUrls,
  [configType.changeVisitorListColumnOrder]: changeVisitorListColumnOrder,
  [configType.changeVisitorListColumnSize]: changeVisitorListColumnSize,
  [`${web.webResponse}/${chatAction.agentExtendChargeDate}`]: (state, action) =>
    updateIn(state, 'settings', 'dunningInfo', action.payload.extend.dunningInfo),
  [`${web.webResponse}/${chatAction.agentGetSFIntegrationConfig}`]: (state, action) =>
    updateIn(state, 'salesforce', 'integrationConfig', action.payload),
  [`${web.webResponse}/${chatAction.agentGetSFContactFields}`]: (state, action) =>
    updateIn(state, 'salesforce', 'contactFields', action.payload),
  [`${web.webResponse}/${chatAction.agentGetSFAccountFields}`]: (state, action) =>
    updateIn(state, 'salesforce', 'accountFields', action.payload),
  [`${web.webResponse}/${chatAction.agentGetSFLeadFields}`]: (state, action) =>
    updateIn(state, 'salesforce', 'leadFields', action.payload),
  [configType.updatePreference]: (state, action) =>
    Object.assign({}, state, {
      preference: Object.assign({}, state.preference, action.payload),
    }),
  [configType.updateCustomFilter]: (state, action) =>
    Object.assign({}, state, {
      preference: Object.assign({}, state.preference, {
        others: Object.assign({}, state.preference.others, action.payload) }),
    }),
  [constants.switchServer]: (state, action) =>
    updateIn(state, 'ifUseMainServer', action.payload.isUseMainServer),
  [`${web.serverNotAvailable}`]: state => updateServerStatus(state, false),
};

export default (state = defaultState, action, totalState) => {
  if (stateMapping[action.type] === undefined) return state;
  return stateMapping[action.type](state, action, totalState);
};



