import * as _ from 'lodash';
import axios from 'axios';
import { ipcRenderer } from '../services/electronAPI';
import * as actions from '../actions/agent';
import * as localStorage from '../utils/localStorage';
import * as localStorageKeys from '../constants/localStorage';
import * as enums from '../constants/enums';
import login from '../services/login/index';
import * as uiActions from '../actions/ui';
import { errorCode } from '../constants/web';
import * as helper from '../utils/helper';
import * as channelsType from '../constants/enums/channels';
import * as loginConstants from '../constants/login';
import * as config from '../constants/config';
import memoryStore from '../services/memoryStore';
import * as memoryStoreKey from '../constants/memoryStore';
import * as salesforceActions from '../actions/salesforce';
import * as permission from '../constants/enums/liveChatPermission';
import * as simpleEncrypt from '../utils/simpleEncrypt';

/* eslint-disable no-underscore-dangle */
const onSubmitFail = (error, dispatch) => {
  dispatch(uiActions.loginFail(error));
};

const handleLoginResponse = (loginResponse, dispatch) => {
  switch (loginResponse.code) {
    case loginConstants.loginResultCode.loginSuccess:
    case loginConstants.loginResultCode.multipleSites:
      break;
    case loginConstants.loginResultCode.normalErrorAndShowVCode:
    case loginConstants.loginResultCode.verificationCodeNotRightAndShowVCode:
    case loginConstants.loginResultCode.verificationCodeNotRight:
      dispatch(uiActions.showVerificationCode(
        loginResponse.verficationCodeUrl,
        loginResponse.loginSessionId
      ));
      onSubmitFail(loginResponse.message, dispatch);
      break;
    case loginConstants.loginResultCode.agentPasswordExpired:
      dispatch(uiActions.showChangePassword(true));
      onSubmitFail('', dispatch);
      break;
    case loginConstants.loginResultCode.agentPasswordWillExpired:
      dispatch(uiActions.showChangePassword(false));
      onSubmitFail('', dispatch);
      break;
    case loginConstants.loginResultCode.agentPasswordExpiredAndShowVCode:
      dispatch(uiActions.showChangePassword(true));
      dispatch(uiActions.showVerificationCode(
        loginResponse.verficationCodeUrl,
        loginResponse.loginSessionId
      ));
      onSubmitFail('', dispatch);
      break;
    case loginConstants.loginResultCode.agentPasswordWillExpiredAndShowVCode:
      dispatch(uiActions.showChangePassword(false));
      dispatch(uiActions.showVerificationCode(
        loginResponse.verficationCodeUrl,
        loginResponse.loginSessionId
      ));
      onSubmitFail('', dispatch);
      break;
    case loginConstants.loginResultCode.agentIPBlock:
      dispatch(uiActions.showActiveIP(false));
      onSubmitFail('', dispatch);
      break;
    case loginConstants.loginResultCode.adminIPBlock:
      // showActiveIP
      dispatch(uiActions.showActiveIP(true));
      onSubmitFail('', dispatch);
      break;
    case loginConstants.loginResultCode.agentIPBlockAndShowVCode:
      dispatch(uiActions.showActiveIP(false));
      dispatch(uiActions.showVerificationCode(
        loginResponse.verficationCodeUrl,
        loginResponse.loginSessionId
      ));
      onSubmitFail('', dispatch);
      break;
    case loginConstants.loginResultCode.adminIPBlockAndShowVCode:
      dispatch(uiActions.showActiveIP(true));
      dispatch(uiActions.showVerificationCode(
        loginResponse.verficationCodeUrl,
        loginResponse.loginSessionId
      ));
      onSubmitFail('', dispatch);
      break;
    case loginConstants.loginResultCode.sitePurged:
      onSubmitFail('Your account has been deleted. Please try to register again.', dispatch);
      break;
    default:
      if (loginResponse.message) {
        onSubmitFail(loginResponse.message, dispatch);
      }
  }
};

const handleRouteServerError = (error, dispatch) => {
  if (error.data.loginResponse) {
    handleLoginResponse(error.data.loginResponse, dispatch);
  } else if (error.errorCode === errorCode.multipleSites) {
    dispatch(uiActions.showSiteSelect(error.data));
    onSubmitFail('', dispatch);
  } else if (error.data.message) {
    if (error.data.message === 'Network Error') {
      dispatch(uiActions.showSuggestion());
    }
    onSubmitFail(error.data.message, dispatch);
  }
};

const handleChatServerError = (error, dispatch) => {
  if (error.data.loginResponse) {
    handleLoginResponse(error.data.loginResponse, dispatch);
  } else if (error.errorCode === errorCode.agentAlreadyLogin) {
    dispatch(uiActions.showForceLogin(true));
  /*
  } else if (error.errorCode === errorCode.desktopLoginNotAllowed) {
    dispatch(uiActions.showForceLogin(true));
    throw new SubmissionError({ _error: '' });
  */
    onSubmitFail('', dispatch);
  } else if (error.errorCode === errorCode.trialExpired) {
    dispatch(uiActions.showTrialExpired(true));
    onSubmitFail('', dispatch);
  } else if (error.errorCode === errorCode.serverSiteNotActive) {
    const { controlPanel } = memoryStore.get('urls', error.data.siteId, null);
    const billurl = `${controlPanel}/billing/client/billing/billinginfo.aspx?siteid=${error.data.siteId}`;
    dispatch(uiActions.siteNotActive(billurl));
    onSubmitFail('', dispatch);
  } else if (error.errorCode === errorCode.connectionError) {
    dispatch(uiActions.showSuggestion());
    onSubmitFail(error.data.errorString || 'network error', dispatch);
  /*
  } else if (error.errorCode === errorCode.systemUpgrade) {
    dispatch(uiActions.showForceLogin(true));
    throw new SubmissionError({ _error: '' });
  */
  } else if (error.data.errorString) {
    onSubmitFail(error.data.errorString, dispatch);
  } else if (error.data.message) {
    if (error.data.message === 'Network Error') {
      dispatch(uiActions.showSuggestion());
    }
    onSubmitFail(error.data.message, dispatch);
  }
};

const initApp = (result, dispatch) => {
  window.document.title = ` Comm100 Live Chat Agent Console - ${loginConstants.liveChatVisitorMonitorVersion} - Agent ${result.agent.name}`;
  const { settings, agent, preference, visitors, ifUseMainServer, form } = result;
  const currentAgentInfo = Object.assign({}, agent,
    { password: agent.password || '', token: form.token || '', loginWithWebSession: !!form.autoWebLogin });
  dispatch(actions.initCurrentAgent(currentAgentInfo));
  dispatch(actions.initConfig({
    preference,
    settings,
    ifUseMainServer,
  }));
  dispatch(actions.initVisitors(visitors));
  if (window.beta) {
    axios.get(`${loginConstants.portalLivechatUrl}/BetaSurveyHandler.ashx?siteId=${agent.siteId}&agentId=${agent.id}&version=1`);
  }
  memoryStore.set(memoryStoreKey.loginWithWebSession, currentAgentInfo.loginWithWebSession);
};

export const onSubmitSuccess = (result, dispatch) => {
  initApp(result, dispatch);
  if (result.settings.trialInfo &&
    !result.settings.trialInfo.haveBillingInfo &&
    result.settings.trialInfo.daysRemaining > result.settings.trialInfo.trialPeriodReminderDays
  ) {
    dispatch(uiActions.showTrialExpired(
      false,
      result.settings.trialInfo.trialPeriodReminderDays
    ));
  } else if (result.settings.dunningInfo &&
    result.settings.dunningInfo.status !== enums.siteDunningInfoEnum.Normal) {
    dispatch(uiActions.showDunning());
  } else {
    dispatch(uiActions.showVisitor());
    memoryStore.set('loginResponse', null);
  }
  const agentInfo = {
    agent: result.agent,
    customAways: helper.formatAgentStatus(result.settings.siteInfo.ifEnableCustomAwayStatus ?
                                          result.settings.customAways :
                                          []),
  };
  memoryStore.set(memoryStoreKey.ifLogout, false);
  ipcRenderer.send(channelsType.login, agentInfo);
  if (result.settings.dashboard && result.settings.dashboard.length > 0) {
    dispatch(uiActions.switchDashboardMessagesWindow(true));
  }
  if (result.preference.others.ifEnableAutoMonitor &&
      (result.preference.permission & permission.monitorChat)) { // eslint-disable-line
    dispatch(actions.agentMonitorAllChats());
  }
  dispatch(salesforceActions.agentGetSFIntegrationConfig(result.agent.siteId));
  dispatch(salesforceActions.agentGetSFContactFields(result.agent.siteId));
  dispatch(salesforceActions.agentGetSFAccountFields(result.agent.siteId));
  dispatch(salesforceActions.agentGetSFLeadFields(result.agent.siteId));

  if (!process.env.IS_WEB_APP) {
    if (localStorage.getBool(localStorageKeys.ifOpenNewWindow)) {
      dispatch(uiActions.openNewWindow());
    }
  }
};

export const onloginForm = async (form, dispatch, props, isAutoLogin, isForceLogin) => {
  dispatch(uiActions.startSubmitLogin());
  let successFlag = false;
  let resp;
  const options = Object.assign({}, form);
  if (!options.autoWebLogin) {
    const rememberMe = localStorage.getBool(localStorageKeys.rememberMe) || false;
    if (props.siteClick && props.siteId > 0) {
      options.siteId = props.siteId;
    } else if (rememberMe && isAutoLogin) {
      const cacheData = localStorage.getNumber(`${_.trim(form.email)}_siteId`);
      if (cacheData) {
        options.siteId = cacheData;
      }
    }
    if (props.ifShowVerificationCode) {
      options.session = props.sessionId;
    } else {
      options.verificationCode = '';
    }
    options.email = _.trim(form.email);
  }
  if (isForceLogin) {
    options.forceLogin = true;
  } else {
    options.forceLogin = props.ifShowForceLogin;
  }
  try {
    resp = await login(Object.assign({
      routeServer: helper.getUrl(loginConstants.routeServer, config.isSSL),
      backupRouteServer: helper.getUrl(loginConstants.backupRouteServer, config.isSSL),
    }, options), dispatch);
    resp.form = form;
    if (!form.autoWebLogin) {
      const tosave = {
        email: _.trim(form.email),
        password: simpleEncrypt.encrypt(form.password),
        status: form.status,
      };
      localStorage.setObject(localStorageKeys.loginForm, tosave);
    }
    const { loginResponse, agent } = resp;
    if (!loginResponse || loginResponse.code === loginConstants.loginResultCode.loginSuccess ||
      loginResponse.code === loginConstants.loginResultCode.multipleSites) {
      dispatch(uiActions.loginSuccess());
      if (!form.autoWebLogin) {
        localStorage.set(localStorageKeys.rememberMe, form.rememberMe);
        localStorage.set(`${_.trim(agent.email)}_siteId`, agent.siteId);
      }
      successFlag = true;
    } else {
      memoryStore.set('loginResponse', resp);
      handleLoginResponse(loginResponse, dispatch);
    }
  } catch (e) {
    if (e.type === 'routeServerError') {
      handleRouteServerError(e, dispatch);
    } else if (e.type === 'chatServerError') {
      handleChatServerError(e, dispatch);
    } else if (!navigator.onLine) {
      dispatch(uiActions.showSuggestion());
      onSubmitFail('Network Error', dispatch);
    } else if (e.message === 'Network Error') {
      dispatch(uiActions.showSuggestion());
      onSubmitFail('Network Error', dispatch);
    } else if (e.message.indexOf('timeout') >= 0) {
      onSubmitFail('Your login request has timed out. Please try again.', dispatch);
    } else {
      onSubmitFail(e.message, dispatch);
    }
  }
  if (successFlag) {
    onSubmitSuccess(resp, dispatch);
  }
};



