import React from 'react';
import Reload from 'src/components/Widget/Reload';
import Modal from './Modal';

const ModalReload = props => (
  <Modal
    ifOpen
    shouldCloseOnOverlayClick={false}
    ifShowCloseIcon={false}
  >
    <Reload onRetry={props.onRetry} />
  </Modal>
);

export default ModalReload;

ModalReload.propTypes = {
  ifOpen: React.PropTypes.bool.isRequired,
  onRetry: React.PropTypes.func,
};



