import React from 'react';
import classnames from 'classnames';
import * as _ from 'lodash';
import commonStyle from '../CommonResources/common.css';
import IconInput from '../Input/IconInput';
import Select from '../Select/Select';
import Checkbox from '../Checkbox/Checkbox';
import Button from '../Button/Button';
import style from './Page.css';
import * as enums from '../../constants/enums';
import Icon from '../Icon/Icon';
import Link from '../Link/Link';
import * as loginConstants from '../../constants/login';
import * as str from '../../utils/str';
import { shell } from '../../services/electronAPI';

/* eslint-disable no-underscore-dangle */
const validate = (values, ifShowVerificationCode) => {
  const errors = {};
  if (!values.email) {
    errors.email = 'Login Email is Required!';
    errors._error = 'Login Email is Required!';
  } else if (!str.isEmail(_.trim(values.email))) {
    errors.email = 'Login Email is invalid!';
    errors._error = 'Login Email is invalid!';
  }

  if (!values.password) {
    errors.password = 'Password is Required!';
    errors._error = 'Password is Required!';
  }
  if (!values.email && !values.password) {
    errors._error = 'Email and Password are Required!';
  }
  if (ifShowVerificationCode && !values.verificationCode) {
    errors.verificationCode = 'Verification Code is Required!';
    errors._error = 'Verification Code is Required!';
  }
  return errors;
};

class LoginForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      errors: {},
    };

    this.handleValueChange = this.handleValueChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }
  shouldComponentUpdate(nextProps, nextState) {
    if (!_.isEqual(this.props, nextProps)
      || !_.isEqual(this.state.errors, nextState.errors)) {
      return true;
    }
    return false;
  }
  onSubmit() {
    this.props.preHandleSubmit();
    const e = validate(this.props.formValues, this.props.ifShowVerificationCode);
    if (Object.keys(e).length === 0) {
      this.props.handleSubmit();
    }
    this.setState({
      errors: e,
    });
  }
  handleValueChange(name, value) {
    const e = Object.assign({}, this.state.errors);
    if (e[name]) {
      e[name] = '';
    }
    e._error = '';
    const temp = Object.assign({}, this.props.formValues);
    temp[name] = value;
    if (!_.isEqual(temp, this.props.formValues)) {
      this.props.formValuesChange(temp);
      this.setState({
        errors: e,
      });
    }
  }
  render() {
    const props = this.props;
    const error = this.state.errors._error || props.loginSubmitError;
    return (
      <div>
        <div className={style.errorMessageWrapper}>
          <div
            className={style.errorMessage}
            style={{ display: error && !props.ifLoginSubmitting && !props.ifShowSuggestion ? 'inline-block' : 'none' }}
            dangerouslySetInnerHTML={{ __html: error }}
            onMouseDown={(e) => {
              e.preventDefault();
              e.stopPropagation();
              if (e.target.tagName === 'A') {
                const href = e.target.getAttribute('href');
                if (href) {
                  if (process.env.IS_WEB_APP) {
                    window.open(href);
                  } else {
                    shell.openExternal(href);
                  }
                }
              }
            }}
          />
          <div className={style.errorMessage} style={{ display: error && !props.ifLoginSubmitting && props.ifShowSuggestion ? 'inline-block' : 'none' }}>
            {error}
            <Link className={style.fixLink} newWindow href={loginConstants.kb_login_error}>
              How to fix it?
            </Link>
          </div>
          <div className={style.errorMessage} style={{ display: !props.ifLoginSubmitting && !error && props.agentLogout === 2 ? 'inline-block' : 'none' }}>
            <span>You have logged out.</span>
          </div>
          <div className={style.errorMessage} style={{ display: !props.ifLoginSubmitting && !error && props.billUrl ? 'inline-block' : 'none' }}>
            {'We are sorry that you\'re unable to sign in as you have overdue payment. Please'}
            <Link className={style.billLink} newWindow href={props.billUrl}>
              pay off the payment
            </Link>
            to get Live Chat reactivated.
          </div>
        </div>
        <div className={style.login}>
          <div className={style.line}>
            <IconInput
              name="email"
              isDisabled={props.ifLoginSubmitting}
              className={style.inputWidth}
              icon={<Icon type={enums.icons.loginUser} color="#aaa" size="larger" />}
              type="text"
              placeholder="Email"
              input={{ value: this.props.formValues.email || '',
                onChange: e => this.handleValueChange('email', e.target.value) }}
              meta={{ touched: true, invalid: !!this.state.errors.email, error: this.state.errors.email || '' }}
              onKeyDown={(event) => {
                if (event.nativeEvent.keyCode === 13) {
                  this.onSubmit();
                }
              }}
            />
          </div>
          <div className={style.line}>
            <IconInput
              name="password"
              className={style.inputWidth}
              icon={<Icon type={enums.icons.loginPassword} color="#aaa" size="larger" />}
              type="password"
              isDisabled={props.ifLoginSubmitting}
              maxLength={16}
              placeholder="Password"
              input={{ value: this.props.formValues.password || '',
                onChange: e => this.handleValueChange('password', e.target.value) }}
              meta={{ touched: true, invalid: !!this.state.errors.password, error: this.state.errors.password || '' }}
              onKeyDown={(event) => {
                if (event.nativeEvent.keyCode === 13) {
                  this.onSubmit();
                }
              }}
            />
          </div>
          {
            props.ifShowVerificationCode &&
            (<div className={classnames(style.line, style.verificationCode)}>
              <IconInput
                name="verificationCode"
                icon={<Icon type={enums.icons.verifyCode} size="smaller" />}
                type="text"
                placeholder="Verification Code"
                isDisabled={props.ifLoginSubmitting}
                input={{ value: this.props.formValues.verificationCode || '',
                  onChange: e => this.handleValueChange('verificationCode', e.target.value) }}
                meta={{ touched: true, invalid: !!this.state.errors.verificationCode, error: this.state.errors.verificationCode || '' }}
                onKeyDown={(event) => {
                  if (event.nativeEvent.keyCode === 13) {
                    this.onSubmit();
                  }
                }}
              />
              <img
                role="presentation"
                src={props.verificationCodeUrl}
                onClick={props.refreshVCode}
              />
              <Icon
                type={enums.icons.refresh}
                className={style.refreshIcon}
                size="smaller"
                onClick={props.refreshVCode}
              />
            </div>)
          }
          <div className={style.buttonWrapper}>
            <Button
              onClick={this.onSubmit}
              text="LOG IN"
              size="large"
              type="primary"
              className={props.ifLoginSubmitting ? classnames(style.waiting, style.buttonWidth)
                : style.buttonWidth}
              disabled={props.ifLoginSubmitting}
              htmlType="submit"
            />
          </div>
          <div className={classnames(style.optionWrapper, commonStyle.clearfix)}>
            <div className={classnames(style.left, style.marginLeft40)}>
              <Select
                name="status"
                className={style.dropdownlist}
                options={[
                  { text: 'Online', value: enums.agentStatus.online },
                  { text: 'Away', value: enums.agentStatus.away },
                ]}
                width={86}
                input={{ value: this.props.formValues.status || enums.agentStatus.online,
                  onChange: value => this.handleValueChange('status', value) }}
              />
            </div>
            <div className={classnames(style.right, style.marginRight40)}>
              <Checkbox
                name="rememberMe"
                text="Remember Me"
                className={style.checkbox}
                input={{ value: !!this.props.formValues.rememberMe,
                  onChange: value => this.handleValueChange('rememberMe', value) }}
              />
            </div>
          </div>
          <div className={style.forgot}>
            <Link newWindow href={loginConstants.forgotPasswordUrl}>Forgot Password</Link>
          </div>
        </div>
      </div>
    );
  }
}

LoginForm.propTypes = {
  preHandleSubmit: React.PropTypes.func,
  formValues: React.PropTypes.shape({
    rememberMe: React.PropTypes.bool,
    status: React.PropTypes.number,
    verificationCode: React.PropTypes.string,
    email: React.PropTypes.string,
    password: React.PropTypes.string,
  }),
  handleSubmit: React.PropTypes.func.isRequired,
  loginSubmitError: React.PropTypes.string,
  ifLoginSubmitting: React.PropTypes.bool,
  ifShowVerificationCode: React.PropTypes.bool,
  isShowForceLoginModal: React.PropTypes.bool,
  isShowSiteSelectModal: React.PropTypes.bool,
  isShowChangePasswordModal: React.PropTypes.bool,
  isShowIPBlockModal: React.PropTypes.bool,
  verificationCodeUrl: React.PropTypes.string,
  agentLogout: React.PropTypes.number,
  refreshVCode: React.PropTypes.func,
  billUrl: React.PropTypes.string,
  ifShowSuggestion: React.PropTypes.bool,
  formValuesChange: React.PropTypes.func,
};

export default LoginForm;



