import * as _ from 'lodash';
import * as channelsType from '../constants/enums/channels';
import { agentStatus } from '../constants/enums';

const warn = _.once(() => {
  console.warn('Electron API called, but currently running in browser');
});
let timer = null;
const electronAPI = {
  ipcRenderer: {
    on() {
      if (process.env.NODE_ENV !== 'production') {
        warn();
      }
    },
    send(channel, datas) {
      if (channel === channelsType.changeStatus &&
          datas.code === agentStatus.exit) {
        window.open('', '_self').close();
      } else if (channel === channelsType.sendExitFlag) {
        if (timer !== null) {
          clearTimeout(timer);
          timer = null;
        }
        timer = setTimeout(() => {
          window.open('', '_self').close();
        }, 5000);
      }
    },
  },
  shell: {
    openExternal(url) {
      window.open(url);
    },
  },
  quitAndInstall() { },
};
if (!process.env.IS_WEB_APP) {
  const electron = require('electron'); // eslint-disable-line

  electronAPI.ipcRenderer = electron.ipcRenderer;
  electronAPI.shell = electron.shell;
  electronAPI.quitAndInstall = () => {
    electron.ipcRenderer.send(channelsType.quitAndInstall);
  };
}

export const ipcRenderer = electronAPI.ipcRenderer;
export const shell = electronAPI.shell;
export const quitAndInstall = electronAPI.quitAndInstall;



