import * as _ from 'lodash';
import * as visitorsAction from 'src/actions/visitors';
import * as selectors from '../reducers/selectors/index';

let timer = null;

export default (store, threshold = 100) => {
  const unsubscribe = store.subscribe(() => {
    if (timer) return;
    const state = store.getState();
    /**
     * visitors here contains unfinished wrap-up visitors, thus visitors.size is not very accurate
     * but will still use it to compare with currentVisitorNumber, as getting an accurate number
     * requires unnecessary extra calculation. Will use this rough number to determine whether
     * should clean up visitors.
     */
    const visitors = selectors.getVisitors(state);
    const list = selectors.getVisitorList(state);
    const selected = selectors.getSelectedVisitorId(state);
    const currentVisitorNumber = list.length;
    if (visitors.size - currentVisitorNumber >= threshold) {
      timer = setTimeout(() => {
        /**
         * Map, { chatId: visitorId }
         * @todo could be a performance issue, if there are lots of chats. Need investigation.
         */
        const chatVisitors = selectors.getChats(state).map(c => c.get('visitorId'));
        const currentVisitors = selectors.getVisitors(state)
          .filter((visitor, key) => {
            /** unfinished-wrapup visitors should not be cleaned */
            if (_.startsWith(key, 'v')) return false;
            /**
             * selected visitor should not be removed
             * (so that visitor details tab can always show data)
             */
            if (key === selected) return false;
            /** visitor associated with ongoing chat should not be cleaned */
            if (chatVisitors.contains(visitor.get('id'))) return false;
            return true;
          });
        const needToRemove = _.difference([...currentVisitors.keys()], list);
        store.dispatch(visitorsAction.removeVisitors(needToRemove));
        clearTimeout(timer);
        timer = null;
      }, 0);
    }
  });

  return () => {
    if (timer) clearTimeout(timer);
    unsubscribe();
  };
};



