import _ from 'lodash';
import Immutable from 'immutable';

const memoryStore = (function () {
  let cache = Immutable.Map({});

  function get(key, id = null, defaultValue = null) {
    if (!key) {
      throw new Error('key is required');
    }
    const temp = cache.get(key);
    if (!_.isUndefined(temp)) {
      if (id !== null) {
        if (temp.id === id) {
          return temp.data;
        }
        return defaultValue;
      }
      return _.isUndefined(temp.data) ? temp : temp.data;
    }
    return defaultValue;
  }

  function set(key, value, id = null) {
    if (!key && !value) {
      throw new Error('key and value are required');
    }
    if (id !== null) {
      cache = cache.set(key, { id, data: value });
    } else {
      cache = cache.set(key, value);
    }
  }
  return {
    get,
    set,
  };
}());

export default memoryStore;



