import * as types from '../constants/types/chatList';

export const createConfigAction = (code, type) => ({
  type,
  payload: code,
});
export const togglePin = chatId => ({
  type: types.togglePin,
  payload: chatId,
});
export const setSelected = chatId => ({
  type: types.setSelected,
  payload: chatId,
});
/**
 * This is for manual invitation only.
 *
 * It's required to jump to chat when embedded manual invitation is sent out.
 * However, the server only provides visitor id in response. As a result, provide this API
 * here for this feature. See issue 4066 for details.
 * @param {number} visitorId - Visitor id.
 * @return {{type:string,payload:number,meta:number}} result is an action where payload and meta
 * are all visitor id.
 */
export const setSelectedViaVisitorId = visitorId => ({
  type: types.setSelectedViaVisitorId,
  payload: visitorId,
  meta: visitorId,
});
export const wrapUp = visitorId => ({
  type: types.wrapUp,
  payload: visitorId,
});
export const chatItemWidthChanged = info => ({
  type: types.chatItemWidthChanged,
  payload: info,
});



