import _ from 'lodash';
import flow from 'lodash/fp/flow';
import take from 'lodash/fp/take';
import filter from 'lodash/fp/filter';
import map from 'lodash/fp/map';
import flatten from 'lodash/fp/flatten';
import some from 'lodash/fp/some';
import each from 'lodash/fp/each';
import * as notifyManager from '../services/notifyManager';
import * as soundTypes from '../constants/soundType';
import * as web from '../constants/web';
import * as selectors from '../reducers/selectors';
import * as chatAction from '../constants/enums/chatAction';
import * as chatType from '../constants/types/chat';
import * as actions from '../actions/ui';
import * as chatListAction from '../actions/chatList';
import * as visitorStatus from '../constants/enums/visitorStatus';
import * as agentsAction from '../actions/agents';
import { allAvailableId } from '../constants/enums';
import { switchServer } from '../constants/agent';
import * as visitorAudioVideoChatStatus from '../constants/enums/visitorAudioVideoChatStatus';

let waitingIds = {};
let timer = null;
const timerDuration = 6 * 1000;
let commonTimer = null;


function repeatSound(soundType, volumne, store) {
  const state = store.getState();
  const session = selectors.getCurrentAgentSession(state);
  if ((selectors.getIfEnableAutoAcceptChat(state) ||
    selectors.getIfEnableAutoAllocation(state) ||
    Object.keys(waitingIds).length === 0 ||
    !selectors.getNotificationIfSoundOnVolumeBySoundType(state, soundType) ||
    !session
  ) && timer) {
    clearTimeout(timer);
    timer = null;
  } else {
    notifyManager.playSound(soundType, volumne);
    timer = setTimeout(repeatSound, timerDuration, soundType, volumne, store);
  }
  if (!session) waitingIds = {};
}

function formatAgentChatMessage(sender, message, alterMessage) {
  if (message && !_.isEmpty(message)) {
    return `${sender}: ${message}`;
  }
  return `${sender}: ${alterMessage}`;
}

function getAgentchatOptions(state, action, dispatch) {
  const agent = selectors.getAgentById(state, action.payload.agentId);
  notifyManager.showNotification({
    needNotify: true,
    soundType: soundTypes.agentMessage,
    volume: selectors.getVolumeBySoundType(state, soundTypes.agentMessage),
    popupOn: selectors.getNotificationIfPopupOnBySoundType(state, soundTypes.agentMessage),
    soundOn: selectors.getNotificationIfSoundOnVolumeBySoundType(state, soundTypes.agentMessage),
    message: formatAgentChatMessage(agent.name, action.payload.message, action.payload.fileName),
  }, () => {
    dispatch(actions.showAgent());
    if (commonTimer !== null) {
      clearTimeout(commonTimer);
    }
    commonTimer = setTimeout(() => {
      dispatch(agentsAction.updateAgent(`${allAvailableId}_${action.payload.agentId}`));
    }, 300);
  });
}

function getNewResponseNotificationOption(state, { message, chatId }, dispatch) {
  notifyManager.showNotification({
    needNotify: true,
    soundType: soundTypes.newResponse,
    volumn: selectors.getVolumeBySoundType(state, soundTypes.newResponse),
    popupOn: selectors.getNotificationIfPopupOnBySoundType(state, soundTypes.newResponse),
    soundOn: selectors.getNotificationIfSoundOnVolumeBySoundType(state, soundTypes.newResponse),
    message,
  }, () => {
    dispatch(chatListAction.setSelected(chatId));
  });
}

function getChatEndNotificationOption(state, { message, chatId }, dispatch) {
  notifyManager.showNotification({
    needNotify: true,
    soundType: soundTypes.chatEnded,
    volumn: selectors.getVolumeBySoundType(state, soundTypes.chatEnded),
    popupOn: selectors.getNotificationIfPopupOnBySoundType(state, soundTypes.chatEnded),
    soundOn: selectors.getNotificationIfSoundOnVolumeBySoundType(state, soundTypes.chatEnded),
    message,
  }, () => {
    dispatch(chatListAction.setSelected(chatId));
  });
}

function getAudioVideoChatEndNotificationOption(state, { message, chatId }, dispatch) {
  notifyManager.showNotification({
    needNotify: true,
    soundType: soundTypes.audioVideoChatEnd,
    volumn:
    selectors.getVolumeBySoundType(state, soundTypes.audioVideoChatEnd),
    popupOn:
    selectors.getNotificationIfPopupOnBySoundType(state, soundTypes.audioVideoChatEnd),
    soundOn:
    selectors.getNotificationIfSoundOnVolumeBySoundType(state, soundTypes.audioVideoChatEnd),
    message,
  }, () => {
    dispatch(chatListAction.setSelected(chatId));
  });
}

function getChatTransferNotificationOption(state, { message, chatId }, dispatch) {
  notifyManager.showNotification({
    needNotify: true,
    soundType: soundTypes.transferingChat,
    volumn: selectors.getVolumeBySoundType(state, soundTypes.transferingChat),
    popupOn: selectors.getNotificationIfPopupOnBySoundType(state, soundTypes.transferingChat),
    soundOn: selectors.getNotificationIfSoundOnVolumeBySoundType(state, soundTypes.transferingChat),
    message,
  }, () => {
    dispatch(chatListAction.setSelected(chatId));
  });
}

function getChatMessageNotificationOption(state, action, dispatch) {
  const type = action.payload.type;
  const message = action.payload.message;
  const chatId = selectors.getVisitorChatId(state, action.payload.visitorId);
  if (type === chatAction.systemEndChat) {
    getChatEndNotificationOption(state, { message, chatId }, dispatch);
  } else if (type === chatAction.visitorAudioChatStop
    || type === chatAction.visitorVideoChatStop
    || type === chatAction.systemAudioChatEnd
    || type === chatAction.systemVideoChatEnd) {
    getAudioVideoChatEndNotificationOption(state, { message, chatId }, dispatch);
  } else if (type === chatAction.visitorSendFile
    || type === chatAction.visitorSendImage
    || type === chatAction.visitorAddTextMessage
    || type === chatAction.visitorSetName
    || type === chatAction.visitorSetEmail
    || type === chatAction.visitorAudioChatRequest
    || type === chatAction.visitorVideoChatRequest
    || type === chatAction.visitorAudioChatCancelRequest
    || type === chatAction.visitorVideoChatCancelRequest
    || type === chatAction.visitorAudioChatRefuse
    || type === chatAction.visitorVideoChatRefuse
    || type === chatAction.visitorAudioChatAccept
    || type === chatAction.visitorVideoChatAccept) {
    getNewResponseNotificationOption(
      state,
      {
        message: type === chatAction.visitorAddTextMessage ?
          `${action.payload.sender}: ${message}` :
          message,
        chatId,
      },
      dispatch
    );
  } else if (type === chatAction.agentTransferChat) {
    getChatTransferNotificationOption(state, { message, chatId }, dispatch);
  }
}

function selectInComingChat(dispatch, chatId) {
  if (commonTimer !== null) {
    clearTimeout(commonTimer);
  }
  commonTimer = setTimeout(() => {
    dispatch(chatListAction.setSelected(chatId));
  }, 500);
}

function getChatInQueueNotificationOption(state, action, dispatch, store) {
  const volumne = selectors.getVolumeBySoundType(state, soundTypes.chatInQueue);
  const visitorId = action.payload;
  if (!waitingIds[visitorId]) {
    waitingIds[visitorId] = true;
    if (!timer) {
      timer = setTimeout(repeatSound, timerDuration, soundTypes.chatInQueue, volumne, store);
    }
  }

  notifyManager.showNotification({
    needNotify: true,
    soundType: soundTypes.chatInQueue,
    volumn: volumne,
    popupOn: selectors.getNotificationIfPopupOnBySoundType(state, soundTypes.chatInQueue),
    soundOn: selectors.getNotificationIfSoundOnVolumeBySoundType(state, soundTypes.chatInQueue),
    message: 'You‘ve got a new chat request.', // move to language
  }, () => {
    dispatch(actions.showChat());
    selectInComingChat(dispatch, action.meta);
  });
}

function getWaitingForChatNotificationOption(state, action, dispatch, store) {
  const volumne = selectors.getVolumeBySoundType(state, soundTypes.incomingChat);
  const visitorId = action.payload;
  if (!waitingIds[visitorId]) {
    waitingIds[visitorId] = true;
    if (!timer) {
      timer = setTimeout(repeatSound, timerDuration, soundTypes.incomingChat, volumne, store);
    }
  }

  notifyManager.showNotification({
    needNotify: true,
    soundType: soundTypes.incomingChat,
    volumn: volumne,
    popupOn: selectors.getNotificationIfPopupOnBySoundType(state, soundTypes.incomingChat),
    soundOn: selectors.getNotificationIfSoundOnVolumeBySoundType(state, soundTypes.incomingChat),
    message: 'You‘ve got a new chat request.', // move to language
  }, () => {
    dispatch(actions.showChat());
    selectInComingChat(dispatch, action.meta);
  });
}

function getNewVisitor(state, action, dispatch) {
  notifyManager.showNotification({
    needNotify: true,
    soundType: soundTypes.visitorEnterSite,
    volumn: selectors.getVolumeBySoundType(state, soundTypes.visitorEnterSite),
    popupOn: selectors.getNotificationIfPopupOnBySoundType(state, soundTypes.visitorEnterSite),
    soundOn:
    selectors.getNotificationIfSoundOnVolumeBySoundType(state, soundTypes.visitorEnterSite),
    message: 'A new visitor lands on your site.',
  }, () => {
    dispatch(actions.showVisitor());
  });
}

function getSegmentChanges(state, visitorObject, dispatch, getSegmentsByIds) {
  notifyManager.showNotification({
    needNotify: true,
    soundType: soundTypes.segmentation,
    volumn: selectors.getVolumeBySoundType(state, soundTypes.segmentation),
    popupOn: selectors.getNotificationIfPopupOnBySoundType(state, soundTypes.segmentation),
    soundOn:
      selectors.getNotificationIfSoundOnVolumeBySoundType(state, soundTypes.segmentation),
    message: 'A visitor with segment(s) below has arrived: ',
    segments: getSegmentsByIds(visitorObject.segmentIds),
  }, () => {
    dispatch(actions.showVisitor());
  });
}

function getException(state) {
  notifyManager.showNotification({
    needNotify: true,
    soundType: soundTypes.exception,
    volumn: selectors.getVolumeBySoundType(state, soundTypes.exception),
    popupOn: selectors.getNotificationIfPopupOnBySoundType(state, soundTypes.exception),
    soundOn: selectors.getNotificationIfSoundOnVolumeBySoundType(state, soundTypes.exception),
    message: 'An exception occured.',
  });
}

function notifySwitchServer(isUseMainServer) {
  notifyManager.showNotification({
    message: isUseMainServer ? 'Live Chat has switched back to primary server.'
      : 'Live Chat has switched to secondary server.',
  });
}

function beforeUpdate(store, next, action) {
  const state = store.getState();

  selectors.checkSoundExisted(state, soundTypes.exception);
  if (action.type.indexOf(web.webResponse) === 0) {
    if (action.type.indexOf(`${web.webResponse}/agentchat/`) === 0) {
      getAgentchatOptions(state, action, next);
    } else if (action.type.indexOf(`${web.webResponse}/chat/`) === 0) {
      getChatMessageNotificationOption(state, action, next, store);
    } else if (action.type.indexOf(`${web.webResponse}/${chatAction.agentGetVisitors}`) === 0) {
      const { visitors: { full, moreDiff } } = action.payload;
      if (full.length !== 0) {
        flow(
          filter(v => !selectors.getVisitor(state, v.id)),
          take(3),
          each(() => getNewVisitor(state, undefined, next))
        )(full);
      }
      if (moreDiff.length !== 0) {
        const agentId = selectors.getCurrentAgentId(state);
        const getSegmentsById = selectors.getSegmentsByIds.bind(null, state);
        const segments = selectors.getSegmentations(state);
        const segmentChanges = _.filter(moreDiff, (v) => {
          const id = v.id;
          const segmentIds = v.segmentIds;
          const currentVisitor = selectors.getVisitor(state, id);
          const currentSegmentIds = currentVisitor.get('segmentIds');
          const diff = _.difference(segmentIds, currentSegmentIds);
          if (diff.length <= 0) return false;
          return flow(
            map(segId => _.find(segments, seg => seg.id === segId)),
            map(seg => (seg !== undefined ? seg.alertToIds : [])),
            flatten,
            some(alertToId => alertToId === agentId)
          )(diff);
        });
        _.each(segmentChanges, v => getSegmentChanges(state, v, next, getSegmentsById));
      }
    }
  } else if (action.type === web.webError && action.payload.errorCode === 'sessionTimeout') {
    getException(state);
  } else if (action.type === switchServer) {
    notifySwitchServer(action.payload.isUseMainServer);
  }
}

const checkIfAgentAudioVideoRequesting = (state, agentId) =>
  selectors.getChattingVisitors(state).some((v) => {
    const activeAgents = v.get('chatActiveAgents') || [];
    if (activeAgents.indexOf(agentId) === -1) return false;

    const avStatus = v.get('audioVideoChatStatus');
    const avChatAgentId = v.get('audioVideoChatAgentId');
    return (((avStatus === visitorAudioVideoChatStatus.agentRequestAudioChat
      || avStatus === visitorAudioVideoChatStatus.agentRequestVideoChat)
      && (agentId === avChatAgentId))
      || avStatus === visitorAudioVideoChatStatus.visitorRequestAudioChat
      || avStatus === visitorAudioVideoChatStatus.visitorRequestVideoChat);
  });

function playAudioVideoRequestSound(state) {
  const soundOn =
    selectors.getNotificationIfSoundOnVolumeBySoundType(state, soundTypes.audioVideoChatRequest);
  const volumne =
    selectors.getVolumeBySoundType(state, soundTypes.audioVideoChatRequest);
  if (soundOn) {
    notifyManager.playSound(soundTypes.audioVideoChatRequest, volumne);
  }
}

let audioVideoRequestSoundTimer;
function startPlayAudioVideoRequestSound(state) {
  selectors.checkSoundExisted(state, soundTypes.exception);

  if (checkIfAgentAudioVideoRequesting(state, selectors.getCurrentAgentId(state))) {
    if (audioVideoRequestSoundTimer == null) {
      playAudioVideoRequestSound(state);
      audioVideoRequestSoundTimer = setInterval(playAudioVideoRequestSound.bind(null, state), 4000);
    }
  } else if (audioVideoRequestSoundTimer != null) {
    clearInterval(audioVideoRequestSoundTimer);
    audioVideoRequestSoundTimer = null;
  }
}

function afterUpdate(store, next, action) {
  const state = store.getState();
  if (action.type === chatType.newChat) {
    const visitorId = action.payload;
    const visitor = selectors.getVisitor(state, visitorId);
    const status = visitor.get('status');
    if (status === visitorStatus.waitingForChat || status === visitorStatus.transferring) {
      if (selectors.getIfEnableAutoAcceptChat(state) ||
        selectors.getIfEnableAutoAllocation(state)) {
        getChatInQueueNotificationOption(state, action, next, store);
      } else {
        getWaitingForChatNotificationOption(state, action, next, store);
      }
    } else {
      getWaitingForChatNotificationOption(state, action, next, store);
    }
  } else if (action.type.indexOf(`${web.webResponse}/${chatAction.agentGetVisitors}`) === 0) {
    _.each(Object.keys(waitingIds), (visitorId) => {
      const visitor = selectors.getVisitor(state, +visitorId);
      if (!visitor) return;
      const status = visitor.get('status');
      if (status !== visitorStatus.waitingForChat && status !== visitorStatus.transferring) {
        delete waitingIds[visitorId];
      }
    });

    startPlayAudioVideoRequestSound(state);
  }

  if (action.type === chatType.deleteChat && selectors.getChat(state, action.meta)) {
    const visitorId = selectors.getVisitorIdByChatId(state, action.meta);
    if (waitingIds[visitorId]) delete waitingIds[visitorId];
  }
}

export default store => next => (action) => {
  if (action.type === 'batch') {
    _.each(action.payload, innerAction => beforeUpdate(store, next, innerAction));
  } else {
    beforeUpdate(store, next, action);
  }

  const result = next(action);

  if (action.type === 'batch') {
    _.each(action.payload, innerAction => afterUpdate(store, next, innerAction));
  } else {
    afterUpdate(store, next, action);
  }

  return result;
};



