/* eslint-disable compat/compat */
import * as axios from 'axios';
import * as browser from 'src/utils/browser';
import * as reporter from 'src/utils/reporter';
import swURL from 'file-loader?name=../sw.js!./serviceWorker/sw';
import swConfigURL from 'file-loader?name=../sw.json!./serviceWorker/sw.json';

export function post(command) {
  if (!browser.isServiceWorkerSupport) return;
  /** service worker is not in controll yet (possible refreshing page required) */
  if (!navigator.serviceWorker.controller) return;
  navigator.serviceWorker.controller.postMessage(command);
}

function unregister() {
  navigator.serviceWorker.getRegistrations()
    .then((registrations) => {
      Array.from(registrations).forEach(reg => reg.unregister());
    });
}

export function initialize() {
  if (!browser.isServiceWorkerSupport) return;
  axios.get(swConfigURL).then((response) => {
    /**
     * unregister service worker if sw.json does not contain useSW attribute or
     * attribute is not `true`.
     *
     * However, for debugging purpose, will always register service worker, if
     * debugSW=1 is found in URL.
     */
    if (
      !(location.search && location.search.indexOf('debugSW=1') > 0) &&
      (!response.data || !response.data.useSW)
    ) {
      unregister();
    } else {
      navigator.serviceWorker.register(swURL)
        .catch((e) => {
          reporter.error(e, 'serviceWorker', 17, 0);
        });
    }
  }).catch(() => {
    unregister();
  });
}



