import { errorCode } from '../../constants/web';
import { checkTimeout } from '../../utils/time';

// 累计网络错误指标，报告错误
export default class ConnectionErrorReporter {
  constructor(options) {
    this.options = options;
    this.reset();
  }

  // 清除累计错误
  reset() {
    this.errorTimes = 0;
    this.lastSuccessTime = new Date();
  }

  // 记录错误指标，连续错误一定次数或者一段时间就报错
  record() {
    this.errorTimes += 1;

    if (this.errorTimes > this.options.connectionErrorCount
      && checkTimeout(this.lastSuccessTime, this.options.connectionErrorTimeout)
    ) {
      this.options.onreport({
        errorCode: errorCode.connectionError,
        errorString: 'Connection Error',
      });
    }

    if (checkTimeout(this.lastSuccessTime, this.options.maxonServerErrorCheckPoint)) {
      this.options.onreport({
        errorCode: errorCode.maxonServerCheck,
        errorString: '',
      });
    }
  }
}



