import * as types from 'src/constants/types/chat';
// import { switchServer } from 'src/constants/agent';
import * as selectors from './selectors/index';

const defaultState = { };

const stateMapping = {
  [types.deleteChat](state, action) {
    if (!state[action.meta]) return state;
    const ret = Object.assign({}, state);
    delete ret[action.meta];
    return ret;
  },
  [types.tryCloseChat](state, action, totalState) {
    if (!selectors.getChat(totalState, action.meta)) {
      const ret = Object.assign({}, state);
      delete ret[action.meta];
      return ret;
    }
    return state;
  },
  [types.updateContent](state, action) {
    const ret = Object.assign({}, state);
    ret[action.meta] = action.payload;
    return ret;
  },
  [types.insertContent](state, action) {
    const ret = Object.assign({}, state);
    ret[action.meta] = action.payload;
    return ret;
  },
  // [`${switchServer}`](state) {
  //   /** @todo useless? */
  //   return state;
  // },
};

export default (state = defaultState, action, totalState) => {
  if (stateMapping[action.type] === undefined) return state;
  return stateMapping[action.type](state, action, totalState);
};



