import React from 'react';
import Button from 'src/components/Button/Button';
import Icon from 'src/components/Icon/Icon';
import { icons } from 'src/constants/enums';
import ConfirmUpgrade from 'src/components/Modal/ConfirmUpgrade/ConfirmUpgrade';
import styles from './Header.css';

class Upgrade extends React.Component {
  constructor(props) {
    super(props);
    this.state = { showModal: false };
    this.show = this.toggle.bind(this, true);
    this.hide = this.toggle.bind(this, false);
  }

  toggle(flag) {
    this.setState({ showModal: flag });
  }

  render() {
    return (
      <div>
        <Button
          text="Upgrade"
          onClick={this.show}
          className={styles.iconButton}
          style={{ color: '#329fd9' }}
        >
          <Icon size={16} type={icons.upgrade} className={styles.statusIcon} />
        </Button>
        {this.state.showModal &&
          <ConfirmUpgrade ifOpen={this.state.showModal} onCancel={this.hide} />
        }
      </div>
    );
  }
}

export default Upgrade;



