import * as _ from 'lodash';
import memoize from 'lru-memoize';
import * as filterOperatorType from 'src/constants/enums/filterOperatorType';
import * as filterFieldType from 'src/constants/enums/filterFieldType';
import * as chats from './chats';
import * as ui from './ui';
import * as visitors from './visitors';
import * as config from './config';
import * as agents from './agents';
import * as helper from '../../utils/helper';
import * as columnType from '../../constants/enums/columnType';
import * as visitorTab from '../../constants/enums/visitorTab';
import * as visitorStatus from '../../constants/enums/visitorStatus';
import * as visitorAudioVideoChatStatus from '../../constants/enums/visitorAudioVideoChatStatus';
import * as common from '../../utils/common';
import sort from './sortHelper';

export const getSelectedChat = state => chats.getChat(state, ui.getSelectedChatId(state));

export const getVisitorByChatId = (state, chatId) => {
  const chat = chats.getChat(state, chatId);
  const visitorId = chat.get('visitorId');
  const key = chat.get('isUnfinishedWrapup') ? `v${visitorId}c${chatId}` : visitorId;
  return visitors.getVisitor(state, key);
};
export const getTransferOnlineDepartments = (state, visitor) => {
  if (!visitor) return [];
  const departments = config.getDepartments(state);
  return departments.filter(
    department => (department.ifOnline && department.id !== visitor.get('departmentId')));
};
export const getVisitorOfSelectedChat = (state) => {
  const chatId = ui.getSelectedChatId(state);
  if (!chatId) {
    if (process.env.NODE_ENV !== 'production') {
      console.error(
        '[WARNING]: getting visitor of selected chat failed, as selected chat cannot be found.'
      );
    }
    return undefined;
  }
  return getVisitorByChatId(state, chatId);
};

export const getLocalChats = (state) => {
  const agentId = agents.getCurrentAgentId(state);
  return chats.getChats(state)
    .filter((ch) => {
      if (ch.get('isUnfinishedWrapup')) return false;
      const visitor = getVisitorByChatId(state, ch.get('id'));
      return common.isChattingWithAgent(visitor, agentId) ||
        common.isMonitorWithAgent(visitor, agentId) ||
        common.checkIfWaitingVisitorAcceptInvitationFromMe(visitor, agentId);
    })
    .map(ch => ({
      visitorId: ch.get('visitorId'),
      maxMessageId: ch.get('maxMessageId'),
    }))
    .toArray();
};

export const ifCurrentAgentChatting =
  state => chats.getChats(state)
    .map(ch => visitors.getVisitor(state, ch.get('visitorId')))
    .some(v => v && (
      v.get('status') === visitorStatus.chatting || v.get('status') === visitorStatus.transferring)
    );

export const ifRequireWrapup = (state, chatId, visitor = null) => { // TODO
  let tempVisitor = visitor;
  if (!chats.getChat(state, chatId)) return false;
  if (tempVisitor === null) {
    tempVisitor = getVisitorByChatId(state, chatId);
  }
  const plan = config.getCodePlan(state, tempVisitor.get('codePlanId'));

  return config.getSiteInfo(state).ifEnableWrapupChat &&
         tempVisitor.get('chatInvolvedAgents')[0] === agents.getCurrentAgentId(state) &&
         !chats.getWrapupSubmitted(state, chatId) &&
         (chats.getIsUnfinishedWrapup(state, chatId) ||
         (_.isPlainObject(plan) && plan.ifWrapupRequired));
};

export const hasUnfinishedWrapup = (state) => {
  const unfinishedWrapupChat = chats.getChats(state).find(chat =>
    ifRequireWrapup(state, chat.get('id'), getVisitorByChatId(state, chat.get('id')))
  );
  return _.isObject(unfinishedWrapupChat);
};

/**
 * @todo use lru-memoize instead
 * @param {object} state - Total state
 * @return {{code:number,desc:string}[]} agent status list
 */
export const getAgentStatus = (function () {
  let cachedCustomAway;
  let cache;
  return (state) => {
    const customAways = config.getCustomAways(state);
    if (cachedCustomAway === customAways) return cache;
    const status = helper.formatAgentStatus(customAways);
    cache = status;
    cachedCustomAway = customAways;
    return status;
  };
}());

export const getInvolvedAgentNames = (state, visitorId) => {
  const involvedAgentIds = visitors.getVisitorProp(state, visitorId, 'chatInvolvedAgents');
  const agentList = config.getAgents(state);
  return _.map(
    involvedAgentIds,
    id => _.find(agentList, agent => agent.id === id).name
  ).join(', ');
};

const getColumns = memoize(1)((columns, {
  ifEnableCustomVariables,
  ifEnableSegmentation,
  ifDepartmentEnabled,
  ifCanCustomAutoInvitationRules,
  ifTicketOpen,
  ifEnableMultipleCodePlan,
}) =>
  columns.filter((column) => {
    if (!column.ifVisible) return false;
    if (!ifEnableSegmentation && column.enumColumn === columnType.visitorSegmentation) {
      return false;
    }
    if (!ifEnableCustomVariables && column.enumColumn === columnType.customVariable) {
      return false;
    }
    if (!ifDepartmentEnabled && column.enumColumn === columnType.department) {
      return false;
    }
    if (!ifCanCustomAutoInvitationRules && column.enumColumn === columnType.autoInvitation) {
      return false;
    }
    if (!ifTicketOpen && column.enumColumn === columnType.ticketId) {
      return false;
    }
    if (!ifEnableMultipleCodePlan && column.enumColumn === columnType.campaign) {
      return false;
    }
    return true;
  }));
/**
 * @param {object} state - Total state.
 * @return {{customVariableId:number,enumColumn:number,ifVisible:boolean,width:number}[]} columns
 */
export const getVisibleVisitorListColumns = state =>
  getColumns(
    config.getVisitorListColumns(state),
    config.getSiteInfo(state),
  );

/**
 * Get column that need to be used for sort.
 *
 * @param {object} state - Total state.
 * @return {object} column
 */
export const getVisitorUiSortColumn = (state) => {
  const c = ui.getVisitorUiSortColumnConfig(state);
  return _.find(config.getVisitorListColumns(state), column => _.isMatch(column, c));
};

export const getCurrentAgent = state =>
  Object.assign({
    id: agents.getCurrentAgentId(state),
    name: agents.getCurrentAgentName(state),
    email: agents.getCurrentAgentEmail(state),
  }, config.getPreference(state));

export const getRecoverChats =
  state => chats.getChats(state)
    .filter((ch) => {
      if (ch.get('isUnfinishedWrapup')) return false;
      const visitor = getVisitorByChatId(state, ch.get('id'));
      const agentId = agents.getCurrentAgentId(state);
      const status = visitor.get('status');
      return ((status === visitorStatus.chatting || status === visitorStatus.transferring) &&
        common.isAgentInChatActiveList(visitor, agentId)) ||
        status === visitorStatus.waitingForChat;
    })
    .map((ch) => {
      /** @todo performance issue */
      const visitor = visitors.getVisitorObject(state, ch.get('visitorId'));
      const messages = (visitor.status === visitorStatus.waitingForChat) ? null :
        chats.getRecoverMessages(state, ch.get('id'));
      return {
        visitor,
        messages,
      };
    })
    .toArray();

export const getSimulateVisitorLink = (state) => {
  const firstCampaign = _.first(config.getCampaigns(state));
  const url = config.getLiveChatFuncUrl(state);
  const siteId = config.getSiteId(state);
  return `${url}/PlanPreview.aspx?codePlanId=${firstCampaign.id}&siteId=${siteId}&ssl=1`;
};

export const getInstallCodeLink = (state) => {
  const url = config.getLiveChatFuncUrl(state);
  const siteId = config.getSiteId(state);
  return `${url}/WebsiteIntegration.aspx?siteId=${siteId}`;
};

const isChat = status => status === visitorStatus.chatting ||
  status === visitorStatus.chatEnded || status === visitorStatus.transferring ||
  status === visitorStatus.waitingForChat;

const extractValueForCustomFilter = (visitor, filter, state) => {
  switch (filter.field) {
    case filterFieldType.status: {
      const status = visitor.get('status');
      if (status === visitorStatus.manuallyInvitedAndWaitingVisitor) {
        return visitorStatus.invited;
      }
      return status;
    }
    case filterFieldType.visitorSegment: {
      return visitor.get('segmentIds');
    }
    case filterFieldType.agents: {
      return visitor.get('chatInvolvedAgents');
    }
    case filterFieldType.department: {
      return visitor.get('departmentId');
    }
    case filterFieldType.productService: {
      return visitor.get('prechatProductService');
    }
    case filterFieldType.autoInvitation: {
      return visitor.get('autoInvitationId');
    }
    case filterFieldType.campaign: {
      return visitor.get('codePlanId');
    }
    case filterFieldType.city: {
      return visitor.get('city');
    }
    case filterFieldType.countryRegion: {
      return visitor.get('country');
    }
    case filterFieldType.customVariable: {
      const list = visitor.get('customVariables');
      const customVariableId = filter.variableId;
      const customVariable = config.getCustomVariableById(state, customVariableId);
      return _.get(_.find(list, element => element.name === customVariable.name), 'value');
    }
    default:
      return null;
  }
};

const operatorIs = (value, candidates, ignoreCase) =>
  _.some(candidates, (candidate) => {
    if (ignoreCase) return value.toString().toLowerCase() === candidate.toString().toLowerCase();
    return value === candidate;
  });

const operatorIsNot = _.negate(operatorIs);

const operatorIsLessThan = (value, candidates) =>
  _.some(candidates, candidate => +value < +candidate);

const operatorIsMoreThan = (value, candidates) =>
  _.some(candidates, candidate => +value > +candidate);

const operatorContains = (value, candidates) =>
  _.some(candidates, (candidate) => {
    if (_.isString(value)) return value.indexOf(candidate) >= 0;
    if (_.isArray(value)) return _.indexOf(value, candidate) >= 0;
    return false;
  });

const operatorNotContains = _.negate(operatorContains);

const operatorTypeMapping = {
  [filterOperatorType.is]: operatorIs,
  [filterOperatorType.isNot]: operatorIsNot,
  [filterOperatorType.isLessThan]: operatorIsLessThan,
  [filterOperatorType.isMoreThan]: operatorIsMoreThan,
  [filterOperatorType.contains]: operatorContains,
  [filterOperatorType.notContains]: operatorNotContains,
};

const matchCustomFilter = (visitor, filter, state) => {
  const field = filter.field;
  if (field === filterFieldType.status) {
    // status 包括两个字段：visitor.status和visitor.audioVideoChatStatus
    const status = visitor.get('status');
    const avStatus = visitor.get('audioVideoChatStatus');
    let status2 = -1;
    if (avStatus === visitorAudioVideoChatStatus.visitorRequestAudioChat) {
      status2 = visitorStatus.audioChatRequesting;
    } else if (avStatus === visitorAudioVideoChatStatus.audioChatting) {
      status2 = visitorStatus.audioChatting;
    } else if (avStatus === visitorAudioVideoChatStatus.visitorRequestVideoChat) {
      status2 = visitorStatus.videoChatRequesting;
    } else if (avStatus === visitorAudioVideoChatStatus.videoChatting) {
      status2 = visitorStatus.videoChatting;
    }
    return operatorIs(status, filter.values) || operatorIs(status2, filter.values);
  }
  const value = extractValueForCustomFilter(visitor, filter, state);
  const ignoreCase = (
      field === filterFieldType.city ||
      field === filterFieldType.state ||
      field === filterFieldType.countryRegion
    );
  const filterfunc = operatorTypeMapping[filter.op];
  if (!filterfunc) {
    if (process.env.NODE_ENV !== 'production') {
      console.error(`[WARNING]: detect unkown custom filter operator type: ${filter.op}`);
    }
    return false;
  }
  return filterfunc(value, filter.values, ignoreCase);
};

export const getSortedVisitorList = state => sort(
  state.visitor.list,
  getVisitorUiSortColumn(state),
  ui.getVisitorUiSortOrder(state),
  state
);

export const getVisibleVisitorsList = (state) => {
  const visitorList = getSortedVisitorList(state);
  const allVisitors = visitors.getVisitors(state);
  const filter = ui.getVisitorsUiConfig(state).filter;
  if (filter === visitorTab.allChats) {
    return _.filter(
      visitorList,
      visitorId => isChat(allVisitors.get(visitorId).get('status'))
    );
  } else if (filter === visitorTab.myChats) {
    const agentId = agents.getCurrentAgentId(state);
    return _.filter(visitorList, (visitorId) => {
      const visitor = allVisitors.get(visitorId);
      const status = visitor.get('status');
      if (!isChat(status)) return false;
      if (status === visitorStatus.waitingForChat || status === visitorStatus.transferring) {
        return true;
      }
      const chatMonitorAgents = visitor.get('chatMonitorAgents');
      const chatInvolvedAgents = visitor.get('chatInvolvedAgents');
      return _.indexOf(chatMonitorAgents, agentId) >= 0 ||
        _.indexOf(chatInvolvedAgents, agentId) >= 0;
    });
  } else if (filter === visitorTab.customized) {
    const customFilter = config.getCustomFilter(state);
    if (customFilter.length === 0) return visitorList;
    return _.filter(visitorList, (visitorId) => {
      const visitor = allVisitors.get(visitorId);
      return _.every(customFilter, f => matchCustomFilter(visitor, f, state));
    });
  }
  return visitorList;
};

export const checkVisitorCanMonitor = (state, v, agentId) =>
  visitors.checkIfVisitorStatusChatting(v.get('status')) &&
  common.isHavePermissionDealChat(v.get('status'), v.chatInvolvedAgents,
    config.getDepartmentById(state, v.get('departmentId')), agentId) &&
  !common.isAgentInChatActiveList(v, agentId) &&
  !common.isAgentInMonitorList(v, agentId) &&
  !common.isAgentInInvolvedList(v, agentId);

export const getAllCanMonitorChatVisitors = (state) => {
  const agentId = agents.getCurrentAgentId(state);
  return visitors.getVisitors(state)
    .filter(v => visitors.checkIfVisitorStatusChatting(v.get('status')) &&
      checkVisitorCanMonitor(state, v, agentId)) // todo
    .map(v => v.get('id'));
};

/**
 * Find all canned messages with shortcut defined.
 *
 * @param {object} state - Complete state from store.
 * @return {Array.<{shortcut:string,content:string}>} All canned messages with shortcut defined.
 */
export const getCannedMessagesWithShortcuts = state => (
  config.getIfEnableCannedMessageShortcuts(state) ?
    _.filter(config.getCannedMessages(state), cannedMessage => cannedMessage.shortcut)
      .sort((msgA, msgB) => {
        if (msgA.shortcut === msgB.shortcut) {
          if (msgA.content === msgB.content) return 0;
          return msgA.content > msgB.content ? 1 : -1;
        }
        return msgA.shortcut > msgB.shortcut ? 1 : -1;
      }
      ) :
    []
);

export const ifChatExists = (state, chatId) => {
  const chat = chats.getChat(state, chatId);
  const visitorId = chat ? chat.get('visitorId') : undefined;
  const visitor = visitorId ? visitors.getVisitor(state, visitorId) : undefined;
  return visitor
    && visitors.isVisitorExists(state, visitorId)
    && visitor.get('chatId') === chatId;
};

export const getChattingVisitors = state =>
  chats.getChats(state)
    .map(c => visitors.getVisitor(state, c.get('visitorId')).filter(v => !!v));

export const getIfAgentAudioVideoChatting = (state, agentId) =>
  getChattingVisitors(state).some((v) => {
    const avStatus = v.get('audioVideoChatStatus');
    const avChatAgentId = v.get('audioVideoChatAgentId');
    return (avStatus === visitorAudioVideoChatStatus.audioChatting
      || avStatus === visitorAudioVideoChatStatus.videoChatting)
      && (agentId === avChatAgentId);
  });

export const getIfAgentRequestAudioVideoChat = (state, agentId) =>
  getChattingVisitors(state).some((v) => {
    const avStatus = v.get('audioVideoChatStatus');
    const avChatAgentId = v.get('audioVideoChatAgentId');
    return (avStatus === visitorAudioVideoChatStatus.agentRequestAudioChat
      || avStatus === visitorAudioVideoChatStatus.agentRequestVideoChat)
      && (agentId === avChatAgentId);
  });




