import * as constants from '../constants/agent';
import { webResponse } from '../constants/web';
import { agentGetVisitors } from '../constants/enums/chatAction';

/**
 * @module actions/agent
 */

/**
 * Update agent session action
 * @param {object} info - agent info
 * @return {object} action
 */
export const initCurrentAgent = info => ({
  type: constants.initCurrentAgent,
  payload: info,
});

export const initConfig = config => ({
  type: constants.initConfig,
  payload: config,
});

export const updatePreference = preference => ({
  type: constants.updatePreference,
  payload: preference,
});

export const initVisitors = visitors => ({
  type: `${webResponse}/${agentGetVisitors}`,
  payload: { visitors },
});

export const serverSwitched = (isUseMainServer, session, recoverVistiorIds) => ({
  type: constants.switchServer,
  payload: { isUseMainServer, session, recoverVistiorIds },
});

export * from './web';



