import * as _ from 'lodash';
import memoize from 'lru-memoize';
import * as selectors from '../../reducers/selectors';
import { agentStatus, icons } from '../../constants/enums';

export const getCurrentAgentId = state => state.agent.id;
export const getCurrentAgentName = state => state.agent.name;
export const getCurrentAgentEmail = state => state.agent.email;
export const getCurrentAgentStatus = state => state.agent.status;
export const getCurrentAgentSession = state => state.agent.session;
export const getCurrentAgentPassword = state => state.agent.password;
export const getIsAuthenticated = state => getCurrentAgentSession(state) !== '';

export const getOnlineAvailableAgents = (state, visitor) => {
  const chatActiveAgents = visitor.get('chatActiveAgents');
  return state.config.settings.agents.filter(
    agent => (
      agent.status === agentStatus.online && chatActiveAgents.indexOf(agent.id) < 0
  ));
};

export const getAwayAvailableAgents = (state, visitor) => {
  const chatActiveAgents = visitor.get('chatActiveAgents');
  return state.config.settings.agents.filter(
    agent => (
      (agent.status === agentStatus.away || agent.status >= 100)
        && chatActiveAgents.indexOf(agent.id) < 0
  ));
};

const getAllAvailableAgentsInternal = memoize(1)((chatActiveAgents, agents, customAways) => {
  const filterAgents = agents.filter(item => (
    ((item.status === agentStatus.online) || (item.status === agentStatus.away)
    || item.status >= 100) &&
    chatActiveAgents.indexOf(item.id) < 0
  ));
  return filterAgents.map((item) => {
    let customAway = null;
    if (item.status >= 100) {
      customAway = _.find(customAways, away => away.id === item.status);
    }
    const customAwayName = customAway ? `(${customAway.name})` : '';
    return {
      id: item.id,
      name: `${item.name} ${customAwayName}`,
      status: item.status,
    };
  });
});

export const getAllAvailableAgents = (state, visitor) =>
  getAllAvailableAgentsInternal(
    visitor.get('chatActiveAgents'),
    state.config.settings.agents,
    selectors.getCustomAways(state)
  );

export const getAgentIdsByDepartmentId = (state, departmentId) => {
  const department = _.find(state.config.settings.departments, dep => dep.id === departmentId);
  return department ? department.agentIds : [];
};

export const getAvailableAgentsNumberByDepartmentId = (state, departmentId, visitor) => {
  const agentIdsByDepartmentId = getAgentIdsByDepartmentId(state, departmentId);
  const allAgents = state.config.settings.agents;
  const chatActiveAgents = visitor.get('chatActiveAgents');
  return _.reduce(agentIdsByDepartmentId, (prev, agentId) => {
    if (_.indexOf(chatActiveAgents, agentId) >= 0) return prev;
    const agent = _.find(allAgents, a => a.id === agentId);
    if (!agent) return prev;
    if (agent.status === agentStatus.online) {
      prev.onlineAgentNumber += 1; // eslint-disable-line no-param-reassign
    } else if (agent.status === agentStatus.away || agent.status >= 100) {
      prev.awayAgentNumber += 1; // eslint-disable-line no-param-reassign
    }
    return prev;
  }, {
    onlineAgentNumber: 0,
    awayAgentNumber: 0,
  });
};

export const getIconByCode = (code, desc = 'no description') => {
  switch (code) {
    case agentStatus.online:
      return [icons.statusOnline, '#329fd9', desc];
    case agentStatus.exit:
      return [icons.exit, '#ee4b3d', desc];
    case agentStatus.logout:
      return [icons.logout, '#ee4b3d', desc];
    case agentStatus.offline:
      return [icons.exit, '#888', desc];
    default:
      return [icons.statusAway, '#fe8e14', desc];
  }
};



