import * as _ from 'lodash';
import * as reporter from 'src/utils/reporter';
import Client from '../../store/web/client';
import * as chatAction from '../../constants/enums/chatAction';
import LoginError from './LoginError';
import { errorCode } from '../../constants/web';
import { getUrl } from '../../utils/helper';
import * as config from '../../constants/config';

const removeNullProps = o => Object.keys(o).reduce((res, k) => {
  if (res[k] === null) {
    /* eslint-disable no-param-reassign */
    delete res[k];
  }
  return o;
}, o);

export default async function getSettings({ chatServer, session, siteId }) {
  const commands = [
    chatAction.agentGetDepartments,
    chatAction.agentGetAgents,
    chatAction.agentGetCannedMessages,
    chatAction.agentGetDashboardMessage,
    chatAction.agentGetAutoInvitations,
    chatAction.agentGetCodePlans,
    chatAction.agentGetCustomVariables,
    chatAction.agentGetUnfinishedWrapups,
    chatAction.agentGetCustomAways,
    chatAction.agentGetSegments,
    chatAction.agentGetSecureForms,
    chatAction.agentGetSounds,
    chatAction.agentGetSiteDunningInfo,
    chatAction.agentGetVisitors,
  ].map(type => ({
    visitorId: -1,
    type,
    message: '',
  }));

  commands.push({
    type: chatAction.agentSetTimezone,
    message: `${(new Date()).getTimezoneOffset()}`,
  });

  _.each(commands, (cmd, i) => { cmd.id = i + 2; });

  const req = {
    commands,
    session,
    siteId: 0,
    localChats: [],
    agentId: 0,
  };
  let livechathandler3 = `${chatServer}/livechathandler3.ashx`;
  livechathandler3 = getUrl(livechathandler3, config.isSSL);
  const client = new Client(livechathandler3);
  let resp = null;
  try {
    resp = await client.send(req);
  } catch (err) {
    /**
     * @todo this try-catch seems useless if just log the error and re-throws it.
     * disabled re-throw functionality, removed log, and record error instead
     */
    // throw err;
    reporter.error(err, 'settings', 65, 0);
  }

  if (resp && resp.errorCode === errorCode.noerror) {
    const visitorsResp = resp.commands.find(cmd => cmd.visitors !== null);
    const visitors = visitorsResp ? visitorsResp.visitors : {};
    const settings = Object.assign(...resp.commands.map(cmd => removeNullProps(cmd.extend || {})));
    return { settings, visitors };
  }
  const error = {
    errorCode: resp ? resp.errorCode : errorCode.serverException,
    errorString: resp ? resp.errorString : 'Server Exception',
    siteId,
    loginResponse: null,
  };
  throw new LoginError('chatServerError', error.errorCode, error);
}




