import * as selectors from '../reducers/selectors';
import * as common from '../utils/common';
import { agentCheckSFAutoIntegrationResult } from '../actions/salesforce';


export default (store) => {
  let previousState = store.getState();

  const clcChatRemoved = (state) => {
    const previousChats = selectors.getChats(previousState);
    const chats = selectors.getChats(state);
    if (previousChats !== chats) {
      const enableSalesforce = selectors.ifEnableSFIntegration(state);
      if (!enableSalesforce) return;

      const ifManually = selectors.getIfSalesforceManually(state);
      const agentId = selectors.getCurrentAgentId(state);
      previousChats.filter(chat => chats.get(chat.get('id')) === undefined)
        .forEach((chat, id) => {
          const visitor = selectors.getVisitorByChatId(previousState, id);
          if (!selectors.checkIfWaitingVisitorAcceptInvitationFromMe(visitor, agentId) &&
            common.isAgentInInvolvedList(visitor, agentId) &&
            (!ifManually || visitor.get('salesforcePurposeInManualMode') !== 0)) {
            store.dispatch(agentCheckSFAutoIntegrationResult(visitor.get('id')));
          }
        });
    }
  };

  const unsubscribe = store.subscribe(() => {
    const state = store.getState();
    clcChatRemoved(state);
    previousState = state;
  });

  return () => {
    unsubscribe();
  };
};



