import _ from 'lodash';
import * as selectors from '../../reducers/selectors';
import { agentStatus, allAvailableId } from '../../constants/enums';
import memoryStore from '../../services/memoryStore';
import * as memoryStoreKey from '../../constants/memoryStore';
import { format } from '../../utils/time';

let doCheckUnreadMsg = false;
export const updateCheckUnreadMsgFlag = (flag) => {
  doCheckUnreadMsg = flag;
};
const checkIfAgentAvailable = (agent, ifDepartmentEnabled) => {
  if (!ifDepartmentEnabled) {
    return true;
  } else if (agent.status !== agentStatus.offline) {
    return true;
  }
  return false;
};

const getAgentDescription = (state, agent) => {
  switch (agent.status) {
    case agentStatus.online:
      return agent.chatCount > 0 ? `(${agent.chatCount} chats)` : '';
    case agentStatus.offline:
      return '';
    default:
      {
        const customAway = selectors.getCustomAwayById(state, agent.status);
        let awayName = 'Away';
        if (!_.isUndefined(customAway)) {
          awayName = customAway.name;
        }
        return agent.chatCount > 0 ?
              `(${agent.chatCount} chats)(${awayName}; ${format(agent.lastStatusChangeTime, 'HH:mm')})` :
              `(${awayName}; ${format(agent.lastStatusChangeTime, 'HH:mm')})`;
      }
  }
};

const convertUIModel = (state, agent, dptId) => {
  const [type, color] = selectors.getIconByCode(agent.status);
  const unreadNumber = _.isObject(selectors.getAgentChat(state, agent.id)) ?
                    selectors.getAgentUnreadMessages(state, agent.id) :
                    0;
  return {
    id: `${dptId}_${agent.id}`,
    status: agent.status,
    icon: { type, color },
    name: agent.name,
    description: getAgentDescription(state, agent),
    unreadMessage: unreadNumber,
    selected: false,
    originalId: agent.id,
  };
};

const compare = (a, b) => {
  if (a < b) return -1;
  if (a > b) return 1;
  return 0;
};

const getAllAvailableAgents = (state, agents, selectedId, ifDepartmentEnabled) => {
  const availableAgents = [];
  if (!_.isArray(agents)) {
    return availableAgents;
  }
  _.each(agents, (ag) => {
    if (!checkIfAgentAvailable(ag, ifDepartmentEnabled)) return;
    const uAgent = convertUIModel(state, ag, allAvailableId);
    uAgent.selected = uAgent.id === selectedId;
    availableAgents.push(uAgent);
  });
  return availableAgents.sort((a, b) => {
    const result = compare(a.status, b.status);
    if (result === 0) return compare(a.name, b.name);
    return result;
  });
};


const getAgentsInfo = (state, agents, dptId, selectedId, ids = [], ifConvert = false) => {
  if (!_.isArray(ids)) return [];
  let dptAgents = agents;
  if (ids.length > 0) {
    dptAgents = agents.filter(agent => ids.indexOf(agent.id) >= 0);
  } else if (ifConvert) {
    dptAgents = [];
  }
  let online = 0;
  let away = 0;
  const opened =
    !_.isUndefined(dptAgents.find(val =>
                  selectors.getAgentUnreadMessages(state,
                            ifConvert ? val.id : val.originalId) > 0));

  dptAgents = dptAgents.map((agt) => {
    if (agt.status === agentStatus.online) {
      online += 1;
    } else if (agt.status !== agentStatus.offline) {
      away += 1;
    }
    let cAgent = Object.assign({}, agt);

    if (ifConvert) {
      cAgent = convertUIModel(state, agt, dptId);
    }
    cAgent.selected = cAgent.id === selectedId;
    return cAgent;
  });
  return { dptAgents, online, away, opened };
};

function addOpenedDepartmentToCache(departmentId, state) {
  const loginAgentId = selectors.getCurrentAgentId(state);
  const dptIds = memoryStore.get(memoryStoreKey.openedDepartment, loginAgentId, []);
  const index = dptIds.indexOf(departmentId);
  if (index < 0) {
    dptIds.push(departmentId);
    memoryStore.set(memoryStoreKey.openedDepartment, dptIds, loginAgentId);
  }
}


/* agents it is available agents */
const getNoDepartment = (state, agents, openedIds = [], selectedId) => {
  if (!_.isArray(agents) || agents.length === 0) return null;
  const noDepartment = {
    id: allAvailableId,
    name: '',
    selected: false,
    opened: false,
    agents: [],
  };
  noDepartment.selected = selectedId === noDepartment.id;
  noDepartment.opened = openedIds.indexOf(noDepartment.id) >= 0;
  const { dptAgents, online, away, opened } =
    getAgentsInfo(state, agents, noDepartment.id, selectedId);
  if (doCheckUnreadMsg && opened) {
    noDepartment.opened = opened;
    addOpenedDepartmentToCache(noDepartment.id, state);
  }
  noDepartment.agents = dptAgents;
  noDepartment.name = 'All Available';
  noDepartment.onlineNum = online;
  noDepartment.awayNum = away;
  return noDepartment;
};

const convertToAwayStatus = (status) => {
  if (status !== agentStatus.online &&
      status !== agentStatus.offline) {
    return agentStatus.away;
  }
  return status;
};

const generateDepartment = (state, agents, openedIds = [], currentDepartment, selectedId) => {
  if (!_.isArray(agents) || agents.length === 0) return null;
  const temp = {
    id: -currentDepartment.id,
    name: '',
    selected: false,
    opened: false,
    agents: [],
  };
  temp.selected = selectedId === temp.id;
  temp.opened = openedIds.indexOf(temp.id) >= 0;
  const { dptAgents, online, away, opened } = getAgentsInfo(state,
                                              agents,
                                              temp.id,
                                              selectedId,
                                              currentDepartment.agentIds,
                                              true);
  if (doCheckUnreadMsg && opened) {
    temp.opened = opened;
    addOpenedDepartmentToCache(temp.id, state);
  }
  temp.name = `${currentDepartment.name}`;
  temp.onlineNum = online;
  temp.awayNum = away;
  temp.agents = dptAgents.sort((a, b) => {
    const result = compare(convertToAwayStatus(a.status), convertToAwayStatus(b.status));
    if (result === 0) return compare(a.name, b.name);
    return result;
  });
  return temp;
};

/* eslint-disable no-param-reassign */
const initData = (state) => {
  const props = {
    agentData: [],
    isDepartment: false,
  };

  const agents = selectors.getAgents(state);
  const departments = selectors.getDepartments(state);
  const openedItems = selectors.getAgentsOpenedItems(state);
  const selectedId = selectors.getSelectedIdForAgents(state);

  const enableDepartment = selectors.getifDepartmentEnabled(state);
  const availableAgents = getAllAvailableAgents(state, agents, selectedId, enableDepartment);
  if (enableDepartment && _.isArray(departments) && departments.length > 0) {
    props.isDepartment = true;
    if (availableAgents.length > 0) {
      props.agentData.push(getNoDepartment(state, availableAgents, openedItems, selectedId));
    }
    _.each(departments, (dpt) => {
      props.agentData.push(generateDepartment(state, agents, openedItems, dpt, selectedId));
    });
  } else {
    props.agentData = availableAgents;
  }
  return props;
};
export default initData;



