const getInitialValues = () => {
  if (process.env.IS_WEB_APP) {
    /** @todo use redux initial value param instead of window.xxx function */
    const email = typeof getAgentEmail === 'undefined' ? '' : window.getAgentEmail();
    const agentId = typeof getAgentId === 'undefined' ? '' : window.getAgentId();
    const token = typeof getToken === 'undefined' ? '' : window.getToken();
    const siteId = typeof getSiteId === 'undefined' ? '' : window.getSiteId();
    if (email && token && agentId && siteId) {
      return {
        status: enums.agentStatus.online,
        rememberMe: false,
        email,
        password: '',
        agentId,
        token,
        siteId,
        autoWebLogin: true,
      };
    }
  }
  const cache = getObject(localStorageKeys.loginForm);
  const rememberMe = getBool(localStorageKeys.rememberMe) || false;
  let cachePassword = '';
  if (rememberMe && cache && cache.password) {
    cachePassword = simpleEncrypt.decrypt(cache.password);
  }
  return {
    status: rememberMe && cache ? cache.status : enums.agentStatus.online,
    rememberMe,
    email: cache ? cache.email : '',
    password: cachePassword,
  };
};

