import * as _ from 'lodash';

// eslint-disable-next-line
const emailPattern = /^[A-Za-z0-9!#$%&'*+=?^_`{|}~-]+(?:\.[A-Za-z0-9!#$%&'*+=?^_`{|}~-]+)*@(?:[A-Za-z0-9](?:[A-Za-z0-9-]*[A-Za-z0-9])?\.)+[A-Za-z0-9](?:[A-Za-z0-9-]*[A-Za-z0-9])?$/;
export const isEmail = input => emailPattern.test(input);

const passwordShift = [1, 2, 4, 6, 8, 10, 12, 14, 15, 17, 19, 21, 23, 25, 27, 28];
export const passwordConvert = (password) => {
  if (password.length > 16) throw new Error('Password is too large');
  return Array.from(password)
    .map((char, i) => char.charCodeAt(0) + passwordShift[i])
    .join('-');
};

export const joincolon = (...args) => args.join(':');

const urlPattern = /(?:www\.|(?:https?|ftp|news|rtsp|mss):\/\/)[\w$%][\w_!~*'();?:@&=+$,%#-./]*[\w_!~*'();?:@&=+$%#-]\/?/ig; // eslint-disable-line max-len
export function handleLinks(input, callback) {
  const contents = input.split(urlPattern);
  const urls = input.match(urlPattern);
  const ret = new Array((contents.length * 2) - 1);
  for (let i = 0; i < contents.length; i += 1) {
    ret[i * 2] = contents[i];
    if (i !== contents.length - 1) ret[(i * 2) + 1] = callback(urls[i], i);
  }
  return ret;
}

const imageSufix = ['.png', '.jpg', '.gif', '.jpeg'];
export const isImageSuffix = (url) => {
  const queryIndex = url.lastIndexOf('?');
  const pureLink = (queryIndex >= 0 ? url.substr(0, queryIndex) : url).toLowerCase();
  return imageSufix.some(sufix => _.endsWith(pureLink, sufix));
};

export const isNullOrEmpty = str =>
  (!_.isString(str)) || _.isEmpty(str);

export const isEmptyMessage = (str) => {
  if (_.isEmpty(str)) return true;
  const regexPattern = /^\s+$/;
  return regexPattern.test(str);
};



