import * as _ from 'lodash';
import flow from 'lodash/fp/flow';
import map from 'lodash/fp/map';
import filter from 'lodash/fp/filter';
import * as config from 'src/config';
import { webError, errorCode } from '../constants/web';
import { agentStatus } from '../constants/enums';
import * as fieldType from '../constants/enums/fieldType';

let cachedConfig = config;

export function getReducerHandler(action, mapping) {
  const handler = mapping[action.type];
  if (handler == null) return handler;

  if (action.type === webError) {
    return handler[action.payload.errorCode];
  }
  return handler;
}

export function log(...args) {
  /* eslint-disable no-console */
  console.log(...args);
  return args[args.length - 1];
}

export function throwRequired(field) {
  if (process.env.NODE_ENV !== 'production') throw new Error(`${field} is required`);
}

export function mapObject(xs, fn) {
  if (_.isArray(xs)) {
    return xs.map(obj => mapObject(obj, fn));
  } else if (_.isObject(xs)) {
    return Object.keys(xs).reduce((newxs, key) => {
      newxs[key] = mapObject(xs[key], fn); // eslint-disable-line no-param-reassign
      return newxs;
    }, {});
  }
  return fn(xs);
}

const getNotEqualKeys = (props, nextProps) => (
  !props ? [] : Object.keys(props).reduce((prev, key) => {
    const value = props[key];
    const nextValue = nextProps[key];
    if (value && value.toJS) {
      // duck type of Immutable
      if (value !== nextValue) return prev.concat(key);
      return prev;
    }
    if (value instanceof Date) {
      if (JSON.stringify(value) !== JSON.stringify(nextValue)) return prev.concat(key);
      return prev;
    }
    if (typeof value === 'object') {
      return prev.concat(getNotEqualKeys(value, nextValue));
    }
    if (value !== nextValue) return prev.concat(key);
    return prev;
  }, [])
);

/**
 * High order function to create helper function for `shouldComponentUpdate`.
 *
 * @param {string} name - Component name. Use to output useful info in development mode.
 * @param {string[]} propKeys - Keys of props that should trigger component update if values are
 * different in two render cycle.
 * @param {string[]} stateKeys - Keys of states that should trigger component update if values are
 * different in two render cycle.
 * @return {function(object,object,object,object):boolean} Helper function to determine whether
 * component should be updated.
 */
export const shouldComponentUpdateGen = (name, propKeys = [], stateKeys = []) =>
/**
 * @param {object} props - Current props.
 * @param {object} nextProps - Props in next cycle.
 * @param {?object} state - Current state (optional).
 * @param {?object} nextState - State in next cycle.
 * @return {boolean} Whether component should update
 */
(props, nextProps, state = { }, nextState = { }) => {
  const shouldComponentUpdate = propKeys.some((key) => {
    let shouldUpdate = false;
    if (process.env.NODE_ENV !== 'production' && key.indexOf('.') > 0) {
      console.error('[ERROR]: _.get() does not support path with ".", please modify!');
    }
    const nextValue = _.get(nextProps, key);
    const value = _.get(props, key);
    if (nextValue !== value) {
      shouldUpdate = true;
    } else if (nextValue instanceof Date) {
      if (JSON.stringify(nextValue) !== JSON.stringify(value)) {
        shouldUpdate = true;
      }
    }
    if (process.env.NODE_ENV !== 'production' && cachedConfig.logShouldComponentUpdate) {
      if (shouldUpdate) {
        console.groupCollapsed(`Component ${name} should update because of prop ${key}`);
        console.log('previous: ', value && value.toJS ? value.toJS() : value);
        console.log('current: ', nextValue && nextValue.toJS ? nextValue.toJS() : nextValue);
        console.groupEnd();
      }
    }
    return shouldUpdate;
  }) ||
  stateKeys.some((key) => {
    let shouldUpdate = false;
    const nextValue = _.get(nextState, key);
    const value = _.get(state, key);
    if (nextValue !== value) {
      shouldUpdate = true;
    } else if (nextValue instanceof Date) {
      if (JSON.stringify(nextValue) !== JSON.stringify(value)) {
        shouldUpdate = true;
      }
    }
    if (process.env.NODE_ENV !== 'production' && cachedConfig.logShouldComponentUpdate) {
      if (shouldUpdate) {
        console.groupCollapsed(`Component ${name} should update because of state ${key}`);
        console.log('previous: ', value && value.toJS ? value.toJS() : value);
        console.log('current: ', nextValue && nextValue.toJS ? nextValue.toJS() : nextValue);
        console.groupEnd();
      }
    }
    return shouldUpdate;
  });

  if (process.env.NODE_ENV !== 'production') {
    if (cachedConfig.logShouldComponentUpdatePossibleIssue && !shouldComponentUpdate) {
      const notEqualPropKeys = getNotEqualKeys(props, nextProps);
      if (notEqualPropKeys.length > 0) {
        console.groupCollapsed(`Component ${name} should not update but has following prop changes:`);
        notEqualPropKeys.forEach((key) => {
          const value = props[key];
          const nextValue = nextProps[key];
          console.group(`Prop with key: ${key}`);
          console.log('previous: ', value && value.toJS ? value.toJS() : value);
          console.log('current: ', nextValue && nextValue.toJS ? nextValue.toJS() : nextValue);
          console.groupEnd();
        });
        console.groupEnd();
      }
      const notEqualStateKeys = getNotEqualKeys(state, nextState);
      if (notEqualStateKeys.length > 0) {
        console.groupCollapsed(`Component ${name} should not update but has following state changes:`);
        notEqualStateKeys.forEach((key) => {
          const value = state[key];
          const nextValue = nextState[key];
          console.group(`State with key: ${key}`);
          console.log('previous: ', value && value.toJS ? value.toJS() : value);
          console.log('current: ', nextValue && nextValue.toJS ? nextValue.toJS() : nextValue);
          console.groupEnd();
        });
        console.groupEnd();
      }
    }
  }

  return shouldComponentUpdate;
};

export const formatAgentChatUploadUrl = (uploadUrl, sessionId, siteId, privateMessage = 1, guid) =>
   `${uploadUrl}?sessionId=${sessionId}&privateMessage=${privateMessage}&siteId=${siteId}&guid=${guid}`;

export const formatAgentChatDownloadUrl =
  (downloadUrl, sessionId, siteId, privateMessage = 1, guid, name) =>
   `${downloadUrl}?sessionId=${sessionId}&privateMessage=${privateMessage}&siteId=${siteId}&guid=${guid}&displayName=${name}&maxWidth=400&maxHeight=400`;

export const formatVisitorChatUploadUrl = (uploadUrl, sessionId, visitorId, siteId) =>
   `${uploadUrl}?sessionId=${sessionId}&visitorId=${visitorId}&siteId=${siteId}`;

const filterCustomAway = filter(item => item.ifVisible && !item.ifSystem);
const mapCustomAway = map(item => ({ code: item.id, desc: item.name }));
const customAwayFirstPart = [
  {
    code: agentStatus.online, desc: 'Online',
  },
  {
    code: agentStatus.away, desc: 'Away',
  },
];
const customAwaySecondPart = [
  {
    code: agentStatus.logout, desc: 'Logout',
  },
  {
    code: agentStatus.exit, desc: 'Exit',
  },
];
export const formatAgentStatus = customAway => customAwayFirstPart
.concat(
  flow(
    filterCustomAway,
    mapCustomAway
  )(customAway)
).concat(customAwaySecondPart);

export const jsonString2Object = (str) => {
  const JSONObj = JSON.parse(str);
  return (typeof JSONObj === 'object') ? JSONObj : str;
};

export const getErrorMessage = (code, defaultDesc) => {
  if (code === errorCode.connectionError) {
    return 'Internet connection is unstable or unavailable. Please check your Internet connection.';
  }
  return defaultDesc;
};

export const reduceArray = array => array.reduce((accumulatedMap, item) => {
  const obj = {};
  obj[item.name] = item.value;
  return Object.assign(accumulatedMap, obj);
}, {});

export const getInitValues = (fields, fieldsValue, ifEnableAdvancedCategoryMode = false) =>
  reduceArray(fields.map((field) => {
    const name = `${field.displayText}_${field.id}`;
    let v;
    if (fieldsValue && fieldsValue.length > 0) {
      const t = _.find(fieldsValue, item => item.id === field.id);
      if (t) {
        v = t.value;
      }
    }
    if (field.type === fieldType.CheckBox) {
      return { name, value: typeof v === 'boolean' ? v : (v === 'true') };
    }
    if (field.type === fieldType.DropDownList || field.type === fieldType.Radio) {
      const opt = _.find(field.options, option => option.displayText === v);
      return { name, value: opt ? opt.id.toString() : '' };
    }
    if (field.type === fieldType.WrapupCategory) {
      if (ifEnableAdvancedCategoryMode) {
        return { name: field.id, value: [] };
      }
      return { name, value: -1 };
    }
    if (field.type === fieldType.CheckBoxList) {
      return { name, value: v ? field.options.filter(option => _.indexOf(v.split('⊙'), option.displayText) !== -1).map(option => option.id.toString()).join('⊙') : '' };
    }
    return { name, value: typeof v === 'undefined' ? '' : v };
  }));

export const htmlEncode = (str) => {
  let s = '';
  if (str.length === 0) return '';
  s = str.replace(/&/g, '&amp;');
  s = s.replace(/</g, '&lt;');
  s = s.replace(/>/g, '&gt;');
  s = s.replace(/ /g, '&nbsp;');
  s = s.replace(/'/g, '&#39;');
  s = s.replace(/"/g, '&quot;');
  return s;
};

export const defaultValidate = (values, fields, ifEnableAdvancedCategoryMode = false) => {
  if (values) {
    return reduceArray(fields
    .filter((field) => {
      const name = `${field.displayText}_${field.id}`;
      if (field.type === fieldType.DropDownList) {
        return field.ifRequired && !values[name];
      }
      if (field.type === fieldType.WrapupCategory) {
        let val = values[name];
        if (ifEnableAdvancedCategoryMode) {
          val = (_.isArray(values[field.id]) && values[field.id].length) > 0 ?
                values[field.id] : -1;
        }
        return field.ifRequired && val === -1;
      }
      return field.type !== fieldType.CheckBox && field.ifRequired && !values[name];
    })
    .map(field => ({ name: `${field.displayText}_${field.id}`, value: 'Required' })));
  }
  return {};
};
export const getUrl = (url, isSSL = true) => {
  if (isSSL) {
    if (url.indexOf('http://') >= 0) {
      return _.replace(url, 'http://', 'https://');
    }
    if (url.indexOf('https://') >= 0) {
      return url;
    }
    return `https://${url}`;
  }
  if (url.indexOf('http://') >= 0) {
    return url;
  }
  if (url.indexOf('https://') >= 0) {
    return _.replace(url, 'https://', 'http://');
  }
  return `http://${url}`;
};

if (module.hot) {
  module.hot.accept('../config', () => {
    cachedConfig = require('../config'); // eslint-disable-line
  });
}

/* cookies helper */
export const getCookie = (cName) => {
  if (document.cookie.length > 0) {
    let cStart = document.cookie.indexOf(`${cName}=`);
    if (cStart !== -1) {
      cStart = cStart + cName.length + 1;
      let cEnd = document.cookie.indexOf(';', cStart);
      if (cEnd === -1) cEnd = document.cookie.length;
      return unescape(document.cookie.substring(cStart, cEnd));
    }
  }
  return '';
};

export const setCookie = (cName, value, expiredays) => {
  const exdate = new Date();
  exdate.setDate(exdate.getDate() + expiredays);
  document.cookie = `${cName}=${escape(value)
}${(expiredays == null) ? '' : `; expires=${exdate.toGMTString()}`}`;
};
/* cookies helper end*/

export const formatPromptNumber = (number) => {
  if (!_.isNumber(number)) return number;
  if (number >= 100) {
    return '99+';
  }
  return number.toFixed(0);
};

export const isChildWindow = () => !!window.ifChildWindow;



