import React from 'react';

/**
 * Base Component. Recommend not to use it directly.
 */

const Icon = props => (
  <i {...props} />
);

Icon.propTypes = {
  /** Class name, used to control icon display */
  className: React.PropTypes.string.isRequired,
};

export default Icon;



