import * as messageType from '../../constants/enums/messageType';

export const getChats = state => state.chats;
export const getChat = (state, chatId) => state.chats.get(chatId);
export const getVisitorIdByChatId = (state, chatId) => getChat(state, chatId).get('visitorId');
export const getChatMaxMessageId = (state, chatId) => getChat(state, chatId).get('maxMessageId');
export const getChatMessages = (state, chatId) => getChat(state, chatId).get('messages');
export const getChatEditorState = (state, chatId) => state.editor[chatId];
export const getTypingMessage = (state, chatId) => getChat(state, chatId).get('typingMessage');
export const getChatScrollTop = (state, chatId) => getChat(state, chatId).get('scrollTop');
export const getUnreadMessages = (state, chatId) => getChat(state, chatId).get('unreadMessages');
export const getLastRepliedTime = (state, chatId) => getChat(state, chatId).get('lastRepliedTime');
export const getChatIsPinned = (state, chatId) => getChat(state, chatId).get('isPinned');
export const getWrapupSubmitted = (state, chatId) => getChat(state, chatId).get('wrapupSubmitted');
export const getIfShowWrapupButton = (state, chatId) => getChat(state, chatId).get('ifShowWrapupButton');
export const getIsUnfinishedWrapup = (state, chatId) => getChat(state, chatId).get('isUnfinishedWrapup');
export const getRecoverMessages = (state, chatId) => getChat(state, chatId).get('sourceMessages');
export const getIfChatEnableTransaltion = (state, chatId) => getChat(state, chatId).get('enableTranslation');
export const getVisitorMessageCount = (state, chatId) =>
  getChatMessages(state, chatId).reduce((count, messages) =>
    count + (messages[0].type === messageType.visitor ? messages.length : 0), 0);
export const getVisitorLanguage = (state, chatId) => getChat(state, chatId).get('translationLanguage');

export const ifPopTranslation = (state, chatId) => getChat(state, chatId).get('ifPopTranslation');
export const getErrorMessageForChat = (state, chatId) => getChat(state, chatId).get('errorMessageForChat');
export const getAudioVideoChatObj = (state, chatId) => getChat(state, chatId).get('p2pChat');
export const getAudioVideoStatus = (state, chatId) => getChat(state, chatId).get('audioVideoStatus');
export const getUploadedFiles = (state, chatId) => getChat(state, chatId).get('files');



