import React from 'react';
import classnames from 'classnames';
import imagesStyle from '../Icons/Icon.css';
import style from './Image.css';
import Link from '../Link/Link';
import * as loginConstants from '../../constants/login';

/**
 * Component Description
 */

const Logo = props => (
  <Link
    newWindow
    href={loginConstants.liveChatUrl}
    className={classnames(style.logo, props.className)}
  >
    <span
      className={props.showText ?
        classnames(style.logo_imageWrapper, style.logo_seperator, imagesStyle.logo)
        : classnames(style.logo_imageWrapper, imagesStyle.logo)}
    />
    <span className={props.showText ? style.logo_text : style.none}>
      100% Communication, 100% Success
    </span>
  </Link>
);

Logo.propTypes = {
  className: React.PropTypes.string,
  showText: React.PropTypes.bool,
};

Logo.defaultProps = {
  showText: false,
};

Logo.examples = {
  DefaultExample: {
    description: 'Default Example',
    component: <Logo />,
  },
  LogoShowText: {
    description: 'Logo ShowText',
    component: <Logo showText />,
  },
};

export default Logo;



