import { webRequest } from '../../constants/web';

/**
 * Param is function to handle accepted actions;
 * Then return a function to accept all actions. This function returns true if accept this action,
 * or false if it should pass longside to reducers.
 *
 * @param {Function} handleAction - Handle accepted actions. No return.
 * @return {Function} Function to accept all actions.
 */
export default handleAction => (action) => {
  if (action.type.indexOf(webRequest) !== 0) return false;

  handleAction(Object.assign({}, action, {
    type: action.type.split('/')[1],
  }));

  return true;
};



