import React from 'react';
import Modal from './Modal';
import ModalStyle from './Modal.css';
import * as enums from '../../constants/enums';
import Icon from '../Icon/Icon';
import iconStyle from '../Icons/Icon.css';

const ModalLoading = props => (
  <Modal
    ifOpen={props.ifOpen}
    shouldCloseOnOverlayClick={false}
    ifShowCloseIcon={false}
    className={ModalStyle.ModalLoading}
  >
    <Icon
      type={enums.icons.loadingBlue}
      className={iconStyle.loadingBlue}
    />
    {props.children}
  </Modal>
);

export default ModalLoading;

ModalLoading.propTypes = {
  className: React.PropTypes.string,
  ifOpen: React.PropTypes.bool.isRequired,
  children: React.PropTypes.oneOf([
    React.PropTypes.element,
    React.PropTypes.arrayOf(React.PropTypes.element),
    React.PropTypes.string,
  ]),
};



